
import Layout from '@/views/vpc/vpc';
import secList from '@/views/vpc/secList';
import secDetail from '@/views/vpc/secDetail';
import secCreate from '@/views/vpc/createSec';

let Router ={
	path:'sec',
	name:'sec',
	component:Layout,
	redirect:'sec/secList',
	children:[
		{
      		path:'secList',
      		name:'secList',
      		component:secList
      	},
      	{
      		path:'secDetail/:secId',
      		name:'secDetail',
      		component:secDetail,
      		meta:{
      			dependOn:'vpcSecSub',
      		}
      	},
      	{
      		path:'secCreate',
      		name:'secCreate',
      		component:secCreate,
      		meta:{
      			aisdeHide:true
      		}
      	},
	]
};

export default Router;



