import i18n from '@/lang'

import Layout from '@/views/Layout'
import EcmDashboard from '@/views/ecm/ecmDashboard'
import EcmList from '@/views/ecm/EcmList'
import EcmDetail from '@/views/ecm/ecmDetail'
import EcmCreate from '@/views/ecm/ecmCreate'
import EcmCreateAfter from '@/views/ecm/EcmCreateAfter'

let Router ={
	path:'/ecm',
	name:'ecm',
	component:Layout,
	redirect:'/ecm/ecmDashboard',
	children:[
		{
      		path:'ecmDashboard',
      		name:'ecmDashboard',
      		component:EcmDashboard,
      	},
      	{
      		path:'ecmList',
      		name:'ecmList',
      		component:EcmList,
      		props: { name: i18n.ecm.ecmPathName, buttonText: i18n.ecm.ecmButtonName }
      	},
      	{
      		path:'ecmDetail/:serverId',
      		name:'ecmDetail',
      		component:EcmDetail,
      		meta:{
      			dependOn:'ecmSub'
      		}
      	},
        {
      		path:'ecmCreate',
      		name:'ecmCreate',
      		component:EcmCreate,
      		meta:{
      			dependOn:'ecmSub',
      			aisdeHide:true
      		}
      	},
        {
            path:'ecmCreateAfter',
            name:'ecmCreateAfter',
            component:EcmCreateAfter,
            meta:{
      			dependOn:'ecmSub'
      		}
        }
	]
};



export default Router;


