
import Layout from '@/views/Layout'
import userInfo from '@/views/user/userInfo'
import editAccount from '@/views/user/editAccount'
import certification from '@/views/user/certification'
import infoAlert from '@/views/user/infoAlert'
import editTemp from '@/views/user/editTemp'

import i18n from '@/lang'

let Router = {
	path:'/user',
	name:'user',
	component:Layout,
	redirect:'/user/userInfo',
	children:[
		{
      		path:'userInfo',
      		name:'userInfo',
      		component:userInfo,
      	},
      	{
      		path:'editAccount',
      		name:'editAccount',
      		component:editAccount,
      	},
      	{
      		path:'certification',
      		name:'certification',
      		component:certification,
      	},
      	{
      		path:'infoAlert',
      		name:'infoAlert',
      		component:infoAlert,
      	},
      	{
      		path:'userInfo/editPwd',
      		name:'editPwd',
      		component:editTemp
      	}
      
	]
};

export default Router;



