<template>
	<div>
		<el-row class="sec-top">
			<el-col :span="20">
				<span class="top-rules">
					<svg-icon icon-class="Egress" class="top-svg"></svg-icon>
					出方向规则：
					<span class="top-rules-num">
						{{sec.EgressNum || 0 }}
					</span>
				</span>
				<span class="top-rules">
					<svg-icon icon-class="Ingress" class="top-svg"></svg-icon>
					入方向规则：
					<span class="top-rules-num">
						{{sec.IngressNum || 0 }}
					</span>
				</span>
			</el-col>
			<el-col :span="4">
				<detail-btn :btnMsg="btnMsg" @deatilBtnClick="handleClick"></detail-btn>
			</el-col>
		</el-row>
		<el-row>
			<ConsoleTable :tableData = "sec.rules" :tableOptions="options" :operationOrNot = true @buttonClick="handleClick"></ConsoleTable>
		</el-row>
	</div>
</template>

<script>
	import DetailBtn from '@/components/DetailBtn';
	import ConsoleTable from '@/components/ConsoleTable';
	import {getAclList,addRule,deleteRule} from '@/api/vpc'
	
	import secAddrules from './secAddrules'
	export default {
		components:{
			DetailBtn,
			ConsoleTable
		},
		props:{
			secDetail:[Object]
		},
		data(){
			return {
				btnMsg:[{
					value:'添加规则',
					fun:()=>{
						return this.addRules()
					}
				},{
					value:'快速添加规则',
					fun:()=>{
						return this.addRules(true)
					}
				}],
				options:[{
					prop:'directionTxt',
					label:'方向',
				},{
					prop:'ethertype',
					label:'类型',
				},{
					prop:'protocolTxt',
					label:'协议',
				},{
					prop:'portRange',
					label:'端口范围/ICMP类型',
				},{
					prop:'remoteIp',
					label:'远端',
				}],
				icmpPortList:[{
					"name": "Any",
					"fromPort": -1,
					"toPort": -1
				}, {
					"name": "Echo",
					"fromPort": 8,
					"toPort": 0
				}, {
					"name": "Echo reply",
					"fromPort": 0,
					"toPort": 0
				}, {
					"name": "Fragment need DF set",
					"fromPort": 3,
					"toPort": 4
				}, {
					"name": "Host redirect",
					"fromPort": 5,
					"toPort": 1
				}, {
					"name": "Host TOS redirect",
					"fromPort": 5,
					"toPort": 3
				}, {
					"name": "Host unreachable",
					"fromPort": 3,
					"toPort": 1
				}, {
					"name": "Information reply",
					"fromPort": 16,
					"toPort": 0
				}, {
					"name": "Information request",
					"fromPort": 15,
					"toPort": 0
				}, {
					"name": "Net redirect",
					"fromPort": 5,
					"toPort": 0
				}, {
					"name": "Net TOS redirect",
					"fromPort": 5,
					"toPort": 2
				}, {
					"name": "Net unreachable",
					"fromPort": 3,
					"toPort": 0
				}, {
					"name": "Parameter problem",
					"fromPort": 12,
					"toPort": 0
				}, {
					"name": "Port unreachable",
					"fromPort": 3,
					"toPort": 3
				}, {
					"name": "Protocol unreachable",
					"fromPort": 3,
					"toPort": 2
				}, {
					"name": "Reassembly timeout",
					"fromPort": 11,
					"toPort": 1
				}, {
					"name": "Source quench",
					"fromPort": 4,
					"toPort": 0
				}, {
					"name": "Source route failed",
					"fromPort": 3,
					"toPort": 5
				}, {
					"name": "Timestamp reply",
					"fromPort": 14,
					"toPort": 0
				}, {
					"name": "Timestamp request",
					"fromPort": 13,
					"toPort": 0
				}, {
					"name": "TTL exceeded",
					"fromPort": 11,
					"toPort": 0
				}],
			}
		},
		methods:{
			handleClick(fun,data){
				fun(data)
			},
			addRules(fast){
				let option = {
					data:{
						title:'增加规则',
						fast:false
					},
					view:secAddrules,
				}
				if(fast){
					option.fast = true;
					option.data.title="快速增加规则";
				}
				this.$alertDialog(option).then(resForm=>{
					this.$message({
						type:"info",
						message:"正在增加规则，请稍后……"
					})
					let params = resForm.data;
					params.security_group_id = this.secDetail.id;
					addRule(params).then(res=>{
						console.log(res);
						this.$message({
							type:'success',
							message:'规则增加成功！'
						})
						this.$emit('refresh');
					}).catch(error=>{
						console.log(error)
					})
				})
				
			},
			filterRules(rule){
				this.addOperation(rule);
				rule.protocolTxt = null === rule.protocol ? "Any" : (rule.protocol + "").toUpperCase()
				rule.portRange = this.getPortal(rule);
				rule.remoteIp = this.getPortRange(rule);
				
			},
			getPortRange(data){
				return data.remote_ip_prefix || this.groupMap[data.remote_group_id] && this.groupMap[data.remote_group_id] + " (" + data.remote_group_id + ")" || data.remote_group_id || "0.0.0.0/0";
			},
			getPortal(data) {
				let min, max, re, pro = data.protocol;
				if (!pro) return "Any";
				min = data.port_range_min, max = data.port_range_max;
				if ("icmp" === pro) {
					if (void 0 === min && void 0 === max) return "Any";
					if (0 === min && 0 === max) return "Echo reply";
					if (max === min) return this.port2type[min + "-" + min];
					return this.port2type[min + "-" + max]
				}
				re = null === max || max === min ? min : min + "-" + max;
				return void 0 !== re ? re : "--"
			},
			deleteSec(rowData){
				let option={
					data:{
						title:'删除规则',
						actionTip:['删除规则可能影响您云服务器的使用，确认要删除该规则吗？'],
						warn:true
					},
				}
				this.$alertDialog(option).then(form=>{
					
					this.$message({
						type:"info",
						message:"正在删除规则，请稍后……"
					})
					deleteRule(rowData.id).then(res=>{
						this.$message({
							type:"success",
							message:"规则删除成功！"
						})
						this.$emit('refresh');
					}).catch(error=>{
						console.log(error)
					})
				})
			},
			addOperation(sec){
				let operation = [{
					value:'删除',
					fun:(data)=>{
						return this.deleteSec(data)
					}
				}];
				
				this.$set(sec,'operation',operation)
			}
		},
		computed:{
			sec(){
				let sec = {
					rules:[],
					EgressNum:"--",
					IngressNum:"--"
				}
				if(this.secDetail){
					sec.rules = this.secDetail.security_group_rules || [];
					sec.EgressNum = sec.rules.length && sec.rules.filter(item=>{
						this.filterRules(item);
						if(item.direction == 'egress'){
							item.directionTxt = "出方向"
							return true
						}
					}).length;
					sec.IngressNum = sec.rules.length && sec.rules.filter(item=>{
						if(item.direction == 'ingress'){
							item.directionTxt = "入方向"
							return true
						}
					}).length;
				}
				
				return sec;
			},
			port2type(){
				let arr = this.icmpPortList,
					reMap = {};
				this._.each(arr, function(item) {
					reMap[item.fromPort + "-" + item.toPort] = item.name
				});
				return reMap;
			},
			groupMap(){
				let params = {
					page:1,
					limit:100
				};
				let groupMap ={};
				getAclList(params).then(res=>{
					let data = res.data.results;
					data.map(item=>{
						groupMap[item.id] = item.name;
					})
					
				})
				return groupMap;
			}
		},
		mounted(){
			
		},
		
	}
</script>

<style lang="scss" scoped="scoped">
	.sec-top{
		padding:20px;
		font-size:12px;
		color:#333333;
		.top-rules{
			margin-right:5px;
			line-height:30px;
			.top-svg{
				font-size:20px;
				margin-right:5px;
			}
			.top-rules-num{
				margin:0 5px;
				font-size:20px;
			}
		}
	}
</style>


