<br /><template>
	<div class="eipContent">
		<RegionList 
			:name ='name' :regionData='regionData'
			:buttonText ='buttonText'
			@create='create' @changeRegion = 'changeRegion'></RegionList>
		<div class="content">
			<div class="content_top">
		        <ConsoleSearch
		          class="eip_search"
		          :statusOnOff=true 
		          :optionsValue ='optionsValue'
		          @searchEvent = 'search'
		          @clear = 'clear'
		          @refreshEvent = 'refresh'
		          :disableData='changeTableData'
		          :PublicOperation='eipPublicOperation' 
		          @PublicOperEvent = 'buttonClick'
		           :SearchNameOptions = 'SearchNameOptions' :SearchNameValue='SearchNameValue'
		        ></ConsoleSearch>
		    </div>
		    <!--<div class="eip_mark">您还可以创建{{eipRemainCount}}个弹性公网IP</div>-->
			<div>
				<ConsoleTable :checkBox=true :eipStatus=true @selectChange = "selectChange"
					:operationOrNot =true :selectOrNot=true :dragable=true
					:tableData = 'eipListData' :tableOptions = 'tableOptions' 
					:OperationEvents='eipOperation' :loading = 'loading' 
					@buttonClick = 'buttonClick' @changeTable="changeTable"
					:Pagination = 'Pagination' @pagingEvent = 'pagingChange'></ConsoleTable>
			</div>
		</div>
	</div>
