      		addOperation(server){
      			let bindingDisable = ['binding','up'];
      			let unbundingDisable = ['unbunding','down'];
      			let upgradeDisable = ['upgrade'];
//    			let ubsubscribeDisable = ['ubsubscribe'];
      			let Operation = [{
        			value:i18n.vpc.binding,
        			id:'binding',
        			fun:(data)=>{
        				return this.binding(data);
        			}
        		},{
        			value:i18n.vpc.unbunding,
        			id:'unbunding',
        			fun:(data)=>{
        				return this.unbunding(data);
        			}
        		},{
        			value:i18n.vpc.upgrade,
        			id:'upgrade',
        			fun:(data)=>{
