//      			value:i18n.common.ubsubscribe,
//      			id:'ubsubscribe',
//      			fun:(data)=>{
//      				return this.ubsubscribe(data);
//      			}
        		}];
      			Operation.map(item=>{
      				('INSTANCE' == server.state || 'PORTFORWARDING' == server.state || 'LOADBALANCER' == server.state || 'UP' == server.state) && bindingDisable.includes(item.id) && (item.disabled = true);
      				('unbinding' == server.state || 'DOWN' == server.state) && unbundingDisable.includes(item.id) && (item.disabled = true);
      				server.expired && bindingDisable.includes(item.id) && (item.disabled = true) && (item.title="当前弹性公网IP已过期，请续订后再执行操作" );
      				server.expired && unbundingDisable.includes(item.id) && (item.disabled = true) && (item.title="当前弹性公网IP已过期，请续订后再执行操作" );
      				server.expired && upgradeDisable.includes(item.id) && (item.disabled = true) && (item.title="当前弹性公网IP已过期，请续订后再执行操作" );
      				server.with_vm && unbundingDisable.includes(item.id) && (item.disabled = true) && (item.title="与云主机一起购买的弹性公网IP，不可卸载" );
//    				server.expired && ubsubscribeDisable.includes(item.id) && (item.disabled = true)
					if(server.state == 'bindings' || server.state == 'unbundings'){
						item.disabled = true
					}
      			});
      			this.$set(server,'operation',Operation)
      		},
      		refreshDetail(id){
      			GetEipDetail(id).then((res)=>{
					let Data = res.data.results[0];
//					this.expiredShow = 0;
					Data.createAt = Data.created_at ? timeService(Data.created_at) : '--';
      				Data.finishAt = Data.expire_date ? timeService(Data.expire_date) : '--';
      				Data.expired = timeExpired(Data.finishAt);
      				if(Data.resource_type){
      					if(Data.resource_type == 'LOADBALANCER'){
      						Data.instanceName = '--';
      						Data.instanceType = '--';
      					}else{
      						Data.instanceName = Data.virtualmachinedisplayname ? Data.virtualmachinedisplayname : Data.virtualmachinename;
      						Data.instanceType = '云服务器';
      					}
      				}else{
      					Data.instanceName = '--';
      					Data.instanceType = '--';
      				}
//    				if(Data.expired){
//    					this.expiredShow++;
//    				}
      				Data.bandwidthNum = parseInt(Data.networkrate);
      				Data.state = Data.resource_type ? Data.resource_type : 'unbinding';
      				let count = 0;
      				this.eipListData.map((item,index) =>{
						count++
						if(item.id == Data.id){
							this.$set(this.eipListData,index,Data)
							return true;
						}
					})
      				this.addOperation(Data);
				}).catch((req)=>{
					console.log(req)
				})
