  					res.id = [];
      				option.data.data[0].map((item)=>{
						res.id.push(item.id)
					})
      				let data = res;
      				renewEip(data).then((res)=>{
	      				console.log(res)
	      				this.$message({
				          	message: '命令下发成功',
				          	type: 'success'
				        });
				        setTimeout(()=>{
				        	this.refresh();
  						},4000)
	      			}).catch((error)=>{
	      				console.log(error);
