      		unbunding(data){
      			console.log('解绑');
      			let tipMsg = '';
      			if(data.resource_type == 'INSTANCE'){
      				tipMsg = '确定要解绑这个IP的静态NAT';
      			}else if(data.resource_type  == 'PORTFORWARDING'){
      				tipMsg = '确定要解绑这个IP绑定的所有端口映射规则吗？'
      			}else if(data.rsroure_type == 'LOADBALANCER'){
      				tipMsg = '解除负载均衡IP将会删除负载均衡？'
      			}else{
      				tipMsg = '确定要解绑该弹性公网IP吗？'
      			}
      			let option = {
      				'data':{
      					'type':'warn',
      					'option':'',
      					'title':'解绑',
      					'actionTip':[tipMsg],
      					'data':'',
      					'warn':true
      				},
      				'view':ConsoleAction,
      				'form':{},
      			};
      			let _this = this;
      			this.$alertDialog(option).then((res)=>{
      				this.loading =true;
					console.log(res);
					deleteEip(data.id).then((res)=>{
	      				console.log(res)
	      				this.loading =false;
	      				this.$message({
				          	message: '弹性公网IP解绑成功',
				          	type: 'success'
				        });
				        data.state = 'unbundings';
						_this.addOperation(data);
				        setTimeout(()=>{
				        	this.refreshDetail(data.id);
  						},4000)
	      			}).catch((error)=>{
	      				this.loading =false;
	      				console.log(error);
//	      				this.$message({
//				          	message: '命令下发失败',
//				          	type: 'error'
//				        });
	      			})
  				})
      		},
      		upgrade(data){
      			console.log('升级');
      			let Data = [data];
      			console.log(Data);
      			let option = {
      				'data':{
      					'option':'',
      					'title':'升级',
      					'actionTip':['弹性公网IP不支持降级操作，升级时请谨慎操作。'],
      					'data':Data
      				},
      				'view':EipUpGrade,
      				'form':{}
      			};
      			this.$alertDialog(option).then((resp)=>{
  					if(!resp.data){
  						resp.data = data.bandwidthNum;
  					}
  					let bandwidth;
  					let _this=this;
  					if(resp.data == data.bandwidthNum){
  						return
  					}else{
