  						upgradeEip(bandwidth,data.id).then((res)=>{
		      				console.log(res)
		      				this.$message({
					          	message: '命令下发成功',
					          	type: 'success'
					        });
					        let count=0;
					        data.bandwidthNum = resp.data;
					        _this.eipListData.map((item,index) =>{
	  							count++
	  							if(item.id == data.id){
	  								_this.$set(_this.eipListData,index,data)
	  								console.log(index,data)
	  								return true;
	  							}
	  						});
		      			}).catch((error)=>{
		      				console.log(error);
//		      				this.$message({
//					          	message: '命令下发失败',
//					          	type: 'error'
//					        });
		      			})
  					}
  				})
      		},
      		ubsubscribe(data){
      			console.log('tuiding');
      			let attachDataOption = [{
        			prop:'ipaddress',
        			label:i18n.vpc.Eip,
        			temp:true,
        			EipIp:true,
        		},{
        			prop:'status',
        			label:i18n.common.status,
        			temp:true,
        			status:true
        		}];
		        let Data = [data];
      			let option = {
      				'data':{
      					'type':'warn',
      					'option':attachDataOption,
      					'title':'退订弹性公网IP',
      					'actionTip':['确定要对以下弹性公网IP进行退订操作吗？','退订公网IP不可恢复，且关联网站备案信息将一并清除，请谨慎操作。'],
      					'data':Data
      				},
      				'view':ConsoleAction,
      				'form':{}
      			};
      			this.$alertDialog(option).then((res)=>{
  					console.log(res);
  					let id = data.id;
  					unsubscribeEip(id).then((res)=>{
	      				console.log(res)
	      				this.$message({
				          	message: '命令下发成功',
				          	type: 'success'
				        });
				        this.refresh();
	      			}).catch((error)=>{
	      				console.log(error);
//	      				this.$message({
//				          	message: '命令下发失败',
//				          	type: 'error'
//				        });
	      			})
  					
  				})
      		},
      		changeRegion(uuid){
      			service.cancle('CancelToken');
      			this.regionId = uuid;
      			this.refresh();
//    			this.GetRegionNums()
      			//this.queryQuotas();
//    			this.getEipList();
      		},
      		getRegion(){
      			let timer1;
      			if(getRegionId()){
      				clearTimeout(timer1)
					this.changeRegion();
				}else{
					let _this = this;
					timer1 = setTimeout(function(){
						_this.getRegion()
					},300)
				}
      		},
      		filterData(data,action){
				/*根据action 过滤数据*/
				let _this = this;
				_this.unRenewList = [];
				data = data.filter((item)=>{
					switch(action){
						case 'renew':
						if(item.with_vm){
							_this.unRenewList.push(item);
							return false
						}else{
							return item.state = item.resource_type ? item.resource_type : 'unbinding';
						}
						break;
						case 'unsubscribe':
						if(item.with_vm){
							return false
						}else{
							return item.state = item.resource_type ? item.resource_type : 'unbinding';
						}
						break;
						default:
						if(item.with_vm){
							return false
						}else{
							return true
						}
					}
					
				});
				return data
			},
      		changeTable(val){
				console.log(val)
				this.changeTableData = val;
				let RunState = this._.find(this.changeTableData, { 'state': "Running"});
//				let shutState = this._.find(this.changeTableData, { 'state': "Stopped"});
				let count = 0;
				if(this.changeTableData.length > 0){
					this.changeTableData.filter(function(item){
						if(item.state == 'ERROR'){
							count++
						}
					})
//					this.ebsPublicOperation[1].disable =!!RunState;
//					this.ebsPublicOperation[2].disable =!!RunState;
//					this.ebsPublicOperation[0].disable = !!shutState;
					if(count == this.changeTableData.length){
						this.eipPublicOperation[0].disable = false;
					}else{
						this.eipPublicOperation[0].disable =true;
					}
					this.eipPublicOperation[0].title = '';
				}else{
					this.eipPublicOperation[0].disable = false;
					this.eipPublicOperation[0].title = '请选择弹性IP';
				}
			},
      		pagingChange(type,val){
      			if(type == 'num'){
      				this.filter.limit = val;
      			}else if(type == 'page'){
      				this.filter.page = val;
      			}
      			this.loading =true;
      			this.getEipList();
      		},
      		getDetail(id){
      			let _this = this;
      			let timer;
      			if(this.detroyAutoRefresh) return

      			GetEipDetail(id).then((res)=>{
					let data = res.data.results;
					let Data = data.length && data[0] || item;
//					this.expiredShow = 0;
					Data.createAt = Data.created_at ? timeService(Data.created_at) : '--';
      				Data.finishAt = Data.expire_date ? timeService(Data.expire_date) : '--';
      				Data.expired = timeExpired(Data.finishAt);
      				if(Data.resource_type){
      					if(Data.resource_type == 'LOADBALANCER'){
      						Data.instanceName = '--';
      						Data.instanceType = '--';
      					}else{
      						Data.instanceName = Data.virtualmachinedisplayname ? Data.virtualmachinedisplayname : Data.virtualmachinename;
      						Data.instanceType = '云服务器';
      					}
      				}else{
      					Data.instanceName = '--';
      					Data.instanceType = '--';
      				}
//    				if(Data.expired){
//    					this.expiredShow++;
//    				}
      				Data.bandwidthNum = parseInt(Data.networkrate);
      				Data.state = Data.resource_type ? Data.resource_type : 'unbinding';
      				let count = 0;
      				this.eipListData.map((item,index) =>{
						count++
						if(item.id == Data.id){
							this.$set(this.eipListData,index,Data)
							return true;
						}
					})
      				this.addOperation(Data);
      				if(Data.no_time && Data.no_time == '1'){
      					setTimeout(()=>{
							_this.getDetail(Data.id)
						},5000)
      				}else{
      					this._.remove(this.noTimeList,(item)=>{
							return item.id == Data.id;
						})
      				}
				}).catch((req)=>{
					console.log(req)
				})
      		},
      		autoRefresh(Ids){
      			if(Ids){
      				if(typeof(jobIds)=='string'){
      					
      					this.getDetail(jobIds)
      					
	      			}else if(Array.isArray(Ids)){
	      				Ids.map(item =>{
	      					console.log(item)
	      					item.id && this.getDetail(item.id)
	      				})
	      			}else{
	      				console.log(jobIds)
	      			}
      			}
      		},
      		getEipList(){
      			let params = this.filter;
      			this.loading =true;
      			this.noTimeList = [];
      			getEipList(params).then((res)=>{
//      				console.log(res)
      				let servers = res.data.results.filter(function(item){
      					return	!item.issourcenat
      				});
      				let totalCount = res.data.total_count;
      				this.filterServers(servers);
      				this.eipListData = servers;
      				if(this.noTimeList.length > 0){
      					this.autoRefresh(this.noTimeList);
      				}
      				this.totalCount = totalCount;
      				this.Pagination.total = totalCount;
      				if(totalCount > this.Pagination.pagingData[0]){
      					this.Pagination.show = true;
      				}
      				this.loading=false
      			}).catch((error)=>{
      				if(error.message == 'CancelToken'){
      					
      				}else{
      					this.loading=false
      				}
      				console.log(error)
      			})
      		},
      		filterServers(servers){
      			let _this = this;
//    			_this.expiredShow = 0;
      			servers.forEach(function(item,index){
      				item.createAt = item.created_at ? timeService(item.created_at) : '--';
      				item.finishAt = item.expire_date ? timeService(item.expire_date) : '--';
      				if(item.resource_type){
      					if(item.resource_type == 'LOADBALANCER'){
      						item.instanceName = '--';
      						item.instanceType = '--';
      					}else{
      						item.instanceName = item.virtualmachinedisplayname ? item.virtualmachinedisplayname : item.virtualmachinename;
      						item.instanceType = '云服务器';
      					}
      				}else{
      					item.instanceName = '--';
      					item.instanceType = '--';
      				}
      				item.chargeMode = item.cycle_type == 'M' ? '按月' : '按年';
  					item.expired = timeExpired(item.finishAt);
//    				if(item.expired){
//    					_this.expiredShow++;
//    				}
					if(item.no_time && item.no_time == '1'){
						_this.noTimeList.push({id : item.id});
					}
      				item.bandwidthNum = parseInt(item.networkrate);
      				item.state = item.resource_type ? item.resource_type : item.status ? item.status == 'ACTIVE' ?  'UP' : 'DOWN' :'unbinding';
      				_this.addOperation(item);
      			})
      		},
      		search(filter){
      			console.log(filter);
      			this.filter.resource_type = '';
      			this.filter.status = '';
      			if(filter.name == 'id'){
					this.filter.name='';
					this.filter.id=filter.value;
	//				this.filter[filter.name] = filter.value;
					this.filter.keyword = '';
      			}else{
			        this.filter.id='';
	//				this.filter[filter.name] = filter.value;
					this.filter.keyword = filter.value;
      			}
		        this.getEipList()
      		},
      		selectChange(value){
      			console.log(value);
      			this.filter.status = '';
		        this.filter.keyword = '';
		        this.filter.id='';
      			if(value == 'ready'){
      				this.filter.attached = 0;
      				this.filter.resource_type = '';
      			}else if(value == 'INSTANCE'){
      				this.filter.resource_type = 'INSTANCE';
      				this.filter.attached = '';
      			}else if(value == 'PORTFORWARDING'){
      				this.filter.resource_type = 'PORTFORWARDING';
      				this.filter.attached = '';
      			}else if(value == 'LOADBALANCER'){
      				this.filter.resource_type = 'LOADBALANCER';
      				this.filter.attached = '';
      			}else{
      				this.filter.attached = '';
      				this.filter.resource_type = '';
      			}
      			this.filter.page = 1;
				console.log(this.filter);
				this.getEipList()
			},
      		refresh(){
      			this.noTimeList = [];
//		        this.filter.status = '';
//		        this.filter.keyword = '';
		        this.filter.id='';
//		        this.filter.attached = '';
//    			this.filter.resource_type = '';
      			this.GetRegionNums()
		        this.getEipList()
      		},
      		queryQuotas(){
		      	queryQuota().then((res)=>{
		          console.log(res.data.results);
		          let Quota = res.data.results[0];
		          this.eipTotalCount = Quota.iplimit;
		        	this.eipExistCount = Quota.iptotal;
		        	this.eipRemainCount = Quota.ipavailable;
		        }).catch((error)=>{
		          console.log(error)
		        })
		     },
		     GetRegionNums(){
				let params ={
					resource:'floating',
					regionid:getRegionId()
				}
				GetRegionNum(params).then( res => {
					let data = res.data.results[0].Public_IP;
					console.log(data)
					if(data && data.detail){
						this.regionData = data.detail;
					}
					
				})
			},
		},
		mounted(){
			this.getRegion();
//			this.queryQuotas();
		},
		beforeDestroy(){
			console.log('detroy')
			this.detroyAutoRefresh = true;
		}
	}
</script>

<style lang="scss" scoped="scoped">
	.eipContent{
		.content{
		  	.content_top{
		  		background: #ffffff;
		  		padding-top: 30px;
		  		padding-left: 20px;
		  		padding-bottom: 20px;
		  	}
		  	.eip_mark{
		  		background: #FFFFFF;
		  		font-size: 12px;
		  		padding-bottom: 20px;
		  		padding-left: 20px;
		  	}
		}
	}
</style>


