<template>
	<div class="ebs_monitor">
		<RegionList :name ='name' :regionData='regionData' :buttonText ='buttonText' @create='exportData' @changeRegion = 'changeRegion' v-if="!isMonitorInfo"></RegionList>
		<div class="content" v-if="!isMonitorInfo">
			<div class="content_top">
				<el-input placeholder="请根据下拉框选项输入内容" v-model="keyword" class="input-with-select" size="small" style="width:350px" clearable>
				    <el-select v-model="searchType" slot="prepend" placeholder="请选择">
				      <el-option label="名称" value="name"></el-option>
				      <el-option label="ID" value="uuid"></el-option>
				    </el-select>
				    <el-button slot="append" icon="el-icon-search" style="line-height: 2;" @click.stop="getEvsMonitorList"></el-button>
				  </el-input>
				  <el-button icon="el-icon-refresh" style="vertical-align: top;" size="small" @click.stop="getEvsMonitorList"></el-button>
			</div>
			<div>
				<ConsoleTable 
					:tableData='evsMonitorData'
					:tableOptions="tableOptions"
					:operationOrNot=true
					:OperationEvents='ecmOperation'
					@buttonClick="handleClick"
					:loading = 'loading'
					:dragable=true
					:Pagination = 'Pagination'
					@pagingEvent = 'pagingChange'
					></ConsoleTable>
			</div>
		</div>
		<monitorInfo v-if="isMonitorInfo" :itemData="itemData" :infoKeyValue="infoKeyValue" @returnList="returnList"></monitorInfo>
	</div>
</template>

<script>
	import RegionList from '@/components/RegionList'
	import ConsoleTable from '@/components/ConsoleTable'
	import { getEvsMonitor,getZabbixStatus,getEcmMonitor } from '@/api/monitor'
	import { GetRegionNum } from '@/api/ecm'
	import { metric } from './config/monitorConfig'
	import exportTemplate from './dialogViews/export'
	import monitorInfo from './monitorInfo'
	import service from '@/utils/request'
	import i18n from '@/lang'
	
	export default {
		components:{
			RegionList,
			ConsoleTable,
			monitorInfo
		},
		data(){
			return {
				isMonitorInfo: false,
				name: '云硬盘监控',
				buttonText: '导出监控数据',
				loading:false,
				keyword: '',
      			searchType: 'name',
      			regionData: [],
				filter:{
					page_size: 10,
					page: 1,
					keyword: undefined,
					volume_uuid: undefined,
					regionid: undefined
				},
				evsMonitorData:[],
				zabbixQueue: [],
				itemData: {},
      			infoKeyValue: [],
      			disksTypeObj:{
					SATA:'普通IO',SAS:'高IO',SSD:'超高IO'
				},
				Pagination:{
					'pagingData':[10,20,50],
		        	'total':0,
		        	'show':true
				},
        		tableOptions:[{
        			prop:'name',
        			label:i18n.common.name,
        		},{
        			prop:'zabbixStatus',
        			label:'监控状态',
        		},{
        			prop:'id',
        			label: 'ID',
        			width: 400,
        		},{
        			prop:'indexNum',
        			label: '指标个数',
        		}],
        		ecmOperation:[{
        			value: '查看监控图表',
        			id:'info',
        			fun:(data)=>{
        				return this.monitorInfo(data);
        			}
        		}/*,{
        			value: '创建告警规则',
        			id:'create',
        			fun:(data)=>{
        				return this.create(data)
        			}
        		}*/]
			}
		},
		methods:{
			exportData(){
      			var data = {
					defaultData: {
						defaultSer: '云硬盘',
						defaultDim: '磁盘',
						defaultKey: 'disk_read_bytes_rate',
						num_index: 10,
						params: {
							page_size: this.Pagination.total+20,
							page: 1,
							regionid: this.filter.regionid
						}
					},
					title: '导出监控数据',
				};
				var obj = {
					data,
					view: exportTemplate,
					form: []
				};
				this.$alertDialog(obj).then(formData => {
					//console.log(formData);
				});
      		},
      		create(){
      			this.$router.push('/ecm/ecmCreate')
      		},
      		monitorInfo(data){
      			if(!data.id || data.id == ''){
      				this.$message({
						message: '查看失败，ID不存在，请联系管理员',
						type:'warning'
					});
					return;
      			}
      			let loading = this.$loading({
				    lock: true,
				    text: '获取详情中...',
				    spinner: 'el-icon-loading',
				    background: 'rgba(0, 0, 0, 0.7)'
				});
      			getEvsMonitor({
      				page_size: 10,
					page: 1,
					volume_uuid: data.id,
					regionid: this.filter.regionid
      			}).then((res)=>{
      				loading.close();
					loading = null;
      				if(res.data.code == 200 && res.data.results.length > 0){
      					this.itemData = Object.assign({infoName:'云硬盘详情',returnName:'云硬盘监控列表'},data,res.data.results[0],{evs:true});
      					//console.log(this.itemData,'afdasds');
      					this.infoKeyValue = [
		      				{
		      					label: '名称：',
		      					value: this.itemData.name,
		      				},
		      				{
		      					label: '磁盘ID：',
		      					value: this.itemData.id,
		      				},
		      				{
		      					label: '创建时间：',
		      					value: new Date(this.itemData.created).Format("yyyy-MM-dd hh:mm:ss"),
		      				},
		      				{
		      					label: '磁盘属性：',
		      					value: this.itemData.type == 'DATADISK' ? '数据盘' : '系统盘',
		      				},
		      				{
		      					label: '容量(GB)：',
		      					value: this.itemData.size/(1024*1024*1024),
		      				},
		      				{
		      					label: '到期时间：',
		      					value: this.itemData.expire_date ? new Date(this.itemData.expire_date).Format("yyyy-MM-dd hh:mm:ss") : '-----',
		      				},
		      				{
		      					label: '状态：',
		      					value: this.itemData.status,
		      					className: this.itemData.state == 'Ready' ? 'status_normal' : 'status_error'
		      				},
		      				{
		      					label: '磁盘类型：',
		      					value: this.disksTypeObj[this.itemData.disk_types],
		      				},
		      				{
		      					label: '云服务器ID：',
		      					value: (this.itemData.virtualmachineid || '-----'),
		      				},
		      				{
		      					label: '镜像：',
		      					value: '-----',
		      				}
		      			];
		      			if(this.itemData.virtualmachineid){
		      				getEcmMonitor({
		      					page_size: 10,
								page: 1,
								instance_uuid: this.itemData.virtualmachineid,
								regionid: this.filter.regionid
		      				}).then(res => {
		      					if(res.data.code == 200 && res.data.results.length > 0){
		      						this.infoKeyValue[9].value = res.data.results[0].metadata.image_name;
		      					}
		      				});
		      			}
      					this.isMonitorInfo = true;
      				}else{
      					this.$message({
							message: res.msg || res.data.msg || '获取云服务器详情失败',
							type:'error'
						});
      				}
      			}).catch((error)=>{
      				if(loading){
      					loading.close();
						loading = null;
      				}
      			});
      		},
      		returnList(){
      			this.isMonitorInfo = false;
      			this.getEvsMonitorList();
      		},
      		changeRegion(uuid){
      			service.cancle('CancelToken');
      			this.filter.regionid = uuid;
      			this.getEvsMonitorList();
      		},
      		getEvsMonitorList(){
      			this.getRegionNums();
      			let params = this.filter;
      			this.zabbixQueue = [];
      			params.keyword = params.volume_uuid = undefined;
      			if(this.searchType == 'name' && this.keyword != ''){
      				params.keyword = this.keyword;
      			}else if(this.searchType == 'uuid' && this.keyword != ''){
      				params.volume_uuid = this.keyword;
      			}
      			this.loading =true;
      			this.evsMonitorData = [];
      			getEvsMonitor(params).then((res)=>{
      				if(res.data.code == 200){
      					this.evsMonitorData = res.data.results;
      					this.Pagination.total = res.data.total_count;
      					let tempNum = metric.evs.length;
      					this.evsMonitorData.map((item,index,array) => {
      						this.$set(item,'indexNum',tempNum);
      						if(item.zabbix_name&&item.zabbix_name != ''){
      							this.$set(item,'zabbixStatus','获取中...');
	      						this.zabbixQueue.push({
	      							uuid: item.zabbix_name,
	      							id: item.id,
	      							getZabbixStatus: 'notStart',
	      							_zabbixStatus: false,
	      							_index: index
	      						});
      						}else{
      							this.$set(item,'zabbixStatus','获取失败');
      						}
      					});
      					this.openQueue();
      				}else{
      					this.$message({
							message: res.msg || res.data.msg || '获取云服务器监控列表失败',
							type:'error'
						});
      				}
      				this.loading=false;
      			}).catch((error)=>{
      				this.loading=false;
      			})
      		},
      		openQueue(){
      			var len = this.zabbixQueue.length;
      			len = len > 5 ? 5 : len;
      			for(var i=0; i<len; i++){
      				this._getZabbixStatus(this.zabbixQueue[i]);
      			}
      		},
      		queueNext(){
      			for(var i=0; i<this.zabbixQueue.length; i++){
      				if(this.zabbixQueue[i].getZabbixStatus == 'notStart'){
      					this._getZabbixStatus(this.zabbixQueue[i]);
      					return;
      				}
      			}
      		},
      		_getZabbixStatus(item){
      			item.getZabbixStatus = 'loading';
      			getZabbixStatus({uuid:item.uuid}).then(res => {
	      				if(res.data.status == 0){
	      					item.getZabbixStatus = 'success';
	      					item._zabbixStatus = res.data.data.agent_status;
	      					if(item._zabbixStatus){
	      						this.evsMonitorData[item._index].zabbixStatus = '正常';
	      					}else{
	      						this.evsMonitorData[item._index].zabbixStatus = '异常';
	      					}
	      				}else{
	      					item.getZabbixStatus = 'fail';
	      					this.evsMonitorData[item._index].zabbixStatus = '获取失败';
	      				}
	      				this.queueNext();
	      			},error => {
	      				item.getZabbixStatus = 'error';
	      				this.evsMonitorData[item._index].zabbixStatus = '获取失败';
	      				this.queueNext();
	      			});
      		},
      		handleClick(funName,rowData){
      			funName(rowData)
      		},
      		pagingChange(type,val){
      			if(type == 'num'){
      				this.filter.page_size = val;
      			}else if(type == 'page'){
      				this.filter.page = val;
      			}
      			this.getEvsMonitorList();
      		},
      		getRegionNums(){
				GetRegionNum({resource:'volume'}).then( res => {
					if(res.data.code == 200){
						let data = res.data.results[0].Volume;
						if(data && data.attatch_count){
							this.regionData = data.attatch_count;
						}
					}					
				});
			}
		},
		created(){
			this.getEvsMonitorList();
		},
		mounted(){
			
		},
		beforeDestroy(){
			
		}
		
	}
</script>

<style lang="scss" scoped="scoped">
	.ebs_monitor{
  		background: #f6f8fb;
		 .content{
		  	.content_top{
		  		background: #ffffff;
		  		padding-top: 30px;
		  		padding-left: 20px;
		  		padding-bottom: 20px;
		  		padding-right: 20px;
    			text-align: right;
		  	}
		}
  	}
</style>
<style lang="scss">
.ebs_monitor{
	.el-select .el-input {
		    width: 100px;
	}
	.input-with-select .el-input-group__prepend {
		background-color: #fff;
	}
	.el-select .el-input.is-focus .el-input__inner{
		border-color:transparent;
	}
}
</style>


