<template>
	<div class="ecm_monitor">
		<RegionList :name ='name' :regionData='regionData' :buttonText ='buttonText' @create='exportData' @changeRegion = 'changeRegion' v-if="!isMonitorInfo"></RegionList>
		<div class="content" v-if="!isMonitorInfo">
			<div class="content_top">
				<el-input placeholder="请根据下拉框选项输入内容" v-model="keyword" class="input-with-select" size="small" style="width:350px" clearable>
				    <el-select v-model="searchType" slot="prepend" placeholder="请选择">
				      <el-option label="名称" value="name"></el-option>
				      <el-option label="ID" value="uuid"></el-option>
				    </el-select>
				    <el-button slot="append" icon="el-icon-search" style="line-height: 2;" @click.stop="getEcmMonitorList"></el-button>
				</el-input>
				<el-button icon="el-icon-refresh" style="vertical-align: top;" size="small" @click.stop="getEcmMonitorList"></el-button>
			</div>
			<div>
				<ConsoleTable 
					:tableData='ecmMonitorData'
					:tableOptions="tableOptions"
					:operationOrNot=true
					:OperationEvents='ecmOperation'
					@buttonClick="handleClick"
					:loading = 'loading'
					:dragable=true
					:Pagination = 'Pagination'
					@pagingEvent = 'pagingChange' 
					@selectChange = "selectChange"
					></ConsoleTable>
			</div>
		</div>
		<monitorInfo v-if="isMonitorInfo" :itemData="itemData" :infoKeyValue="infoKeyValue" @returnList="returnList"></monitorInfo>
	</div>
</template>

<script>
	import RegionList from '@/components/RegionList'
	import ConsoleTable from '@/components/ConsoleTable'
	import monitorInfo from './monitorInfo'
	import { getEcmMonitor,getZabbixStatus } from '@/api/monitor'
	import { GetEbsDetailById,GetRegionNum } from '@/api/ecm'
	import { metric } from './config/monitorConfig'
	import exportTemplate from './dialogViews/export'
	import service from '@/utils/request'
	import i18n from '@/lang'
	
	export default {
		components:{
			RegionList,
			ConsoleTable,
			monitorInfo
		},
		data(){
			return {
				name: '云服务器监控',
				buttonText: '导出监控数据',
				loading:false,
				isMonitorInfo: false,
				keyword: '',
      			searchType: 'name',
      			zabbixQueue: [],
      			itemData: {},
      			regionData:[],
      			infoKeyValue: [],
				filter:{
					page_size: 10,
					page: 1,
					keyword: undefined,
					instance_uuid: undefined,
					status: undefined,
					regionid: undefined
				},
				Pagination:{
					'pagingData':[10,20,50],
		        	'total':0,
		        	'show':true
				},
				ecmMonitorData: [],
        		tableOptions:[{
        			prop: 'name',
        			label: i18n.common.name,
        			EipIp: true,
        			temp: true
        		},{
        			prop:'status',
        			label:i18n.common.status,
        			status: true,
        			temp: true,
        			filter:true,
        			selectOptions: [{
			          	value: 'Running',
			          	text: i18n.common.active
			        }, {
			          	value: 'Stopped',
			          	text: i18n.common.shutOff
			        }],
        		},
        		{
        			prop:'zabbixStatus',
        			label:'监控状态',
        			fun:(data)=>{
        				return this.catchGuide(data);
        			}
        		},
        		{
        			prop:'id',
        			label: 'ID',
        			width: 300,
        		},
        		{
        			prop:'indexNum',
        			label: '指标个数',
        		}],
        		ecmOperation:[{
        			value: '查看监控图表',
        			id:'info',
        			fun:(data)=>{
        				return this.monitorInfo(data);
        			}
        		}/*,{
        			value: '创建告警规则',
        			id:'create',
        			fun:(data)=>{
        				return this.create(data)
        			}
        		}*/]
			}
		},
		methods:{
      		exportData(){
      			var data = {
					defaultData: {
						defaultSer: '云服务器',
						defaultDim: '云服务器',
						defaultKey: 'cpu_util',
						num_index: 10,
						params: {
							page_size: this.Pagination.total+20,
							page: 1,
							regionid: this.filter.regionid
						}
					},
					title: '导出监控数据',
				};
				var obj = {
					data,
					view: exportTemplate,
					form: []
				};
				this.$alertDialog(obj).then(formData => {
					//console.log(formData);
				});
      		},
      		selectChange(value){
				this.filter.status =value;
				this.filter.page = 1;
				this.getEcmMonitorList();
			},
      		monitorInfo(data){
      			if(!data.id || data.id == ''){
      				this.$message({
						message: '查看失败，ID不存在，请联系管理员',
						type:'warning'
					});
					return;
      			}
      			let loading = this.$loading({
				    lock: true,
				    text: '获取详情中...',
				    spinner: 'el-icon-loading',
				    background: 'rgba(0, 0, 0, 0.7)'
				});
      			getEcmMonitor({
      				page_size: 10,
					page: 1,
					instance_uuid: data.id,
					regionid: this.filter.regionid
      			}).then((res)=>{
      				loading.close();
					loading = null;
      				if(res.data.code == 200 && res.data.results.length > 0){
      					this.itemData = Object.assign({infoName:'云服务器详情',returnName:'云服务器监控列表'},data,res.data.results[0]);
      					//console.log(this.itemData,'afdasds');
      					this.infoKeyValue = [
		      				{
		      					label: '名称：',
		      					value: this.itemData.name,
		      				},
		      				{
		      					label: 'ID：',
		      					value: this.itemData.id,
		      				},
		      				{
		      					label: '创建时间：',
		      					value: new Date(this.itemData.created).Format("yyyy-MM-dd hh:mm:ss"),
		      				},
		      				{
		      					label: '状态：',
		      					value: this.itemData.status,
		      					className: this.itemData.state == 'Running' ? 'status_normal' : 'status_error'
		      				},
		      				{
		      					label: '虚拟私有云：',
		      					value: this.itemData.metadata.vpcname || '-----',
		      				},
		      				{
		      					label: '到期时间：',
		      					value: this.itemData.expire_date ? new Date(this.itemData.expire_date).Format("yyyy-MM-dd hh:mm:ss") : '-----',
		      				},
		      				{
		      					label: '规格：',
		      					value: this.itemData.flavor.vcpus + '核 | '+ this.itemData.flavor.ram/1024 + 'GB',
		      				},
		      				{
		      					label: '镜像：',
		      					value: this.itemData.metadata.image_name,
		      				},
		      				{
		      					label: '磁盘：',
		      					value: '-----',
		      				},
		      				{
		      					label: '网卡：',
		      					value: this.itemData.nic.length + '个',
		      				},
		      				{
		      					label: '操作系统：',
		      					value: this.itemData.metadata.ostypename,
		      				},
		      			];
		      			GetEbsDetailById(data.id).then(res => {
		      				if(res.data.code == 200){
		      					this.infoKeyValue[8].value = res.data.current_count + '个';
		      				}
		      			},error => {

		      			});
      					this.isMonitorInfo = true;
      				}else{
      					this.$message({
							message: res.msg || res.data.msg || '获取云服务器详情失败',
							type:'error'
						});
						loading.close();
						loading = null;
      				}
      			}).catch((error)=>{
      				if(loading){
      					loading.close();
						loading = null;
      				}
      			});
      		},
      		returnList(){
      			this.isMonitorInfo = false;
      			this.getEcmMonitorList();
      		},
      		changeRegion(uuid){
      			service.cancle('CancelToken');
      			this.filter.regionid = uuid;
      			this.getEcmMonitorList();
      		},
      		getEcmMonitorList(){
      			this.getRegionNums();
      			let params = this.filter;
      			this.zabbixQueue = [];
      			params.keyword = params.instance_uuid = undefined;
      			if(this.searchType == 'name' && this.keyword != ''){
      				params.keyword = this.keyword;
      			}else if(this.searchType == 'uuid' && this.keyword != ''){
      				params.instance_uuid = this.keyword;
      			}
      			this.loading =true;
      			this.ecmMonitorData = [];
      			getEcmMonitor(params).then((res)=>{
      				if(res.data.code == 200){
      					this.ecmMonitorData = res.data.results;
      					this.Pagination.total = res.data.total_count;
      					let tempNum = metric.ecs.length;
      					this.ecmMonitorData.map((item,index,array) => {
      						this.$set(item,'indexNum',tempNum);
      						if(item.zabbix_name&&item.zabbix_name!=''){
      							this.$set(item,'zabbixStatus','获取中...');
	      						this.zabbixQueue.push({
	      							uuid: item.zabbix_name,
	      							id: item.id,
	      							getZabbixStatus: 'notStart',
	      							_zabbixStatus: false,
	      							_index: index
	      						});
      						}else{
      							this.$set(item,'zabbixStatus','获取失败');
      						}
      					});
      					this.openQueue();
      				}else{
      					this.$message({
							message: res.msg || res.data.msg || '获取云服务器监控列表失败',
							type:'error'
						});
      				}
      				this.loading=false;
      			}).catch((error)=>{
      				this.loading=false;
      			});
      		},
      		handleClick(funName,rowData){
      			funName(rowData)
      		},
      		openQueue(){
      			var len = this.zabbixQueue.length;
      			len = len > 5 ? 5 : len;
      			for(var i=0; i<len; i++){
      				this._getZabbixStatus(this.zabbixQueue[i]);
      			}
      		},
      		queueNext(){
      			for(var i=0; i<this.zabbixQueue.length; i++){
      				if(this.zabbixQueue[i].getZabbixStatus == 'notStart'){
      					this._getZabbixStatus(this.zabbixQueue[i]);
      					return;
      				}
      			}
      		},
      		_getZabbixStatus(item){
      			item.getZabbixStatus = 'loading';
      			getZabbixStatus({uuid:item.uuid}).then(res => {
	      				if(res.data.status == 0){
	      					item.getZabbixStatus = 'success';
	      					item._zabbixStatus = res.data.data.agent_status;
	      					if(item._zabbixStatus){
	      						this.ecmMonitorData[item._index].zabbixStatus = '正常';
	      					}else{
	      						this.ecmMonitorData[item._index].zabbixStatus = '异常';
	      					}
	      				}else{
	      					item.getZabbixStatus = 'fail';
	      					this.ecmMonitorData[item._index].zabbixStatus = '获取失败';
	      				}
	      				this.queueNext();
	      			},error => {
	      				item.getZabbixStatus = 'error';
	      				this.ecmMonitorData[item._index].zabbixStatus = '获取失败';
	      				this.queueNext();
	      			});
      		},
      		pagingChange(type,val){
      			if(type == 'num'){
      				this.filter.page_size = val;
      			}else if(type == 'page'){
      				this.filter.page = val;
      			}
      			this.getEcmMonitorList();
      		},
      		getRegionNums(){
				GetRegionNum({resource:'vm'}).then( res => {
					if(res.data.code == 200){
						let data = res.data.results[0].VM;
						if(data && data.detail){
							this.regionData = data.detail;
						}
					}
				});
			},
			catchGuide(item){
				console.log(item,'----------',this);
				
				this.$alert('<div><p><div>Linux系统</div>内部服务异常，请登录并运行curl  <a href="/tool/qga/install_qga.sh" target="_blank">'+location.host+'/tool/qga/install_qga.sh</a> | bash 如已无法登陆，请提交工单处理</p><p><div>Windows系统</div>内部服务异常请从以下链接下载，并解压运行install.bat  <a href="/tool/qga/qga.zip" target="_blank"> '+location.host+'/tool/qga/qga.zip</a></p></div>', '异常指引', {
		          dangerouslyUseHTMLString: true,
		          confirmButtonClass: 'dsafsdfas'
		        });
			}
		},
		created(){
			this.getEcmMonitorList();
		},
		mounted(){
			
		},
		beforeDestroy(){
			
		}
		
	}
</script>

<style lang="scss" scoped="scoped">
	.ecm_monitor{
  		background: #f6f8fb;
		 .content{
		  	.content_top{
		  		background: #ffffff;
		  		padding-top: 30px;
		  		padding-left: 20px;
		  		padding-bottom: 20px;
		  		padding-right: 20px;
    			text-align: right;
		  	}
		}
  	}
</style>
<style lang="scss">
.ecm_monitor{
	.el-select .el-input {
		width: 100px;
	}
	.input-with-select .el-input-group__prepend {
		background-color: #fff;
	}
	.el-select .el-input.is-focus .el-input__inner{
		border-color:transparent;
	}
}
</style>



