<template>
	<div>
		<console-detail
			:detailList = 'detailList'
			:detail_image = 'detailImg'
			:detail_label='detailLabel'
			>
		</console-detail>
		<detail-tab :tabOption="tabOption">
			<div slot="secDetailRules">
				<component is='secDetailRules' :secDetail='secDetail' @refresh="handleClick"></component>
			</div>
		</detail-tab>
		
	</div>
</template>

<script>
	import ConsoleDetail from '@/components/ConsoleDetail';
	import detailImg from '@/assets/ecm/ecmDetail.png';
	import DetailTab from '@/components/DetailTab';
	import secDetailRules from './secDetailRules'
	import { getAclDetailById } from '@/api/vpc';
	export default {
		components:{
			ConsoleDetail,
			DetailTab,
			secDetailRules
		},
		methods:{
			getSecDetail(){
				let id = this.$route.params.secId;
				getAclDetailById(id).then(res=>{
					let data = res.data.results[0];
					this.filterSec(data);
					this.detailLabel = data.name;
					this.secDetail = data;
				}).catch(error=>{
					console.log(error)
				})
			},
			filterSec(sec){
				let detailList = this.detailList;
				detailList.map(item=>{
					
					item.intro = sec[item.name] == 'default' ? "默认安全组" : sec[item.name];
				})
			},
			handleClick(){
				this.getSecDetail()
			}
		},
		data(){
			return {
				detailLabel:'安全组',
				detailImg,
				secDetail:{},
				detailList:[
					{
						name:'name',
						label:'名称',
						intro:'--'
					},
					{
						name:'id',
						label:'ID',
						intro:'--'
					},
					{
						name:'description',
						label:'描述',
						intro:'--'
					},
				],
				tabOption:[{
					label:'规则',
					name:'secDetailRules',
					view:''
				}]
			}
		},
		mounted(){
			this.getSecDetail()
		}
	}
</script>

<style>
</style>


