<template>
	<div class="detail-content">
		<el-row class="detail-title">
			安全组
		</el-row>
		<el-row class="detail-top" v-if="quotaShow">
			<el-col :span="20">
				您还可以添加 
					{{ addNicNum }}
				个安全组
			</el-col>
			<!--<el-col :span="4">
				<el-row type="flex" justify="end" >
					<detail-btn :btnMsg='btnMsg' @deatilBtnClick="addNic"></detail-btn>
				</el-row>
			</el-col>-->
			
		</el-row>
		
		<el-row class="detail-nic">
			<el-col :span="8" v-for="sec in secs" :key="sec.id">
				<detail-card :cardData='sec' className="sec" :opearationBtn='opearationBtn' @clickFun='action' @remove="remove">
					<div slot="detailMore">
						<detail-more :detailLabel='secLabel' :Data='sec'></detail-more>
					</div>
				</detail-card>
			</el-col>
		</el-row>
		<el-row class="border-style"></el-row>
	</div>
</template>

<script>
	import {GetEcmSecByOs} from '@/api/ecm'
	import DetailCard from '@/components/DetailCard'
	import DetailMore from '@/components/DetailCard/DetailMore'
	export default {
		components:{
			DetailCard,
			DetailMore
		},
		props:{
			serverId:[String]
		},
		data(){
			return {
				quotaShow:false,
				secs:[],
				secLabel:[
					{
						label:'VLAN1',
						value:'',
						hide:true
					}
				],
				opearationBtn:[
					{
						value:'查看更多详情',
						id:'showDetail',
						fun:(id)=>{
							return this.switchMore(id)
						}
					}
				]
			}
		},
		methods:{
			getEcmSecByOs(id){
				GetEcmSecByOs(id).then(res=>{
					let data = res.data.results;
					this.filterSecs(data)
					this.secs = data;
				}).catch(error=>{
					console.log(error)
				})
			},
			remove(){
				
			},
			action(fun,item){
				let id = item.id;
				fun(id)
			},
			switchMore(id){
				this.secs.map((item)=>{
					if(item.id == id){
						let bool = !item.showDetail;
						console.log(item)
						this.$set(item,'showDetail',bool)
					}
				})
			},
			filterSecs(secs){
				secs && secs.length && secs.map(item=>{
					let rules = item.security_group_rules;
					let ingress = rules.filter(item=>{
						return item.direction == 'ingress'
					});
					let egress = rules.filter(item=>{
						return item.direction == 'egress'
					});
					item.ingress = ingress;
					item.egress = egress;
					
					
				})
			}
		},
		computed:{
			
		},
		created(){
			this.getEcmSecByOs(this.serverId);
		},
		destroyed(){
			
		}
		
	}
</script>

<style scoped="scoped" lang="scss">
	.detail-title{
		padding:20px 0;
	}
	.detail-top{
		padding-bottom:20px;
		line-height: 30px;
		div{
			font-size:14px;
		}
		
	}
	.detail-content{
		padding:20px;
		background:#FFFFFF;
		/*min-height:300px;*/
	}
	.border-style{
		height:20px;
		border-bottom:3px solid #f6f8fb;
	}
</style>


