<template>
  <div :loading='loading' class="vpc-action-content">
    <div v-if="type==='delete'">
      <svg-icon class="vpcDelete" icon-class="warn"></svg-icon>
      <div class="content">确定要删除{{form.name}}吗？</div>
    </div>
    <el-form v-if="type==='edit'" :model="form" :rules="rules" ref="dataform" label-width="100px" class="demo-ruleForm">
      <el-form-item
        label="名称："
        prop="name"
        class='name'
      >
        <el-input type="age" v-model.trim="form.name" auto-complete="off"></el-input>
        <el-tooltip popper-class='editVpcTip' content="不能使用中文,且长度为2-63字符" placement="bottom"><i class="el-icon-question"></i></el-tooltip>
      </el-form-item>
      <el-form-item
        label="描述："
        prop="displaytext"
      >
        <el-input type="age" v-model.trim="form.displaytext" auto-complete="off"></el-input>
      </el-form-item>
    </el-form>
  </div>
</template>


<script>
  import ConsoleTable from '@/components/ConsoleTable'
  import { getVpcList} from '@/api/vpc'
  import { nameRule } from '@/utils/validator';
  export default {
    props:{
      type:[String],
      actionTip:[Array],
      tableData:[Object],
      action:[String],
      loading:[Boolean],
      name:[String],
      form:[Object,String]
    },
    components:{
      ConsoleTable
    },
    data(){
    	let _this = this;
      var checkName=(rule, value, callback) => {
//    	let nameRule = /^[a-zA-Z][a-zA-Z0-9\-]*[a-zA-Z0-9]$/;
      	if(value.length > 63 || value.length < 2){
      		callback(new Error('长度为2-63字符'));
				}else if(nameRule(value)){
					callback(new Error('不能使用中文'));
      	}else{
	        let params= {
	          status:'',
	          id:'',
	          name:value
	        };
	        if(this.dataform.name == value){
	        	if(this.form.displaytext == this.dataform.displaytext){
	        		_this.form.edited = false;
	        		callback();
	        	}else{
	        		console.log('fasong');
	        		_this.form.edited = true;
	        		callback();
	        	}
	        }else{
	        	getVpcList(params).then((res)=>{
		          let vpclist = res.data.results;
		          if(vpclist.length>0){
		            callback(new Error('VPC名称不能重复'));
		          }else{
		          	_this.form.edited = true;
		            callback();
		          }
		        }).catch((error)=>{
		          _this.form.edited = true;
		          console.log(error)
		        })
	        }
	      }
      }
      return {
        dataform:{
            name:this.form.name,
          	displaytext:this.form.displaytext,
        },
        rules:{
            name:[
              { validator: checkName, trigger: 'blur' },
              { required: true, message: '名称不能为空', trigger: 'blur' }
//            {min: 3, max: 16, message: '长度在 3 到 16 个字符', trigger: 'blur'}
            ]
        }
      }
    },
    methods:{
      validateForm(){
          if(this.type=='edit'){
            return this.$refs['dataform'].validate();
          }else{
            return new Promise((resolve,reject)=>{
              resolve();
            })
          }
      }
    },
    computed:{
      typeCom(){
        let type = this.type || 'warn'
        return type
      },
      actionCom(){
        if(this.action == 'start' || this.action == 'stop' || this.action == 'reboot' || this.action == 'delete'){
          return 'action'
        }
      },
      fliterData(){
        let date = (new Date()).getTime();
        this.$set(this.tableData,'date',date)
        return this.tableData
      },

    },
    created(){

    },
    beforeDestroyd(){

    }

  }
</script>

<style lang="scss" scoped="scoped">
  .vpc-action-content{
    text-align: center;
    max-width: 600px;
    .vpcDelete{
      font-size: 40px;
      color: #F9CD76;
      margin-bottom: 10px;
    }
    .content{
      font-size: 16px;
    }
    .name{
    	margin-bottom: 28px;
    	.el-form-item__content{
    		width: 195px!important;
    		text-align: left;
    	}
    }
    .el-input{
    		width: 190px;
    	}
  }

  .action-tip{
    /*margin-top: -20px;*/
    padding-bottom: 10px;
    font-size:12px;
    min-height: 30px;
    color: #666666;
    .action-tip-title{
      line-height: 30px;
    }
    .action-tip-waring{
      .action-img{
        font-size:30px;
        color:#F9CD76;
        text-align: left;
        padding-left:2px;
      }
      .action-tip-title{
        p{
          line-height: 20px;
        }
        .action-tip-subtip{
          color:#999999;
        }
      }
    }


  }
  .action-select{
    padding-top:20px;
  }

</style>
<style lang="scss">
	.vpc-action-content{
		.el-form-item{
			.el-form-item__content{
				text-align: left;
			}
		}
	}
	.editVpcTip{
		width: 100px;
	}
</style>



