<template>
  <div :loading='loading' class="action-content">
    <div v-if="type==='delete'">
      <svg-icon class="vpcDelete" icon-class="warn"></svg-icon>
      <div class="content">确定要删除{{form.name}}吗？</div>
    </div>
    <el-form v-if="type==='create'" size='small' :model="dataform" :rules="rules" ref="dataform" label-width="100px" class="acl-create">
      <el-form-item
        class="vpc"
        label="VPC :"
        prop="vpcid"
        v-if="!form.vpc" required
      >
        <el-select v-model='dataform.vpcid' :disabled = "seleceCheck">
          <el-option v-for = 'item in dataform.vpclist'
                     :label='item.name'
                     :value='item.id' :key="item.id"></el-option>

        </el-select>
      </el-form-item>
      <el-form-item
        class="name"
        label="ACL名称 :"
        prop="name"
        required
      >
        <el-input type="age" v-model.trim="dataform.name" auto-complete="off"></el-input>
        <el-tooltip popper-class='createAclTip' content="不能使用中文,且长度为2-63字符" placement="top"><i class="el-icon-question"></i></el-tooltip>
      </el-form-item>
      <el-form-item
        class="desc"
        label="描述 :"
        prop="desc"
      >
        <el-input type="age" v-model.desc="dataform.desc" auto-complete="off"></el-input>
      </el-form-item>
    </el-form>
    <el-form v-if="type==='createRule'" size='small' :model="dataform" :rules="rules" ref="dataform" label-width="100px" class="subnet-create">
      <el-form-item
        label="CIDR :"
        prop="net"
        required
       >
        <div class="tiny-net">
          <span class="tiny_input_ip_container valid_error_input" id="CidrIp">
            <el-input v-on:keydown.native ="onkeydowns($event,0,'CidrIp')" :maxlength=3
            	 v-model="dataform.net.first" type="text" class="tiny_input_ip_anchor tiny_input_ip_octet" @keypress.native="replaceNum($event)"></el-input>
                <span class="tiny_input_ip_division">.</span>
            <el-input v-on:keydown.native ="onkeydowns($event,1,'CidrIp')" :maxlength=3
            	 v-model="dataform.net.second" type="text" class="tiny_input_ip_anchor tiny_input_ip_octet" @keypress.native="replaceNum($event)"></el-input>
                <span class="tiny_input_ip_division">.</span>
            <el-input v-on:keydown.native ="onkeydowns($event,2,'CidrIp')" :maxlength=3
            	 v-model="dataform.net.third" type="text" class="tiny_input_ip_anchor tiny_input_ip_octet" @keypress.native="replaceNum($event)"></el-input>
                <span class="tiny_input_ip_division">.</span>
            <el-input v-on:keydown.native ="onkeydowns($event,3,'CidrIp')" :maxlength=3
            	 v-model="dataform.net.four" type="text" class="tiny_input_ip_anchor tiny_input_ip_octet" @keypress.native="replaceNum($event)"></el-input>
              </span>
            <span class="ng-scope">/</span>
          <span class="tiny-textbox-container" id="VpcPortal" style="width: 50px;">
            <el-input :maxlength=2 v-model="dataform.net.last" type="text" value="24" spellcheck="false" class="tiny-input-text valid_error_input" @keypress.native="replaceNum($event)" validator="true"></el-input>
              </span>
        </div>
      </el-form-item>
      <el-form-item
        class="protocol"
        label="协议 :"
        prop="protocol"
      >
        <el-select class="tiny-net protocol" v-model='dataform.protocol' @change="changeProtocaol(dataform.protocol)">
          <el-option v-for = 'item in dataform.protocolList'
                     :label='item.label'
                     :value='item.protocol' :key="item.protocol"></el-option>

        </el-select>
      </el-form-item>
      <el-form-item class="traffictype" label="方向 :" prop="traffictype">
        <el-radio-group v-model="dataform.traffictype" class="tiny-net">
          <el-radio label="ingress" prop="Ingress">入方向</el-radio>
          <el-radio label="egress" prop="Egress">出方向</el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item class="acltype" label="类型 :" prop="type">
        <el-radio-group v-model="dataform.type" class="tiny-net">
          <el-radio label="allow" prop="allow">Allow</el-radio>
          <el-radio label="deny" prop="deny">Deny</el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item v-if="dataform.protocol==='tcp'||dataform.protocol==='udp'" label="端口范围 :" prop="port">
          <div class="tiny-net rulePort">
            <span class="start">
              <el-form-item prop="minport" required>
                   <el-input v-model="dataform.minport"></el-input>
              </el-form-item>
            </span>
            <span class="line">一</span>
            <span class="end">
              <el-form-item prop="maxport" required>
                <el-input v-model="dataform.maxport"></el-input>
              </el-form-item>
            </span>
            <span class="tip">
            	<el-tooltip popper-class='editSubTip' content="在备案功能上线前,Tcp协议暂不支持80、443、8080、8443端口" placement="bottom"><i class="el-icon-question"></i></el-tooltip>
            </span>
          </div>
      </el-form-item>
      <el-form-item id="icmptype" v-if="dataform.protocol==='icmp'" label="ICMP类型 :" prop="icmptype" required>
          <el-input type="age" v-model="dataform.icmptype" auto-complete="off"></el-input>
      </el-form-item>
      <el-form-item id="icmpcode" v-if="dataform.protocol==='icmp'" label="ICMP代码 :" prop="icmpcode" required>
        <el-input type="age" v-model="dataform.icmpcode" auto-complete="off"></el-input>
      </el-form-item>
    </el-form>
  </div>
</template>


<script>
  import ConsoleTable from '@/components/ConsoleTable'
  import ElFormItem from "../../../node_modules/element-ui/packages/form/src/form-item";
  import { getAclList,getVpcList} from '@/api/vpc'
  import { getName } from '@/utils/shareFunction'
  import ElForm from "../../../node_modules/element-ui/packages/form/src/form";
  import { ipValidatorSelf, maskAndSubnetValidator, prefix2mask, subnetValidator, getIpValue, ipValue2Str, nameRule } from '@/utils/validator';
  import ElInput from "../../../node_modules/element-ui/packages/input/src/input";
  export default {
    props:{
      type:[String],
      regionId:[String],
      actionTip:[Array],
      tableData:[Object],
      action:[String],
      loading:[Boolean],
      form:[Object,String]
    },
    components:{
      ElInput,
      ElForm,
      ElFormItem,
      ConsoleTable
    },
    data(){
    	let _this = this;
    	  let lowest = 0
    	  let ipNotStartWith100_64 = (ip)=>{
					let data, ip1, ip2;
					try {
						data = ip.split(".");
						if (4 === data.length) {
							ip1 = parseInt(data[0], 10);
							ip2 = parseInt(data[1], 10);
							if (10 === ip1) {
								lowest = 8;
								return !0
							}
							if (172 === ip1 && ip2 > 15 && ip2 < 32) {
								lowest = 12;
								return !0
							}
							if (192 === ip1 && 168 === ip2) {
								lowest = 16;
								return !0
							}
							return !1
						}
						return !1
					} catch (e) {
						return !1
					}
				}
	    	var checkCidr=(rule, value, callback)=>{
	    		console.log(value);
	        let ipNotStartTip = '不能使用此网段。'
	        let ip = value.first + '.' + value.second + '.' + value.third + '.' + value.four;
	        //检测输入的ip是否合法
	        if(ipValidatorSelf(ip)){
	        	//检测网段是否合理
	        	if(value.name == 'cidr' && ipNotStartWith100_64(ip) || value.name=='sub_cidr'){
	        		//检测port取值范围是否合理
	        		let port = parseInt(value.last);

	        		if(value.name == 'sub_cidr' && (port>256 || port < parseInt(lowest,10))){
	        			callback(new Error('端口值不合法'))
	        		}else{
	        			//子网ip与掩码匹配检测
	        			if(value.name == 'sub_cidr'){
	        				let cidr = _this.dataform.net;
	        				let cidrPort = parseInt(cidr.last);
	        				//子网端口与Vpc端口对比
	            			if(parseInt(cidrPort) > parseInt(port)){
	            				callback(new Error('子网IP地址不在可用网段内。'))
	            			}
	            			let mask = prefix2mask(cidrPort)
	            			let subnet = cidr.first + '.' + cidr.second + '.' + cidr.third + '.' + cidr.four;
			            	let cidrResult = subnetValidator(mask, ip,subnet)
			            	if(cidrResult){
			            		callback()
			            	}else{
			            		callback(new Error('网段IP地址与掩码不匹配。'))
			            	}
	            		}

	        			//vpc子网IP与掩码匹配检测
	        			if(value.name == 'cidr'){
	        				let mask = prefix2mask(parseInt(value.last))
			            	let cidrResult = maskAndSubnetValidator(mask, ip)
			            	if(cidrResult){
			            		_this.cidrOk = true
			            		callback()
			            	}else{
			            		callback(new Error('网段IP地址与掩码不匹配。'))
			            	}
	        			}

	        		}
	        	}else{
	        		callback(new Error(ipNotStartTip))
	        	}
	        }else{
	        	callback(new Error('IP地址不合法'))
	        }
	    };
      var checkName=(rule, value, callback)=>{
//    	let nameRule = /^[a-zA-Z][a-zA-Z0-9\-]*[a-zA-Z0-9]$/;
      	if(value.length > 63 || value.length < 2){
      		callback(new Error('长度为2-63字符'));
				}else if(nameRule(value)){
					callback(new Error('不能使用中文'));
      	}else{
	        let params= {
	          id: '',
	          name: value
	        }
	        getAclList(params).then((res)=>{
	          let acllist = res.data.results;
	          if(acllist.length>0){
	            callback(new Error('ACL名称不能重复!'));
	          }else{
	            callback();
	          }
	        }).catch((error)=>{
	          console.log(error)
	        })
	      }
      };
      var check = (rule, value, callback) => {
          var tt=/^\d+$/g;
          if(tt.test(value)){
            if (value < 1) {
              callback(new Error('端口范围在 1 到 65535 之间'));
            } else if(value > 65535) {
              callback(new Error('端口范围在 1 到 65535 之间'));
            }else{
              callback();
            }
          }else{
            callback(new Error('端口值必须为数字值'));
          }
      };
      var check3 = (rule, value, callback) => {
        if(this.dataform.minport){
          if(parseInt(value) < parseInt(this.dataform.minport)){
            callback(new Error('端口下限应小于等于端口上限'));
          }else{
            callback();
          }
        }
      };
      var check4 = (rule, value, callback) => {
        if(this.dataform.maxport){
          if(parseInt(value) > parseInt(this.dataform.maxport)){
            callback(new Error('端口下限应小于等于端口上限'));
          }else{
            callback();
          }
        }
      };
      return {
        name:'',
        desc:'',
        aclId:'',
        seleceCheck:true,
        dataform:{
          name:'',
          desc:'',
          protocol:'tcp',
          traffictype:'ingress',
          type:'allow',
          icmptype:'-1',
          icmpcode:'-1',
          net:{
          	name:'sub_cidr',
            first:0,
            second:0,
            third:0,
            four:0,
            last:0
          },
          protocolList: [{
            label: 'TCP',
            protocol: 'tcp'
          }, {
            label: 'UDP',
            protocol: 'udp'
          }, {
            label: 'ICMP',
            protocol: 'icmp'
          }, {
            label: 'ALL',
            protocol: 'all'
          }],
          vpclist: [],
          vpcid:'',
        },
        rules:{
            name:[
              { required: true, message: '名称不能为空', trigger: 'blur' },
              {min: 3, max: 16, message: '长度在 3 到 16 个字符', trigger: 'blur'},
              {validator: checkName, trigger: 'blur'},
            ],
          net:[
            {validator: checkCidr, trigger: 'blur'}
          ],
          vpcid:[
          	{ required: true, message: 'VPC选择不能为空', trigger: 'blur' },
          ],
          minport:[
            { required: true, message: '请输入端口范围', trigger: 'blur' },
            { validator: check, trigger: 'blur' },
            { validator: check4, trigger: 'blur' }
          ],
          maxport:[
            { required: true, message: '请输入端口范围', trigger: 'blur' },
            { validator: check, trigger: 'blur' },
            { validator: check3, trigger: 'blur' }
          ],
          icmptype:[
            { required: true, message: '请输入icmp类型', trigger: 'blur' },
          ],
          icmpcode:[
            { required: true, message: '请输入icmp代码', trigger: 'blur' },
          ]
        }
      }
    },
    methods:{
      changeProtocaol(value){
          this.dataform.protocol=value;
          console.log(value);
      },
      onkeydowns(event,num,target){
				let e = window.event || event; 
				let inputs=document.getElementById(target).getElementsByTagName("input");
				let valueLength = e.target.value.length;
				let InputValue = e.target.value - 0;
	　 			let code = e.keyCode||e.which||e.charCode; 
				if((code >= 48 && code<=57) || (code >= 96 && code<=105)){
					if(valueLength >= 3){
						if(inputs[num].selectionStart == '0' && valueLength == inputs[num].selectionEnd && inputs[num].value ){
							return true
						}else{
							if(num <3){
								inputs[num+1].focus();
							}
						}
					}
					return true
				}else if((code == 39) || (code == 190)){
					let cursurPosition=-1;
					if(inputs[num].value || inputs[num].value == '0'){
						if(inputs[num].selectionStart || inputs[num].selectionStart == '0'){//非IE浏览器
							cursurPosition= inputs[num].selectionStart;
						}else{//IE
							let range = document.selection.createRange();
							range.moveStart("character",-inputs[num].value.length);
							cursurPosition=range.text.length;
						}
					}else{
						cursurPosition = 0;
					}
					console.log(inputs[num].selectionStart);
					if(cursurPosition == inputs[num].value.length){
						if(num <3){
							let pos = inputs[num+1].value.length;
							if(inputs[num+1].setSelectionRange){
								inputs[num+1].focus()
								inputs[num+1].setSelectionRange(0,0);
							}else if (inputs[num+1].createTextRange) {
								let range = obj.createTextRange();
								range.collapse(!0);
								range.moveEnd("character", pos);
								range.moveStart("character", pos);
								range.select();
							}
						}
					}
					if(code == 190){
						event.preventDefault()
					}
				}else if(code == 37){
					let cursurPosition=-1;
					if(inputs[num].value || inputs[num].value == '0'){
						if(inputs[num].selectionStart || inputs[num].selectionStart == '0'){//非IE浏览器
							cursurPosition= inputs[num].selectionEnd;
						}else{//IE
							let range = document.selection.createRange();
							range.moveStart("character",-inputs[num].value.length);
							cursurPosition=range.text.length;
						}
					}else{
						cursurPosition = 0;
					}
					console.log(cursurPosition,inputs[num].selectionEnd);
					if(cursurPosition == '0'){
						if(num > 0){
							let ele = inputs[num-1];
							let pos = ele.value.length;
							if(ele.setSelectionRange){
								setTimeout(function(){
									ele.focus()
									ele.selectionStart = pos;
									ele.selectionEnd = pos;
									ele.setSelectionRange(pos,pos);
								},10)
							}else if (ele.createTextRange) {
								setTimeout(function(){
									let range = obj.createTextRange();
									range.collapse(!0);
									range.moveEnd("character", pos);
									range.moveStart("character", pos);
									range.select();
								},10)
							}
						}
					}
				}else if(code == 8){
					let cursurPosition=-1;
					let inputVaLen = inputs[num].value.length;
					if(inputs[num].value || inputs[num].value == '0'){
						if(inputs[num].selectionStart || inputs[num].selectionStart == '0'){//非IE浏览器
							cursurPosition= inputs[num].selectionStart;
						}else{//IE
							let range = document.selection.createRange();
							range.moveStart("character",-valueLength);
							cursurPosition=range.text.length;
						}
					}else{
						cursurPosition = 0;
					}
					
					if(cursurPosition == 0){
						if(inputs[num].selectionStart == '0' && inputVaLen == inputs[num].selectionEnd && inputs[num].value ){
							return true
						}else{
							if(num > 0){
								inputs[num-1].selectionStart = inputs[num-1].value.length;
								inputs[num-1].focus()
							}
						}
					}
					return true
				}else if(code == 9){
					return true
				}else{
					event.preventDefault()
				}
			},
      replaceNum(event){
			let code = window.event ? event.keyCode : event.which;
			if(!window.event){
				let arr = [8,9,110,37,39,0];
				if(arr.includes(code)) return
			}
			if(code==46){
				let next = event.target.parentElement.nextElementSibling ? event.target.parentElement.nextElementSibling.nextElementSibling.firstElementChild :'';
				next && next.focus()
				next && next.setSelectionRange(0,3);
			}
			code>=48 && code<=57 || event.preventDefault()
		},
        setname(){
            this.dataform.name='acl-'+getName();
        },
      validateForm(){
          if(this.type!=='delete'){
            return this.$refs['dataform'].validate();
          }else{
            return new Promise((resolve,reject)=>{
              resolve();
            })
          }
      },
      filterFakeVpc(vpcs){
				return vpcs.filter(item=>{
					return !(item.fake_vpc && item.fake_vpc == 1)
				})
			},
    },
    watch:{
      dataform:{
        handler(val,old){
          console.log(val)
          console.log(old)
          this.form.desc=this.dataform.desc;
          this.form.vpcid=this.dataform.vpcid;
          this.form.name=this.dataform.name;
          this.form.protocol=old.protocol;
          this.form.direction=old.traffictype;
          this.form.action=old.type;
          this.form.port_range_min=old.minport;
          this.form.port_range_max=old.maxport;
          this.form.icmptype=old.icmptype;
          this.form.icmpcode=old.icmpcode;
          this.form.remote_ip_prefix=old.net.first
            +'.'+this.dataform.net.second
            +'.'+this.dataform.net.third
            +'.'+this.dataform.net.four
            +'/'
            +this.dataform.net.last;
        },
        deep:true,
      }
    },
    computed:{
      getfrom:{
        get: function () {
          console.log('get')
          return;
        },
        set: function (newValue) {
          console.log('set');
          console.log(newValue);
        }
      },
      typeCom(){
        let type = this.type || 'warn'
        return type
      },
      actionCom(){
        if(this.action == 'start' || this.action == 'stop' || this.action == 'reboot' || this.action == 'delete'){
          return 'action'
        }
      },
      fliterData(){
        let date = (new Date()).getTime();
        this.$set(this.tableData,'date',date)
        return this.tableData
      },

    },
    created(){
    	if(this.type == 'create'){
    		let uuid=this.regionId;
	      let params={

	      }
	      this.seleceCheck = true;
	      getVpcList(params,uuid).then((res)=>{
	        console.log(res);
	        let vpclist = this.filterFakeVpc(res.data.results);
	//      vpclist.filter(function(vpc){
	//        vpc['vpcid']=vpc.id;
	//        vpc['label']=vpc.name;
	//        return;
	//      })
	//      console.log(vpclist);
	        this.dataform.vpclist=vpclist;
	        this.seleceCheck = false;
	        this.dataform.vpcid=vpclist[0].id;
	        this.loading=false
	      }).catch((error)=>{
	        this.loading=false
	        console.log(error)
	      })
    	}
      this.setname();
    },
    beforeDestroyd(){

    }

  }
</script>

<style lang="scss" scoped="scoped">
  .action-content{
    text-align: center;
    max-width: 600px;
    .vpcDelete{
      font-size: 40px;
      color: #F9CD76;
      margin-bottom: 10px;
    }
    .content{
      font-size: 16px;
    }
  }

  .action-tip{
    /*margin-top: -20px;*/
    padding-bottom: 10px;
    font-size:12px;
    min-height: 30px;
    color: #666666;
    .action-tip-title{
      line-height: 30px;
    }
    .action-tip-waring{
      .action-img{
        font-size:30px;
        color:#F9CD76;
        text-align: left;
        padding-left:2px;
      }
      .action-tip-title{
        p{
          line-height: 20px;
        }
        .action-tip-subtip{
          color:#999999;
        }
      }
    }
  }
  .action-select{
    padding-top:20px;
  }

  .tiny-net{
    /*width: 300px;*/
    /*height: 32px;*/
    display: flex;
    flex-direction: row;
    .ng-scope{
    	margin: 0 5px;
    }
    .tiny_input_ip_container{
      display: flex;
      flex-direction: row;
      line-height: 20px;
      border: 1px solid #cccccc;
      border-radius: 4px;
      padding: 0;
      /*margin-right: 10px;*/
      input{
        margin: 0;
        display: inline-block;
        line-height: 20px;
        width: 40px;
        border: none;
      }
    }
    .tiny-textbox-container{
      line-height: 20px;
      border: 1px solid #cccccc;
      border-radius: 4px;
      padding: 0;
      input{
        margin: 0;
        display: inline-block;
        line-height: 25px;
        width: 40px;
        border: none;
      }
    }

  }
  .mark{
    text-align: left;
    font-size: 12px;
  }
  .protocol{
    width: 230px;
  }
  .rulePort{
    margin-top: 5px;
    .start{
      display: inline-block;
      width: 80px;
      line-height: 25px;
      margin-right: 10px;
      input{
        display: block;
        width: 100%;
        line-height: 25px;
      }
    }
    .end{
      display: inline-block;
      width: 80px;
      line-height: 25px;
      input{
        display: block;
        width: 100%;
        line-height: 25px;
      }
    }
    .tip{
      display: inline-block;
      margin-left: 7px;
      width: 17px;
      height: 43px;
      line-height: 43px;
    }
    .line{
      margin-right: 10px;
      line-height: 30px;
    }
  }

</style>
<style lang="scss">
  .acl-create{
  	padding-left: 35px;
    .name,.vpc,.desc,.acl{
      .el-form-item__content{
        width: 170px;
      }
    }
    .name{
    	margin-bottom: 25px;
    	.el-form-item__content{
    		width: 195px!important;
    		text-align: left;
    	}
    	.el-input{
    		width: 170px;
    	}
    }
    .el-input__inner{
      height: 30px;
    }
  }
  .traffictype{
    .el-radio__input{
      line-height: 3;
    }
  }
  .acltype{
    .el-radio{
      line-height: 3;
    }
  }
  .createAclTip{
			width: 100px;
  }
  #CidrIp input{
    padding: 0;
    margin: 0;
    display: inline-block;
    width: 40px;
    border: none;
    text-align: center;
    
  }
  /*#CidrIp .tiny_input_ip_division{
    	line-height: 33px;
  }*/
  #VpcPortal input{
    border: none;
  }
  #icmptype input{
    padding: 0;
    margin: 0;
    display: block;
    width: 60px;
    text-align: center;
  }
  #icmpcode input{
    padding: 0;
    margin: 0;
    display:block;
    width: 60px;
    text-align: center;
  }
	.editSubTip{
			width: 100px;	    	
	}
</style>



