<template>
  <div class="main">
    <RegionList :name ='name' :regionData='regionData' :buttonText ='buttonText' @create='create' @changeRegion = 'changeRegion'></RegionList>
    <div class="content">
      <div class="content_top">
        <!--<div class="subnet_mark">已有{{subExistCount}}个子网，您还可以创建{{subRemainCount}}个子网</div>-->
        <div class="subnet_mark"></div>
        <ConsoleSubnetFilter
          class="subnet_search"
          :options ='options'
          :ByVpc=true
          @clear = 'clear'
          @selectChange="selectChange"
          :optionsValue ='optionsValue'
          @searchEvent = 'search'
          @refreshEvent = 'refresh'
        ></ConsoleSubnetFilter>
      </div>
      <div id="subList">
        <SubnetList v-if='!loaded'
          :tableData="tableData"
          :netOperation="netOperation"
          @buttonClick = 'buttonClick'
          :deleteNet="deleteNet"
          :edit="editNet"
          :Pagination = 'Pagination'
          @pagingEvent = 'pagingChange'
        ></SubnetList>
        <el-table v-if='loaded'
		      :data="tableData"
		      v-loading = 'loaded'
		      style="width: 100%">
		      <!--<el-table-column
		        prop="date"
		        label="日期">
		      </el-table-column>
		       <el-table-column
		        prop="name"
		        label="姓名">
		      </el-table-column>-->
		    </el-table>
      </div>
    </div>
  </div>
</template>

<script>
	import service from '@/utils/request'
  import RegionList from '@/components/RegionList'
  import ConsoleSubnetFilter from '@/components/ConsoleSubnetFilter'
  import SubnetList from '@/components/SubnetList'
  import createSubnetView from './createSubnet'
  import { getRegionId, setRegionId,getZoneId } from '@/utils/auth'
  import { getEcmPlatforms,GetRegionNum } from '@/api/ecm'
  import i18n from '@/lang'
  import { statFormat } from '@/utils/statformat'
  import { getNetList,GetVpcDetail,listZone,getVpcList ,createSubnet,deleteSubnet,editSubnet,queryAsyncJob,queryQuota } from '@/api/vpc'
  export default {
    props:{
      name:[String],
      buttonText:[String]
    },
    components:{
      RegionList,
      ConsoleSubnetFilter,
      SubnetList
    },
    data(){
      return {
        totalCount:0,
        loaded:false,
        subdetailRefresh:false,
        jobid:[],
        regionData:[],
//      subTotalCount:'--',
//      subExistCount:'--',
//      subRemainCount:'--',
        Pagination:{
          'pagingData':[10,20,50],
          'total':0,
          'show':false
        },
        filter:{
          status:'',
          limit:10,
          page:1,
          name:'',
          id:'',
          vpcid:''
        },
        options: [{value:'',label:'全部VPC'}],
        optionsValue: '',
        tableData:[],
        netOperation:[{
          label:i18n.vpc.name,
          value:'name'
        },{
          label:i18n.vpc.state,
          value:'state'
        },{
          label:i18n.vpc.cidr,
          value:'cidr'
        },{
          label:i18n.vpc.vpcname,
          value:'vpcname'
        },{
          label:i18n.vpc.gateway,
          value:'gateway'
//      },{
//        label:i18n.vpc.dns1,
//        value:'dns1'
//      },{
//        label:i18n.vpc.dns2,
//        value:'dns2'
        }],
      }
    },
    methods:{
      selectChange(filter){
        this.filter.vpcid=filter.vpcid;
        this.getNetList()
      },
      search(filter){
          console.log(filter);
        this.filter.name='';
        this.filter.keyword=filter.subnetName;
        this.getNetList();
      },
      refresh(){
        this.filter.status = ''
//      this.filter.name=''
//      this.filter.id=''
        this.filter.vpcid=''
//      this.filter.keyword=''
        this.GetRegionNums()
        this.getNetList()
      },
      telnet(){
        console.log('telnet')
      },
      handleSelectionChange(val) {
        this.multipleSelection = val;
      },
      handleClick(){

      },
      autoRefresh(){
      	if(this.subdetailRefresh){
  				return
  			}
      	let _this = this;
				if(this.jobid){
  				this.jobid.map((item,index) =>{
  					console.log(item);
						if(item.fjobid){
							this.getJobDetail(item.fjobid,index,()=>{
								item.fjob = true;
							})
						}
						this.getJobDetail(item.jobid,index,()=>{
								item.job = true;
						})
  				})
				}
			},
  		getJobDetail(id,index,fn){
				let _this = this;
				let timer;
				queryAsyncJob(id).then(res=>{
					console.log(fn)
					let result = res.data.results[0];
					if(result.jobstatus == 1){
						fn();
						if(_this.jobid[index].fjobid){
								if(_this.jobid[index].fjob && _this.jobid[index].job){
									_this.filter.keyword=_this.jobid[index].name;
		        			let netlist;
		        			getNetList(_this.filter).then((res)=>{
		        				_this.filter.keyword= '';
		        			 	netlist = res.data.results[0];
		        			 	_this.setData(netlist);
		        			 	_this.tableData.map((item,index) =>{
				  							if(item.id == netlist.id){
					  								_this.$set(_this.tableData,index,netlist)
					  								console.log(index,netlist)
					  								return true;
					  							}
					  						})
		        			}).catch((error)=>{
						          console.log(error)
						      })
								}
						}else{
							if(_this.jobid[index].job){
								_this.filter.keyword=_this.jobid[index].name;
		        			let netlist;
		        			getNetList(_this.filter).then((res)=>{
		        				_this.filter.keyword= '';
		        			 	netlist = res.data.results[0];
		        			 	_this.setData(netlist);
		        			 	_this.tableData.map((item,index) =>{
				  							if(item.id == netlist.id){
					  								_this.$set(_this.tableData,index,netlist)
					  								console.log(index,netlist)
					  								return true;
					  							}
					  						})
		        			}).catch((error)=>{
						          console.log(error)
						      })
							}
						}
					}else if(result.jobstatus == 2){
						clearTimeout(timer)
						this.refresh()
					}else{
						timer = setTimeout(()=>{
							_this.getJobDetail(result.jobid,index,fn)
						},5000)
					}
				}).catch(error=>{
					this.subdetailRefresh = true;
					setTimeout(()=>{
						this.refresh()
					},5000)

				})
  		},
      buttonClick(funName,data){
        funName(data);
      },
      create(){
//        this.$router.push({path:'/net/creatSubnet'})
//					if(this.subRemainCount < 0 ){
//						this.$message({
//	            message: '配额不足!',
//	            type: 'error'
//	          });
//					}else{
		        let view = createSubnetView;
		        let data = {};
		        data.type='create';
		        data.title="创建子网";
		        data.regionId=this.regionId;
		        let params={
	      			regionid:getRegionId()
	      		};
	          let form = {
	            "vpc": false,
	            "aclid": "",
	            "desc": "",
	            "sub_name": "",
	            "sub_cidr": "",
	            "gateway_ip": "",
	            "vpcid":"",
	            "zoneid": getZoneId(),
	            "quotas":this.$store.state.user.user.quotas
	          }
	          let option = {
	            data,
	            view,
	            form
	          }
	          this.$alertDialog(option).then((res)=>{
	            let data={
	              "aclid": res.aclid,  //#acllist的id
	              "desc": res.desc,  //#描述
	              "sub_name":res.sub_name,
	              "sub_cidr": res.sub_cidr,
	              "gateway_ip":res.gateway_ip,
	              "vpcid": res.vpcid,
	              "is_lb": res.subType,
	              "zoneid": res.zoneid,//#上面zone列表中的第一个id
	            }
	            if(res.sub_cidr == 'acl'){
	            	return
	            }else{
	            	createSubnet(data).then((response)=>{
		              let network=response.data.results[0].network;
		              this.tableData.push(network);
		              this.$message({
		                message: '创建子网成功',
		                type: 'success'
		              });
		              this.refresh();
		            }).catch((error)=>{
		              console.log(error);
		//              this.$message('删除VPC失败，此VPC下存在子网！');
		            })
	            }

	          });
//				}

      },
      buttonClick(funName,data){
//        console.log(funName);
//      console.log(data);
        funName(data);
      },
      deleteNet(arg){
      	if(arg.deleter){
      		return
      	}
        let alertMsg=this.$message;
        let view = createSubnetView;
        let data = {}
        data.type='delete';
        data.title="删除子网";
        let form ={
          name:arg.name
        }
        let option = {
          data,
          view,
          form
        }
        let netId=arg.id;
        let vpcid=arg.vpcid;
        let _this = this;
        this.$alertDialog(option).then((res)=>{
        	arg.deleter = true;
        	this.tableData.map((item,index) =>{
  							if(item.id == arg.id){
  								this.$set(this.tableData,index,arg)
  								console.log(index,arg)
  								return true;
  							}
  						})
          deleteSubnet(netId,vpcid).then((response)=>{
            alertMsg('删除子网成功');
//          let count = 0;
//          for(var i =0;i < _this.tableData.length;i++){
//          	if(netId == _this.tableData[i].id){
//          		count = i;
//          		break;
//          	}
//          }
//          _this.tableData.splice(count,1);
            console.log(response);
            this.refresh();
          }).catch((error)=>{
          	this.refresh();
//          let result=error.response.data.code;
//          if(result=='SUBNET.004'){
//            this.$message({
//              message: '该子网下有云服务器，不能删除！',
//              type: 'error'
//            });
//          }else{
//            console.log(error);
//          }
          })
        });
      },
      editNet(arg){
        let alertMsg=this.$message;
        let refresh=this.refresh;
        let view = createSubnetView;
        let data = {}
        console.log(arg);
        data.type='edit';
        data.title="修改子网";
        let form ={
          name:arg.name,
          aclName:arg.aclname,
          aclId:arg.aclid,
          vpcid:arg.vpcid,
          desc:arg.displaytext,
          edit:true
        }
        let option = {
          data,
          view,
          form
        }
        let netId=arg.id;
        let _this = this;
        this.$alertDialog(option).then((res)=>{
        	console.log(res);
        		if(res.name == arg.name && res.aclId == arg.aclid && res.desc == arg.displaytext){
        			return
        		}else{
        			let data;
        			if(res.aclId == arg.aclid){
        				data={
		            	"name":res.name,
		            	"desc":res.desc,
		          	}
        			}else{
        				data={
		            	"name":res.name,
		            	"desc":res.desc,
		            	"firewall_uuid":res.aclId,
		          	}
        			}
		          editSubnet(netId,data).then((response)=>{
		          	alertMsg('修改子网成功');
		          	this.subdetailRefresh = false;
		          	_this.jobid.push({
		          		id:netId,
		          		jobid:response.data.results[0].jobid,
		          		fjob : false,
									job : false,
									name:data.name,
		          		fjobid:response.data.results[0].fjobid?response.data.results[0].fjobid : ''
		          		});
		            _this.autoRefresh(_this.jobid);
		          }).catch((error)=>{
		            console.log(error);
		          })
        		}
        });
      },
      clear(){
  			this.filter.keyword='';
  			this.filter.name='';
  			this.filter.id='';
  		},
      changeRegion(uuid){
      	service.cancle('CancelToken');
        this.regionId = uuid;
        this.GetRegionNums()
//      this.queryQuotas();
//				this.filter ={
//	          status:'',
//	          limit:10,
//	          page:1,
//	          name:'',
//	          id:'',
//	          vpcid:''
//     	};
        this.options = [{value:'',label:'全部VPC'}];
        this.subdetailRefresh = true;
        this.getVpcList()
        this.getNetList()
      },
      getRegion(){
        let timer1;
        if(getRegionId()){
          clearTimeout(timer1)
          this.changeRegion();
        }else{
          let _this = this;
          timer1 = setTimeout(function(){
            _this.getRegion()
          },300)
        }
      },
      getVpcList(uuid){
        let params ={
            limit:10,
            page:1,
            name:'',
            id:'',
            vpcid:''
        };
        getVpcList(params,uuid).then((res)=>{
          let vpclist = res.data.results;
//        if(this.options.length===0){
//          this.options=this.filterserch(vpclist);
            this.filtersearch(vpclist);
//        }
        }).catch((error)=>{
//        this.loading=false
          this.options = [{value:'',label:'全部VPC'}];
          console.log(error)
        })

      },
      getNetList(){
        let params =this.filter;
		    this.loaded = true;
        getNetList(params).then((res)=>{
        	this.loaded = false;
          let netlist = res.data.results;
          this.tableData=this.filterData(netlist);
          let totalCount = res.data.total_count;
          this.totalCount = totalCount;
          this.Pagination.total = this.totalCount;
          if(totalCount>this.Pagination.pagingData[0]){
            this.Pagination.show = true
          }
        }).catch((error)=>{
        	if(error.message == 'CancelToken'){

  				}else{
  					this.loaded=false
  				}
          console.log(error)
        })
      },
      filtersearch(data){
//      var options=[{value:'',label:'全部VPC'}];
				let _this = this;
        data.filter(function(val){
            var obj={};
            let fakeVpc = val.fake_vpc && (val.fake_vpc == 1);
          obj['id']=val.id;
          obj['value']=val.id;
          val.name = fakeVpc ? (val.name == 'fake_vpc' ? '默认网络' : val.name) : val.name;
//        obj['label']= fakeVpc ? '默认网络' : val.name;
          obj['label']= val.name;
          _this.options.push(obj);
          return true;
        })
//        options.push();
//      return options;
      },
      filterData(data){
        var timer;
        var Implementing=[];
        let filter=this.filter;
        let changeData=this.changeData;
        let _this = this;
        data=data.filter(function(net,index){
        	net.fake_subnet && net.fake_subnet == 1 && (net.disabled = true) && (net.vpcname = (net.vpcname == 'fake_vpc' ? '默认网络' : net.vpcname))
          if(_this.regionType == 'cs'){
          	if(net.state==='Allocated'){
          	  net.state="已分配";
          	}else if(net.state==='Implementing'){
          	  Implementing.push(net.name);
          	  net.state = net.state ? statFormat(net.state) : '--';
          	}else{
          	  net.state = net.state ? statFormat(net.state) : '--';
          	}
          }
 
          if(_this.regionType == 'os'){
	        	net.gateway = net.gateway_ip;
	        	net.status = net.status == 'ACTIVE' && '正常';
	        	net.displaytext = net.description;
	        }

          net.deteler = false;
          return true;
        });
        //
        if(Implementing.length>0){
          Implementing.map(function(val){
            let timer;
            timer=setInterval(function(){
              filter.name=val;
              getNetList(filter).then((res)=>{
                let netDetail = res.data.results[0];
                if(netDetail.state!=='Implementing'){
                    clearInterval(timer);
                    changeData(netDetail);
                }
              }).catch((error)=>{
                if(error.message == 'CancelToken'){

                }else{
                  this.loaded=false
                }
                console.log(error)
              })
            },9000);
          });
        }
        //
        return data;
      },
      changeData(data){
        let _this=this;
        this.tableData.map(function (item, index) {
          if (item.id == data.id) {
            data.state = data.state ? statFormat(data.state) : '--';
            _this.$set(_this.tableData, index, data)
          }
        });
      },
      setData(data){
        	data.fake_subnet && data.fake_subnet == 1 && (data.disabled = true) && (data.vpcname = (data.vpcname == 'fake_vpc' ? '默认网络' : data.vpcname))
          if(data.state==='Allocated'){
            data.state="已分配";
          }else{
            data.state = data.state ? statFormat(data.state) : '--';
          }
          data.deteler = false;
        return data;
      },
      pagingChange(type,val){
        if(type == 'num'){
          this.filter.limit = val;
        }else if(type == 'page'){
          this.filter.page = val;
        }
        this.loaded = false;
        this.getNetList();
      },
      queryQuotas(){
      	queryQuota().then((res)=>{
          console.log(res.data.results);
          let Quota = res.data.results[0];
          this.subTotalCount = Quota.networklimit;
        	this.subExistCount = Quota.networktotal;
        	this.subRemainCount = Quota.networkavailable;
        }).catch((error)=>{
          console.log(error)
        })
      },
      GetRegionNums(){
				let params ={
					resource:'subnet',
					regionid:getRegionId()
				}
				GetRegionNum(params).then( res => {
					let data = res.data.results[0].SUBNET;
					console.log(data)
					if(data && data.detail){
						this.regionData = data.detail;
					}

				})
			},
    },
    computed:{
      changeRowSty(row,index){
        return{'background':'#f0f2f7'}
      },
      changethSty(row,index){
        return{'background':'#f0f2f7','color':'#333333','font-size':'12px','font-weight':'600',}
      },
      regionType(){
      	return this.$store.getters.regionType;
      }
    },
    mounted(){
      this.getRegion();
//    this.queryQuotas();
    },
    beforeDestroy(){
			this.subdetailRefresh = true;
		}
  }
</script>

<style lang="scss" scoped="scoped">
  .main{
    background: #f6f8fb;
  }

  /*.content{*/
    /*.content_top{*/
      /*background: #ffffff;*/
      /*padding-top: 30px;*/
      /*padding-left: 20px;*/
      /*padding-bottom: 20px;*/
    /*}*/
  /*}*/
  .content{
    .content_top{
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      background: #ffffff;
      padding-top: 30px;
      padding-left: 20px;
      padding-bottom: 10px;
      .acl_mark{
        /*display: inline-block;*/
        /*width: 45%;*/
        position: relative;
        top:15px;
        font-size: 12px;
      }
      .acl_search{
        /*display: inline-block;*/
        width: 60%;
        text-align: right;
        .el-row{
          text-align: right;
          .el-col{
            float: right;
          }
        }
      }
    }
  }
  .subnet_mark{
    /*display: inline-block;*/
    /*width: 45%;*/
    /*position: relative;*/
    line-height: 35px;
    /*top:15px;*/
    font-size: 12px;
  }
  .subnet_search{
    /*display: inline-block;*/
    width: 60%;
    text-align: right;
    .el-row{
      text-align: right;
      .el-col{
        float: right;
      }
    }
  }
</style>
<style lang="scss">
  .subnet_search{
    /*display: inline-block;*/
    width: 60%;
    text-align: right;
    .el-row{
      text-align: right;
      .el-col-14{
        float: right;
      }
    }
  }
  #subList{
		min-height: 100px;
		margin-top: 10px;
		background-color: #FFFFFF;
		.el-table{
			height: 150px;
		}
	}
</style>



