<template>
	<div  class="action-content">
		<!--<el-row class="action-tip" v-if="!warn">
			<el-row class="action-tip-title" v-if=" typeCom=='info' ">
				{{actionTip[0]}}
			</el-row>
			<el-row class="action-tip-waring" v-if=" typeCom=='warn' ">
				<el-col :span='2' class="action-img">
					<svg-icon icon-class="warn"></svg-icon>
				</el-col>
				<el-col :span="21" class="action-tip-title">
					<p>
						{{actionTip[0]}}
					</p>
					
				</el-col>
				
			</el-row>
		</el-row>
		<el-row class="action-tip" v-if="warn" id='warnStyle'>
			<el-row class="action-tip-waring">
				<el-col class="action-img">
					<svg-icon icon-class="warn"></svg-icon>
				</el-col>
				<el-col class="action-tip-title">
					<span>
						{{actionTip[0]}}
					</span>
					
				</el-col>
			</el-row>
		</el-row>
		<el-row>
			<p class="action-tip-subtip">
				{{actionTip[1]}}
			</p>
		</el-row>-->
		<el-row class='action-table'>
			<el-col>
				<console-table :tableData='fliterData.data'
					:operationOrNot=false
					:tableOptions = 'fliterData.option'
					:noEffect=true
					:key='fliterData.date'
					:radioShow=true
					:height='350'
					:loading='loading'
					@radioChange='radioChange'
					:Pagination = 'Pagination'
					@pagingEvent = 'pagingChange'
					
					></console-table>
				
			</el-col>
			
		</el-row>
		
	
	
	
	</div>
</template>


<script>
	import ConsoleTable from '@/components/ConsoleTable'
	import ConsoleSearch from '@/components/ConsoleSearch'
	import i18n from '@/lang'
	import { getEbsList } from '@/api/ebs'
	
	export default {
		props:{
			type:[String],
			warn:[Boolean],
			attachOperation:[Boolean],
			actionTip:[Array],
			action:[String],
			form:[Object]
		},
		components:{
			ConsoleTable,
			ConsoleSearch
		},
		data(){
			return {
				
				i18n,
				tableData:{
					data:[],
					option:[{
						prop:'name',
						label:i18n.common.name,
						temp:true,
						linkProp:true
					},{
						prop:'status',
						label:i18n.common.status,
						temp:true,
						status:true
					},{
						prop:'size',
        				label:i18n.ebs.capacityGB,
					},{
						prop:'expire_date',
						label:i18n.common.finishAt,
					}]
					
				},
				snapshot:{
					name:''
				},
				filter:{
					limit: 10,
					status:'',
					page:1,
					attached:0,
					has_disk:1,
				},
				Pagination:{
					'pagingData':[10,20,50],
		        	'total':0,
		        	'show':false
				},
				
			}
		},
		methods:{
			
			getEbsList(){
				this.loading = true;
				getEbsList(this.filter).then((res)=>{
					let data = res.data;
					let volumes = data.results;
					console.log(volumes)
					this.filterVolume(volumes);
					this.tableData.data = volumes;
					this.Pagination.total = data.total_count;
      				if(this.Pagination.total > this.Pagination.pagingData[0]){
      					this.Pagination.show = true
      				}
					this.loading= false
				}).catch((error)=>{
					this.loading = false;
					console.log(error)
				})
			},
			filterVolume(volumes){
				volumes.map(item=>{
					if(item.state == 'ERROR'){
	      					item.state = 'ERROR';
      				}else{
      					item.state = item.virtualmachineid ? 'mount' : 'uninstall';
      				}
      				item.size = item.size/1024/1024/1024;
					//return (item.type == 'DATADISK' && !item.virtualmachineid)
				})
				
			},
			radioChange(rowData){
				this.form.rowData = rowData;
			},
			pagingChange(type,val){
      			if(type == 'num'){
      				this.filter.limit = val;
      			}else if(type == 'page'){
      				this.filter.page = val;
      			}
      			this.getEbsList();
      		},
      		validateForm(){
      			return new Promise((resolve,reject)=>{
      				if(!this.form.rowData){
		      			this.$message({
		      				message:"请选择一个磁盘",
		      				type:'error'
		      			})
      					reject()
      				}else{
      					resolve(this.form)
      				}
      			})
      		}
		},
		computed:{
			typeCom(){
				let type = this.type || 'warn'
				return type 
			},
			
			fliterData(){
				let date = (new Date()).getTime();
				this.$set(this.tableData,'date',date)
				return this.tableData
			},
			
		},
		created(){
			this.getEbsList()
		}
		
		
	}
</script>

<style lang="scss" scoped="scoped">
.action-content{
	min-width: 600px;
}
	.action-tip{
		/*margin-top: -20px;*/
		padding-bottom: 10px;
		font-size:12px;
		min-height: 30px;
		color: #666666;
		.action-tip-title{
			line-height: 30px;
		}
		.action-tip-waring{
			.action-img{
				font-size:30px;
				color:#F9CD76;
				text-align: left;
				padding-left:2px;
			}
			.action-tip-title{
				p{
					line-height: 20px;
				}
			}
		}	
		
		
	}
	.action-table{
		padding:10px 0;
	}
	#warnStyle{
		.action-tip-waring{
			.action-img{
				font-size:40px;
				text-align: center;
				padding-left:0px;
			}
			.action-tip-title{
				text-align: center;
				span{
					font-size: 18px;
				}
				i{
					font-size: 18px;
				}
				.action-tip-subtip{
					color:#999999;
				}
			}
		}
	}
	.action-tip-subtip{
		color:#F9CD76,;
		font-size:12px;
	}
	
</style>


