
import Layout from '@/views/ecm/ecm'
import SnapShotList from '@/views/ecm/SnapShotList'


let Router ={
	path:'snapshot',
	name:'snapshot',
	component:SnapShotList,
	redirect:'snapshot/snapShotList',
	children:[
		{
      		path:'snapShotList',
      		name:'snapShotList',
      		component:SnapShotList
      	}
	]
};

export default Router;



