<template>
	<div class="EipBindChoose">
		<ul>
			<li @click="change(index,item)" v-for="(item,index) in tableData.data" :class="{'active':index===current}">
				<div>
					<div class="svg_bg"><svg-icon :icon-class="item.svg"></svg-icon></div>
					<div class="title">{{item.title}}</div>
					<div class="msg">{{item.msg}}</div>
				</div>
			</li>
		</ul>
	</div>
</template>

<script>
	export default{
		props:{
			tableData:[Object],
			form:[Object],
		},
		data(){
			return{
				current:'',
			}
		},
		methods:{
			change(val,item){
				this.current = val;
				this.form.data = item;
			},
		}
	}
</script>

<style lang="scss">
	.EipBindChoose{
		li{
			float: left;
			border: 1px dashed #3ac0f1;
			/*width: 180px;*/
			width: 250px;
			height: 220px;
			color: #3ac0f1;
			text-align: center;
			margin-bottom: 20px;
			padding-top: 15px;
			cursor: pointer;
			&.active{
				background: #ebf6fa;
			}
			.svg_bg{
				font-size: 65px;
			}
			.title{
				margin-top: 15px;
			}
			.msg{
				padding: 0 10px;
				margin-top: 55px;
			}
		}
	}
</style>


