
import Layout from '@/views/ecm/ecm'
import EipList from '@/views/vpc/EipList'
import EipCreate from '@/views/vpc/EipCreate'



let Router ={
	path:'eip',
	name:'ecmEip',
	component:Layout,
	redirect:'eip/eipList',
	children:[
	    {
	      path: 'eipList',
	      name: 'ecmEipList',
	      component: EipList,
	    },
	    {
	      path: 'eipCreate',
	      name: 'ecmEipCreate',
	      component: EipCreate,
	    }
	]
};

export default Router;



