<template>
	<div class="edit-board">
		<el-form ref="formData" :model="formData" label-width="80px" size="mini" label-position="left" :rules="rules">
			<el-form-item label="名称：" prop="boardName">
			    <el-input placeholder="请输入内容" v-model="formData.boardName" clearable></el-input>
			</el-form-item>
		</el-form>
	</div>
</template>


<script>
	export default {
		props:{
			form:[Object,String],
			tableData:[Object,Array],
		},
		data(){
			return {
				formData: {
					boardName: ''
				},
				rules: {
					boardName: [
			            { required: true, message: '请输入面板名称', trigger: 'blur' },
			            { min: 3, max: 15, message: '长度在 3 到 15 个字符', trigger: 'blur' }
			        ],
				},
			}
		},
		methods:{
			validateForm(){
				return new Promise((resolve,reject,instance)=>{
					var form_valid = false;
					this.$refs['formData'].validate((valid) => {
			          if (valid) {
			          	form_valid = true;
			          }
			        });
					this.form.boardName = this.formData.boardName;
			        form_valid ? resolve() : reject();
				})
			}
		},
		created(){
			this.formData.boardName = this.form.boardName;
		}
	}
</script>

<style lang="scss">
.edit-board {
	.el-form-item--mini.el-form-item, .el-form-item--small.el-form-item{
		margin-top: 18px;
	}
}
</style>


