<template>
	<div class="create_Eip">
		<el-container>
		  	<el-main class="padding-left">
		  		<div>
					<p class="title">基本信息</p>
				</div>
		  		<el-form :model="ruleForm" label-position="left" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm" size='small'>
					<el-form-item label="地域：" prop="resourceType" required>
  						<el-radio-group v-model="ruleForm.resourceType" size="medium" fill='#f9cc76'>
					      	<el-radio-button :label="item.uuid" v-for='(item,index) in regionList' :key='index' @change.native='regionChange(item)'>{{item.name}}</el-radio-button>
    					</el-radio-group>
  					</el-form-item>
  					<el-form-item
				        class="vpc"
				        label="VPC :"
				        prop="vpcid" required
				        v-if="regionType == 'cs'"
				    >
				        <el-select v-model='ruleForm.vpcid' :disabled = "seleceCheck">
				          	<el-option v-for = '(item,index) in vpclist'
				                     :label='item.name'
				                     :value='item.id' :key="index"></el-option>
				        </el-select>
      				</el-form-item>
  					<el-form-item label="带宽：" required>
						<!--<el-slider v-model="ruleForm.bandwidth" @change="bandwidthChange" :max="ruleForm.broadbandMax" :show-input="ruleForm.showInput" :step="broadbandStep"></el-slider>
						<div class="slider-mark">
							<div class="scale">
								<span v-for="number in temp2" :style="'width:' + 100/temp2 + '%'" :key="number">|</span>
								<span>|</span>
							</div>
							<div class="scaleInfo">
								<span style="margin-left: -6px;">0M</span>
								<span style="margin-left: 28.5%;">100M</span>
								<span style="margin-left: 27%;">200M</span>
								<span style="margin-top: 0;margin-right: -16px;">300M</span>
							</div>
						</div>-->
						<slideScale @ofterCallback="bbCallback" :isOften=false></slideScale>
					</el-form-item>
					<div class="title">购买量</div>
					<el-form-item class='eipNumber' label="购买数量：" prop="eipNumber" required>
    					<el-input-number v-model="ruleForm.eipNumber" controls-position="right" @change="sizeChange" @keypress.native="dian190($event)" :min="1" :max="maxCreate"></el-input-number>
  					</el-form-item>
  					<el-form-item label="创建时长：" required>
						<!--<el-slider v-model="ruleForm.broadband" :min="createNumberInfo.broadbandMin" :max="createNumberInfo.broadbandMax" :show-input="createNumberInfo.showInput" @change="timeChange" :format-tooltip="sliderFormatTooltip"></el-slider>
						<div id="time" class="slider-mark">
							<div class="scale">
								<span v-for="number in temp" :style="'width:' + 100/(temp-1) + '%'">|</span>
							</div>
							<div class="scaleInfo" ref="scaleInfo">
								<span  v-for="number in temp" :style="{width: 100/(temp-1) + '%',marginLeft:  (number == 1 ?(-100/(temp-1)/2):'') + '%'}" :key="number"><span v-if="number == 12">1年</span>
								<span v-else-if="number == 13">2年</span>
								<span v-else-if="number == 14">3年</span>
								<span v-else>{{number}}个月</span>
								</span>
							</div>
						</div>-->
						<slideScale :cycleCnt="ruleForm.broadband" @ofterCallback="ofterCallback"></slideScale>
					</el-form-item>
				</el-form>
		  	</el-main>
		  	<el-aside width="200px">
		  		<div class="currentInfo">
			  		<h4>当前配置</h4>
			  		<div class="message">
			  			<dl>
				  			<dt>地域：</dt>
				  			<dd>{{ruleForm.regionName ? ruleForm.regionName : '--'}}</dd>
				  		</dl>
				  		<dl>
				  			<dt>VPC：</dt>
				  			<dd>{{vpcChange}}</dd>
				  		</dl>
				  		<dl>
				  			<dt>带宽：</dt>
				  			<dd>{{ruleForm.bandwidth ? ruleForm.bandwidth :'--'}}Mbit/s</dd>
				  		</dl>
				  		<dl>
				  			<dt>购买数量：</dt>
				  			<dd>{{ruleForm.eipNumber ? ruleForm.eipNumber :'--'}}个</dd>
				  		</dl>
				  		<dl>
				  			<dt>购买时长：</dt>
				  			<dd>{{ruleForm.broadbandMsg ? ruleForm.broadbandMsg :'--'}}</dd>
				  		</dl>
			  		</div>
			  		<el-button class='prepaid' type="warning" icon='el-icon-check'>预付费</el-button>
			  		<!--<div class="show_money">
			  			<span class=" money_icon">￥</span><span class="money_icon price">0.00</span>元
			  		</div>-->
			  		<!--<p class="link" @click='openHelp'>了解计费详情</p>-->
		  		</div>
		  	</el-aside>
		</el-container>
	</div>
</template>

<script>
	import service from '@/utils/request'
	import AttachAction from '@/views/bks/AttachAction'
	import i18n from '@/lang'
	import {getRegionList} from '@/api/login'
	import { getRegionId, setRegionId, SetZoneId, SetVnc,getZoneId, SetRegionType,getRegionType } from '@/utils/auth'
	import { RegiongetVpcList,queryQuota } from '@/api/vpc'
	import slideScale from '@/components/SlideScale'
	export default{
		components:{
			AttachAction,
			slideScale
		},
		data(){
			return {
				temp: 14,
				scaleWidth:60,
				broadbandStep:10,
				maxCreate:0,
				temp2:30,
				seleceCheck:true,
				regionList:[],
				vpclist:[],
				ruleForm:{
					eipNumber:1,
					storageType:'',
		      		resourceType: '',
		      		regionName:'',
		      		uuid:'',
		      		vpcid:'',
		      		vpcName:'',
		      		broadbandMax: 300,
		      		showInput: true,
					bandwidth: 100,
					broadband:6,
		      		broadbandMsg:'6个月',
		      		cycleType:'M',
		      		cycleCnt:6,
		      	},
		      	createNumberInfo: {
					btnType: 'primary',
					btnIcon: '',
					broadbandMin: 1,
					broadbandMax: 14,
					showInput: true,
					num: 2 ,
				},
				rules:{
		      		resourceType: [
	            		
	          		],
	          		vpcid:[
			        	{ required: true, message: '所选VPC不能为空', trigger: 'blur,change' },
			        ]
	          	},
			}
		},
		methods:{
			regionChange(val){
				console.log(val);
				service.cancle('CancelToken');
				this.vpclist = [];
				this.ruleForm.regionName = val.name;
				this.ruleForm.uuid = val.uuid;
				let regionType = val.regionType;
				setRegionId(val.uuid)
				SetZoneId(val.details.zoneId)
				SetVnc(val.details.hasVnc)
				SetRegionType(regionType)
				this.$store.dispatch('changeRegionType',getRegionType())
				regionType == 'cs' && this.getVpcList(val.uuid);
			},
			dian190(ev) {
		        let code = window.event ? event.keyCode : event.which;
		        if(code == 46){
		        	event.preventDefault();return;
		        }
		   	},
			ofterCallback (val) {
	      		this.ruleForm.broadband = val;
	      		if(val == 12){
	      			this.ruleForm.broadbandMsg = '1年';
	      			this.ruleForm.cycleCnt = 1;
	      			this.ruleForm.cycleType = 'Y';
	      		}else if(val == 13){
	      			this.ruleForm.cycleCnt = 2;
	      			this.ruleForm.broadbandMsg = '2年';
	      			this.ruleForm.cycleType = 'Y';
	      		}else if(val == 14){
	      			this.ruleForm.broadbandMsg = '3年';
	      			this.ruleForm.cycleCnt = 3;
	      			this.ruleForm.cycleType = 'Y';
	      		}else{
	      			this.ruleForm.cycleType = 'M';
	      			this.ruleForm.cycleCnt = this.ruleForm.broadband;
	      			this.ruleForm.broadbandMsg = this.ruleForm.broadband + '个月';
	      		}
	      	},
	      	bbCallback (val) {
      			this.ruleForm.bandwidth = val;
      		},
			bandwidthChange(val){
				console.log(val);
			},
			sizeChange(val){
				console.log(val);
			},
			sliderFormatTooltip (value) {
	      		if(value == 12){
	      			this.cycleType = 'Y';
	      			return '1年';
	      		}else if(value == 13){
	      			this.cycleType = 'Y';
	      			return '2年';
	      		}else if(value == 14){
	      			this.cycleType = 'Y';
	      			return '3年';
	      		}else{
	      			this.cycleType = 'M';
	      			return value+'月';
	      		}
	      	},
	      	order(){
	      		var basicInfo_valid = false;
	      		this.$refs['ruleForm'].validate((valid) => {
		          if (valid) {
		          	basicInfo_valid = true;
		          	this.$emit('orderResult',true,this.ruleForm);
		          }else{
		          	this.$emit('orderResult',false,'');
		          }
		        });
	      	},
	      	getRegionList(){
	      		let _this = this;
				this.regionList = this.$store.state.user.regionList;
				console.log(this.regionList);
				this.ruleForm.uuid = getRegionId();
				this.ruleForm.resourceType = getRegionId();
				this.regionList.forEach(function(item){
					/*item.zoneid = item.details && item.details.zoneId;
					item.hasVnc = item.details && item.details.hasVnc;*/
					item.regionType = 1 == item.type ? 'cs' : 2 == item.type && 'os';
					if(item.uuid == _this.ruleForm.uuid){
						_this.ruleForm.regionName = item.name;
					}
				});
				this.getVpcList(this.ruleForm.uuid);
			},
			getVpcList(id){
				let param={
	      			regionid:id
	      		};
	      		let _this = this;
	      		console.log(123,param);
		      	let params={
					regionid: id,
					create:'create',
		      	}
		      	_this.seleceCheck = true;
				RegiongetVpcList(params,getZoneId()).then((res)=>{
			        console.log(res);
			        let vpclist = res.data.results;
			        let vpcs = this.filterFakeVpc(vpclist);
			        if(vpcs.length > 0){
				        _this.vpclist=vpcs;
				        _this.seleceCheck = false;
				        console.log(vpcs);
				        _this.ruleForm.vpcid = vpclist[0].id;
				        _this.ruleForm.vpcName = vpclist[0].name;
			        }else{
			        	_this.vpclist=[];
			        	_this.seleceCheck = false;
			        	_this.ruleForm.vpcid = '';
			        	_this.ruleForm.vpcName = '';
				        _this.$message({
				          	message: '该节点下没有虚拟私有云',
				          	type: 'warning'
				        });
			        }
			    }).catch((error)=>{
			        _this.seleceCheck = false;
			        _this.vpclist = [];
			        _this.ruleForm.vpcid = '';
			        _this.ruleForm.vpcName = '';
			        console.log(error)
			    })
			},
			filterFakeVpc(vpcs){
				return vpcs.filter(item=>{
					return !(item.fake_vpc && item.fake_vpc == 1)
				})
			},
			openHelp(){
	      		open("http://www.ctyun.cn/help");
	      	},
	      	queryQuotas(){
		      	queryQuota().then((res)=>{
		          console.log(res.data.results);
		          let Quota = res.data.returnObj;
//		          this.snapshotLimitQuota = Quota.snapshot_limit - 0;
					this.maxCreate =Quota.public_ip_limit_each_time　? Quota.public_ip_limit_each_time - 0　:'';
		        }).catch((error)=>{
		          console.log(error)
		        })
		    },
		},
		computed:{
			vpcChange: function(){
				if(this.vpclist.length>0){
					this.ruleForm.vpcName = this._.find(this.vpclist,{id:this.ruleForm.vpcid}).name;
					return this.ruleForm.vpcName
				}else{
					return '--'
				}
			},
			regionType(){
				return this.$store.getters.regionType;
			}
		},
		mounted(){
			this.getRegionList();
			let quotas = this.$store.state.user.user.quotas;
//			this.vpcTotalCount = quotas.vpc_limit - 0;
			console.log(quotas);
			if(quotas.public_ip_limit_each_time){
				this.maxCreate = quotas.public_ip_limit_each_time - 0;
			}else{
				this.queryQuotas();
			}
			
		}
	}
</script>

<style lang="scss" scoped="scoped">
	.create_Eip{
		/*width: 300px;*/
		padding: 0 0px 20px 0px;
		min-height: 260px;
		.demo-ruleForm{
			padding-right: 20px;
		}
		.prepaid{
			background-color: #f9cc76;
			border-color: #f9cc76;
		}
		.padding-left{
			padding-left: 0px;
		}
		.el-main{
			padding-left: 20px;
			background-color: #fff;
			padding-right: 0px;
			margin-right: 20px;
		}
		.el-aside{
			background-color: #fff;
		}
		.currentInfo{
			background-color: #fff;
		    height: 360px;
		    padding: 10px;
		    padding-top: 18px;
		    border-radius: 3px;
		    .el-button{
		    	padding: 7px 15px;
		    }
		}
		.title{
			padding: 20px 0;
			background-color: #fff;
		}
		.message{
			margin-top: 20px;
			dl{
				font-size: 12px;
				color: #333333;
				padding-bottom: 15px; 
				&:after{
					display: table;
					content: '';
				}
				dt{
					float: left;
					font-size: 12px;
					color: #333333;
				}
				dd{
					white-space: nowrap;
					overflow: hidden;
					text-overflow: ellipsis;
					font-size: 13px;
				}
			}
		}	
		.show_money{
			margin-top: 30px;
			.money_icon{
				color: #f9cd76;
				font-size: 17px;
				font-weight: 600;
			}
		}
		.link{
			color: #3dc7fa;
			font-size: 12px;
			margin-top: 15px;
		}
	}
</style>
<style lang="scss">
	.create_Eip{
		/*.el-input--small .el-input__inner{
			height: 30px;
		}
		.el-form-item--small .el-form-item__content, 
		.el-form-item--small .el-form-item__label{
			height: 30px;
		}*/
		.el-form-item--small{
			label{
				text-align: left;
			}
		}
		.el-form-item__content{
			.el-radio-group{
				.el-radio-button__inner{
					padding: 7px 15px;
				}
			}
		}
		.el-textarea{
			width: 180px;
			.el-textarea__inner{
				min-height: 80px!important;
			}
		}
		.el-radio-group{
			.el-radio-button{
				margin-right: 10px;
				margin-bottom: 6px;
				.el-radio-button__inner{
					border: 1px solid #dcdfe6;
					border-radius: 4px;
					background: #ebedf2;
				}
			}
		}
		.el-form-item--small{
			label{
				text-align: left;
			}
		}
		/*.slider-mark {
			margin-right: 160px;
		    margin-top: -15px;
		    color: #afbfda;
		}
		.slider-mark > .scale {
			height: 20px;
		}
		.slider-mark > .scale > span {
			display: inline-block;
			height:100%;
		}
		.slider-mark > .scale > span:last-child {
			width: auto !important;
			display: inline;
		    float: right;
		    margin-top: -33px;
		}
		.slider-mark > .scaleInfo {
			font-size: 12px;
		}
		.slider-mark > .scaleInfo > span{
			display: inline-block;
			text-align: center;
		}
		.slider-mark > .scaleInfo > span:last-child {
		    float: right;
		    margin-top: -40px;
		    margin-right: -25px;
		}*/
		#time .scaleInfo{
			position: relative;
		}
		#time .scaleInfo > span:last-child {
			position: absolute;
			float: none;
		    margin-top: 0;
		    margin-right: 0;
		}
	}
</style>


