<template>
	<div style="background: #ffffff; min-height: 330px;">
		<div class="attach_content">
    		<ul class="ebs_operate">
    			
    		</ul>
    	</div>
    	<ConsoleTable 
    		:operationOrNot = false
    		:checkBox='checkBox' 
    		:expand='expand' 
    		:selectOrNot='selectOrNot' 
    		:tableData = 'tableData' 
    		:tableOptions = 'tableOptions'></ConsoleTable>
	</div>
</template>

<script>
	import DetailBtn from '@/components/DetailBtn'
	import ConsoleTable from '@/components/ConsoleTable'
	import i18n from '@/lang'
	
	import { getRegionId } from '@/utils/auth'
	import { timeService } from '@/utils/timeformat'
	import { statFormat } from '@/utils/statformat'
	import { GetEbsDetailBackup } from '@/api/ebs'
	
	export default{
		props:{
			serverId:[String],
			ecmId:[String]
		},
		components:{
			DetailBtn,
			ConsoleTable,
		},
		data(){
			return{
				checkBox:true,
				selectOrNot:true,
				expand:false,
				bksData:[],
				tableOptions:[{
        			prop:'name',
        			label:i18n.common.name,
        			temp:true,
        			detail:true,
        		},{
        			prop:'status',
        			label:i18n.common.status,
        			temp:true,
        			status:true
        		},{
        			prop:'id',
        			label:i18n.common.ID,
        		},{
        			prop:'created',
        			label:i18n.ebs.time,
        		}],
	    	}
		},
		methods:{
			
      		getEbsDetailBackup(id){
      			GetEbsDetailBackup(id).then((res)=>{
      				console.log(res);
      				let Data = res.data.results;
      				this.filterServers(Data);
      				console.log(Data);
      				this.bksData = Data;
      				//return Data;
      			}).catch((req)=>{
					console.log(req)
				})
      		},
      		filterServers(servers){
      			servers.forEach(function(item){
      				item.size = (item.size/1024/1024/1024).toFixed(2);
      				item.status = statFormat(item.state) || item.state;
      			})
      		},
		},
		created(){
			this.getEbsDetailBackup(this.serverId)
		},
		computed:{
			tableData(){
				//this.getEbsDetailBackup(this.serverId);
				return this.bksData;
			}
		}
	}
</script>

<style lang="scss" scoped="scoped">
	.attach_content{
		padding: 20px 0;
		line-height: 30px;
		.left_title{
			font-size: 12px;
			padding-left: 20px;
			height: 30px;
			line-height: 30px;
		}
	}
</style>


