<template>
	<div>
		<ConsoleCreate ref="createProcess" :system_type='name' :switchPage = 'nextPage' :checked='!checked' :submitForm="submitForm" @orderEvent = 'orderEvent'>
			<div slot='create_title' class="create_title" v-show="page==0">
					<BksCreateFirst ref="createOrder" @orderResult = 'orderResult'></BksCreateFirst>
			</div>
			<div slot="confirm_title" class="confirm_title" v-show="page==1">
				<div style="padding: 20px 0;background-color: #fff;">资源详情</div>
		        <ConsoleConfirm :tableData="tableData"></ConsoleConfirm>
		        <div class="Agreement_component">
				    <p>
				      	<el-checkbox v-model="checked">我已阅读并同意相关协议</el-checkbox>
				      	<span @click="goProtocol" class="agreement">《 云硬盘备份服务协议 》</span>
				    </p>
			  	</div>
			</div>
			<div slot="submit_title" class="submit_title"  v-if="page===2">
	        	<ConsoleSuccess :return_url='return_url' :orderWay=true :system_type='name' :system_name="tableData.system_name" ></ConsoleSuccess>
	      	</div>
		</ConsoleCreate>
	</div>
	
</template>

<script>
	import ConsoleCreate from '@/components/ConsoleCreate'
	import ConsoleConfirm from '@/components/ConsoleConfirm'
  	import ConsoleSuccess from '@/components/ConsoleSuccess'
  	import BksCreateFirst from '@/views/bks/BksCreateFirst'
	import i18n from '@/lang'
	import { CreateBks } from '@/api/bks'
	import { listZone } from '@/api/ebs'
	import { getEcmPlatforms } from '@/api/ecm'
	import { getRegionId } from '@/utils/auth'
	export default {
		components:{
			ConsoleCreate:ConsoleCreate,
			ConsoleConfirm,
	     	ConsoleSuccess,
	      	BksCreateFirst,
		},
	    data() {
	      	return {
	      		page:0,
	      		checked:false,
	      		name:'云硬盘快照',
	      		return_url:'/ebs/bks/bksList',
	      		ebsID:'',
	      		Agreement:{
	          		url:'http://www.baidu.com',
	          		name:'<<服务协议>>'
	        	},
	      		tableData:{
		          	system_name:'',//资源名称
		          	elseData: [{
		          		system_type:'快照',//资源类型
		      			num:'1',
		      			confirmData:[],
		           }],
		          	tableOptions: [{
		    			prop:'num',
		    			label:i18n.ebs.Number,
		    		}],
		        },
		        ruleFormData:'',
	      	}
	    },
	
	    methods: {
	    	orderEvent(){
				this.$refs.createOrder.order();
	    	},
	    	goProtocol(){
	    		window.open('http://www.ctyun.cn/term/vbs');
	    	},
	    	orderResult(val,data){
	    		console.log(val);
				if(val){
					this.ruleFormData = data;
					console.log(this.ruleFormData);
					this.$refs.createProcess.next();
				}else{
					return
				}
	    	},
	      	nextPage(index){
	      		console.log(index);
	      		this.page=index;
	      		if(index == 1){
	      			this.tableData.elseData = [{
	      				system_type:'备份',//资源类型
	      				num:'1',
	      				confirmData:[{
			      			label: i18n.common.name,
			      			value:this.ruleFormData.name
			      		},{
			      			label: i18n.ebs.disk,
			      			value:this.ruleFormData.diskName
			      		},{
			      			label: i18n.bks.describe,
			      			value:this.ruleFormData.desc
			      		}],
	      			}];
		      		this.tableData.system_name=this.ruleFormData.name;
	      		}
	      	},
	      	submitForm(fn) {
//	          	let params={
//	      			regionid:getRegionId()
//	      		};
//	      		let NowPlatforms = '';
//	        	getEcmPlatforms(params).then((res)=>{
//	        		console.log(res.data.platforms);
//	        		let Platforms = res.data.platforms;
//	        		NowPlatforms = Platforms.filter((item)=>{
//	        			if(item.uuid == params.regionid){
//	        				return true
//	        			}else{
//	        				return false
//	        			}
//	        		});
//	        		console.log(NowPlatforms);
	          		this.ebsID = this.ruleFormData.ebsId
	          		let data={
						'name':this.ruleFormData.name,
						'description':this.ruleFormData.desc,
						'volume_id':this.ebsID,
			          };
		          	CreateBks(data).then((res)=>{
	      				console.log(res)
	      				if(res.data.code == 200){
					        this.$message({
					          	message: '命令下发成功',
					          	type: 'success'
					        });
					        fn();
		      			}else{
		      				this.$message({
					          showClose: true,
					          message: res.data.reason || res.data.msg,
					          type: 'error'
					        });
		      			}
	      			}).catch((error)=>{
	      				console.log(error);
	      				this.$message({
				          	message: '命令下发失败',
				          	type: 'error'
				        });
	      			});
//		        }).catch((error)=>{
//		          this.loading=false
//		          console.log(error)
//		        });
		    },
	    }
	}
</script>

<style lang="scss" scoped="scoped">
	.Agreement_component{
	    font-size: 12px;
	    margin-top: 20px;
      	.agreement {
			color: #3dc7fa;
			cursor: pointer;
			font-weight: bold;
		}
	}
</style>
<style lang="scss">
	.create_title{
		.el-form-item--small{
			label{
				text-align: left;
			}
		}
		.el-textarea{
			width: 180px;
			.el-textarea__inner{
				min-height: 80px!important;
			}
		}
	}
	.confirm_title{
		.mt5{
			display: block;
			margin-top: 5px;
		}
	}
</style>



