
import Layout from '@/views/ebs/ebs'
import BksList from '@/views/bks/BksList'
import BksCreate from '@/views/bks/BksCreate'

import i18n from '@/lang'

let Router ={
	path:'bks',
	name:'bks',
	component:Layout,
	redirect:'bks/bksList',
	children:[
		{
      		path:'bksList',
      		name:'bksList',
      		component:BksList,
      	},
		{
      		path:'bksCreate',
      		name:'bksCreate',
      		component:BksCreate,
      		meta:{
      			dependOn:'ebsBksSub'
      		}
      	},
	]
};

export default Router;



