<template>
	<div class="detail-content">
		<el-row class="detail-title">
			云硬盘
		</el-row>
		<el-row class="detail-top">
			
			<el-col :span="20">
				您还可以挂载
					{{ attachNum }}
				块磁盘
			</el-col>
			<el-col :span="4">
				<el-row type="flex" justify="end" >
					<detail-btn :btnMsg='btnMsg' @deatilBtnClick="attach"></detail-btn>
				</el-row>
			</el-col>
		</el-row>
		<!--<el-row class="detail-top" type="flex" justify="end"  v-if="!quotaShow">
			<detail-btn :btnMsg='btnMsg' @deatilBtnClick="attach"></detail-btn>
		</el-row>-->
		<el-row>
			<console-table :tableData = 'tableData'
				:tableOptions="tableOption"
				:operationOrNot = true
				:loading = 'loading'
				:OperationEvents = 'OperationEvents'
				@buttonClick="detach"
				></console-table>
		</el-row>
		<el-row class="border-style"></el-row>
	</div>
	
</template>

<script>
	import DetailBtn from '@/components/DetailBtn'
	import ConsoleTable from '@/components/ConsoleTable'
	import ecmAttchVolume from './ecmAttachVolume'
	import i18n from '@/lang'
	
//	import quota from '@/baseConfig/quota';
	
	import { GetEcmDetail, GetEbsDetailById } from '@/api/ecm'
	import { EbsAttach, EbsDetach } from '@/api/ebs'
	import { queryQuota} from '@/api/vpc'
	import { GetJobDetail } from '@/api/console'
	
	let timer
	export default {
		props:{
			serverId:[String],
			serverData:[Object]
		},
		components:{
			DetailBtn,
			ConsoleTable
		},
		methods:{
			attach(value){
				if(this.attachNum > 0){
					let option = {
						data:{
							title:i18n.ecm.attachVolume,
						},
						view:ecmAttchVolume,
						form:{}
					}
					this.$alertDialog(option).then((formRes)=>{
						console.log(formRes)
						
						EbsAttach(formRes.rowData.id,this.serverId).then((res)=>{
							this.$message({
								message:'命令下发成功，请稍后……',
								type:'warning'
							});
							let data = res.data.results;
							this.autoRefresh(data)
						}).catch((error)=>{
							console.log(error)
						})
					})
				}else{
					this.$message({
			          	message: '一台云主机只能挂载五块数据盘!请卸载一块磁盘后再执行该操作',
			          	type: 'warning'
			        });
				}
				
			},
			detach(fun,rowdata){
				console.log(rowdata);
				
					let option={
						data:{
							actionTip:['确定要卸载磁盘:'+ rowdata.name],
							title:'卸载',
							warn:true
						}
					}
					
					this.$alertDialog(option).then(()=>{
						this.$message({
							message:'卸载磁盘命令下发成功，请稍后',
							type:'warning'
						})
						
						EbsDetach(rowdata.id,this.serverId).then((res)=>{
							console.log(rowdata)
							this.tableData.map(item=>{
								item.id == rowdata.id && this.addOperation(item,'detach')
							})
							//this.serverData.publicipid = '';
							let data = res.data.results;
							this.autoRefresh(data)
						}).catch((error)=>{
							console.log('cuowu1')
							console.log(error)
							this.refresh()
						})
					})
				
			},
			getEbsDetailById(id){
				this.loading = true;
				GetEbsDetailById(id).then((res)=>{
					
					let data = res.data;
					let volumes = data.results;
					this.volumeCount = data.total_count || 0;
//					this.formate(volumes)
					this.serverData.volumeDetail = [];
					this.serverData.volumeDetail = volumes;
					console.log(volumes)
					this.loading = false
				}).catch((req)=>{
					this.loading = false
					console.log(req)
				})
			},
			formate(volumes){
				let _this = this;
				let checkNum = /^[0-9]*$/;
				volumes && volumes.map((item)=>{
					item.type = "DATADISK" == item.type ? '数据盘' : '系统盘';
					//判断OS或者CS字段
					let diskType = item.disk_types || item.volume_type;
					item.disk_types = _this.diskType[diskType] || diskType;
					let size = 0;
					if('cs' == _this.regionType){
						size = item.size//1024/1024/1024
					}
					if('os' == _this.regionType){
						size = item.size
					}
					item.size = size + 'GB';
					item.sata = item.sata || '--';
					item.dev = item.dev || '--'
					this.addOperation(item)
				})
			},
			addOperation(volume,detach){
				let Operation = [];
				if(this.serverData.servers.state == 'Running'){
					Operation =[
						{
							value:i18n.ebs.detach,
		        			disabled:true,
		        			title:i18n.ebs.detachTip
						}
					];
				}else if(volume.type && volume.type != 'DATADISK'){
					Operation =[
						{
							value:i18n.ebs.detach,
		        			disabled:true,
		        			title:i18n.ebs.rootTip
						}
					];
				}else if(volume.with_vm){
					Operation =[
						{
							value:i18n.ebs.detach,
		        			disabled:true,
		        			title:i18n.vpc.withVmTip
						}
					];
				}else{
					Operation =[
						{
							value:i18n.ebs.detach,
		        			disabled:false,
						}
					];
				}
				if(detach == 'detach'){
					Operation =[
						{
							value:i18n.ebs.detach,
		        			disabled:true,
						}
					];
				}
				
				this.$set(volume,'operation',Operation)
			},
			goEbsEetail(name,data){
				console.log(name)
				console.log(data)
				let id = data.id
				id && this.$router.push('/ebs/ebsDetail/'+id);
			},
			refresh(){
				this.getEbsDetailById(this.serverId);
			},
			autoRefresh(jobIds){
      			if(jobIds){
      				if(typeof(jobIds)=='string'){
      					
      					this.getJobDetail(jobIds)
      					
	      			}else if(Array.isArray(jobIds)){
	      				jobIds.map(item =>{
	      					console.log(item)
	      					item.jobid && this.getJobDetail(item.jobid)
	      				})
	      			}else{
	      				console.log(jobIds)
	      			}
      			}
      		},
      		getJobDetail(id){
      			let _this = this;
      			let timer;
      			if(this.detroyAutoRefresh) return
      			GetJobDetail(id).then(res=>{
  					let result = res.data.results[0];
  					if(result.jobstatus == 1){
  						this.$message({
							message:'操作成功',
							type:'success'
						})
  						this.refresh();
  						
  					}else if(result.jobstatus == 2){
  						clearTimeout(timer)
  						console.log('失败')
  						this.$message({
  							message:'操作失败'+result.jobresult.errortext,
  							type:'error'
  						})
  						this.refresh()
  					}else{
  						timer = setTimeout(()=>{
  							_this.getJobDetail(result.jobid)
  						},5000)
  					}
  					
  				}).catch(error=>{
  					console.log(error)
  					this.refresh()
  				})
      		},
      		queryQuotas(){
		      	queryQuota().then((res)=>{
		          console.log(res.data.results);
		          let Quota = res.data.returnObj;
//		          this.volumeLimitCount = Quota.volume_limit_each_vm - 0;
					this.volumeLimitCount =Quota.volume_limit_each_vm ? Quota.volume_limit_each_vm - 0 : '';
		        }).catch((error)=>{
		          console.log(error)
		        })
		    },
		},
		data (){
			return {
				detroyAutoRefresh:false,
				loading:false,
				attachNum:'--',
				volumeCount:'--',
				volumeLimitCount:'--',
				diskType:{
					'SATA':'普通IO',
					'SAS':'高IO',
					'SSD':'超高IO'
				},
				btnMsg:[
					{
						value:'挂载磁盘',
						disabled:false,
						fun:'attach'
					}
				],
				tableOption:[
					{
	        			prop:'name',
	        			label:i18n.common.name,
	        			temp:true,
	        			detail:true,
	        			path:'/ebs/ebsDetail',
		        	},
		        	{
	        			prop:'types',
	        			label:i18n.ebs.diskModel,
	        		},
		        	{
	        			prop:'disk_types',
	        			label:i18n.ebs.storageType,
	        		},
	        		{
	        			prop:'size',
	        			label:i18n.ebs.capacityGB,
	        		},
	        		/*{
	        			prop:'sata',
	        			label:i18n.ebs.type,
	        		},
	        		{
	        			prop:'dev',
	        			label:i18n.ebs.attachDev,
	        		}*/
				],
				//tableData:[],
				OperationEvents:[
					{
						value:i18n.ebs.detach,
	        			fun:(data)=>{
	        				return this.detach(data)
	        			}
					}
				]
			}
		},
		
		mounted(){
			//console.log(this.serverId)
			/*console.log()
			let volumeDetail = this.serverData && this.serverData.volumeDetail;
			volumeDetail ? this.getEbsFromCache(volumeDetail) : this.getEbsDetailById(this.serverId)*/
//			this.queryQuotas();
			let quotas = this.$store.state.user.user.quotas;
			if(quotas.volume_limit_each_vm){
				this.volumeLimitCount = quotas.volume_limit_each_vm - 0;
			}else{
				this.queryQuotas();
			}
			
		},
		computed:{
			tableData(){
				let volumeDetail = this.serverData && this.serverData.volumeDetail;
				//console.log(volumeDetail)
				//volumeDetail && this.formate(volumeDetail)
				
				volumeDetail && volumeDetail != '--' && this.formate(volumeDetail)
				if(this.volumeLimitCount == '--' || volumeDetail=='--'){
		        	this.attachNum = '--';
		        }else{
		        	this.volumeCount = volumeDetail.length - 1;
		        	if(this.volumeCount < 0){
		        		this.volumeCount = 0;
		        	}
		        	let RemainCount = this.volumeLimitCount - this.volumeCount;
		        	if(RemainCount < 0 ){
		        		this.attachNum =  0;
		        	}else{
		        		this.attachNum = RemainCount;
		        	}
		        }
		        if(this.serverData.servers.state == 'Starting'){
		        	this.btnMsg.map(function(item){
						item.disabled = true
					});
		        }
		        if(volumeDetail == '--'){
		        	return []
		        }else{
		        	return volumeDetail;
		        }
				//return volumeDetail;
			},
			regionType(){
				return this.$store.getters.regionType;
			}
		},
		destroyed(){
			this.detroyAutoRefresh = true;
		}
	}
</script>

<style scoped="scoped" lang="scss">
	.detail-title{
		padding-top:20px;
	}
	.detail-top{
		padding:20px 0;
		line-height: 30px;
		div{
			font-size:14px;
		}
		
	}
	.detail-content{
		padding:0 20px;
		background:#FFFFFF;
		/*min-height:330px;*/
	}
	.border-style{
		height:20px;
		border-bottom:3px solid #f6f8fb;
	}
</style>


