import i18n from '@/lang'

import Layout from '@/views/vpc/vpc'
import NetList from '@/views/vpc/netList'


let Router ={
  path:'net',
  name:'net',
  component:Layout,
  redirect:'net/netList',
  children:[
    {
      path:'netList',
      name:'netList',
      component:NetList,
      props: { name: i18n.vpc.subnet, buttonText: i18n.vpc.createSubnet }
    },
  ]
};

export default Router;



