<template>
	<div class="sec-addrules">
		<el-form label-width="80px" size="mini" :model="secRules" :rules="rules" ref="secRules">
			<el-form-item label="方向 :" prop="direction">
				<el-radio v-for="item in secRules.direction.option" v-model="secRules.direction.model" size="mini" :label="item.value" :key="item.value">{{item.label}}</el-radio>
			</el-form-item>
			<el-form-item label='协议 :' prop="protocol">
				<el-select v-model="secRules.protocol.model" size="mini" @change="resetFileds">
					<el-option v-for="item in secRules.protocol.option" :label="item.label" :value="item.value" :key="item.value"></el-option>
				</el-select>
			</el-form-item>
			<el-form-item label="类型 :" prop="ethertype" v-show="secRules.protocol.model =='icmp'">
				<el-select v-model="secRules.ethertype.model" size="mini">
					<el-option v-for="item in secRules.ethertype.option" :label="item.label" :value="item.value" :key="item.value"></el-option>
				</el-select>
			</el-form-item>
			<el-form-item label="端口范围 :" class="secrules-portrange" prop="port_range" v-if="secRules.protocol.model =='tcp' || secRules.protocol.model =='udp'">
				<el-input v-model="secRules.port_range.model" :placeholder="secRules.port_range.content"></el-input>
			</el-form-item>
			<el-form-item label="源地址 :" prop="source">
				<el-radio v-for="item in secRules.source.option" v-model="secRules.source.model" size="mini" :label="item.value" :key="item.value" @change="getSecList">{{item.label}}</el-radio>
			</el-form-item>
			<el-form-item v-if="secRules.source.model=='sgs'" prop="sgsId">
				<el-select v-model="secRules.sgsId" size="mini" :disabled="sgsLoading">
					<el-option v-for="item in secRules.sgsList" :label="item.name" :value="item.id" :key="item.value"></el-option>
				</el-select>
				<i class="el-icon-loading" v-if="sgsLoading"></i>
			</el-form-item>
			<el-form-item v-if="secRules.source.model=='ip'" prop="cidr">
				<!--<div id="secRulesIp" class="secRulesIp">
					<div class="secRulesIp-cidr secRulesIp-inline">
						<el-input type="text" 
							v-model="secRules.cidr.first"
							@keydown.native ="onkeydowns($event,0,'secRulesIp')"
							:maxlength='3'
							 ></el-input>
						<span>.</span>
						<el-input type="text" 
							@keydown.native ="onkeydowns($event,1,'secRulesIp')"
							v-model="secRules.cidr.second"
							:maxlength='3'
							 ></el-input>
						<span>.</span>
						<el-input type="text" 
							@keydown.native ="onkeydowns($event,2,'secRulesIp')"
							v-model="secRules.cidr.third"
							:maxlength='3'
							></el-input>
						<span>.</span>
						<el-input type="text" 
							@keydown.native ="onkeydowns($event,3,'secRulesIp')"
							:maxlength='3'
							v-model="secRules.cidr.fourth"
							 ></el-input>
					</div>
					<div class="secRulesIp-spilt secRulesIp-inline">/</div>
					<div class="secRulesIp-port secRulesIp-inline">
						<el-input type="text" 
							@keypress.native="replaceNum($event)" :maxlength="2"
							v-model="secRules.cidr.last"
							 ></el-input>
					</div>
				</div>-->
				<ConsoleIp :cidr='secRules.cidr' :toolTip="toolTip"></ConsoleIp>
			</el-form-item>
		</el-form>
	</div>
</template>

<script>
	import {IpInputKeyDown} from '@/utils/shareFunction';
	import {digitReg,secMaskCheck,maskReg} from '@/utils/validator';
	import {getAclList} from '@/api/vpc';
	import ConsoleIp from '@/components/ConsoleIp';
	export default {
		props:{
			tableData:[Object],
			form:[Object]
		},
		components:{
			ConsoleIp
		},
		data(){
			let that = this;
			let checkPort = (rules,value,callback)=>{
				let port = value.model
				if(port){
					let ports = port.split("-");
					let portRangeTest = port.indexOf("-") >= 0 ? !!(/^\d+$/.test(ports[0]) && ports[0] >= 1 && ports[0] < 65536 && /^\d+$/.test(ports[1]) && ports[1] >= 1 && ports[1] < 65536) : !!(/^\d+$/.test(port) && port >= 1 && port < 65536)
					if(portRangeTest){
						let portalRelation = !(port.indexOf("-") >= 0) || parseInt(ports[0], 10) <= parseInt(ports[1], 10);
						if(portalRelation){
							if(digitReg(port)){
								that.secRules.port_range.min = ports[0];
								that.secRules.port_range.max = ports[1] || ports[0];
								callback()
							}else{
								callback(new Error('取值是整数'))
							}
						}else{
							callback(new Error('端口下限应小于等于端口上限'))
						}
					}else{
						callback(new Error('端口取值范围在1-65536'))
					}
				}else{
					callback(new Error('请输入端口号'))
				}
			};
			let checkMask = (rules,value,callback)=>{
				let ip,mask;
				if(value.data){
					let ips = value.data.split('/');
					ip=ips[0];
					mask=ips[1];
				}else{
					ip = `${value.first}.${value.second}.${value.third}.${value.fourth}`;
					mask = value.last;
				}
				if(secMaskCheck(ip,mask)){
					if(maskReg(mask)){
						that.secRules.cidr.ip = `${ip}/${mask}`;
						callback()
					}else{
						callback(new Error('子网掩码不合法'))
					}
				}else{
					callback(new Error('子网IP地址与子网掩码不匹配'))
				}
			};
			return {
				getFirst:false,
				sgsLoading:false,
				toolTip:{
					content:'0.0.0.0/0表示所有IP地址',
					show:true,
					tipPosition:'bottom-end'
				},
				secRules:{
					direction:{
						model:"ingress",
						option:[{
							label:'入方向',
							value:'ingress'
						},{
							label:'出方向',
							value:'egress'
						}],
					},
					protocol:{
						model:'tcp',
						option:[{
							label:'TCP',
							value:'tcp'
						},{
							label:'ICMP',
							value:'icmp'
						},{
							label:'UDP',
							value:'udp'
						},{
							label:'ANY',
							value:'any'
						}]
					},
					port_range:{
						content:'例如20或20-33',
						model:'',
						min:'',
						max:''
					},
					ethertype:{
						model:"any",
						option:[{
							label:'Any',
							value:'any'
						}]
					},
					source:{
						model:'ip',
						option:[{
							label:'IP',
							value:'ip'
						},{
							label:'安全组',
							value:'sgs'
						}],
					},
					cidr:{
						first:'0',
						second:'0',
						third:'0',
						fourth:'0',
						last:'0',
						ip:'0.0.0.0/0'
					},
					sgsList:[],
					sgsId:"",
				},
				
				rules:{
					direction:[{required:true,trigger:'blur'}],
					protocol:[{required:true,trigger:'blur'}],
					source:[{required:true,trigger:'blur'}],
					sgsId:[{required:true,trigger:'blur',message:'请选择一个安全组'}],
					ethertype:[{required:true,trigger:'change'}],
					port_range:[{validator:checkPort,trigger:'blur,change'},{required:true}],
					cidr:[{validator:checkMask,trigger:'blur,change'}],
				}
			}
		},
		methods:{
			onkeydowns(event,num,target){
				IpInputKeyDown(event,num,target)
			},
			validateForm(){
				let secRules = this.secRules;
				let form = {
					direction:secRules.direction.model,
					ethertype:'',
					protocol:secRules.protocol.model,
					port_range_min:"",
					port_range_max:"",
					remote_ip_prefix:secRules.cidr.ip,
					remote_group_id:secRules.sgsId
				}
				secRules.protocol.model == 'icmp' && (form.ethertype = secRules.ethertype.model);
				if(secRules.protocol.model == 'tcp' || secRules.protocol.model == 'unp'){
					form.port_range_min = 	secRules.port_range.min;
					form.port_range_max = 	secRules.port_range.max;
				}
				this.form.data = form;
				console.log(this.form)
				console.log(this)
				return this.$refs.secRules.validate();
			},
			resetFileds(){
				//this.$refs.secRules.resetFields();
				//console.log(this.secRules.ethertype)
			},
			replaceNum(event){
				let code = window.event ? event.keyCode : event.which;
				if(!window.event){
					let arr = [8,9,110,37,39,0];
					if(arr.includes(code)) return
				}
				if(code==46){
					let next = event.target.parentElement.nextElementSibling ? event.target.parentElement.nextElementSibling.nextElementSibling.firstElementChild :'';
					next && next.focus()
					next && next.setSelectionRange(0,3);
				}
				code>=48 && code<=57 || event.preventDefault()
			},
			getSecList(){
				if(this.getFirst) return
				let params = {
					page:1,
					limit:100
				};
				this.sgsLoading = true;
				getAclList(params).then(res=>{
					let data = res.data.results;
					this.getFirst = true
					this.sgsLoading = false;
					this.secRules.sgsList = data;
					console.log(this.sgsList)
				}).catch(error=>{
					this.sgsLoading = false;
				})
			},
		},
		mounted(){
			//this.getSecList()
		}
	}
</script>

<style lang="scss" scoped="scoped">
	
	.secrules-portrange{
		.el-input{
			width: 200px;
		}
	}
	.secRulesIp{
		width:100%;
		.secRulesIp-inline{
			display: inline-block;
			height:28px;
			line-height: 28px;
			vertical-align:middle;
			input{
				height:100%;
				border:none;
				padding:0;
				text-align: center;
				width:38px;
				color:#606266;
			}
		}
		.secRulesIp-cidr{
			border:1px solid #dcdfe6;
			border-radius: 4px;
			overflow: hidden;
			background: #FFFFFF;
			width:200px;
			color:#606266;
		}
		.secRulesIp-spilt{
			margin:0 5px;
		}
		.secRulesIp-port{
			border:1px solid #dcdfe6;
			border-radius: 4px;
			overflow: hidden;
			background: #FFFFFF;
			input{
				width:40px;
			}
		}
	}
	.el-form-item.is-error{
		.secRulesIp-cidr,.secRulesIp-port{
			border-color:#f56c6c
		}
	}
</style>
<style lang="scss">
	.sec-addrules{
		.el-form{
			label.el-form-item__label{
				font-size:12px;
			}
		}
		.secRulesIp{
			.el-input{
				width:38px;
				.el-input__inner{
					border:none;
					text-align: center;
					padding:0;
				}
			}
		}
		
	}
</style>


