<template>
	<div class="bill">
		<el-container>
			<el-header height="100px" class="content_bg">
				<div class="contentInfo">
					<h4 class="botton_15 top_10">已出账单</h4>
					<el-radio-group v-model="year" size="mini" fill="#f9cd76">
						<el-radio-button class="radio-group-btn" label="2017">2017年</el-radio-button>
						<el-radio-button class="radio-group-btn" label="2016">2016年</el-radio-button>
						<el-radio-button class="radio-group-btn" label="2015">2015年</el-radio-button>
						<el-radio-button class="radio-group-btn" label="2014">2014年</el-radio-button>
					</el-radio-group>
				</div>
			</el-header>
			<div class="item_list">
				<el-row v-for = "(number,index) in temp" :key="index">
				  <el-col :span="24">
				  	<div class="grid-content bg-purple-dark">
				  		<div class="month">
				  			<div class="month_border">
				  				12月
				  			</div>
				  		</div>
				  		<div class="info">
				  			<div class="info_row">账单地址：新疆博乐博州五台工业区（湖北工业园）管理委员会</div>
				  			<div class="info_row">账单时间：2017-12-26 13:23</div>
				  			<div>金额：<span class="money">¥1184.4</span></div>
				  		</div>
				  		<div class="btns">
				  			<el-button size="mini" icon="el-icon-printer">打印</el-button>
				  			<el-button size="mini" icon="el-icon-message">邮件</el-button>
				  			<el-button size="mini" icon="el-icon-tickets">查看明细</el-button>
				  		</div>
				  	</div>
				  </el-col>
				</el-row>
			</div>
		</el-container>
	</div>
</template>

<script>
	export default {
		
		data (){
			return {
				year:2017,
				temp:3,
			}
		}
	}
</script>

<style scoped lang="scss">
.bill{
	.item_list {
		margin-top: 30px;
	}
	.content_bg {
		background-color: #fff;
	    border-radius: 3px;
	}
	.radio-group-btn {
		margin-right: 10px;
	}
	.botton_15 {
		margin-bottom: 15px;
	}
	.top_10 {
		margin-top: 10px;
	}
	.contentInfo {
		height: 100%;
	}
	.el-row {
	    margin-bottom: 10px;
	    &:last-child {
	      margin-bottom: 0;
	    }
	  }
	.grid-content {
	    border-radius: 4px;
	    height: 100px;
	}
	.bg-purple-dark {
	    background: #fff;
	}
	.month {
		height: 100%;
	    float: left;
		width: 70px;
	    padding: 0 20px;
	    border-right: 2px solid #f6f8fb;
	}
	.info {
		display: inline-block;
		padding: 20px;
	}
	.info_row {
		font-size: 13px;
	    padding-bottom: 10px;
	}
	.btns {
		display: inline-block;
		float: right;
	    margin-top: 36px;
	    margin-right: 30px;
	}
	.month_border {
		text-align: center;
	    line-height: 60px;
		width: 60px;
	    height: 60px;
	    border-radius: 50%;
	    border: 2px dashed #68c5f5;
	    color:#68c5f5;
	    margin-top: 18px;
	}
	.money {
		color:#f9cd76;
	}
}
</style>
<style>
.bill .el-radio-button__inner{
	border-radius: 3px !important;
}
.bill .el-radio-button__inner {
		border: 1px solid #dcdfe6;
	    border-radius: 3px !important;
	}
</style>


