<template>
	<div class="ecm_monitor">
		<div class="top">
			<el-row>
			  	<el-col :span="15">
			  		<div class="left">
			  			<p class="node_name">{{name}}</p>
			  			<span class="areaButton"
			  				:class="{'areaButton-bg':currentId==area.uuid}" 
							@click="changeArea(area)" 
							v-for="(area,index) in areas" >
			  				{{area.name }}
			  			</span>
			  		</div>
			  	</el-col>
			  	<el-col :span="9">
			  		<div class="right">
			  			<el-date-picker
					      v-model="value4"
					      type="datetimerange"
					      :picker-options="pickerOptions2"
					      range-separator="至"
					      start-placeholder="开始日期"
					      end-placeholder="结束日期"
					      align="center" size="small">
					    </el-date-picker>
			  		</div>
			  	</el-col>
			</el-row>
		</div>
		<div class="line"></div>
		<div class="content">
			<div class="content_top">
				<el-input placeholder="请输入内容" v-model="input5" class="input-with-select" size="small" style="width:350px">
				    <el-select v-model="select" slot="prepend" placeholder="请选择">
				      <el-option label="餐厅名" value="1"></el-option>
				      <el-option label="订单号" value="2"></el-option>
				      <el-option label="用户电话" value="3"></el-option>
				    </el-select>
				    <el-button slot="append" icon="el-icon-search"></el-button>
				  </el-input>
			</div>
			<div>
				<ConsoleTable 
					:tableData='ecmListData'
					:tableOptions="tableOptions"
					:operationOrNot=true
					:OperationEvents='ecmOperation'
					@buttonClick="handleClick"
					:loading = 'loading'
					:dragable=true
					:Pagination = 'Pagination'
					@pagingEvent = 'pagingChange'
					></ConsoleTable>
			</div>
		</div>
	</div>
</template>

<script>
	import RegionList from '@/components/RegionList'
	import ConsoleTable from '@/components/ConsoleTable'
	import i18n from '@/lang'
	
	export default {
		components:{
			RegionList,
			ConsoleTable,
		},
		data(){
			return {
				name: '告警历史',
				loading:false,
				input5: '',
      			select: '',
      			value4: '',
      			pickerOptions2: {
		          shortcuts: [{
		            text: '最近一周',
		            onClick(picker) {
		              const end = new Date();
		              const start = new Date();
		              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
		              picker.$emit('pick', [start, end]);
		            }
		          }, {
		            text: '最近一个月',
		            onClick(picker) {
		              const end = new Date();
		              const start = new Date();
		              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
		              picker.$emit('pick', [start, end]);
		            }
		          }, {
		            text: '最近三个月',
		            onClick(picker) {
		              const end = new Date();
		              const start = new Date();
		              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
		              picker.$emit('pick', [start, end]);
		            }
		          }]
		        },
      			currentId: '4m',
      			areas: [
      				{
      					name: '1小时',
      					uuid: '1m'
      				},
      				{
      					name: '2小时',
      					uuid: '2m'
      				},
      				{
      					name: '4小时',
      					uuid: '4m'
      				},
      				{
      					name: '6小时',
      					uuid: '6m'
      				},
      				{
      					name: '12小时',
      					uuid: '12m'
      				},
      				{
      					name: '1天',
      					uuid: '1y'
      				},
      				{
      					name: '3天',
      					uuid: '3y'
      				},
      				{
      					name: '7天',
      					uuid: '7y'
      				},
      				{
      					name: '一个月',
      					uuid: '1M'
      				},
      			],
				filter:{
					status:'',
					limit:10,
					page:1,
					keyword:'',
					id:''
				},
				Pagination:{
					'pagingData':[10,20,50],
		        	'total':0,
		        	'show':false
				},
				ecmListData: [
					{
						displayname: 'ecs-a8fe',
						status: 'dsafas',
						flavor: '正常',
						id: 'adsfd2345dsdffsd',
						num: 10,
					}
				],
        		tableOptions:[{
        			prop:'displayname',
        			label: '产品类型',
        		},{
        			prop:'status',
        			label: '故障资源',
        		},
        		{
        			prop:'flavor',
        			label: '发生时间',
        		},{
        			prop:'id',
        			label: '持续时间',
        			
        		},{
        			prop:'num',
        			label: '规则名称',
        		},{
        			prop:'num',
        			label: '通知方式',
        		},{
        			prop:'num',
        			label: '状态',
        		},{
        			prop:'num',
        			label: '通知对象',
        		}],
        		ecmOperation:[{
        			value: '展开',
        			id:'telnet',
        			fun:(data)=>{
        				return this.monitorInfo(data);
        			}
        		}]
			}
		},
		methods:{
      		monitorInfo(data){
      			console.log(data);
      		},
      		changeRegion(uuid){
      			this.getEcmList()
      		},
      		getEcmList(action){
      			let params = this.filter;
      			this.loading =true;
      			getEcmList(params).then((res)=>{
      				let servers = Array.from(res.data.results);
      				let totalCount = res.data.total_count;
      				this.filterServers(servers,action)
      				this.ecmListData = servers;
      				this.totalCount = totalCount;
      				this.Pagination.total = this.totalCount;
      				if(totalCount>this.Pagination.pagingData[0]){
      					this.Pagination.show = true
      				}
      				this.loading=false
      			}).catch((error)=>{
      				this.loading=false
      				console.log(error)
      			})
      		
      		},
      		handleClick(funName,rowData){
      			funName(rowData)
      		},
      		pagingChange(type,val){
      			if(type == 'num'){
      				this.filter.limit = val;
      			}else if(type == 'page'){
      				this.filter.page = val;
      			}
      			this.getEcmList();
      		},
      		changeArea(item) {
      			console.log(item);
      		}
		},
		
		mounted(){
			
		},
		beforeDestroy(){
			
		}
		
	}
</script>

<style lang="scss" scoped="scoped">
	.ecm_monitor{
  		background: #f6f8fb;
		 .content{
		  	.content_top{
		  		background: #ffffff;
		  		padding-top: 30px;
		  		padding-left: 20px;
		  		padding-bottom: 20px;
		  	}
		}
		.top{
	  	background: #ffffff;
	  	min-height: 119px;
	  	border-bottom: 1px solid #f8f9fb;
	  	position: relative;
	  	&:after{
	  		content: '';
	  		height: 0;
	  		width: 0;
	  		border: 8px solid #f6f8fb;
	  		border-color: transparent transparent transparent #f6f8fb;
	  		position: absolute; 
	  		bottom: -9px;
	  		left: 0px;
	  	}
	  	&:before{
	  		content: '';
	  		height: 0;
	  		width: 0;
	  		border: 8px solid #f6f8fb;
	  		border-color: transparent #f6f8fb transparent transparent;
	  		position: absolute; 
	  		bottom: -9px;
	  		right: 0px;
	  	}
	  	.left{
		  	/*width: 65%;*/
		  	/*float: left;*/
		  	padding-left: 2%;
		  	text-align: left;
		  	.node_name{
				font-size: 14px;
				color: #333333;
				padding: 20px 0px;
			}
			.areaButton{
				display: block;
				min-width: 68px;
				height: 30px;
				margin-bottom: 15px;
				background: #f0f2f7;
				float: left;
				text-align: center;
				line-height: 30px;
				border-radius: 5px;
				margin-right: 10px;
				font-size: 11px;
				cursor: pointer;
				-webkit-user-select:none;
   				-moz-user-select:none;
  	 			-ms-user-select:none;
   				user-select:none;
			}
			.areaButton-bg{
				background: #f9cd76;
				color: #ffffff;
			}
			.areaButton:hover{
				background: #f9cd76;
				color: #ffffff;
			}
		}
		.right{
		  	padding-top: 52px;
		  	.createButton{
				width: 120px;
				height: 36px;
				background: #3ac0f1;
				border-color: #3ac0f1;
				color: #ffffff;
				font-size: 12px;
				margin-right: 15px;
				&:hover{
					background: #5ed4ff;
				}
			}
		}
	  }
	  .line{
		  	height: 10px;
		  	width: 100%;
		  	background: #f0f2f7;
		  	background-size: 50% 100%;
		  	background-repeat: no-repeat;
		  }
  	}
</style>
<style lang="scss">
.ecm_monitor{
	.el-select .el-input {
		    width: 100px;
		  }
		  .input-with-select .el-input-group__prepend {
		    background-color: #fff;
		  }
}
</style>


