<template>
	<div class="renewInfo">
		<el-container v-if="submit">
			<el-main>
				<h4 class="renew_info_title">{{dataList.titleName}}</h4>
				<div class="renew_info_details">
					<el-row class="border_bottom">
					  <el-col :span="12">
					  	<div class="grid-content font_14_3">
					  		实例名称：{{dataList.name}}
					  	</div>
					  </el-col>
					  <el-col :span="12">
					  	<div class="grid-content right">
					  		<i class="el-icon-time time_color"></i>
					  		<label class="font_14_6">到期时间：</label>
					  		<span class="time_color">{{dataList.expire_date}}</span>
					  	</div>
					  </el-col>
					</el-row>
					<el-row>
					  <el-col :span="8" v-for="(data,index) in dataList.nodeList" :key="index">
					  	<div class="grid-content font_12">
					  		<span class="color_6">{{data.title}}：</span>
					  		<span class="color_3">{{data.value}}</span>
					  	</div>
					  </el-col>
					</el-row>
				</div>
				<div class="ECSrenew_row">
					<label class="color_3 font_12">续费时长：</label>
					<el-radio-group v-model="renewTime" size="mini" fill="#f9cd76">
						<el-radio-button class="radio-group-btn" :label="time.value" v-for="(time,index) in renewTimes" :key="index">{{time.name}}</el-radio-button>
					</el-radio-group>
				</div>
				<div class="ECSrenew_row">
					<label class="color_3 font_12">续费后到期时间：</label>
					<span class="span_color font_12" style="font-weight: bold;">{{new_expire_date}}</span>
				</div>
				<div class="ECSrenew_row">
					<label class="YaHei_14_3">配置费用：</label>
					<span class="pay_style">¥601.80</span>
				</div>
				<div class="ECSrenew_row font_12">续费价格按照基础带宽进行计算</div>
				<div class="ECSrenew_row">
					<el-checkbox v-model="checkedAgreement">我已经阅读并同意相关协议</el-checkbox>
					<a href="http://www.ctyun.cn/term/ecs" class="agreement" target="_blank">《弹性公网IP服务协议》</a>
				</div>
				<el-button type="primary" size="small" class="payment_btn" :disabled="!checkedAgreement" @click.stop="renew_btn">去支付</el-button>
			</el-main>
		</el-container>
		<div class="success_content" v-if="!submit">
		    <div class="success_component">
				<span class="success_icon">
					<i class="el-icon-circle-check"></i>
				</span>
				<div>
					<div class="create_state div_left">ECS续费成功</div>
					<div class="div_left">
					    <span>订单编号：</span>
					    <span>{{so_number}}</span>
					</div>
					<div class="div_left" v-if="false">
					    <span>续费后日期：</span>
					    <span>2019-01-08</span>
					</div>
					<div class="reback_msg div_left">页面将于{{new_second_number}}秒内返回<span class="timer">{{new_second_number}}</span></div>
					<div class="return_btn div_left">
					    <el-button type="primary" size="small" @click.stop="returnUrl">返回</el-button>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import {renewPay} from '@/api/cost'
	export default {
		props:{
	        dataList: {
	            type: Object,
	            default: {}
	        }
	    },
		data (){
			return {
				submit: true,
				renewTime: '1m',
				cycleType: 'M',
				cycleCnt: '1',
				so_number: '-',
				new_second_number: 5,
				checkedAgreement:false,
				renewTimes: [
					{
						name:'1月',
						value:'1m',
					},
					{
						name:'2月',
						value:'2m',
					},
					{
						name:'3月',
						value:'3m',
					},
					{
						name:'4月',
						value:'4m',
					},
					{
						name:'5月',
						value:'5m',
					},
					{
						name:'6月',
						value:'6m',
					},
					{
						name:'7月',
						value:'7m',
					},
					{
						name:'8月',
						value:'8m',
					},
					{
						name:'9月',
						value:'9m',
					},
					{
						name:'10月',
						value:'10m',
					},
					{
						name:'11月',
						value:'11m',
					},
					{
						name:'1年',
						value:'1y',
					},
					{
						name:'2年',
						value:'2y',
					},
					{
						name:'3年',
						value:'3y',
					},
				],
			}
		},
		methods: {
			renew_btn(){
				const loading = this.$loading({
					          lock: true,
					          text: '拼命续费中',
					          spinner: 'el-icon-loading',
					          background: 'rgba(0, 0, 0, 0.7)'
					        });
				renewPay({
					id: [this.dataList.id],
					cycleType: this.cycleType,
					cycleCnt: this.cycleCnt,
				}).then((res) => {
					loading.close();
					console.log(res);
					if(res.status == 200){
						this.so_number = res.data.returnObj.so_number;
						this.submit = !this.submit;
        				this.timerOut();
					}else{
						this.$message({
				          showClose: true,
				          message: res.data.reason || res.data.msg || '续费失败，请稍后重试',
				          type: 'error'
				        });
					}
				},(error) => {
					loading.close();
					console.log(error);
					this.$message({
				          showClose: true,
				          message: '续费失败，请联系管理员',
				          type: 'error'
				        });
				});
			},
			returnUrl() {console.log('asdas');
				this.$emit('callback');
			},
			timerOut () {
		        this.timer = setTimeout(() => {
		                if(this.new_second_number === 1){
		                  this.returnUrl();
		                }else{
		                  this.new_second_number --;
		                  this.timerOut();
		                }
		            }, 1000);
		     }
		},
		mounted: function(){
			console.log(this.dataList);
		},
		computed: {
			new_expire_date: function() {
				var renew_date = parseInt(this.renewTime);
				var d=new Date(this.dataList.expire_date); 
				if(this.renewTime.indexOf('y') !== -1){
					d.setYear(d.getFullYear()+renew_date);
					this.cycleType = 'Y';
				}else{
					d.setMonth(d.getMonth()+renew_date);
					this.cycleType = 'M';
				}
				this.cycleCnt = renew_date;
				return d.Format("yyyy-MM-dd hh:mm:ss");
			}
		}
	}
</script>

<style lang="scss" scoped="scoped">
.renewInfo {
	height: 100%;
    background: #fff;
	.span_color {
		color: #f9cd76;
	}
	.payment_btn {
		margin-top: 20px;
	}
	.ECSrenew_row {
		margin-top: 30px;
	}
	.border_bottom {
		border-bottom: 1px solid #dcdfe2;
		margin-bottom: 20px;
	}
	.renew_info_title {
		margin-bottom: 25px;
		color: #333;
    	font-weight: bold;
	}
	.right {
		float: right;
	}
	.grid-content {
	    border-radius: 4px;
	    min-height: 36px;
	}
	.renew_info_details {
		background-color: #fbfcfd;
	    padding: 20px;
	}
	.agreement {
		color: #3dc7fa;
		font-weight: bold;
		font-size: 12px;
	}
	.font_14_3 {
		font-size: 14px;
    	font-weight: bold;
    	color: #333;
	}
	.font_12 {
		font-size: 12px;
	}
	.color_3 {
		color: #333;
	}
	.color_6 {
		color: #666;
	}
	.font_14_6 {
		font-size: 14px;
    	color: #666;
	}
	.time_color {
		color: #3ac0f1;
	}
	.YaHei_14_3 {
    	font-size: 14px;
    	color: #333;
	}
	.pay_style {
		font-size: 24px;
    	color: #f9a90c;
	}
	.el-button--primary {
		background-color: #3dc7fa;
    	border-color: #3dc7fa;
	}
	.is-disabled {
		background-color: #75d3f3;
    	border-color: #75d3f3;
	}
	.success_icon{
	    .el-icon-circle-check{
	      color: #56d6c4;
	      font-size: 40px;
	      margin-right: 10px;
	    }
	}
	.return_btn {
	      padding: 20px 0 0 15px;
	      .el-button{
	      	background-color: #3ac0f1;
	      }
	}
	.create_state {
	      font-size: 20px;
	      padding-bottom: 5px;
	}
	.timer {
	      padding-left: 10px;
	      font-size: 15px;
	      color: #ecc79a;
	}
    .success_component{
      display: flex;
      flex-direction: row;
      width: 30%;
      margin: 0 auto;
      text-align: center;
      margin-top: 5%;
      .reback_msg{
        color: #999999;
        margin-top: 20px;
        font-size: 12px;
      }
    }
    .success_content{
    	padding-top: 20px;
    }
}
</style>
<style>
.renewInfo .el-checkbox__label {
	font-size: 12px;
}
.renewInfo .el-checkbox__input.is-checked+.el-checkbox__label {
	color: #3dc7fa;
}
.renewInfo .el-checkbox__input.is-checked .el-checkbox__inner, .el-checkbox__input.is-indeterminate .el-checkbox__inner {
	background-color: #3dc7fa;
    border-color: #3dc7fa;
}
</style>


