<template>
	<div class="main snapshotList">
		<RegionList :name ='name' :regionData='regionData' :buttonText ='buttonText' @create='create' @changeRegion = 'changeRegion'></RegionList>
		<div class="content">
			<div class="content_top">
				<div class="snap_mark">您还可以创建{{snapRemainCount}}个快照</div>
				<ConsoleSearch 
					:optionsValue ='optionsValue' 
					@searchEvent = 'search'
					@clear = 'clear'
					:btnClose=true
					:disableData='changeTableData'
					@refreshEvent = 'refresh'
					></ConsoleSearch>
			</div>
			<div>
				<ConsoleTable 
					:tableData='snapshot.tableData'
					:tableOptions="snapshot.option"
					:checkBox=true
					:operationOrNot=true
					:OperationEvents='snapshot.operationEvent'
					@buttonClick="handleClick"
					@changeTable="changeTable"
					:loading = 'loading'
					:dragable=true
					:noEffect=true
					:Pagination = 'Pagination'
					@pagingEvent = 'pagingChange'
					></ConsoleTable>
			</div>
			
		</div>
	</div>
</template>

<script>
	import service from '@/utils/request'
	import RegionList from '@/components/RegionList'
	import ConsoleSearch from '@/components/ConsoleSearch'
	import ConsoleTable from '@/components/ConsoleTable'
	import i18n from '@/lang'
	import { getRegionId, setRegionId } from '@/utils/auth'
	import { GetSnapshotList, MakeSnapshot, DeleteSnapshot, RebackSnapshot, GetEcmDetail,GetRegionNum } from '@/api/ecm'
	import { queryQuota} from '@/api/vpc'
	import { GetJobDetail } from '@/api/console'
	
	import ecmCreateSnapshot from './ecmCreateSnapshot'
	export default {
		components:{
			RegionList,
			ConsoleSearch,
			ConsoleTable
		},
		data(){
			return {
				detroyAutoRefresh:false,
				loading:true,
				buttonText:i18n.ecm.makeSnapshot,
				name:i18n.ecm.snapshot,
				optionsValue:'',
				newSnapName:'',
				snapshotTotalCount:0,
				snapshotRemainCounts:0,
        		snapshotLimitQuota:0,
				filter:{
					limit:10,
					page:1,
					name:'',
					id:'',
					keyword:''
				},
				Pagination:{
					'pagingData':[10,20,50],
		        	'total':0,
		        	'show':false
				},
				snapshot:{
					tableData:[],
					option:[
						{
							prop:'displayname',
							label:i18n.ecm.snapshotName,
							temp:true,
							detail:true,
							noEffect:true
						},
						{
							prop:'id',
							label:i18n.ecm.snapshotId,
							width:'300px',
							temp:true,
							detail:true,
							noEffect:true
						},
						{
							prop:'vm_displayname',
							label:i18n.ecm.ecmName
						},{
							prop:'status',
							label:i18n.common.status,
							temp:true,
							status:true
						},
						{
							prop:'created',
							label:i18n.common.createAt
						}
					],
					operationEvent:[
						{
							value:i18n.common.delete,
							fun:(data)=>{
								return this.deleteSnapshot(data)
							}
						},
						{
							value:i18n.ecm.reback,
							fun:(data)=>{
								return this.reback(data)
							}
						}
					
					]
				},
				changeTableData:[],
				regionData:[],
				PublicOperation:[
					{
						value:'删除',
						fun:'delete'
					}
				]
			}
		},
		methods:{
			create(){
				if(this.snapRemainCount > 0){
					let option = {
						data:{
							title:i18n.ecm.makeSnapshot,
							actionTip:[i18n.ecm.makeSnapshotTip,i18n.ecm.makeSnapshotTip2],
							type:'warn'
						},
						view:ecmCreateSnapshot,
						form:{}
					}
					this.$alertDialog(option).then((formRes)=>{
						console.log(formRes)
						MakeSnapshot(formRes.rowData.id,formRes.name).then((res)=>{
							this.$message({
								message:'正在创建快照，请稍后……',
								type:'warning'
							});
							let data = res.data.results;
							this.refresh();
							this.autoRefresh(data)
						}).catch((error)=>{
							console.log(error)
						})
					})
				}else{
					this.$message({
			          	message: '快照配额不足!',
			          	type: 'warning'
			        });
				}
			},
			changeRegion(){
				service.cancle('CancelToken');
//				this.GetRegionNums()
//				this.getSnapshotList()
				this.refresh();
			},
			aciton(action){
				(action == 'delete') && this.deleteSnapshot(this.changeTableData)
			},
			handleClick(funName,rowData){
      			funName(rowData)
      		},
			changeTable(val){
				this.changeTableData = val
			},
			
			getSnapshotList(){
				this.loading=true
				let params = this.filter
				GetSnapshotList(params).then((res)=>{
					let data = res.data;
					let snapshotList = data.results;
					this.filterSnapshot(snapshotList);
					this.snapshot.tableData = snapshotList;
					this.Pagination.total = data.total_count;
      				if(this.Pagination.total > this.Pagination.pagingData[0]){
      					this.Pagination.show = true
      				}
					this.loading=false
				}).catch((error)=>{
					if(error.message == 'CancelToken'){
      					
      				}else{
      					this.loading=false
      				}
					console.log(error)
				})
			},
			getSnapshotListById(item){
				let params ={
					id:item.id
				}
				GetSnapshotList(params).then(res=>{
					let data = res.data;
					let snapshot = data.results[0];
					if(snapshot.state !=='Creating' && item.state !== snapshot.state){
						this.$message({
							message:'快照 '+item.displayname +' 创建成功',
							type:'success'
						})
						this.filterSnapshot(snapshot)
						this.snapshot.tableData.map((list,index)=>{
							if(list.id == item.id){
								this.$set(this.snapshot.tableData,index,snapshot)
							}
						})
						
						//this.refresh()
					}else{
						let that = this;
						if(snapshot.state !=='Creating' || this.detroyAutoRefresh ) return
						setTimeout(()=>{that.getSnapshotListById(item)},5000)
					}
					
				}).catch(error=>{
					this.refresh()
					console.log(error)
				})
			},
			filterSnapshot(snapshotList){
				let createOrError = ['creating','error']
				if(Array.isArray(snapshotList)){
					snapshotList.map(item=>{
						if(createOrError.includes(item.state.toLowerCase())){
							
							item.operation = [{
									value:i18n.common.delete,
									disabled:true,
									fun:(data)=>{
										return this.deleteSnapshot(data)
									}
								},
								{
									value:i18n.ecm.reback,
									disabled:true,
									fun:(data)=>{
										return this.reback(data)
									}
								}]
						}
						if(item.state.toLowerCase() == 'creating'){
							item.id ? this.getSnapshotListById(item) && this.getStarting(item,'clear') : this.getStarting(item)
						}
						if(item.state.toLowerCase() == 'error'){
							item.operation = [{
									value:i18n.common.delete,
									fun:(data)=>{
										return this.deleteSnapshot(data)
									}
								},
								{
									value:i18n.ecm.reback,
									disabled:true,
									fun:(data)=>{
										return this.reback(data)
									}
								}]
						}
						
					})
				}else{
					if(createOrError.includes(snapshotList.state.toLowerCase())){
							
						snapshotList.operation = [
							{
								value:i18n.common.delete,
								disabled:true,
								fun:(data)=>{
									return this.deleteSnapshot(data)
								}
							},
							{
								value:i18n.ecm.reback,
								disabled:true,
								fun:(data)=>{
									return this.reback(data)
								}
							}
						];
					}else{
						
					}
				}
			},
			getStarting(item,clear){
      			if(!this.newSnapName){
      				this.startTimer(item)
      			}
      		},
      		startTimer(item,clear){
      			let that = this
      			if(this.detroyAutoRefresh) return
      			that.newSnapName || (that.newSnapName = item.name);
      			if(clear){
      				this.newSnapName = '';
      				this.refresh();
      				return;
      			}
      			setTimeout(()=>{
      				that.refresh()
//    				that.startTimer()
      			},6000)
      		},
			deleteSnapshot(rowData){
				let option ={
					data:{
						title:i18n.common.delete,
						actionTip:[i18n.ecm.deleteSnapshotTip],
						warn:true,
						data:rowData
					}
				}
				if(Array.isArray(rowData)){
					option.data.warn = false;
				}
				this.$alertDialog(option).then((formres)=>{
					console.log(formres);
					const loading = this.$loading({
						lock: true,
				        text: '正在下发命令，请稍后……',
				        spinner: 'el-icon-loading',
				        background: 'rgba(0, 0, 0, 0.7)'
				    })
					DeleteSnapshot(rowData.id).then((res)=>{
						loading.close()
						this.$message({
							message:'删除快照命令下发成功！',
							type:'success'
						});
						this.refresh();
						let data = res.data.results;
						this.autoRefresh(data)
					}).catch((req)=>{
						loading.close()
						console.log(req)
					})
				})
			},
			reback(rowData){
				let option ={
					data:{
						title:i18n.ecm.reback,
						actionTip:[i18n.ecm.rebackTip, i18n.ecm.rebackTip2],
						warn:true,
						data:rowData
					}
				}
				if(Array.isArray(rowData)){
					option.data.warn = false;
				}
				GetEcmDetail(rowData.virtualmachineid).then(res=>{
					let data = res.data.results;
					if(data && data.length){
						let server = data[0];
						if(server.state !== 'Stopped'){
							this.$message({
								type:'error',
								message:'快照所对应的云服务器关机后才能进行此操作'
							})
						
							return
						}
						this.$alertDialog(option).then((formres)=>{
							console.log(formres);
							const loading = this.$loading({
								lock: true,
						        text: '正在下发命令，请稍后……',
						        spinner: 'el-icon-loading',
						        background: 'rgba(0, 0, 0, 0.7)'
						    })
							RebackSnapshot(rowData.id).then((res)=>{
								loading.close()
								this.$message({
									message:'命令下发成功，开始回滚快照……',
									type:'warning'
								})
								let data = res.data.results;
								this.refresh();
								this.autoRefresh(data)
							}).catch((req)=>{
								loading.close()
								console.log(req)
							})
						})
					}
				}).catch(error=>{
					console.log(error)
				})
				
				
			},
			search(filter){
				console.log(filter)
				this.filter.name =''
				this.filter.id =''
				if(filter.name == 'name'){
					filter.name = 'keyword'
				}
				this.filter[filter.name] = filter.value;
				
				this.getSnapshotList()
			},
			refresh(){
//				this.filter.name=''
				this.filter.id=''
//				this.queryQuotas();
				this.GetRegionNums()
				this.getSnapshotList()
			},
			clear(){
      			this.filter.keyword='';
      			this.filter.name='';
      			this.filter.id='';
      		},
			pagingChange(type,val){
      			if(type == 'num'){
      				this.filter.limit = val;
      			}else if(type == 'page'){
      				this.filter.page = val;
      			}
      			this.loading =true;
      			this.getSnapshotList();
      		},
      		autoRefresh(jobIds){
      			if(jobIds){
      				if(typeof(jobIds)=='string'){
      					
      					this.getJobDetail(jobIds)
      					
	      			}else if(Array.isArray(jobIds)){
	      				jobIds.map(item =>{
	      					//console.log(item)
	      					item.jobid && this.getJobDetail(item.jobid)
	      				})
	      			}else{
	      				console.log(jobIds)
	      			}
      			}
      		},
      		getJobDetail(id){
      			let _this = this;
      			let timer;
      			if(this.detroyAutoRefresh) return
      			GetJobDetail(id).then(res=>{
  					let result = res.data.results[0];
  					if(result.jobstatus == 1){
  						let server = result.jobresult.vmsnapshot;
  						server ? this.snapshot.tableData.map((item,index) =>{
  							if(item.id == server.id){
  								this.filterSnapshot(server)
  								this.$set(this.snapshot.tableData[index],'state',server.state)
  								this.$set(this.snapshot.tableData[index],'operation',server.operation)
  							}
  						}) : this.refresh();
  						if(server){
  							this.$message({
	  							message:'操作成功',
	  							type:'success'
	  						})
  							this.refresh()
  						}
  						
  						
  					}else if(result.jobstatus == 2){
  						clearTimeout(timer)
  						console.log('失败')
  						this.$message({
  							message:'操作失败'+result.jobresult.errortext,
  							type:'error'
  						})
  						this.refresh()
  					}else{
  						timer = setTimeout(()=>{
  							_this.getJobDetail(result.jobid)
  						},5000)
  					}
  					
  				}).catch(error=>{
  					console.log(error)
  					this.refresh()
  				})
      		},
      		GetRegionNums(){
				let params ={
					resource:'snapshot',
					regionid:getRegionId()
				}
				GetRegionNum(params).then( res => {
					let data = res.data.results[0].SNAPSHOT;
					console.log(data)
					if(data && data.detail){
						this.regionData = data.detail;
						this.snapshotLimitQuota = data.total_count;
					}
					
				})
			},
			queryQuotas(){
		      	queryQuota().then((res)=>{
		          console.log(res.data.results);
		          let Quota = res.data.returnObj;
//		          this.snapshotTotalCount = Quota.snapshot_limit - 0;
					this.snapshotTotalCount =Quota.snapshot_limit ? Quota.snapshot_limit - 0 :'';
		        }).catch((error)=>{
		          console.log(error)
		        })
		    },
		},
		computed:{
			snapRemainCount(){
				this.snapshotRemainCounts = this.snapshotTotalCount - this.snapshotLimitQuota;
		      	if(this.snapshotRemainCounts > 0){
		      		return this.snapshotRemainCounts
		      	}else{
		      		this.snapshotRemainCounts = 0;
		      		return this.snapshotRemainCounts
		      	}
			}
		},
		mounted(){
			this.changeRegion();
//			this.queryQuotas();
			let quotas = this.$store.state.user.user.quotas;
			if(quotas.snapshot_limit){
				this.snapshotTotalCount = quotas.snapshot_limit - 0;
			}else{
				this.queryQuotas();
			}
			
		},
		beforeDestroy(){
			this.detroyAutoRefresh = true;
			
		}
		
	}
</script>

<style lang="scss" scoped="scoped">
	.main{
  		background: #f6f8fb;
  	}
  	.snapshotList{
  		.content{
		  	.content_top{
		  		display: flex;
		      	flex-direction: row;
		      	justify-content: space-between;
		  		background: #ffffff;
		  		padding-top: 30px;
		  		padding-left: 20px;
		  		padding-bottom: 20px;
		  		.snap_mark{
		  			line-height: 35px;
        			font-size: 12px;
		  		}
		  	}
		}
  	}
</style>



