<template>
	<div class="order">
		<Order-search :name='name' :btnText='btnText'></Order-search>
		<div class="product_type">
			<div class="title">产品类型</div>
			<div>
				<el-row class="console-center" :gutter="20">
					<el-col :span="6" v-for="item in orderProductsList" :key='item.bg' class="console-product-productsList" v-if="!item.hidden">
						<div class="console-product-item">
							<div class="console-product-left">
								<div class="console-product-img" :class="'console-product-img-'+item.bg"></div>
								<div class="console-product-title">{{item.name}}</div>
							</div>
							<div class="console-product-right">
								<el-button>提问</el-button>
							</div>
						</div>
					</el-col>
				</el-row>
			</div>
		</div>
		<div class="product_type">
			<div class="title">功能服务</div>
			<div>
				<el-row class="console-center" :gutter="20">
					<el-col :span="6" v-for="item in serviceList" :key='item.bg' class="console-product-productsList">
						<div class="console-product-item">
							<div class="console-product-left">
								<div class="console-product-img" :class="'console-product-img-'+item.bg"></div>
								<div class="console-product-title">{{item.name}}</div>
							</div>
							<div class="console-product-right">
								<el-button>提问</el-button>
							</div>
							<p>{{item.title}}</p>
						</div>
					</el-col>
				</el-row>
			</div>
		</div>
	</div>
</template>

<script>
	import OrderSearch from '@/components/OrderSearch'
	import i18n from '@/lang'
	export default{
		components:{
			OrderSearch:OrderSearch
		},
		methods: {
      		
    	},
		data(){
			return{
				name: i18n.order.submit_order,
				btnText:i18n.order.my_order,
				orderProductsList:[
					{
						name:'云服务器',
						bg:'ecm',
						id:"VM"
					},
					{
						name:'云硬盘',
						bg:'ebs',
						id:"Volume"
					},
					{
						name:'VPC',
						bg:'vpc',
						id:"VPC"
					},
					{
						name:'弹性公网IP',
						bg:'eip',
						id:"Public_IP"
					},
					{
						name:'镜像',
						bg:'ims',
						id:"Images",
					},
					{
						name:'负载均衡',
						bg:'elb',
						id:'elbs',
					}
				],
				serviceList:[
					{
						name:'财务类',
						bg:'financial',
						id:"financial",
						title:'充值、提现、发票、退款等相关问题'
					},
					{
						name:'账号相关',
						bg:'account',
						id:"account",
						title:'用户账号等相关问题'
					},
					{
						name:'备案相关',
						bg:'record',
						id:"record",
						title:'备案咨询等相关问题',
					},
				],
			}
		},
    	computed:{
    		
    	}
	}
</script>

<style lang="scss" scoped="scoped">
	.order{
		.console-center{
			background: #ffffff;
			margin-bottom: 20px;
			height: 250px;
			padding:20px 10px;
			.console-product-productsList{
				height: 90px;
				margin-top:20px;
				.console-product-item{
					height:100%;
					background: #fbfcfd;
					cursor:pointer;
					padding:0 25px;
					>div{
						display: table-cell;
						vertical-align: middle;
						height:70px;
					}
					p{
						font-size: 12px;
						width: 100%;
					}
					.console-product-left{
						/*padding-left:25px;*/
						.console-product-img{
							display: table-cell;
							width:40px;
							height:40px;
							background: no-repeat center center;
							background-size:contain;
						}
						.console-product-img-ebs{
							background-image: url(../../assets/ecm/ebsIcon.png);
						}
						.console-product-img-ecm{
							background-image: url(../../assets/ecm/ecmIcon.png);
						}
						.console-product-img-elb{
							background-image: url(../../assets/ecm/elbIcon.png);
						}
						.console-product-img-eip{
							background-image: url(../../assets/ecm/eipIcon.png);
						}
						.console-product-img-vpc{
							background-image: url(../../assets/ecm/vpcIcon.png);
						}
						.console-product-img-ims{
							background-image: url(../../assets/ecm/imsIcon.png);
						}
						.console-product-img-financial{
							background-image: url(../../assets/ecm/financial.png);
						}
						.console-product-img-account{
							background-image: url(../../assets/ecm/account.png);
						}
						.console-product-img-record{
							background-image: url(../../assets/ecm/record.png);
						}
						.console-product-title{
							padding-left:20px;
							min-width:130px;
							max-width:170px;
							display: table-cell;
							vertical-align: middle;
							color:#333333;
							font-size:16px;
						}
					}
					.console-product-right{
						font-size:16px;
						color:#333333;
						.el-button{
							padding: 7px 20px;
							background: #f9cd76;
							color: #ffffff;
							border-color: #f9cd76;
						}
					}
				}
			}
		}
	}
</style>
<style lang="scss">
	.order{
		background: #ffffff;
		padding-left: 20px;
		.product_type{
			background: #ffffff;
			.title{
				padding: 20px 0;
				border-bottom: 1px solid #f4f6fa;
				margin-bottom: 20px;
			}
		}
	}
</style>


