<template>
	<div class="billInfo">
		<el-container>
			<el-header height="100px">
				<h4 class="bill_info_title">账单明细</h4>
				<div class="bill_info_tip">2017年11月明细查询，费用小记：10元（不足1小时按一小时计，小于10分钟不计费）</div>
				<el-date-picker
					v-model="date"
					type="date"
					placeholder="选择日期"
					default-value="2010-10-01" size="small">
				</el-date-picker>
				<el-button type="primary" size="small">搜索</el-button>
				<el-button type="primary" size="small" class="right">打印本月账单明细</el-button>
			</el-header>
			<el-main>
				<el-table :data="tableData" headerCellClassName="tableHeaderbg" :emptyText="emptyText">
				        <el-table-column prop="name" label="资源" align="center"></el-table-column>
				        <el-table-column prop="name" label="使用量" align="center"></el-table-column>
				        <el-table-column prop="name" label="费用" align="center"></el-table-column>
				        <el-table-column prop="address" label="使用时段" align="center"></el-table-column>
				        <el-table-column prop="address" label="账单说明" align="center"></el-table-column>
				</el-table>
			</el-main>
		</el-container>
	</div>
</template>

<script>
	export default {
		
		data (){
			return {
				date:'',
				emptyText: '账单数据未生成',
				tableData: [],
			}
		}
	}
</script>

<style>
.billInfo {
	background-color: #fff;
	height: 100%;
}
.tableHeaderbg {
	background-color: #f0f2f6 !important;
	color: #333;
}
.bill_info_title {
	margin: 15px 0;
}
.bill_info_tip {
	color: #f9cd76;
	font-size: 12px;
	margin-bottom: 15px;
}
.right {
	float: right;
}
</style>


