<template>
	<header>
		<div class="header-left">
			<div class="logo" @click="logoClick">
				
					<img width="100%" src="../../../assets/logo.png"/>
				
			</div>
			<div class="console-center hoverClass">
				<router-link :to='console.path'>{{console.title}}</router-link>
			</div>
		</div>
		<div class="hedaer-right">
			<el-dropdown v-for="item in headerList" :class=" 'header-' + item.id" :key="item.id" trigger="click" v-if="!item.hide" class="hoverClass">
		        <span class="el-dropdown-link">{{item.name}}</span>
		        <el-dropdown-menu slot="dropdown">
		          <el-dropdown-item v-for="(subItem,index) in item.subList" :key="index">
		          	<router-link tag='div' :to='subItem.path'>{{subItem.title}}</router-link>
		          </el-dropdown-item>
		        </el-dropdown-menu>
	      	</el-dropdown>
			<!--<router-link
				tag='div'
				v-for='item in rightList'
				:to='item.path'
				:key='item.id'
				:class=" 'header-' + item.id"
				>{{item.title}}</router-link>-->
				
			
			
				<el-dropdown @command="userFun"  class="header-user hoverClass">
				  <span class="el-dropdown-link user-name" style="outline-width: 0px; outline-style: none;">
				  	
					<img height="20" :src="user.img"/>
					
				    {{user.name}}
				  </span>
				  <el-dropdown-menu slot="dropdown">
				    <el-dropdown-item v-for="(userItem,index) in userList" :key="index" :command="userItem.fun">{{userItem.title}}</el-dropdown-item>
				  </el-dropdown-menu>
				</el-dropdown>
			
		</div>
		
		
	</header>
</template>

<script>
	import i18n from '@/lang'
	import userImg from '@/assets/user.png'
	import headerList from '@/baseConfig/headerList'
	export default {
		data (){
			let i18nCommon = i18n.common;
			return {
				console :{
					title : i18nCommon.console,
					path:'/console'
				},
				userList:[
					{
						title: i18nCommon.userCenter,
						path: '',
						fun:()=>{
							this.$router.push('/user/userInfo')
						}
					},
					{
						title: i18nCommon.exitLogon,
						path: '',
						fun:()=>{
							return this.fadeOut()
						}
					}
					
				],
				headerList: headerList,
				user:{
					name:'admin',
					img:userImg
				}
			}
		},
		mounted: function(){
			this.user.name = this.$store.state.user.user.customer_name || this.user.name;
		},
		methods:{
			userFun(fun){
				fun()
			},
			fadeOut(){
				
				this.$store.dispatch('FedLogOut').then((res) => {
					console.log(res)
					//location.reload()
					location.href ='/logout'
				})
			},
			logoClick(){
				this.$router.push('/console')
			}
		}
	}
</script>

<style scoped lang="scss">
 	@import 'src/styles/variables.scss';
 	
 	.hoverClass{
 		&:hover{
			background:$headerHover;
		}
 	}
	header{
		position: fixed;
    	width: 100%;
    	top: 0;
    	z-index: 1000000;
		background: $headerBackground;
		height: $headerHeight;
		/*display:flex;*/
		/*justify-content:space-between;*/
		font-size:$headerFontSize;
		color:$headerColor;
		.header-left{
			float: left;
			>div{
				display:table-cell;
				vertical-align: middle;
			}
			.logo{				
				width:50px;
				height:$headerHeight;
				padding:0 10px 0 10px;
				box-sizing: border-box;
				border-right:$header-border-right;
				cursor: pointer;
			}
			.console-center{
				width:121px;
				text-align: center;
				line-height:$headerHeight ;
				height: $headerHeight;
				border-left: $header-border-left;
				border-right:$header-border-left;
				a{
					display: block;
					width:100%;
					height:100%;
					box-sizing: border-box;
					border-right:$header-border-right;
				}
				
			}
		}
		.hedaer-right{
			float: right;
			border-left:$header-border-right;
			>div{
				height:$headerHeight;
				display: inline-block;
				vertical-align: middle;
				text-align: center;
				cursor:pointer;
				line-height:$headerHeight;
			}
			.header-order{
				width:70px;
				border-left:$header-border-left;
				color:#fff;
			}
			.header-cost,.header-message{
				width:90px;
				border-left:$header-border-left;
				border-right:$header-border-right;
				color:#fff;
			}
			/*.user-img{
				
				width:20px;
				display: inline-block;
				img{
					width:100%;
					border-radius: 50%;
					vertical-align: middle;
				}
			}*/
			
			.header-user{
				border-left:$header-border-left;
				padding-left:20px;
				min-width:130px;
				padding-right:10px;
				
				box-sizing: border-box;
				.user-name{
					display: inline-block;
					text-align: left;
					box-sizing: border-box;
					color: #fff;
					width:100%;
					line-height: $headerHeight;
					overflow: hidden;
					text-overflow: ellipsis;
					white-space:nowrap;
					img{
						width:20px;
						border-radius: 50%;
						margin-right:10px;
						vertical-align: middle;
					}
				}
				
			}
		}
		
	}
	
	
</style>


