<template>
	<div class="export-data">
		<h4 class="note_tip">导出监控数据，支持导出近两天的实时监控数据</h4>
		<div class="table_config">
			<el-table :data="tableItem" style="width: 100%">
				<el-table-column label="服务">
					<template slot-scope="scope">
						<el-select v-model="scope.row.ser" placeholder="请选择服务" size="mini">
							<el-option :label="item.name" :value="item.name" v-for="(item,index) in serData" :key="index" :disabled="(item.name !== tableData.defaultData.defaultSer)"></el-option>
						</el-select>
					</template>
				</el-table-column>
				<el-table-column label="维度">
					<template slot-scope="scope">
						<el-select v-model="scope.row.dim" placeholder="请选择维度" size="mini">
							<el-option :label="item.name" :value="item.name" v-for="(item,index) in dimData" :key="index"></el-option>
						</el-select>
					</template>
				</el-table-column>
				<el-table-column label="监控对象" width='150'>
					<template slot-scope="scope">
						<el-select v-model="scope.row.uuid" filterable placeholder="请选择监控对象" size="mini">
						    <el-option v-for="item in ecmOptions" :key="item.zabbix_name" :label="item.name" :value="item.zabbix_name" v-if="item.zabbix_name&&item.zabbix_name!=''">
						    </el-option>
						</el-select>
					</template>
				</el-table-column>
				<el-table-column label="指标">
					<template slot-scope="scope">
						<el-select v-model="scope.row.item_key" placeholder="请选择指标" size="mini">
							<el-option :label="item.name" :value="item.metric_name" v-for="(item,index) in items" :key="index"></el-option>
						</el-select>
					</template>
				</el-table-column>
				<el-table-column label="操作">
					<template slot-scope="scope">
						<el-button size="mini" type="text" @click="delItem(scope.$index, scope.row)">移除</el-button>
					</template>
				</el-table-column>
			</el-table>
			<el-button size="mini" type="text" @click="addItem()" icon="el-icon-circle-plus-outline" :disabled="tableData.defaultData.num_index-this.tableItem.length == 0">增加一个服务</el-button>
			<span class="config_tip_num">您还可以添加{{ tableData.defaultData.num_index-this.tableItem.length }}个监控项</span>
		</div>
	</div>
</template>


<script>
import { server,metric } from '../config/monitorConfig'
import { getEcmMonitor,exportData,getEvsMonitor } from '@/api/monitor'
	export default {
		props:{
			form:[Object,String,Array],
			tableData:[Object,Array],
		},
		data(){
			return {
				serData: server,
				//items: metric.ecs,
				tableItem: [],
				ecmOptions: [],
			}
		},
		methods: {
			addItem(){
				this.tableItem.push({
					ser: this.tableData.defaultData.defaultSer,
					dim: this.tableData.defaultData.defaultDim,
					uuid: '',
					item_key: this.tableData.defaultData.defaultKey,
				});
			},
			delItem(index){
				this.tableItem.splice(index,1);
			},
			getEcmMonitorList(){
      			getEcmMonitor(this.tableData.defaultData.params).then((res)=>{
      				if(res.data.code == 200){
      					this.ecmOptions = res.data.results;
      				}else{
      					this.$message({
							message: res.msg || res.data.msg || '获取云服务器监控列表失败',
							type:'error'
						});
      				}
      			}).catch((error)=>{
      				console.log(error)
      			})
      		},
      		getEvsMonitorList(){
      			getEvsMonitor(this.tableData.defaultData.params).then((res)=>{
      				if(res.data.code == 200){
      					this.ecmOptions = res.data.results;
      				}else{
      					this.$message({
							message: res.msg || res.data.msg || '获取云服务器监控列表失败',
							type:'error'
						});
      				}
      			}).catch((error)=>{
      				console.log(error)
      			})
      		},
      		validateForm(){
				return new Promise((resolve,reject) => {
					let form_valid = true;
					let exportItem = [];
					this.tableItem.forEach((item,index,array) => {
						if(item.uuid == '' || item.item_key == ''){
							form_valid = false;
						}else{
							exportItem.push({uuid: item.uuid, metric_name: item.item_key, name: this._.find(this.ecmOptions,{'zabbix_name': item.uuid}).name});
						}
			        });
			        if(form_valid){
			        	this.exportMonitorData(exportItem);
			        }else{
			        	this.$message('请完善选择下拉框内容');
			        }
			        form_valid ? resolve() : reject();
				})
			},
			download (res) {
		        if (!res) {
		            return
		        }
		        let url = window.URL.createObjectURL(new Blob([res.data]));
		        let link = document.createElement('a');
		        link.style.display = 'none';
		        link.href = url;
		        link.setAttribute('download', 'export-'+new Date().getTime()+'.csv');
		        
		        document.body.appendChild(link);
		         try {
                        link.click();
                    } catch (e) {
                        navigator.msSaveBlob(new Blob([res.data]), 'export-'+new Date().getTime()+'.csv');
                }
                document.body.removeChild(link);
		        this.$message({
					message: '导出数据成功',
					type:'success'
				});
		    },
			exportMonitorData(exportItem){
				//responseType: 'blob',
				let loading = this.$loading({
				    lock: true,
				    text: '数据导出中...',
				    spinner: 'el-icon-loading',
				    background: 'rgba(0, 0, 0, 0.7)'
				});
		        exportData({
						export: JSON.stringify(exportItem),
						regionid: this.tableData.defaultData.params.regionid
					}).then(res => {
						loading.close();
						loading = null;
						this.download(res);
					},error => {
						loading.close();
						loading = null;
						this.$message({
							message: error.msg || error.data.msg || '文件下载失败，请稍后重试',
							type:'error'
						});
					});
		    }
		},
		created() {
			this.addItem();
			if(this.tableData.defaultData.defaultSer == '云服务器'){
				this.getEcmMonitorList();
			}else{
				this.getEvsMonitorList();
			}
		},
		mounted() {

		},
		computed: {
			dimData() {
				let dimData = this._.find(this.serData,{name: this.tableData.defaultData.defaultSer}).sub;
				return dimData;
			},
			items() {
				return this.tableData.defaultData.defaultSer == '云服务器' ? metric.ecs : metric.evs;
			}
		}
	}
</script>

<style lang="scss" scoped="scoped">
.export-data {
	width: 650px;
	.config_tip_num{
		font-size: 12px;
    	color: #666;
	}
	.note_tip{
	    font-size: 14px;
    	color: #666;
    	padding-bottom: 20px;
	}
}
</style>
<style lang="scss">
.export-data {
	.table_config{
		.el-table th{
		  	padding: 3px 0;
		  	background-color: #f0f2f7;
		 }
		 .el-table td{
		  	padding: 6px 0;
		 }
	}
}
</style>


