
import Layout from '@/views/ecm/ecm'
import ImsList from '@/views/ecm/ImsList'
import createPrivateIms from '@/views/ecm/createPrivateIms'

let Router ={
	path:'ims',
	name:'ims',
	component:Layout,
	redirect:'ims/imsList',
	children:[
		{
      		path:'imsList',
      		name:'imsList',
      		component:ImsList
      },
      {
      		path:'createPrivateIms',
      		name:'createPrivateIms',
      		component:createPrivateIms,
      		meta:{
      			dependOn:'ecmImsSub',
      			aisdeHide:true
      		}
      },
	]
};

export default Router;



