<template>
	<div class="main">
		<RegionList 
			:name ='name' 
			:regionData='regionData'
			:buttonText ='buttonText' 
			:buttonTexts ='buttonTexts'
			@create='create' @strategy='certificate'></RegionList>
		<div class="content">
			<div class="content_top">
				<ConsoleSearch :btnClose=true
					:statusOnOff='statusOnOff' 
					:options ='options' 
					:optionsValue ='optionsValue'
					@searchEvent='search' 
					@clear = 'clear'
					@refreshEvent='refresh' 
					@PublicOperEvent = 'buttonClick'></ConsoleSearch>
			</div>
			<div>
				<ConsoleTable 
					:checkBox='checkBox' 
					:operationOrNot = 'operationOrNot' 
					:selectOrNot='selectOrNot' 
					:tableData = 'elbListData' 
					:tableOptions = 'tableOptions' 
					:OperationEvents='elbOperation' 
					@buttonClick = 'buttonClick'></ConsoleTable>
			</div>
		</div>
	</div>
</template>

<script>
	import RegionList from '@/components/RegionList'
	import searchModule from '@/components/SearchModule'
	import ConsoleTable from '@/components/ConsoleTable'
	import ConsoleSearch from '@/components/ConsoleSearch'
	import i18n from '@/lang'
	
	import { getRegionId, setRegionId } from '@/utils/auth'
	import { getElbList } from '@/api/elb'
	import { getNetList } from '@/api/vpc'
	import { statFormat } from '@/utils/statformat'
	import { timeService } from '@/utils/timeformat'
	export default{
		components:{
			RegionList : RegionList,
			searchModule:searchModule,
			ConsoleTable:ConsoleTable,
			ConsoleSearch:ConsoleSearch
		},
		data(){
			return {
				name:i18n.elb.elbList,
				buttonText:i18n.elb.createElb,
				buttonTexts:i18n.elb.certificate,
				checkBox:true,
				selectOrNot:true,
				operationOrNot:true,
				statusOnOff:false,
				elbListData: [],
				regionData:[],
		        multipleSelection: [],
		        Pagination:{
		        	'pagingData':[5,10,15],
		        	'total':0,
		        	'show':false
		        },
		        filter:{
					status:'',
					limit:5,
					page:1,
					name:'',
					keyword:"",
					id:'',
					attached:'',
				},
		        options: [{
		          	value: '',
		          	label: i18n.common.allStatus
		        }, {
		          	value: 'allocated',
		          	label: i18n.ebs.allocated
		        }, {
		          	value: 'ready',
		          	label: i18n.ebs.ready
		        }],
        		optionsValue: '',
        		tableOptions:[{
        			prop:'name',
        			label:i18n.common.name,
        			temp:true,
        			detail:true,
        			path:'/elb/elbDetail',
        			key:'ip'
        		},{
        			prop:'status',
        			label:i18n.common.status,
        			temp:true,
        			status:true
        		},{
        			prop:'resource_type',
        			label:i18n.elb.type,
        		},{
        			prop:'ip',
        			label:i18n.elb.serviceAddress,
        		},{
        			prop:'belongsToVpc',
        			label:i18n.elb.belongsToVpc,
        		},{
        			prop:'subnet',
        			label:i18n.elb.subnet,
        		},{
        			prop:'bandwidth',
        			label:i18n.elb.bandwidth,
        		},{
        			prop:'created',
        			label:i18n.common.createAt,
        		}],
        		elbOperation:[{
        			value:i18n.elb.modifyBandwidth,
        			fun:(data)=>{
        				return this.modifyBandwidth(data);
        			}
        		},{
        			value:i18n.common.delete,
        			fun:(data)=>{
        				return this.deleter(data);
        			}
        		},{
        			value:i18n.elb.blockUp,
        			fun:(data)=>{
        				return this.blockUp(data);
        			}
        		}],
			}
		},
		methods:{
      		handleClick(){
      			
      		},
      		clear(){
      			this.filter.keyword='';
      			this.filter.name='';
      		},
      		create(){
      			this.$router.push({path:'/elb/ElbCreate'});
      		},
      		certificate(){
      			console.log('备份策略');
      		},
      		buttonClick(funName,data){
      			funName(data);
      		},
      		modifyBandwidth(data){
      			console.log(data);
      			console.log('调用修改带宽');
      		},
      		blockUp(data){
      			console.log('调用停用');
      		},
      		deleter(data){
      			console.log('调用删除');
      		},
      		changeRegion(uuid){
      			this.regionId = uuid;
      			this.getElbList();
      		},
      		getRegion(){
      			let timer1;
      			if(getRegionId()){
      				clearTimeout(timer1)
					this.changeRegion();
				}else{
					let _this = this;
					timer1 = setTimeout(function(){
						_this.getRegion()
					},300)
				}
      		},
      		getElbList(uuid){
      			let params ={
      				limit:10,
      				page:1,
      				status:''
      			}
      			getElbList(params,uuid).then((res)=>{
      				console.log(res)
      				let servers = res.data.results;
      				let totalCount = res.data.total_count;
      				this.filterServers(servers);
      				this.elbListData = servers;
      				this.totalCount = totalCount;
      				this.loading=false
      			}).catch((error)=>{
      				this.loading=false
      				console.log(error)
      			})
      		
      		},
      		filterServers(servers){
      			servers.forEach(function(item){
      				item.status = 'running';
      				item.state = 'running';
      				item.created = item.create_date ? timeService(item.create_date) : '--';
      				item.resource_type = item.resource_type ? statFormat(item.resource_type) : '--';
      				if(item.network_uuid){
      					let params={
						    status:'',
					        limit:10,
					        page:1,
					        id:item.network_uuid,
      					};
      					getNetList(params).then((res)=>{
		      				let Subdetail = res.data.results[0];
		      				console.log(Subdetail)
		      				
		      			}).catch((error)=>{
		      				console.log(error)
		      			})
      				}
      			})
      		},
      		search(filter){
      			console.log(filter)
				this.filter.status = ''
				this.filter.name=''
				this.filter.id=''
//				this.filter[filter.name] = filter.value;
				this.filter.keyword = filter.value;
				this.getElbList()
      		},
      		selectChange(value){
      			console.log(value);
      			if(value == 'ready'){
      				this.filter.attached = 0;
      			}else if(value == 'allocated'){
      				this.filter.attached = 1;
      			}else{
      				this.filter.attached = '';
      			}
      			this.filter.page = 1;
//				this.filter.status =value;
				console.log(this.filter);
				this.getElbList()
			},
      		refresh(){
      			this.filter.status = ''
		        this.filter.keyword = '',
		        this.filter.name=''
		        this.filter.id=''
		        this.filter.attached=''
      			this.getElbList()
      		},
      		pagingChange(type,val){
      			if(type == 'num'){
      				this.filter.limit = val;
      			}else if(type == 'page'){
      				this.filter.page = val;
      			}
      			this.getElbList();
      		},
      		GetRegionNums(){
				let params ={
					resource:'volume',
					regionid:getRegionId()
				}
				GetRegionNum(params).then( res => {
					let data = res.data.results[0].Volume;
					console.log(data)
					if(data && data.detail){
						this.regionData = data.detail;
					}
					
				})
			},
		},
		computed:{
			
		},
		mounted(){
			this.changeRegion();
		}
	}
</script>

<style lang="scss" scoped="scoped">
  	.main{
  		background: #f6f8fb;
  	}
	.content{
	  	.content_top{
	  		background: #ffffff;
	  		padding-top: 30px;
	  		padding-left: 20px;
	  		padding-bottom: 20px;
	  	}
	}
</style>
<style lang="scss">
	.main{
		.el-table__body{
			.el-table__expanded-cell{
				.demo-table-expand{
					.el-form-item{
						text-align: left;
					}
				}
			}
		}
	}
</style>


