
import Layout from '@/views/Layout'
import ElbList from '@/views/elb/ElbList'
import ElbCreate from '@/views/elb/elbCreate'
import ElbDetail from '@/views/elb/ElbDetail'

import i18n from '@/lang'

let Router ={
	path:'/elb',
	name:'elb',
	component:Layout,
	redirect:'/elb/ElbList',
	children:[
		{
      		path:'elbList',
      		name:'elbList',
      		component:ElbList,
      	},{
      		path:'elbDetail/:serverId',
      		name:'elbDetail',
    		component:ElbDetail,
    		meta:{
      			dependOn:'elbSub'
      		}
      	},{
      		path:'elbCreate',
      		name:'elbCreate',
    		component:ElbCreate,
    		meta:{
      			dependOn:'elbSub'
      		}
      	},
	]
};


export default Router;



