
import Layout from '@/views/Layout'
import overview from '@/views/monitor/overview'
import Dashboard from '@/views/monitor/Dashboard'
import Ecm from '@/views/monitor/ecmMonitor'
import Eip from '@/views/monitor/eip'
import Elb from '@/views/monitor/elb'
import Ebs from '@/views/monitor/ebs'
import alarm from '@/views/monitor/alarmServer'
import history from '@/views/monitor/history'
import template from '@/views/monitor/template'
import createTemplate from '@/views/monitor/createTemplate'
import rule from '@/views/monitor/rule'
import createRule from '@/views/monitor/createRule'
import userGroup from '@/views/monitor/userGroup'

let Router ={
	path:'/monitor',
	name:'monitor',
	component: Layout,
	redirect: '/monitor/dashboard',//'/monitor/overview',
	children:[
		{
      		path: 'overview',
      		name: 'overview',
      		component: overview,
      	},{
      		path: 'dashboard',
      		name: 'dashboard',
      		component: Dashboard,
      	},
      	{
      		path: 'ecmMonitor',
      		name: 'ecmMonitor',
      		component: Ecm,
      	},
            {
                  path: 'eipMonitor',
                  name: 'eipMonitor',
                  component: Eip,
            },
            {
                  path: 'elbMonitor',
                  name: 'elbMonitor',
                  component: Elb,
            },
            {
                  path: 'ebsMonitor',
                  name: 'ebsMonitor',
                  component: Ebs,
            },
      	{
      		path: 'alarm',
      		name: 'alarm',
      		component: alarm,
      	},
            {
                  path: 'history',
                  name: 'history',
                  component: history,
            },
            {
                  path: 'template',
                  name: 'template',
                  component: template,
            },
            {
                  path: 'createTemplate',
                  name: 'createTemplate',
                  component: createTemplate,
            },
            {
                  path: 'rule',
                  name: 'rule',
                  component: rule,
            },
            {
                  path: 'createRule',
                  name: 'createRule',
                  component: createRule,
            },
            {
                  path: 'userGroup',
                  name: 'userGroup',
                  component: userGroup,
            },
	]
};
export default Router;


