<template>
	<div class='monitor_info'>
		<div class="header">
			<el-breadcrumb separator-class="el-icon-arrow-right">
			  <el-breadcrumb-item><span @click="returnList">{{itemData.returnName}}</span></el-breadcrumb-item>
			  <el-breadcrumb-item class="font_14_3">{{infoKeyValue[0].value}} 监控图表</el-breadcrumb-item>
			</el-breadcrumb>
			<div class="info">
				<div class="info_title font_12_3">{{itemData.infoName}}</div>
				<el-row>
				  <el-col :span="8" v-for="(keyValue,index) in infoKeyValue" :key="index">
				  	<div class="grid-content p_b_10 font_12_6">
				  		<label>{{keyValue.label}}</label>
				  		<span :class="[keyValue.className ? keyValue.className : '']">{{keyValue.value}}</span>
				  	</div>
				  </el-col>
				  
				</el-row>
			</div>
		</div>
		<div class="main m_t_20">
			<el-row :gutter="20">
			  <el-col :span="8" v-for="(echart,index) in echartList" :key="index">
			  	<div class="grid-content bg-line">
			  		<div v-if="echart.data.msg">
			  			<el-container>
						  <el-header height="30px">
						  	<span>{{echart.metric_name}}</span>
						  	<span class="fl_r">周期：60s  聚合方式：平均值</span>
						  </el-header>
						  <el-main>{{echart.data.msg}}</el-main>
						</el-container>
			  		</div>
			  		<echarts v-else :id="echart.id" width='370px' height='280px' :monitorData="echart" @maximization="openMax"></echarts>
			  	</div>
			  </el-col>
			</el-row>
		</div>
		<div v-if="dialogMaxEchartVisible">
			<el-dialog :visible.sync="dialogMaxEchartVisible" :showClose=false class="maxEchartContent">
			  	<echarts :id="random" width='700px' height='400px' :monitorData="maxEchart" @minimization="closeMin" @getMonitor="getMonitor" :isMax=true></echarts>
			</el-dialog>
		</div>
	</div>
</template>

<script>
import echarts from '@/components/Echarts'
import { getMonitorData } from '@/api/monitor'
import { metric } from './config/monitorConfig'
import { getRegionId } from '@/utils/auth'
	export default {
		components:{
			echarts,
		},
		props:{
			itemData: [Object],
			infoKeyValue: [Array],
		},
		data (){
			return {
				echartList: [],
				dialogMaxEchartVisible: false,
				region_name: this._.find(this.$store.state.user.regionList,{uuid:getRegionId()}).name,
				maxEchart: {
					metric_name: '',
					data: {
						xAxis_data: [],
						chartData: [],
					},
					id: ''
				},
				
			}
		},
		created(){
			metric[this.itemData.evs ? 'evs' : 'ecs'].map((item,index,array) => {
				this._getMonitorData({
					uuid: this.itemData.zabbix_name,
					metric_name: item.metric_name,
					period: 300,
					filter: 'avg',
					time_range: '3h'
				},item.name+'(' + item.unit + ')');
			});
		},
		mounted(){
			
		},
		methods:{
			_getMonitorData(params,name){
				getMonitorData(params).then(res => {
					if(res.data.status == 0){
						this.echartList.push({
							metric_name: name,
							data: this.machiningMonitorData(res.data.data),
							id: params.metric_name,
							region_name: this.region_name
						});
					}else{
						this.echartList.push({
							metric_name: name,
							data: res.data,
							region_name: this.region_name
						});
					}
				},error => {
					this.echartList.push({
						metric_name: name,
						data: {msg: error.data.msg || '数据获取失败'},
						region_name: this.region_name
					});
				});
			},
			returnList(){
				this.$emit('returnList');
			},
			getMonitor(params,callback){
				if(!params.uuid){
					params.uuid = this.itemData.zabbix_name;
				}
				getMonitorData(params).then(res => {
					if(res.data.status == 0){
						this.maxEchart.data = this.machiningMonitorData(res.data.data,params.time_range||params.time_from);
						callback&&callback();
					}else{
						
					}
				},error => {
					console.log(error);
					
				});
			},
			machiningMonitorData(data,time_range){
				var xAxis_data = [],
		            chartData = [],
		            len = data.v_list.length;
		        for (let i = 0; i < len; i++) {
		            var t = new Date(data.v_list[i].timestamp);
		            if(!time_range || time_range == '1h' || time_range == '3h' || time_range == '12h' || time_range == '1d'){
		            	xAxis_data.push(t.Format("hh:mm"));
		            }else{
		            	xAxis_data.push(t.Format("yyyy-MM-dd hh:mm"));
		            }
		            if(typeof data.v_list[i].value == 'string'){
		                chartData.push('无');
		            }else{
		                chartData.push(data.v_list[i].value);
		            }
		        }
		       	return {xAxis_data,chartData};
			},
			openMax(monitorData){
				//this.maxEchart = monitorData;
				this.maxEchart.metric_name = monitorData.metric_name;
				this.maxEchart.data.xAxis_data = monitorData.data.xAxis_data;
				this.maxEchart.data.chartData = monitorData.data.chartData;
				this.maxEchart.id = monitorData.id;
				this.dialogMaxEchartVisible = true;
			},
			closeMin(){
				this.dialogMaxEchartVisible = false;
			}
		},
		computed: {
			random(){
				return new Date().getTime()+'';
			}
		},
		
	}
</script>

<style scoped lang="scss">
.monitor_info{
    height: 100%;
	.header {
		background: #fff;
		padding: 20px;
	}
	.m_t_20 {
		margin-top: 20px;
	}
	.font_14_3 {
		font-size: 14px;
    	color: #333;
	}
	.font_12_3 {
		font-size: 12px;
    	color: #333;
	}
	.font_12_6 {
		font-size: 12px;
    	color: #666;
	}
	.main {

	}
	.bg-line {
		width: 100%;
		height: 280px;
		background: #fff;
		margin-bottom: 20px;
	}
	.info_title {
		padding-top: 20px;
    	padding-bottom: 10px;
	}
	.p_b_10 {
		padding-bottom: 10px;
	}
	.status_normal {
		color: #52ec29;
	}
	.status_error {
		color: red;
	}
	.el-header {
		line-height: 30px;
		font-size: 12px;
	}
	.el-main {
		text-align: center;
    	line-height: 180px;
	}
	.fl_r {
		float: right;
	}
}
</style>
<style lang="scss">
.maxEchartContent {
	.el-dialog__header{
		padding: 0;
	}
	.el-dialog__body{
		padding: 0 10px;
		height: 450px;
	}
	
}
</style>


