<template>
	<div>
		<ConsoleCreate ref="createProcess" :system_type='name' :switchPage = 'nextPage' :checked='!checked' :submitForm="submitForm" @orderEvent = 'orderEvent'>
			<div slot='create_title' class="create_title" v-show="page==0">
				<ElbCreateFirst ref="createOrder" @orderResult = 'orderResult'></ElbCreateFirst>
			</div>
			<div slot="confirm_title" class="confirm_title" v-show="page==1">
				<div style="padding: 20px 0;background-color: #fff;">资源详情</div>
		        <ConsoleConfirm :tableData="tableData"></ConsoleConfirm>
		        <div class="Agreement_component">
				    <p>
				      	<el-checkbox v-model="checked">我已阅读并同意相关协议</el-checkbox>
				      	<span @click="goProtocol" class="agreement">《 天翼云弹性云服务器服务等级协议 》</span>
				    </p>
			  	</div>
			</div>
			<div slot="submit_title" class="submit_title"  v-if="page===2">
	        	<ConsoleSuccess :return_url='return_url' :orderWay=true :system_type='name' :system_name="tableData.system_name" ></ConsoleSuccess>
	      	</div>
		</ConsoleCreate>
	</div>
	
</template>

<script>
	import ConsoleCreate from '@/components/ConsoleCreate'
	import ConsoleConfirm from '@/components/ConsoleConfirm'
  	import ConsoleSuccess from '@/components/ConsoleSuccess'
  	import ElbCreateFirst from '@/views/elb/ElbCreateFirst'
	import i18n from '@/lang'
	import { CreateElb} from '@/api/elb'
	import { getEcmPlatforms } from '@/api/ecm'
	import { getRegionId } from '@/utils/auth'
	export default {
		components:{
			ConsoleCreate:ConsoleCreate,
			ConsoleConfirm,
	     	ConsoleSuccess,
	      	ElbCreateFirst,
		},
	    data() {
	      	return {
		      	page:0,
		      	checked:false,
		      	name:'负载均衡',
		        return_url:'/ebs/ebsList',
	      		tableData:{
		          	system_name:'',//资源名称
		          	elseData: [{
		          		system_type:'负载均衡',//资源类型
	      				num:'1',
	      				total:'￥0.00',
	      				confirmData:[],
		            }],
		          	tableOptions: [{
		    			prop:'length',
		    			label:i18n.ebs.length,
		    		},{
		    			prop:'num',
		    			label:i18n.ebs.Number,
		    		},{
		    			prop:'total',
		    			label:i18n.ebs.totalPrice,
		    		}],
		        },
		        ruleFormData:'',
		    }
	    },
	    methods: {
	    	orderEvent(){
				this.$refs.createOrder.order();
	    	},
	    	goProtocol(){
	    		window.open('http://www.ctyun.cn/term/ecs');
	    	},
	    	orderResult(val,data){
	    		console.log(val);
				if(val){
					this.ruleFormData = data;
					console.log(this.ruleFormData);
					this.$refs.createProcess.next();
				}else{
					return
				}
	    	},
	      nextPage(index){
	      	console.log(index);
	      	this.page=index;
	      	if(index == 1){
	      		if(this.ruleFormData.eipip){
	      			this.tableData.elseData = [{
		      			system_type:'负载均衡',//资源类型
		      			num:1,
		      			total: '￥' + this.ruleFormData.price,
		      			confirmData:[{
			      			label: i18n.common.name,
			      			value:this.ruleFormData.name
			      		},{
			      			label: i18n.common.resourceNodes,
			      			value:this.ruleFormData.regionName
			      		},{
			      			label: i18n.common.VPC,
			      			value:this.ruleFormData.vpcname,
			      		},{
			      			label: i18n.common.eip,
			      			value:this.ruleFormData.eipip
			      		},{
			      			label: i18n.bks.describe,
			      			value:this.ruleFormData.desc
			      		}],
		      		}];
	      		}else{
	      			this.tableData.elseData = [{
		      			system_type:'负载均衡',//资源类型
		      			num:1,
		      			total: '￥' + this.ruleFormData.price,
		      			confirmData:[{
			      			label: i18n.common.name,
			      			value:this.ruleFormData.name
			      		},{
			      			label: i18n.common.resourceNodes,
			      			value:this.ruleFormData.regionName
			      		},{
			      			label: i18n.common.VPC,
			      			value:this.ruleFormData.vpcname,
			      		},{
			      			label: i18n.vpc.publicbw,
			      			value:this.ruleFormData.bandwidths
			      		},{
			      			label: i18n.bks.describe,
			      			value:this.ruleFormData.desc
			      		}],
		      		}];
	      		}
      			
	      		this.tableData.system_name=this.ruleFormData.name;
	      	}
	      },
	      submitForm(fn) {
	      		let params={
	      			regionid:getRegionId()
	      		};
	      		let NowPlatforms = '';
	        	getEcmPlatforms(params).then((res)=>{
	        		console.log(res.data.platforms);
	        		let Platforms = res.data.platforms;
	        		NowPlatforms = Platforms.filter((item)=>{
	        			if(item.uuid == params.regionid){
	        				return true
	        			}else{
	        				return false
	        			}
	        		});
	        		console.log(NowPlatforms);
	          		let data={};
	          		if(this.ruleFormData.eipip){
	          			data={
							'name':this.ruleFormData.name,
							'publicipid':this.ruleFormData.eipid,
							'network_uuid':this.ruleFormData.netid,
							'regionid':this.ruleFormData.uuid,
				            "zoneId":NowPlatforms[0].details.zoneId
			          	};
	          		}else{
	          			
	          		}
		          	CreateElb(data).then((res)=>{
	      				console.log(res)
	      				this.$message({
				          	message: '命令下发成功',
				          	type: 'success'
				        });
				        fn();
	      			}).catch((error)=>{
	      				console.log(error);
	      				this.$message({
				          	message: '命令下发失败',
				          	type: 'error'
				        });
	      			});
		        }).catch((error)=>{
		          this.loading=false;
		          console.log(error)
		        });
		
		    },
	    },
	}
</script>

<style lang="scss" scoped="scoped">
	.Agreement_component{
	    font-size: 12px;
      	.agreement {
			color: #3dc7fa;
			cursor: pointer;
			font-weight: bold;
		}
	}
</style>
<style lang="scss">
	.confirm_title{
		.mt5{
			display: block;
			margin-top: 5px;
		}
	}
	
</style>



