<template>
	<el-main class="console-main">
		<el-row v-if="this.stack=='vip'" :gutter='40'>
			<el-col :span='18'>
				<el-row class="console-header" :gutter="20" v-if="moduleDisplay.consoleHeader">
					<el-col :span="14" >
						<el-row class="console-dashboard">
							<el-col :span="8" v-for="item in dashboarList" :key="item.id">
								<div class="console-title console-dashboard-title">
									{{item.title}}
									<span v-if="item.date">
										{{ item.date }}
									</span>
								</div>
								<div class="console-dashboard-count console-number">
									{{ item.count || 0 }}
								</div>
							</el-col>
						</el-row>


					</el-col>
					<el-col :span="10" >
						<el-row class="console-user-charge">
							<el-col :span="24">
								<div class="console-title">
									账户概况
								</div>
							</el-col>
							<el-col :span="9">

								<div class="console-user-charge-numer">
									<div class="console-number" >15930.00</div>
									账户余额
								</div>
							</el-col>
							<el-col :span="10" class="console-user-cards">
								<div><span>0</span>张银行卡</div>
								<div><span>0</span>张代金券</div>
								<div><span>0</span>张优惠券</div>
							</el-col>
							<el-col :span="5" class="console-user-recharge">充值 》</el-col>
						</el-row>

					</el-col>
				</el-row>
				<el-row class="console-center" :gutter="20">
					<el-col :span="24" class="console-title console-product">
						我的资源
					</el-col>
					<el-col :span="8" v-for="item in productsList" :key='item.bg' class="console-product-productsList" v-if="!item.hidden">
						<router-link tag="div" :to='item.path' class="console-product-item">
							<div class="console-product-left">
								<div class="console-product-img" :class="'console-product-img-'+item.bg"></div>
								<div class="console-product-title">{{item.name}}</div>
							</div>
							<div class="console-product-right">
								{{item.num || 0}}
							</div>
						</router-link>
					</el-col>
				</el-row>
				<el-row class="console-footer-btn" v-if="moduleDisplay.consoleMonitor">
					<el-button v-for = 'item in btnList' @click='handleMonitor(item.id)' v-if='!item.hidden' :key="item.id">

						{{item.name}}
					</el-button>
				</el-row>
				<el-row :gutter="20"  v-if="moduleDisplay.consoleMonitor">
					<el-col :span="24" class="console-footer">
						流量监控图
					</el-col>
				</el-row>


			</el-col>
			<el-col :span='6'>
				<MainRight></MainRight>
			</el-col>

		</el-row>
		<el-row  v-if="this.stack=='console'" :gutter='40'>
      <el-row class="console-center" :gutter="20">
        <el-col :span="8" v-for="item in productsList" :key='item.bg' class="console-product-productsList" v-if="!item.hidden">
          <div>{{item.title}}</div>
          <router-link tag="div" :to='item.path' class="console-product-item">
            <div class="console-product-left">
              <div class="console-product-img" :class="'console-product-img-'+item.bg"></div>
              <div class="console-product-title">{{item.name}}</div>
            </div>
            <div class="console-product-right">
              {{item.num || 0}}
            </div>
          </router-link>
        </el-col>
      </el-row>
    </el-row>
	</el-main>

</template>

<script>
	import moduleDisplay from '@/baseConfig/moduleDisplay'

	import { dashboarList, productsList, btnList } from '@/baseConfig/consoleList'
	import MainRight from './mainRight'
	import { getProductList }from '@/api/console'
  import {loadStyle} from '@/utils/shareFunction'
	export default {
		data (){
			return {
				productsList,
				btnList,
				dashboarList,
				moduleDisplay,
			}
		},
		methods:{
			handleMonitor (id){
				console.log(id)
			},
			getProductListData(){
				let params = {
					all_dc:1,
					regionid:'all'
				}
				getProductList(params).then(response => {
					let data = response.data.results[0];
					let list = this.productsList;
					list.map((item)=>{
						let totalItem = data[item.id];
						if(totalItem){
							//云硬盘个数
							let num = totalItem.vm_total_count ? totalItem.vm_total_count : totalItem.total_count;
							//云硬盘容量
							//let num =  totalItem.total_size ? totalItem.total_size : totalItem.vm_total_count ? totalItem.vm_total_count : totalItem.total_count;
							this.$set(item,'num',num)
						}
					})
				})
			}
		},
		components:{
			MainRight
		},
    beforeCreate(){
      this.stack=this.$store.state.user.stack;

    },
		created(){
			this.getProductListData();
			if(this.stack=='vip'){
        loadStyle('../src/styles/dashboard/consoleCenterVip.css');
      }

		}
	}
</script>

<style lang="scss" scoped="scoped">
	@import '../../styles/variables.scss';

</style>



