<template>
	<div class="create_after">
		<h4>订购云服务器</h4>
		<div class="confirm_title" v-if="isShowCreateInfo">
			<div style="margin: 20px 0; font-size: 12px; color: #333;">资源详情</div>
			<ConsoleConfirm :tableData="tableData"></ConsoleConfirm>
			<p class="orderTip">郑重提示：对云服务器的使用请遵循国家相关法律之规定，对于违反相关法律的行为，服务商将关闭服务器，并视情况决定是否关闭用户帐号，停止所有服务器，不退余款，弹性公网IP不支持降级操作，购买时请慎重选择。</p>
			<div class="agreementContent">
				<el-checkbox class="checkedTip" v-model="checked">我已阅读并同意相关协议</el-checkbox>
				<a href="http://www.ctyun.cn/term/ecs" class="agreement" target="_blank">《天翼云弹性云服务器服务器等级协议》</a>
				<span>和</span>
				<a href="http://www.ctyun.cn/product/agreement_mirror" class="agreement" target="_blank">《天翼云系统镜像服务免责声明》</a>
			</div>
		</div>
		<div v-if="isShowCreateInfo">
			<el-button type="info" @click.stop="cancel">上一步</el-button>
			<el-button class='orderBtn' type="primary" @click.stop="confirmOrder" :disabled="!checked">确认下单</el-button>
		</div>
		<div class="submit_title" v-if="!isShowCreateInfo">
	       <ConsoleSuccess :system_type='name' :system_name="tableData.system_name" :return_url="returnUrl" :order_number="orderNumber"></ConsoleSuccess>
	    </div>
	</div>
	
</template>

<script>
	import ConsoleConfirm from '@/components/ConsoleConfirm'
	import ConsoleSuccess from '@/components/ConsoleSuccess'
	import i18n from '@/lang'
	import { diskType } from '@/utils/statformat'
	import { getEcmBuyserver } from '@/api/ecm/';
	export default {
		components:{
			ConsoleConfirm,
			ConsoleSuccess
		},
		props:{
			orderInfo: {
				type: Object,
				default: {}
			},
			orderData: {
				type: Object,
				default: {}
			},
			isShowCreateInfoP: {
				type: Boolean,
				default: true
			},
			orderNumberP: {
				type: String,
				default: '--'
			}
		},
	    data() {
	      return {
	      	checked:false,
	      	isShowCreateInfo:true,
	      	returnUrl: '/ecm/ecmList',
	      	orderNumber: '--',
	      	name:'弹性云服务器',
      		tableData:{
	          	system_name: this.orderData.vmName,//资源名称
	          	elseData: [],
	          	tableOptions: [{
	          		prop:'resources',
	          		label:'资源中心'
	          	},
	          	{
	    			prop:'length',
	    			label:i18n.ebs.length,
	    		},{
	    			prop:'num',
	    			label:i18n.ebs.Number,
//	    		},{
//	    			prop:'total',
//	    			label:i18n.ebs.totalPrice,
	    		}],
	        },
	      };
	    },
		methods: {
			cancel () {
//				this.$router.push({path: this.returnUrl });
				this.$emit('backBuyEcm');
			},
			confirmOrder () {
				const loading = this.$loading({
					          lock: true,
					          text: '拼命创建中',
					          spinner: 'el-icon-loading',
					          background: 'rgba(0, 0, 0, 0.7)'
					        });
				getEcmBuyserver(this.orderData).then(res => {
					loading.close();
	      			console.log(res);
	      			if(res.data.code == 200){
				        this.orderNumber = res.data.returnObj.so_number;
				        this.isShowCreateInfo = !this.isShowCreateInfo
	      			}else{
	      				this.$message({
				          showClose: true,
				          message: res.data.reason || res.data.msg,
				          type: 'error'
				        });
	      			}
	      		},res => {
	      			loading.close();
//	      			this.$message({
//				          showClose: true,
//				          message: res.msg || '服务器请求出错，请联系管理员',
//				          type: 'error'
//				        });
	      			console.log('error');
	      		});
			}
		},
		mounted: function () {
			var totalDisk = '';
			let _this = this;
//			this.orderData.disks.map(function(currentValue,index,arr){
//				console.log(currentValue);
//				totalDisk += currentValue.size + 'GB('+ diskType(currentValue.type) +')、';
//			});
			let disk=[];
 	 		let currt={};
  			this.orderData.disks.map(function(val,index){
			    if(!currt[val['size']+val['type']]){
			      	let currt_inner={};
			      	_this.orderData.disks.map(function(value,i){
				        if(val['size']==value['size']&&val['type']==value['type']&&index!==i){
				          	currt_inner['num']++;
				        }else if(!currt_inner['size']){
				          	currt_inner['size']=val['size'];
				          	currt_inner['type']=val['type'];
				          	currt_inner['num']=1;
				        }
			      	});
			      	currt[val['size']+val['type']]=currt_inner;
			      	disk.push(currt_inner);
			    }
			});
			disk.map(function(item,index){
				if(item.num > 1){
					totalDisk += item.size + 'GB*' + item.num + '(' + diskType(item.type) +')<br />' 
				}else{
					totalDisk += item.size + 'GB(' + diskType(item.type) +')<br />'
				}
			});
			console.log(disk);
			console.log(this.orderData);
			let EipValue;
			let Eipshow = false;
			if(this.orderData.bandwidth){
				EipValue = this.orderData.bandwidth + 'Mbit/s';
				Eipshow = false;
			}else if(this.orderData.existingIp){
				EipValue = this.orderInfo.eipName;
				Eipshow = false;
			}else{
				Eipshow = true;
			}
			this.tableData.elseData.push({
	            	resources: this.orderInfo.resourcesName,
	             	length: this.orderInfo.cycleCntValue,
	      			num: this.orderData.orderNum,
	      			total:'￥0.00',
	      			system_type:'云服务器',
	      			confirmData:[{
				        	label: 'CPU',
				        	value: this.orderData.cpu + '核',
			      		},{
			      			label: '内存',
			      			value: this.orderData.mem + 'GB'
			      		},{
			      			label: '镜像',
			      			value: this.orderInfo.osDatas
			      		},{
			      			label: '系统盘',
			      			value:	this.orderData.syshd + 'GB'
			      		},{
			      			label: '数据盘',
			      			value:	totalDisk
			      		},{
			      			label: '弹性ip',
			      			show: Eipshow,
			      			value:	EipValue
			      		},{
			      			label: 'vpc',
			      			value: this.orderInfo.vpcName
			      		},{
			      			label: '主网卡',
			      			value: this.orderInfo.vpcNet
			      		},{
			      			label: '操作系统',
			      			value: this.orderData.osType + '/64位'
			      		}
	      			],
	            });
	      	this.isShowCreateInfo = this.isShowCreateInfoP;
	      	this.orderNumber = this.orderNumberP;
		}
	}
</script>


<style>
.create_after {
	height: 650px;
	background-color: #fff;
	padding: 10px 20px;
}
.create_after h4{
	color: #333;
}
.create_after .orderBtn:hover{
	background-color: #6fd7fd;
	border-color: #6fd7fd;
}
.ecmCreate .el-button--primary.is-disabled:hover{
	background-color: #a0cfff;
	border-color: #a0cfff;
}
.create_after .tableHeaderbg {
  background-color: #f0f2f6 !important;
  color: #635e5e;
  padding: 4px 0;
}
.create_after .el-table__row:hover > td {
	background-color: #fff !important;
}
.create_after .orderTip {
	color: red;
    font-size: 12px;
    /*font-weight: bold;*/
    padding: 20px 0;
}
.create_after .checkedTip .el-checkbox__label {
	font-size: 12px;
}
.create_after .agreementContent {
	font-size: 12px;
    padding-bottom: 20px;
}
.create_after .agreement {
	color: #3dc7fa;
	font-weight: 500;
}
</style>



