<template>
	<div class="monitor_config">
		<el-form ref="formData" :model="formData" label-width="80px" size="mini" label-position="left" :rules="rules">
			<el-form-item label="资源池：" prop="region">
			    <el-select v-model="formData.region" placeholder="请选择资源池" style="width: 100%;"  @change="regionChange"  :disabled="tableData.isUpdate">
			      <el-option :label="item.name" :value="item.uuid" v-for="item in regionList" :key="item.uuid"></el-option>
			    </el-select>
			</el-form-item>
			<el-form-item label="标题：" prop="configName">
			    <el-input placeholder="请输入内容" v-model="formData.configName" clearable></el-input>
			</el-form-item>
		</el-form>
		<div class="table_config">
			<el-table :data="formData.tableConfig" style="width: 100%">
				<el-table-column label="服务">
					<template slot-scope="scope">
						<el-select v-model="scope.row.ser" placeholder="请选择服务" size="mini" @change="serverChange(scope.$index, scope.row)">
							<el-option :label="item.name" :value="item.name" v-for="(item,index) in serData" :key="index"></el-option>
						</el-select>
					</template>
				</el-table-column>
				<el-table-column label="维度">
					<template slot-scope="scope">
						<el-select v-model="scope.row.dim" placeholder="请选择维度" size="mini">
							<el-option :label="item.name" :value="item.name" v-for="(item,index) in scope.row.dimData" :key="index"></el-option>
						</el-select>
					</template>
				</el-table-column>
				<el-table-column label="监控对象" width='150'>
					<template slot-scope="scope">
						<el-select v-model="scope.row.uuid" filterable placeholder="请选择监控对象" size="mini">
						    <el-option v-for="item in scope.row.options" :key="item.zabbix_name" :label="item.name" :value="item.zabbix_name" v-if="item.zabbix_name&&item.zabbix_name!=''">
						    </el-option>
						</el-select>
					</template>
				</el-table-column>
				<el-table-column label="指标">
					<template slot-scope="scope">
						<el-select v-model="scope.row.item" placeholder="请选择指标" size="mini">
							<el-option :label="item.name" :value="item.metric_name" v-for="(item,index) in scope.row.items" :key="index" :disabled= "metricUnit=='--' ? false : item.unit!=metricUnit"></el-option>
						</el-select>
					</template>
				</el-table-column>
				<el-table-column label="操作">
					<template slot-scope="scope">
						<el-button size="mini" type="text" @click="delConfig(scope.$index, scope.row)">移除</el-button>
					</template>
				</el-table-column>
			</el-table>
			<el-button size="mini" type="text" @click="addConfig()" icon="el-icon-circle-plus-outline" :disabled="tableData.num_index-formData.tableConfig.length == 0">增加一个服务</el-button>
			<span class="config_tip_num">您还可以添加{{ tableData.num_index-formData.tableConfig.length }}个监控项</span>
		</div>
	</div>
</template>


<script>
import { server,metric } from '../config/monitorConfig'
import { getEcmMonitor,getEvsMonitor,getUserResources } from '@/api/monitor'
	export default {
		props:{
			form:[Object,String],
			tableData:[Object,Array],
		},
		data(){
			return {
				serData: server,
				ecmOptions: [],
				evsOptions: [],
				regionList: [],
				formData: {
					region: '',
					configName: '',
					tableConfig: [],
				},
				rules: {
					configName: [
			            { required: true, message: '请输入配置名称', trigger: 'blur' },
			            { min: 2, max: 40, message: '长度在 2 到 40 个字符', trigger: 'blur' }
			        ],
			        region: [
		            	{ required: true, message: '请选择资源池', trigger: 'change' }
		          	],
				},
			}
		},
		methods:{
			validateForm(){
				return new Promise((resolve,reject)=>{
					var form_valid = false;
					this.$refs['formData'].validate((valid) => {
			          if (valid) {
			          	form_valid = true;
			          }
			        });
			        let itemList = [];
			        if(form_valid){
			        	this.formData.tableConfig.map((item,index,array) => {
				        	if(item.item == '' || item.uuid == ''){
				        		form_valid = false;
				        	}else{
				        		itemList.push({
					        		ser: item.ser,
					        		dim: item.dim,
					        		item: item.item,
					        		uuid: item.uuid,
					        		uuidName: this._.find(item.options,{zabbix_name:item.uuid}).name,
					        		regionid: this.formData.region,
					        	});
				        	}
				        });
				        this.$message('请完善选择下拉框内容');
			        }
			        this.form.regionid = this.formData.region;
			        this.form.title = this.formData.configName;
			        this.form.content = JSON.stringify(itemList);
			        form_valid ? resolve() : reject();
				})
			},
			delConfig(index){
				this.formData.tableConfig.splice(index,1);
			},
			addConfig(){
				this.formData.tableConfig.push({
					ser: server[0].name,
					dim: server[0].sub[0].name,
					uuid: '',
					item: '',//metric.ecs[0].metric_name,
					items: metric.ecs,
					dimData: server[0].sub,
					options: this.ecmOptions,
				});
			},
			serverChange(index,row){
				row.dimData = this._.find(server,{name: row.ser}).sub;
				row.dim = row.dimData[0].name;
				if(row.ser == '云硬盘'){
					row.items = metric.evs;
					row.item = '';//metric.evs[0].metric_name;
					row.options = this.evsOptions;
				}else{
					row.items = metric.ecs;
					row.item = '';//metric.ecs[0].metric_name;
					row.options = this.ecmOptions;
				}
				row.uuid = '';
			},
			getMonitorObj(regionid){
				let params = {
					page_size: 9999,
					page: 1,
					regionid: regionid,
				};
				this.ecmOptions.splice(0,this.ecmOptions.length);
				this.evsOptions.splice(0,this.evsOptions.length);
				getEcmMonitor(params).then(res => {
					if(res.data.code == 200){
						res.data.results.map((item) => {
							this.ecmOptions.push(item);
						});
					}
				},error => {

				});
				getEvsMonitor(params).then(res => {
					if(res.data.code == 200){
						res.data.results.map((item) => {
							this.evsOptions.push(item);
						});
					}
				},error => {
				
				});
			},
			regionChange(val){
				this.getMonitorObj(val);
				if(this.formData.tableConfig.length > 1){
					this.formData.tableConfig.splice(1,this.formData.tableConfig.length);
					this.formData.tableConfig[0].uuid = '';
					this.formData.tableConfig[0].item = '';
				}else if(this.formData.tableConfig.length > 0){
					this.formData.tableConfig[0].uuid = '';
					this.formData.tableConfig[0].item = '';
				}
			}
		},
		mounted(){
			
		},
		created(){
			if(this.form.regionid){this.getMonitorObj(this.form.regionid);}
			this.formData.region = this.form.regionid;
			this.formData.configName = this.form.title;
			if(this.form.content == ''){
				this.addConfig();
			}else{
				this.formData.tableConfig = JSON.parse(this.form.content);
				this.formData.tableConfig.map((config,index,array) => {
					this.$set(config,'dimData',this._.find(this.serData,{name:config.ser}).sub);
					if(config.ser == '云硬盘'){
						this.$set(config,'items',metric.evs);
						this.$set(config,'options',this.evsOptions);
					}else{
						this.$set(config,'items',metric.ecs);
						this.$set(config,'options',this.ecmOptions);
					}
				});
			}
			getUserResources({regionid:'all',details:0}).then(res => {
					console.log(res,'---------');
					if(res.data.code == 200 && res.data.results.length > 0){
						/*res.data.results.map((item,index,array) => {
							obj.data.data.push(item);
						});*/
						this.regionList = res.data.results;
					}else{
						this.$message({
							message: error.msg || error.data.msg || '加载资源池失败，请联系管理员',
							type:'error'
						});
					}
			});
		},
		computed: {
			metricUnit(){
				if(this.formData.tableConfig.length > 0&&this.formData.tableConfig[0].item != ''){
					return this._.find(metric.ecs,{metric_name: this.formData.tableConfig[0].item}).unit;
				}else{
					return '--';
				}
			}
		}
	}
</script>

<style lang="scss" scoped="scoped">
.monitor_config {
	width: 650px;
	.config_tip_num{
		font-size: 12px;
    	color: #666;
	}
}
</style>
<style lang="scss">
.monitor_config {
	.table_config{
		.el-table th{
		  	padding: 3px 0;
		  	background-color: #f0f2f7;
		 }
		 .el-table td{
		  	padding: 6px 0;
		 }
	}
}
</style>


