<template>
	<div class="ebsmain">
		<RegionList :name ='name' :regionData='regionData' :buttonText ='buttonText' @create='create' @changeRegion = 'changeRegion'></RegionList>
		<div class="content">
			<div class="content_top">
				<ConsoleSearch :statusOnOff=true 
					:optionsValue ='optionsValue' 
					:PublicOperation='ebsPublicOperation' 
					@searchEvent='search' 
					@refreshEvent='refresh' 
					@clear = 'clear'
					:disableData='changeTableData'
					@PublicOperEvent = 'buttonClick'></ConsoleSearch>
			</div>
			<div>
				<ConsoleTable :checkBox=true @selectChange = "selectChange" ref="childTable"
					:operationOrNot =true :selectOrNot=true :dragable=true
					:tableData = 'ebsListData' :tableOptions = 'tableOptions' 
					:OperationEvents='ebsOperation' :loading = 'loading' 
					@buttonClick = 'buttonClick' @changeTable="changeTable"
					:Pagination = 'Pagination' @pagingEvent = 'pagingChange'></ConsoleTable>
			</div>
		</div>
	</div>
</template>

<script>
	import service from '@/utils/request'
	import RegionList from '@/components/RegionList'
	import ConsoleTable from '@/components/ConsoleTable'
	import ConsoleSearch from '@/components/ConsoleSearch'
	import ConsoleAction from '@/components/ConsoleAction'
	import AttachAction from '@/views/ebs/AttachAction'
	import ecmRenew from '@/views/ecm/ecmRenew'
	import i18n from '@/lang'
	import { getEbsList,EbsDetach,unsubscribeEbs,renewEbs,EbsAttach,GetEbsDetail } from '@/api/ebs'
	import { getRegionId, setRegionId } from '@/utils/auth'
	import { statFormat,diskType } from '@/utils/statformat'
	import { queryAsyncJob } from '@/api/vpc'
	import { GetEcmDetail,GetRegionNum } from '@/api/ecm'
	import { timeService,timeExpired } from '@/utils/timeformat'
	export default{
		components:{
			RegionList : RegionList,
			ConsoleTable:ConsoleTable,
			ConsoleSearch:ConsoleSearch,
			ConsoleAction:ConsoleAction,
			AttachAction:AttachAction,
			ecmRenew:ecmRenew,
		},
		data(){
			return {
				name:i18n.ebs.ebsPathName,
				buttonText:i18n.ebs.ebsButtonName,
				loading:false,
				ebsdetailRefresh:false,
				totalCount:0,
				num : 0,
//				expiredShow:0,
				newEbsName:'',
				ebsListData: [],
				unRenewList:[],
		        multipleSelection: [],
		        regionData:[],
		        noTimeList:[],
		        changeTableData:[],
		        Pagination:{
		        	'pagingData':[10,20,50],
		        	'total':0,
		        	'show':false
		        },
		        filter:{
					status:'',
					limit:10,
					page:1,
					name:'',
					keyword:"",
					id:'',
					attached:'',
				},
        		optionsValue: '',
        		tableOptions:[{
        			prop:'name',
        			label:i18n.common.name,
        			width:'135px',
        			temp:true,
        			detail:true,
        			path:'/ebs/ebsDetail',
        		},{
        			prop:'stateTxt',
        			label:i18n.common.status,
        			temp:true,
        			filter:true,
        			status:true,
        			selectOptions: [{
			          	value: 'allocated',
			          	text: i18n.ebs.allocated
			        }, {
			          	value: 'ready',
			          	text: i18n.ebs.ready
			        }],
        		},{
//      			prop:'storagetype',
//      			label:i18n.ebs.sharedStorage,
//      		},{
        			prop:'size',
        			label:i18n.ebs.capacityGB,
        		},{
		        	prop:'disk_types',
					label:i18n.ebs.storageType,
		        },{
        			prop:'type',
        			label:i18n.ebs.diskModel,
        		},{
        			prop:'created',
        			label:i18n.common.createAt,
//      		},{
//      			prop:'zonename',
//      			label:i18n.common.selectCorrectAZ,
        		}],
        		ebsOperation:[{
        			value:i18n.ebs.attach,
        			fun:(data)=>{
        				return this.attach(data);
        			}
        		},{
        			value:i18n.ebs.detach,
        			fun:(data)=>{
        				return this.detach(data);
        			}
//      		},{
//      			value:i18n.ebs.createBuckup,
//      			fun:(data)=>{
//      				return this.createBuckup(data);
//      			}
//      		},{
//      			value:i18n.ebs.setupPolicy,
//      			fun:(data)=>{
//      				return this.setupPolicy(data);
//      			}
//      		},{
//      			value:i18n.ebs.capacityExpansion,
//      			fun:(data)=>{
//      				return this.capacityExpansion(data);
//      			}
//      		},{
//      			value:i18n.common.unsubscribe,
//      			fun:(data)=>{
//      				return this.unsubscribe(data);
//      			}
        		}],
        		ebsPublicOperation:[
        		{
        			value:i18n.common.renew,
        			disable:false,
        			title:'请选择云硬盘',
        			fun:(data)=>{
        				return this.renew(data);
        			}
//      		},{
//      			value:i18n.common.unsubscribe,
//      			fun:(data)=>{
//      				return this.unsubscribe(data);
//      			}
        		}
        		],
        		renewActionOptions: [{
        			prop:'name',
        			label:i18n.common.name,
        			temp:true,
        			detail:true,
        		},{
        			prop:'size',
        			label:i18n.ebs.capacityGB,
        		},{
        			prop:'type',
        			label:i18n.ebs.type,
        		},{
        			prop:'created',
        			label:i18n.common.createAt,
        		},{
        			prop:'stateTxt',
        			label:i18n.common.status,
        			temp:true,
        			status:true
        		}],
			}
		},
		methods:{
      		create(){
//    			this.$router.push({path:'/ebs/ebsCreate'});
				let tempWin = window.open("","_blank");
				tempWin.location = '/#/ebs/ebsCreate';
//    			window.open('/#/ebs/ebsCreate',"_blank")
      		},
      		buttonClick(funName,data){
      			funName(data);
      		},
      		autoRefresh(jobIds,data){
      			if(jobIds){
      				if(typeof(jobIds)=='string'){
      					
      					this.getJobDetail(jobIds,data)
      					
	      			}else if(Array.isArray(jobIds)){
	      				jobIds.map(item =>{
	      					console.log(item)
	      					item.jobid && this.getJobDetail(item.jobid)
	      				})
	      			}else{
	      				console.log(jobIds)
	      			}
      			}
      		},
      		getJobDetail(id,data){
      			if(this.ebsdetailRefresh){
      				return
      			}
      			let timer;
      			let _this = this;
//    			timer && clearTimeout(timer)
      			queryAsyncJob(id).then(res=>{
  					console.log(res)
  					let result = res.data.results[0];
  					if(result.jobstatus == 1){
  						let server = result.jobresult.volume;
  						this.filterServers(server);
  						let count=0
  						this.ebsListData.map((item,index) =>{
  							count++
  							if(item.id == server.id){
  								server.disk_types = item.disk_types;
  								this.$set(this.ebsListData,index,server)
  								console.log(index,server)
  								this.ebsdetailRefresh = false;
  								_this.filterServers(item);
  								return true;
  							}
  						})
  					}else if(result.jobstatus == 2){
  						clearTimeout(timer)
  						this.refresh()
  					}else{
  						if(data){
  							_this.addOperation(data);
  						};
  						timer = setTimeout(()=>{
  							_this.getJobDetail(result.jobid)
  						},5000)
  					}
  					
  				}).catch(error=>{
  					this.ebsdetailRefresh = true;
  					setTimeout(()=>{
						this.refresh()
					},5000)
  					
  				})
      		},
      		attach(data){
//    			this.ebsdetailRefresh = true;
      			console.log(data);
      			if(data.virtualmachineid|| (data.attachments && data.attachments.length)){
      				return
      			}
      			console.log('调用挂载');
      			let attachDataOption = [{
        			prop:'name',
        			label:i18n.common.name,
        			temp:true,
        			detail:true,
        		},{
        			prop:'status',
        			label:i18n.common.status,
        			temp:true,
        			status:true
        		},{
        			prop:'templatedisplaytext',
        			label:i18n.common.ims,
        		},{
        			prop:'attached_volumes_number',
        			label:i18n.ebs.attachVoluem,
        		},{
        			prop:'privateIp',
        			label:i18n.common.privateIp,
        		}];
				let attachDataSelectOptions = [{
		          	value: '',
		          	label: i18n.common.allStatus
		        }, {
		          	value: 'Running',
		          	label: i18n.common.active
		        }, {
		          	value: 'Stopped',
		          	label: i18n.common.shutOff
		        }];
      			let option = {
      				'data':{
      					'option':attachDataOption,
      					'title':'挂载磁盘',
      					'actionTip':'',
      					'data':'',
      					'selectOptions':attachDataSelectOptions,
      					'statusOnOff':true,
      					'optionsValue':'',
      				},
      				'view':AttachAction,
      				'form':{}
      			};
      			let _this = this;
      			this.$alertDialog(option).then((res)=>{
      				this.loading =true;
  					console.log(res);
  					let volumed_id = data.id;
  					let id = res.data.id;
  					EbsAttach(volumed_id,id).then((res)=>{
	      				console.log(res)
	      				this.loading =false;
	      				this.$message({
				          	message: '命令下发成功',
				          	type: 'success'
				        });
				        data.state = 'mounting';
				        _this.addOperation(data);
	      				_this.autoRefresh(res.data.results,data);
	      			}).catch((error)=>{
	      				console.log(error);
	      				this.loading =false;
	      			})
  				})
      		},
      		detach(data){
  				console.log(data);
  				if(!data.virtualmachineid && !(data.attachments && data.attachments.length) ){
      				return
      			}
  				if(data.type == '系统盘'){
  					this.$message({
				          	message: '系统盘不允许卸载！',
				          	type: 'warning'
				        });
  					return
  				}
  				let _this = this;
//				this.ebsdetailRefresh = true;
				let ecmId = data.virtualmachineid ? data.virtualmachineid : (data.attachments && data.attachments.length && data.attachments[0].server_id);
  				GetEcmDetail(ecmId).then((res)=>{
	      				console.log(res)
	      				let detail = res.data.results[0];
	      				if(detail.state == 'Stopped'){
	      					let option = {
			      				'data':{
			      					'type':'warn',
			      					'option':'',
			      					'title':'卸载磁盘',
			      					'actionTip':['确定要卸载磁盘: ',data.name],
			      					'data':'',
			      					'warn':true
			      				},
			      				'view':ConsoleAction,
			      				'form':{},
			      			};
			      			_this.$alertDialog(option).then((res)=>{
			      				this.$message({
									message:'卸载磁盘命令下发成功，请稍后',
									type:'warning'
								})
			      				_this.loading =true;
			  					let volumed_id = data.id;
				              	let id = data.virtualmachineid ? data.virtualmachineid : (data.attachments && data.attachments.length && data.attachments[0].server_id);
								EbsDetach(volumed_id,id).then((res)=>{
									_this.loading =false;
				      				console.log(res)
				      				_this.$message({
							          	message: '卸载磁盘成功。',
							          	type: 'success'
							        });
							        data.state = 'uninstalling';
							        _this.addOperation(data);
				      				_this.autoRefresh(res.data.results,data);
				      				
				      			}).catch((error)=>{
				      				_this.loading =false;
				      				console.log(error);
//				      				_this.$message({
//							          	message: '命令下发失败',
//							          	type: 'error'
//							        });
				      			})
			  				})
	      				}else{
	      					_this.$message({
					          	message: '该磁盘挂载的云服务器未处在关机状态,请先进行关机操作,再进行卸载操作。',
					          	type: 'error'
					        });
	      				}
	      				
	      			}).catch((error)=>{
	      				_this.$message({
				          	message: '请先确认磁盘挂载的云服务器的状态再进行卸载。',
				          	type: 'error'
				        });
	      			})
      		},
      		createBuckup(data){
      			console.log('调用创建备份');
      			if(!(data.states == 'Ready')){
      				this.$message({
			          	message: '未挂载的云硬盘不能创建云硬盘备份',
			          	type: 'warning'
			        });
      			}else if(data.state == 'error'){
      				this.$message({
			          	message: '错误状态的云硬盘不能创建云硬盘备份',
			          	type: 'warning'
			        });
      			}else{
      				this.$router.push({path:'/ebs/bks/bksCreate',query:{ebsId:data.id,name: data.name}});
      			}
      		},
//    		setupPolicy(data){
//    			console.log('调用设置备份策略');
//    		},
//    		capacityExpansion(data){
//    			console.log('调用扩容');
//    		},
      		renew(data){
//    			this.ebsdetailRefresh = true;
      			console.log('调用续订');
      			let option = {
      				'data':{
      					'option':this.renewActionOptions,
      					'title':'续订磁盘',
      					'actionTip':['以下云硬盘将进行续订操作，续订将会产生续订订单，请于一日内支付订单，否则续订操作失败数据将被清空。','云硬盘'],
      					'data':[this.filterData(this.changeTableData,'renew'),this.unRenewList],
      				},
      				'view':ecmRenew,
      				//'actionTips':['(按月购买的产品只能按月续费,按年购买的产品只能按年续费)'],
      				'form':{}
      			};
      			console.log(option);
      			if(option.data.data[0].length < 1){
      				this.$message({
			          	message: '您选择的云硬盘都不符合续订条件！',
			          	type: 'warning'
			        });
			        return
      			}
      			this.$alertDialog(option).then((res)=>{
  					res.id = [];
      				option.data.data[0].map((item)=>{
						res.id.push(item.id)
					})
      				let data = res;
      				renewEbs(data).then((res)=>{
	      				console.log(res)
	      				this.$message({
				          	message: '命令下发成功',
				          	type: 'success'
				        });
//				        this.ebsdetailRefresh = false;
				        setTimeout(()=>{
				        	this.refresh();
  						},4000)
	      			}).catch((error)=>{
	      				console.log(error);
	      				
	      			})
  				})
      		},
      		unsubscribe(msg){
      			console.log(msg);
      			console.log('调用退订');
      			let option = {
      				'data':{
      					'type':'warn',
      					'option':this.renewActionOptions,
      					'title':'退订磁盘',
      					'actionTip':['确定要退订以下磁盘吗？'],
      				},
      				'view':ConsoleAction,
      				'form':{}
      			};
      			if(msg =='退订'){
      				option.data.data = this.filterData(this.changeTableData,'unsubscribe');
      			}else{
      				msg.state = msg.virtualmachineid || (msg.attachments && msg.attachments.length) ? 'mount' : 'uninstall';
      				if(msg.with_vm){
      					this.$message({
				          	message: '与云服务器一起订购的云硬盘不可单独退订!',
				          	type: 'warning'
				        });
      				}else if(msg.type == '系统盘'){
      					this.$message({
				          	message: '系统盘不可单独退订!',
				          	type: 'warning'
				        });
      				}else if(msg.virtualmachineid|| (msg.attachments && msg.attachments.length)){
      					this.$message({
				          	message: '已挂载的磁盘不可退订,请解绑后再进行退订!',
				          	type: 'warning'
				        });
      				}else{
      					let data = [msg];
      					option.data.data = data;
      				}
      			}
      			if(!(msg =='退订') && msg.with_vm){
      				return
      			}
      			if(!(msg =='退订') && (msg.type == '系统盘')){
      				return
      			}
      			if(!(msg =='退订') && (msg.virtualmachineid || (msg.attachments && msg.attachments.length))){
      				return
      			}
      			if(option.data.data.length < 1){
      				this.$message({
			          	message: '您选择的云硬盘都不符合退订条件！',
			          	type: 'warning'
			        });
			        return
      			}
      			this.$alertDialog(option).then((res)=>{
      				let volumed_id=[];
      				option.data.data.map((item)=>{
						volumed_id.push(item.id)
					})
      				unsubscribeEbs(volumed_id).then((res)=>{
	      				console.log(res)
	      				this.$message({
				          	message: '命令下发成功',
				          	type: 'success'
				        });
				        this.refresh();
	      			}).catch((error)=>{
	      				console.log(error);
	      				this.$message({
				          	message: '命令下发失败',
				          	type: 'error'
				        });
	      			})
      			});
      		},
      		filterData(data,action){
				/*根据action 过滤数据*/
				let _this = this;
				_this.unRenewList = [];
				let unRenewListState = ['ERROR','Creating','error','creating']
				data = data.filter((item)=>{
					switch(action){
						case 'renew':
						if(item.with_vm || unRenewListState.includes(item.state)){
							_this.unRenewList.push(item);
							return false
						}else{
							//return item.state = item.virtualmachineid || (item.attachments && item.attachments.length) ? 'mount' : 'uninstall';
							return true
						}
						break;
						case 'unsubscribe':
						if(item.with_vm){
							return false
						}else if(item.virtualmachineid || (msg.attachments && msg.attachments.length)){
							return false
						}else{
							return item.state = item.virtualmachineid || (msg.attachments && msg.attachments.length) ? 'mount' : 'uninstall';
						}
						break;
						default:
						if(item.with_vm){
							return false
						}else{
							return true
						}
					}
				});
				return data
			},
      		changeTable(val){
				console.log(val)
				this.changeTableData = val;
//				let RunState = this._.find(this.changeTableData, { 'state': "Running"});
//				let shutState = this._.find(this.changeTableData, { 'state': "Stopped"});
				let count = 0;
				if(this.changeTableData.length > 0){
					this.changeTableData.filter(function(item){
						if(item.state == 'ERROR'){
							count++
						}
					})
//					this.ebsPublicOperation[1].disable =!!RunState;
//					this.ebsPublicOperation[2].disable =!!RunState;
//					this.ebsPublicOperation[0].disable = !!shutState;
					if(count == this.changeTableData.length){
						this.ebsPublicOperation[0].disable = false;
					}else{
						this.ebsPublicOperation[0].disable =true;
					}
					this.ebsPublicOperation[0].title = '';
				}else{
					this.ebsPublicOperation[0].disable = false;
					this.ebsPublicOperation[0].title = '请选择云硬盘';
				}
			},
      		changeRegion(uuid){
      			service.cancle('CancelToken');
      			this.regionId = uuid;
//    			this.GetRegionNums()
//    			this.getEbsList()
				this.refresh();
      		},
      		getRegion(){
      			let timer1;
      			if(getRegionId()){
      				clearTimeout(timer1)
					this.changeRegion();
				}else{
					let _this = this;
					timer1 = setTimeout(function(){
						_this.getRegion()
					},300)
				}
      		},
      		getDetail(id,ebsState){
      			let _this = this;
      			let timer;
      			if(this.ebsdetailRefresh) return

      			GetEbsDetail(id).then((res)=>{
					let data = res.data.results;
					let servers = data.length && data[0] || item;
      				if(servers.no_time && servers.no_time == '1' && ebsState != 'create'){
      					setTimeout(()=>{
							_this.getDetail(servers.id)
						},5000)
      				}else if( ebsState == 'create' && servers.state=='Creating'){
      					setTimeout(()=>{
							_this.getDetail(servers.id,'create')
						},5000)
      				}else{
      					this.filterServers(servers);
	      				this.ebsListData.map((item,index) =>{
							if(item.id == servers.id){
								this.$set(this.ebsListData,index,servers)
								return true;
							}
						})
      					this.addOperation(servers);
      					this._.remove(this.noTimeList,(item)=>{
							return item.id == servers.id;
						})
      				}
				}).catch((req)=>{
					console.log(req)
				})
      		},
      		autoRefreshTime(Ids,ebsState){
      			if(Ids){
      				if(typeof(Ids)=='string'){
      					
      					this.getDetail(Ids,ebsState)
      					
	      			}else if(Array.isArray(Ids)){
	      				Ids.map(item =>{
	      					console.log(item)
	      					item.id && this.getDetail(item.id)
	      				})
	      			}else{
	      				console.log(Ids)
	      			}
      			}
      		},
      		getEbsList(){
      			let params = this.filter;
      			this.loading =true;
      			this.noTimeList = [];
      			getEbsList(params).then((res)=>{
      				console.log(res)
      				let servers = res.data.results;
      				let totalCount = res.data.total_count;
      				this.filterServers(servers);
      				this.ebsListData = servers;
      				if(this.noTimeList.length > 0){
      					this.autoRefreshTime(this.noTimeList);
      				}
      				this.totalCount = totalCount;
      				this.Pagination.total = totalCount;
      				if(totalCount > this.Pagination.pagingData[0]){
      					this.Pagination.show = true;
      				}
      				this.loading=false
      			}).catch((error)=>{
      				console.log(error.message);
      				if(error.message == 'CancelToken'){
      					
      				}else{
      					this.loading=false
      				}
      				console.log(error)
      			})
      		
      		},
      		addOperation(server){
      			let attachDisable = ['attach'];
      			let detachDisable = ['detach'];
//    			let unsubscribeDisable = ['unsubscribe'];
//    			let createBuckupDisable = ['createBuckup'];
      			let Operation = [{
        			value:i18n.ebs.attach,
        			id:'attach',
        			fun:(data)=>{
        				return this.attach(data);
        			}
        		},{
        			value:i18n.ebs.detach,
        			id:'detach',
        			fun:(data)=>{
        				return this.detach(data);
        			}
//      		},{
//      			value:i18n.ebs.createBuckup,
//      			id:'createBuckup',
//      			fun:(data)=>{
//      				return this.createBuckup(data);
//      			}
//      		},{
//      			value:i18n.ebs.setupPolicy,
//      			fun:(data)=>{
//      				return this.setupPolicy(data);
//      			}
//      		},{
//      			value:i18n.ebs.capacityExpansion,
//      			fun:(data)=>{
//      				return this.capacityExpansion(data);
//      			}
//      		},{
//      			value:i18n.common.unsubscribe,
//      			id:'unsubscribe',
//      			fun:(data)=>{
//      				return this.unsubscribe(data);
//      			}
        		}];
      			Operation.map(item=>{
      				'mount' == server.state && attachDisable.includes(item.id) && (item.disabled = true)
      				'uninstall' == server.state && detachDisable.includes(item.id) && (item.disabled = true)
      				if('mounting' == server.state){
      					item.disabled = true
      				}
//    				'mounting' == server.state && createBuckupDisable.includes(item.id) && (item.disabled = true)
					if('uninstalling' == server.state){
						item.disabled = true
					}
//    				'uninstalling' == server.state && createBuckupDisable.includes(item.id) && (item.disabled = true)
					'系统盘' == server.type && detachDisable.includes(item.id) && (item.disabled = true) && (item.title="当前云硬盘为系统盘，不可执行该操作" )
					server.with_vm && detachDisable.includes(item.id) && (item.disabled = true) && (item.title="与云主机一起购买的云硬盘，不可卸载" )
					if(server.state == 'creating'){
						item.disabled = true;
					}
					if(server.state == 'error'){
						item.disabled = true;
					}
					if(server.expired){
						item.disabled = true;
						item.title="当前云硬盘已过期，请续订后再执行操作"
					}
      			})
      			this.$set(server,'operation',Operation)
      		},
      		filterServers(servers){
      			let _this = this;
      			if(Array.isArray(servers)){
	      			servers.forEach(function(item){
	      				item.storagetype = item.storagetype == 'shared' ? '是' : '否';
	      				item.type = item.type == 'DATADISK' ? '数据盘' : '系统盘';
	      				item.chargeMode = item.cycle_type == 'M' ? '按月' : '按年';
	      				item.disk_types = item.disk_types ? diskType(item.disk_types) : item.volume_type ? diskType(item.volume_type) : '--';
	      				item.finishAt = item.expire_date ? timeService(item.expire_date) : '--';
	      				item.states = item.state;
		      				if(item.state == 'ERROR' || item.status == 'ERROR'){
		      					item.state = 'error';
		      				}else if(item.state=='Creating' || item.status == 'Creating'){
		      					item.state = 'creating';
		      				}else{
		      					item.state = item.virtualmachineid || (item.attachments && item.attachments.length) ? 'mount' : 'uninstall';
		      				}
	      				if(_this.regionType == 'cs'){
		      				item.size = item.size/1024/1024/1024;
	      				}
	      				if(_this.regionType == 'os'){
	      					
	      				}
      					item.expired = timeExpired(item.finishAt);
//	      				if(item.expired){
//	      					_this.expiredShow++;
//	      				}
	      				_this.addOperation(item);
	      				item.jobid ? _this.autoRefresh(item.jobid) && (item.name == _this.newEcmName && _this.startTimer(item,'clear')) : !item.id ? (item.state == 'creating') && _this.getStarting(item) : (item.state == 'creating') && _this.autoRefreshTime(item.id,'create')
	      				if(!item.jobid){
	      					if(item.no_time && item.no_time == '1'){
	      						_this.noTimeList.push({id : item.id});
	      					}
	      				}
	      			})
	      		}else{
	      			servers.storagetype = servers.storagetype == 'shared' ? '是' : '否';
      				servers.type = servers.type == 'DATADISK' ? '数据盘' : '系统盘';
      				servers.chargeMode = servers.cycle_type == 'M' ? '按月' : '按年';
      				servers.disk_types = servers.disk_types ? diskType(servers.disk_types) : servers.volume_type ? diskType(servers.volume_type) : '--';
      				servers.finishAt = servers.expire_date ? timeService(servers.expire_date) : '--';
      				servers.states = servers.state;
      				if(servers.state == 'ERROR'){
      					servers.state = 'error';
      				}else if(servers.state == 'Creating'){
      					servers.state = 'creating';
      				}else{
      					servers.state = servers.virtualmachineid || (servers.attachments && servers.attachments.length) ? 'mount' : 'uninstall';
      				}
      				if(_this.regionType == 'cs'){
      					servers.size = servers.size/1024/1024/1024;
      				}
      				servers.expired = timeExpired(servers.finishAt);
//    				if(servers.expired){
//    					_this.expiredShow++;
//    				}
	      			_this.addOperation(servers);
	      		}
      		},
      		getStarting(item,clear){
      			if(!this.newEbsName){
      				this.num += 1;
      				this.startTimer(item)
      			}
      		},
      		startTimer(item,clear){
      			let that = this
      			if(this.ebsdetailRefresh) return
      			if(this.num < 2){
      				if(clear){
	      				this.newEbsName = '';
	      				this.refresh();
	      				this.num = 0;
	      				return;
	      			}
	      			setTimeout(()=>{
	      				that.refresh()
	      				this.num=0;
	      				that.newEbsName || (that.newEbsName = item.name);
	      				that.newEbsName;
	      				that.startTimer()
	      			},60000)
      			}
      		},
      		search(filter){
      			console.log(filter)
				if(filter.name == 'id'){
      				this.filter.status = ''
					this.filter.name=''
					this.filter.id=filter.value;
	//				this.filter[filter.name] = filter.value;
					this.filter.keyword = '';
      			}else{
      				this.filter.status = ''
					this.filter.name=''
					this.filter.id=''
	//				this.filter[filter.name] = filter.value;
					this.filter.keyword = filter.value;
      			}
				this.getEbsList()
      		},
      		clear(){
      			this.filter.keyword='';
      			this.filter.name='';
      			this.filter.id='';
      		},
      		selectChange(value){
      			console.log(value);
      			if(value == 'ready'){
      				this.filter.attached = 0;
      			}else if(value == 'allocated'){
      				this.filter.attached = 1;
      			}else{
      				this.filter.attached = '';
      			}
      			this.filter.page = 1;
//				this.filter.status =value;
				console.log(this.filter);
				this.getEbsList()
			},
      		refresh(){
//    			this.filter.status = ''
//		        this.filter.keyword = '';
		        this.filter.name=''
		        this.filter.id=''
//		        this.filter.attached=''
		        this.GetRegionNums()
      			this.getEbsList()
      		},
      		pagingChange(type,val){
      			if(type == 'num'){
      				this.filter.limit = val;
      			}else if(type == 'page'){
      				this.filter.page = val;
      			}
      			this.loading =true;
      			this.getEbsList();
      		},
      		GetRegionNums(){
				let params ={
					resource:'volume',
					regionid:getRegionId()
				}
				GetRegionNum(params).then( res => {
					let data = res.data.results[0].Volume;
					console.log(data)
					if(data && data.detail){
						this.regionData = data.detail;
					}
					
				})
			},
		},
		computed:{
			regionType(){
				return this.$store.getters.regionType;
			}
		},
		mounted(){
			this.getRegion();
		},
		beforeDestroy(){
			this.ebsdetailRefresh = true;
		}
	}
</script>

<style lang="scss" scoped="scoped">
  	.ebsmain{
  		background: #f6f8fb;
  	}
	.content{
	  	.content_top{
	  		background: #ffffff;
	  		padding-top: 30px;
	  		padding-left: 20px;
	  		padding-bottom: 20px;
	  	}
	}
</style>



