<template>
	<div class='monitor_template'>
		<div class="header">
			<h4 class="header_title">{{name}}</h4>
			<el-row>
				<el-col :span="20">
					<p class="header_tip">
						<i class="el-icon-question"></i>
						<span>告警模版是一组以服务为单位的告警规则组合，您可食用系统默认推荐的或自己创建的告警模版，快速为云资源创建告警规则。</span>
					</p>
				</el-col>
				<el-col :span="4" class="t_a_c">
					<el-button class="createButton" @click='create'>创建自定义告警模版</el-button>
				</el-col>
			</el-row>
		</div>
		<div class="line"></div>
		<div class="content">
			<div class="template_title">
				<el-radio-group v-model="template_type" size="small" fill="#3dc7fa">
			      <el-radio-button label="default">默认告警模版</el-radio-button>
			      <el-radio-button label="custom">自定义告警模版</el-radio-button>
			    </el-radio-group>
			    <div class="fl_r">
				    <el-input placeholder="请输入内容" v-model="input5" class="input-with-select" size="small" style="width:350px">
					    <el-select v-model="select" slot="prepend" placeholder="请选择">
					      <el-option label="餐厅名" value="1"></el-option>
					      <el-option label="订单号" value="2"></el-option>
					      <el-option label="用户电话" value="3"></el-option>
					    </el-select>
					    <el-button slot="append" icon="el-icon-search"></el-button>
					</el-input>
					<el-button icon="el-icon-refresh" size="small" class="m_l_10"></el-button>
			    </div>
			</div>
			<div class="template_default p_t_20" v-show="isDefault">
				<el-table
				    :data="tableData5"
				    style="width: 100%">
				    <el-table-column type="expand">
				      <template slot-scope="props">
				        <el-form label-position="left" inline class="demo-table-expand">
				          <el-form-item label="名称：">
				            <span>{{ props.row.name }}</span>
				          </el-form-item>
				          <el-form-item label="服务：">
				            <span>{{ props.row.shop }}</span>
				          </el-form-item>
				          <el-form-item label="描述：">
				            <span>{{ props.row.id }}</span>
				          </el-form-item>
				          <el-form-item label="维度：">
				            <span>{{ props.row.shopId }}</span>
				          </el-form-item>
				        </el-form>
				        <h4 class="header_title p_t_20">告警规则</h4>
				        <el-table :data="tableData5_1" class="row_table">
				        	<el-table-column label="监控指标" prop="index_name"></el-table-column>
				        	<el-table-column label="监控周期" prop="zhou"></el-table-column>
				        	<el-table-column label="出现次数" prop="num"></el-table-column>
				        </el-table>
				      </template>
				    </el-table-column>
				    <el-table-column
				      label="名称"
				      prop="id">
				    </el-table-column>
				    <el-table-column
				      label="描述"
				      prop="name">
				    </el-table-column>
				    <el-table-column
				      label="服务"
				      prop="name">
				    </el-table-column>
				    <el-table-column
				      label="告警规则数"
				      prop="desc">
				    </el-table-column>
				  </el-table>
			</div>
			<div class="template_custom p_t_20" v-show="!isDefault">
				<el-table
				    :data="tableData5"
				    style="width: 100%">
				    <el-table-column type="expand">
				      <template slot-scope="props">
				        <el-form label-position="left" inline class="demo-table-expand">
				          <el-form-item label="名称：">
				            <span>{{ props.row.name }}</span>
				          </el-form-item>
				          <el-form-item label="服务：">
				            <span>{{ props.row.shop }}</span>
				          </el-form-item>
				          <el-form-item label="描述：">
				            <span>{{ props.row.id }}</span>
				          </el-form-item>
				          <el-form-item label="维度：">
				            <span>{{ props.row.shopId }}</span>
				          </el-form-item>
				        </el-form>
				        <h4 class="header_title p_t_20">告警规则</h4>
				        <el-table :data="tableData5_1" class="row_table">
				        	<el-table-column label="监控指标" prop="index_name"></el-table-column>
				        	<el-table-column label="监控周期" prop="zhou"></el-table-column>
				        	<el-table-column label="出现次数" prop="num"></el-table-column>
				        </el-table>
				      </template>
				    </el-table-column>
				    <el-table-column
				      label="名称"
				      prop="id">
				    </el-table-column>
				    <el-table-column
				      label="描述"
				      prop="name">
				    </el-table-column>
				    <el-table-column
				      label="服务"
				      prop="name">
				    </el-table-column>
				    <el-table-column
				      label="告警规则数"
				      prop="desc">
				    </el-table-column>
				    <el-table-column label="操作">
				      <template slot-scope="scope">
				        <el-button
				          size="mini"
				          type="text"
				          @click="handleEdit(scope.$index, scope.row)">修改</el-button>
				        <el-button
				          size="mini"
				          type="text"
				          @click="handleDelete(scope.$index, scope.row)">删除</el-button>
				      </template>
				    </el-table-column>
				  </el-table>
			</div>
		</div>
	</div>
</template>

<script>
import {getTemplateList} from '@/api/monitor'
	export default {
		data (){
			return {
				name :'告警模版',
				template_type: 'default',
				input5: '',
				select: '',
				tableData5: [{
		          id: '12987122',
		          name: '好滋好味鸡蛋仔',
		          category: '江浙小吃、小吃零食',
		          desc: '荷兰优质淡奶，奶香浓而不腻',
		          address: '上海市普陀区真北路',
		          shop: '王小虎夫妻店',
		          shopId: '10333'
		        }, {
		          id: '12987123',
		          name: '好滋好味鸡蛋仔',
		          category: '江浙小吃、小吃零食',
		          desc: '荷兰优质淡奶，奶香浓而不腻',
		          address: '上海市普陀区真北路',
		          shop: '王小虎夫妻店',
		          shopId: '10333'
		        }, {
		          id: '12987125',
		          name: '好滋好味鸡蛋仔',
		          category: '江浙小吃、小吃零食',
		          desc: '荷兰优质淡奶，奶香浓而不腻',
		          address: '上海市普陀区真北路',
		          shop: '王小虎夫妻店',
		          shopId: '10333'
		        }, {
		          id: '12987126',
		          name: '好滋好味鸡蛋仔',
		          category: '江浙小吃、小吃零食',
		          desc: '荷兰优质淡奶，奶香浓而不腻',
		          address: '上海市普陀区真北路',
		          shop: '王小虎夫妻店',
		          shopId: '10333'
		        }],
		        tableData5_1: [
		        	{
		        		index_name: "异常主机数 原始值>1",
		        		zhou: '2分钟',
		        		num: 2
		        	},
		        	{
		        		index_name: "异常主机数 原始值>1",
		        		zhou: '2分钟',
		        		num: 2
		        	},
		        	{
		        		index_name: "异常主机数 原始值>1",
		        		zhou: '2分钟',
		        		num: 2
		        	}
		        ]
			}
		},
		mounted(){
			getTemplateList().then(res => {
				console.log(res,'adfas');
			},error => {

			})
		},
		
		methods:{
			create(){
				this.$router.push('/monitor/createTemplate');
			}
		},
		computed: {
			isDefault:function(){
				return this.template_type === 'default';
			}
		},
		
	}
</script>

<style scoped lang="scss">
.monitor_template{
	.header{
		background: #fff;
		padding: 20px;
		border-radius: 5px;
	}
	.header_title{
		padding-bottom: 20px;
    	font-size: 14px;
    	color: #333;
	}
	.header_tip{
		background: #fbf9f2;
    	color: #f5b840;
    	height: 36px;
    	line-height: 36px;
    	font-size: 14px;
    	padding-left: 10px;
	}
	.t_a_c{
		text-align: center;
	}
	.createButton{
		width: 146px;
		height: 36px;
		background: #3ac0f1;
		border-color: #3ac0f1;
		color: #ffffff;
		font-size: 12px;
		&:hover{
			background: #5ed4ff;
		}
	}
	.line{
		height: 10px;
		width: 100%;
		background: #f0f2f7;
		background-size: 50% 100%;
		background-repeat: no-repeat;
	}
	.content{
		background: #fff;
		padding: 20px;
		border-radius: 5px;
	}
	.template_title{

	}
	.template_default{

	}
	.template_custom{

	}
	.fl_r{
		float: right;
		vertical-align: middle;
	}
	.m_l_10{
		margin-left: 10px;
	}
	.p_t_20{
		padding-top: 20px;
	}
}
</style>
<style lang="scss">
.monitor_template{
	.el-select .el-input {
		width: 100px;
	}
	.input-with-select .el-input-group__prepend {
		background-color: #fff;
	}
	.el-table th{
	  	padding: 6px 0;
	  	background-color: #f0f2f7;
	}
	.demo-table-expand {
    	font-size: 0;
  	}
  	.demo-table-expand label {
    	width: 60px;
    	color: #99a9bf;
  	}
  	.demo-table-expand .el-form-item {
    	margin-right: 0;
    	margin-bottom: 0;
    	width: 50%;
  	}
  	.row_table {
  		width: 100%;
  	}
  	.row_table th{
		padding: 2px 0 !important;
	}
	.row_table td{
		padding: 6px 0 !important;
	}
}
</style>


