<template>
	<div class="editTemp">
		<el-row class="editemp-padding">
			
		</el-row>
		<el-row>
			
			<el-col>
				<el-row class="editemp-step">
					<el-col>
						<ConsoleProgress @validate='next' ref="progress" :nextBtn = 'nextBtn' :stepValues = 'stepValues'>
							<div slot="formContent" class="formContent">
								<el-form :model="pwd" label-width="90px" ref='pwd' :rules='rules' v-if="formShow" size="mini">
									<el-form-item label="旧密码 :" prop='oldPwd' v-if="!oldPwdSuccess">
										<el-input v-model='pwd.oldPwd' type="password" auto-complete="off" placeholder="请输入旧密码"></el-input>
									</el-form-item>
									
									<el-form-item label="新密码 :" v-if="newPwdSuccess" prop='newPwd1'>
										<el-input v-model='pwd.newPwd1' type="password"></el-input>
										<div class="passGrade">
									    	<span class="infoTip">安全等级</span>
									    	<span class="gradeTabs">
									    		<span class="gradeTab" :class="{ passSimple: passGrade>0 }"></span>
									    		<span class="gradeTab" :class="{ passModerate: passGrade>1 }"></span>
									    		<span class="gradeTab" :class="{ passComplex: passGrade>2 }"></span>
									    	</span>
									    </div>
									</el-form-item>
									<el-form-item  v-if="newPwdSuccess" class="pwd-tip">
										密码必须为8-20位英文（区分大小写）、数字和符号的组合，不能是纯数字或字母
									</el-form-item>
									<el-form-item label="确认密码 :" v-if="newPwdSuccess" prop='newPwd2'>
										<el-input v-model='pwd.newPwd2' type="password"></el-input>
									</el-form-item>
								</el-form>
								<div v-if="!formShow" class="form-tip" :loading='loading'>
									<div v-if="changePwdSuc">
										<p class="form-tip-title">
											<svg-icon icon-class="created" class="form-submit-success"></svg-icon>
											密码更换成功</p>
										<p>页面将于<span class="form-tip-interval">{{ interval || 0}}</span> 秒后返回登录界面</p>
									</div>
									<div v-if="!changePwdSuc">
										<p class="form-tip-title">
											<svg-icon icon-class="error" class="form-submit-error"></svg-icon>
											密码更换失败</p>
										<p>页面将于<span class="form-tip-interval">{{ interval || 0}}</span>秒后返回</p>
									</div>
									
								</div>
							</div>
						</ConsoleProgress>
					</el-col>
				</el-row>
				<el-row>
					
				</el-row>
			
			</el-col>
		</el-row>
		
		
	</div>
</template>

<script>
	import ConsoleProgress from '@/components/ConsoleProgress'
	import { ChangePwd, validationPw } from '@/api/login'
	import { getRegionId } from '@/utils/auth'
	import { analyzePasswordSecurityLevel } from '@/utils/validator'
	export default {
		components:{
			ConsoleProgress
		},
		data(){
			let _this = this;
			let checkOldPwd = (rule,value,callback)=>{
				if(value){
					let data = {
						password:value,
						regionid:getRegionId()
					}	
					validationPw(data.password,data.regionid).then(res=>{
						console.log(res.data);
						if(res.data.returnCode == '400'){
							callback(new Error('密码不正确'))
						}else if(res.data.returnCode == '800'){
							callback()
						}
					}).catch(error=>{
						callback(new Error('密码验证失败'))
					})
					
				}else{
					callback(new Error('密码不能为空'))
				}
				
			}
			
			let checkNewPwd1 = (rule, value, callback)=>{
				if(value){
					if(value.length < 8 || value.length >20 ){
						callback(new Error('密码长度在8-20位之间'))
					}else{
						let level = analyzePasswordSecurityLevel(value,8);
						_this.passGrade =  level
						if(level > 2){
							callback()
						}else{
							callback(new Error('密码强度较低，请重新输入'))
						}
					}
					
					
				}else{
					callback(new Error('密码不能为空'))
				}
			}
			let checkNewPwd2 = (rule, value, callback)=>{
				if(value == ''){
					callback(new Error('请再次输入密码'))
				}else if(value != this.pwd.newPwd1){
					callback(new Error('两次密码不一致'))
				}else{
					callback()
				}
			}
			
			return {
				active:0,
				oldPwdSuccess:false,
				newPwdSuccess:false,
				formShow:true,
				autoRefresh:false,
				interval:5,
				loading:false,
				changePwdSuc:false,
				passGrade:0,
				pwd:{
					oldPwd:'',
					newPwd1:'',
					newPwd2:''
				},
				btnMsg:[
					{
						value:'下一步',
						fun:'dianjixiayibu'
					}
				],
				loginBtn:[
					{
						value:'登录',
						fun:()=>{
							return this.login()
						}
					}
				],
				rebackBtn:[
					{
						value:'返回',
						fun:()=>{
							return this.login()
						}
					}
				],
				stepValues:[
					{
						title:'验证身份',
					},
					{
						title:'修改密码',
					},
					{
						title:'完成',
					},
				],
				rules:{
					oldPwd:[
						{validator:checkOldPwd,trigger:'blur'},
						{required:true,message:'请输入旧密码',trigger:'blur'}
					],
					newPwd1:[
						{validator:checkNewPwd1,trigger:'blur,change'},
						{required:true,message:'不能为空1',trigger:'blur'},
						{min:8,max:20,message:'在8-20位之间',trigger:'change'}
					],
					newPwd2:[
						{validator:checkNewPwd2,trigger:'blur'},
						{required:true,message:'不能为空2',trigger:'blur'}
					]
				}
			}
		},
		computed:{
			nextBtn(){
				if(this.formShow){
					return this.btnMsg
				}else if(!this.changePwdSuc){
					return this.rebackBtn
				}else{
					return this.loginBtn
				}
			}
		},
		methods:{
			next(){
				if(this.formShow){
					console.log(this.formShow);
					this.validaForm('pwd')
				}else if(!this.changePwdSuc){
					this.reback()
				}else{
					this.login()
				}
				
			},
			fadeOut(){
				this.$store.dispatch('FedLogOut').then(() => {
					this.timeOut()
				})
			},
			timeOut(){
				if(this.autoRefresh) return
				let _this = this;
				setTimeout(()=>{
					if(_this.interval-- <= 1){
						_this.changePwdSuc ? (location.href ='/logout') : _this.reback()
					}else{
						_this.timeOut()
					}
				},1000)
			},
			login(){
				this.$router.push('/login')
			},
			reback(){
				this.$router.push('/user/userInfo')
			},
			validaForm(ref){
				this.$refs[ref].validate(valid=>{
					if(valid){
						if(this.$refs['progress'].next() == 2){
							
							const loading = this.$loading({
						          lock: true,
						          text: '正在修改密码，请稍后',
						          spinner: 'el-icon-loading',
						          background: 'rgba(0, 0, 0, 0.7)'
						        });
						    let _this=this
						  /*  setTimeout(()=>{
						    	loading.close()
								_this.formShow = false;
								_this.timeOut()
						    },3000)
						        
						    return */
							
							let data = {
								pwdStrength:'strong',
								password:this.pwd.oldPwd,
								newpasswd:this.pwd.newPwd2
							};
							
							ChangePwd(data).then(res=>{
								
								loading.close()
								this.changePwdSuc = true;
								this.formShow = false;
								this.fadeOut()
							}).catch(error=>{
								loading.close()
								if(error.response.data.errstatus == 'pw400'){
									this.$refs['progress'].next(1)
								}else{
									this.formShow = false;
									this.timeOut()
								}
								console.log(error.response)
							})
							
							
						}else{
							this.oldPwdSuccess = true
							this.newPwdSuccess = true
						}
						
					}else{
						
					}
				})
			},
			
		},
		beforeDestroy(){
			this.autoRefresh = true;
		}
	}
</script>

<style scoped="scoped" lang="scss">
	.editemp-padding{
		padding: 40px;
	}
	.formContent{
		width:400px;
		padding:0 40px;
		margin:0 auto;
	}
	.form-tip{
		padding:20px;
		text-align: center;
		line-height: 28px;
		font-size:14px;
		color:#333333;
		.form-tip-title{
			font-size:22px;
			/*height:30px;
			line-height:30px;*/
			margin-bottom:23px;
			.form-submit-success{
				font-size:140%;
				vertical-align: middle;
				color:#56D6C4;
				margin-right: 5px;
			}
			.form-submit-error{
				font-size:140%;
				vertical-align: middle;
				color:#f6585e;
				margin-right: 5px;
			}
		}
		.form-tip-interval{
			color:#F9CD76;
			padding:0 10px;
		}
	}
	.passGrade{
		display: inline-block;
		.infoTip {
			vertical-align: middle;
		    color: #606266;
		}
		.gradeTabs {
			padding-left: 10px;
			.gradeTab {
				width: 35px;
			    height: 5px;
			    display: inline-block; 
			    margin-right: 4px;
			    background-color: #eee;
			}
		}
	}
	.passSimple {
		background-color: #eb7473 !important;
	}
	.passModerate {
		background-color: #f5b840 !important;
	}
	.passComplex {
		background-color: #55ca95 !important;
	} 
</style>
<style lang="scss">
	.pwd-tip{
		.el-form-item__content{
			font-size:12px;
			color:#666666;
			white-space:nowrap;
		}
		
	}
</style>


