/**
 * Created by liudongxu on 2018/1/11.
 */
<template>
  <div class="main create-main">
    <ConsoleCreate ref="createProcess" :returnUrl='returnUrl' :system_type="tableData.elseData[0].system_type" :checked='!checked' :submitForm="submitForm" :switchPage="nextPage" @orderEvent = 'orderEvent'>
      <div  class="configure_tab" slot="create_title" :dataForm="dataForm" v-show="current==0">
        <ConsoleConfigure ref="createOrder" :dataForm="dataForm" @orderResult = 'orderResult'></ConsoleConfigure>
        <!--<div slot='create_title' class="create_title_right" v-show="current==0">
          <el-row :gutter="20">
            <el-col :span="6">-->
        <el-aside width="200px">
		  	<div class="currentInfo">
              <h6>当前配置</h6>
              <div class="message">
                <dl v-for="(item,index) in dataForm" v-if="item.label && !item.describe">
                  <dt>{{item.label}}</dt>
                  <dd v-if="!item.first">{{item.value}}</dd>
                  <dd v-if="item.first&&item.last">{{item.first}}.{{item.second}}.{{item.third}}.{{item.four}}/{{item.last}}</dd>
                  <dd v-if="item.first&&!item.last">{{item.first}}.{{item.second}}.{{item.third}}.{{item.four}}</dd>
                </dl>
              </div>
              <!--<el-button type="warning" icon='el-icon-check'>预付费</el-button>-->
              <!--<div class="show_money">-->
                <!--<span class=" money_icon">￥</span><span class="money_icon price">12321</span>元-->
              <!--</div>-->
              <!--<p class="link" @click="openHelp">了解计费详情</p>-->
        	</div>
		</el-aside>
            <!--</el-col>
          </el-row>
        </div>-->
      </div>
      <div slot="confirm_title" v-show="current===1">
        <div style="padding: 20px 0;background-color: #fff;">资源详情</div>
        <ConsoleConfirm :tableData="tableData"></ConsoleConfirm>
        <div class="Agreement_component">
		    <p>
		      	<el-checkbox class="checkedTip" v-model="checked">我已阅读并同意相关协议</el-checkbox>
		      	<span @click="goProtocol" class="agreement">《 虚拟私有云服务协议 》</span>
		    </p>
	  	</div>
      </div>
      <div slot="submit_title" class="submit_title"  v-if="current===2">
        <ConsoleSuccess :return_url="returnUrl" :orderWay=true :system_type="tableData.elseData[0].system_type" :system_name="tableData.system_name" ></ConsoleSuccess>
      </div>
    </ConsoleCreate>



  </div>
</template>

<script>
  import ConsoleCreate from '@/components/ConsoleCreate'
  import ConsoleTable from '@/components/ConsoleTable'
  import ConsoleConfigure from '@/components/ConsoleConfigure'
  import ConsoleConfirm from '@/components/ConsoleConfirm'
  import ConsoleSuccess from '@/components/ConsoleSuccess'
  import { getName } from '@/utils/shareFunction'
  import i18n from '@/lang'
  import { getEcmPlatforms } from '@/api/ecm'
  import { getRegionId,getZoneId } from '@/utils/auth'
  import { creatVpc , listZone} from '@/api/vpc'

  export default {
    props:{
      name:[String],
    },
    components:{
      ConsoleCreate,
      ConsoleTable,
      ConsoleConfigure,
      ConsoleConfirm,
      ConsoleSuccess,
    },
//    extends:{
//      validators: { // `numeric` and `url` custom validator is local registration
//        numeric: function (val/*,rule*/) {
//          console.log('123456');
//          return /^[-+]?[0-9]+$/.test(val)
//        },
//        url: function (val) {
//          return /^(http\:\/\/|https\:\/\/)(.{4,})$/.test(val)
//        }
//      },
//    },
    data(){
      return {
        page:0,
        current:0,
        checked:false,
        returnUrl:'/vpc/vpcList/',
        Agreement:{
          url:'http://www.baidu.com',
          name:'<<天翼云弹性云服务器服务协议>>'
        },
        dataForm: {
          name: {
            name: 'name', type: 'input', label: i18n.vpc.name + '：', value: ''
          },
          vpcDisplaytext:{
          	describe:true,
          	label:'VPC描述：',
          	value: ''
          },
          cidr: {
            name: 'cidr',
            label: i18n.vpc.vpc_net + '：',
            type: 'vpc_net',
            mark: true,
            first: '192',
            second: '168',
            third: '0',
            four: '0',
            last: '16',
            value: '192.168.0.0/16'
          },
          sub_name: {
            name: 'sub_name',
            label: i18n.vpc.net_name + '：',
            type: 'input',
            value: ''
          },
          subDisplaytext:{
          	describe:true,
          	label:'子网描述：',
          	value: ''
          },
          sub_type: {
            name: 'sub_type',
            label: i18n.vpc.net_type + '：',
            type: 'input',
            value: '',
            values:0,
          },
          sub_cidr: {
            name: 'sub_cidr',
            label: i18n.vpc.net_paragraph + '：',
            type: 'vpc_net',
            subType:0,
            first: '192',
            second: '168',
            third: '0',
            four: '0',
            last: '24',
            value: '192.168.0.0/24'
          },
          gateway_ip: {
            name: 'gateway_ip',
            label: i18n.vpc.gateway + '：',
            type: 'gateway',
            first: '192',
            second: '168',
            third: '0',
            four: '1',
            value: '192.168.0.1'
          },
          display: {
            name: '隐藏高级选项',
            type: 'radio',
            value: ''
          },
//        dns1: {
//          name: 'dns1',
//          label: i18n.vpc.dns1,
//          type: 'dns',
//          first: '114',
//          second: '114',
//          third: '114',
//          four: '114',
//          value: '114.114.114.114'
//        },
//        dns2: {
//          name: 'dns2',
//          label: i18n.vpc.dns2,
//          type: 'dns',
//          first: '8',
//          second: '8',
//          third: '8',
//          four: '8',
//          value: '8.8.8.8'
//        },
        },
        tableData:{
          system_name:'',//资源名称
          elseData: [
            {
            	system_type:'虚拟私有云',//资源类型
              	num: '1',
              	confirmData:[],
            }
          ],
          tableOptions: [
            {prop:'num',label:'数量',width:''}
          ],

        },
        multipleSelection: [],
        options: [{
          value: i18n.common.allStatus,
          label: i18n.common.allStatus
        }, {
          value: i18n.common.active,
          label: i18n.common.active
        }, {
          value: i18n.common.shutOff,
          label: i18n.common.shutOff
        }],
        optionsValue: i18n.common.allStatus,
        tableOptions:[{
          prop:'name',
          label:i18n.vpc.productName,
          width:'120px'
        },{
          prop:'status',
          label:i18n.vpc.configure,
          width:'120px'
        },{
          prop:'vpc_net',
          label:i18n.vpc.num,
          width:'120px'
        }],
        vpcOperation:[{
          value:i18n.vpc.delete,
          fun:'delete'
        },{
          value:i18n.vpc.edit,
          fun:'edit'
        }]
      }
    },
    methods:{
    	orderEvent(){
    		console.log('进入vpc表单页');
    		this.$refs.createOrder.order();
    	},
    	openHelp(){
      		open("http://www.ctyun.cn/help");
      	},
    	orderResult(){
    		this.$refs.createProcess.next();
    		console.log('出来vpc表单页');
    		//this.$refs.createProcess.next();
    	},
    	goProtocol(){
    		window.open('http://www.ctyun.cn/term/vpc');
    	},
      	submitForm(fn) {
          let params={
      			regionid:getRegionId()
      		};
          let data={
            "name": "",
            "cidr": "",
            "gateway_ip": "",
            "sub_name": "",
            "sub_cidr": "",
            "is_lb": 0,
            "dns1": "",
            "dns2": "",
            "dhcp": 0,
            "zoneid":getZoneId()
          };
          data.name=this.dataForm.name.value;
          data.cidr=this.dataForm.cidr.value;
          data.desc=this.dataForm.vpcDisplaytext.value;
          data.subnet_desc=this.dataForm.subDisplaytext.value;
          data.gateway_ip=this.dataForm.gateway_ip.value;
          data.sub_name=this.dataForm.sub_name.value;
          data.sub_cidr=this.dataForm.sub_cidr.value;
          data.is_lb=this.dataForm.sub_type.values;
          if(this.regionType == 'cs'){
          	if(this.dataForm.Acl.aclid){
          		data.aclid = this.dataForm.Acl.aclid;
          	}else{
          		data.aclname = this.dataForm.Acl.value;
          		data.acldescription = this.dataForm.aclDisplaytext.value;
          		data.acllist = this.dataForm.aclRule.data;
          	}
          }
//        data.dns1=this.dataForm.dns1.value;
//        data.dns2=this.dataForm.dns2.value;
			console.log(data);
		const loading = this.$loading({
          	lock: true,
          	text: '拼命创建中',
          	spinner: 'el-icon-loading',
          	background: 'rgba(0, 0, 0, 0.7)'
		});
          creatVpc(data).then((res)=>{
          	loading.close();
            if(res.data.code == 200){
		        this.$message({
		          	message: '命令下发成功',
		          	type: 'success'
		        });
		        fn();
			}else{
				this.$message({
		          showClose: true,
		          message: res.data.reason || res.data.msg,
		          type: 'error'
		        });
			}
          }).catch(()=>{
			loading.close();
          });

      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      },
      telnet(){
        console.log('telnet')
      },
      handleSelectionChange(val) {
        this.multipleSelection = val;
      },
      handleClick(){

      },
      create(){
        console.log(123231);
        this.$router.push({path:'vpc/creatVpc'})
      },
      nextPage(index){
        this.current = index;
        var system_name=this.dataForm.name.value;
        this.dataForm.cidr.value=this.dataForm.cidr.first + '.' + this.dataForm.cidr.second + '.' + this.dataForm.cidr.third + '.' + this.dataForm.cidr.four+ '/' + this.dataForm.cidr.last ;
        this.dataForm.sub_cidr.value=this.dataForm.sub_cidr.first + '.' + this.dataForm.sub_cidr.second + '.' + this.dataForm.sub_cidr.third + '.' + this.dataForm.sub_cidr.four+ '/' + this.dataForm.sub_cidr.last ;
        this.dataForm.sub_type.value=this.dataForm.sub_type.values ? '负载均衡' : '普通子网';
        this.dataForm.gateway_ip.value=this.dataForm.gateway_ip.first + '.' + this.dataForm.gateway_ip.second + '.' + this.dataForm.gateway_ip.third + '.' + this.dataForm.gateway_ip.four;
//      this.dataForm.dns1.value=this.dataForm.dns1.first + '.' + this.dataForm.dns1.second + '.' + this.dataForm.dns1.third + '.' + this.dataForm.dns1.four ;
//      this.dataForm.dns2.value=this.dataForm.dns2.first + '.' + this.dataForm.dns2.second + '.' + this.dataForm.dns2.third + '.' + this.dataForm.dns2.four;
        this.tableData.elseData[0].confirmData = this.dataForm;
        this.tableData.system_name=system_name;
      },
	  	setname(){
	      	let vpcname;
	      	vpcname='vpc-'+getName();
	      	this.dataForm.name.value=vpcname;
	    	this.dataForm.sub_name.value='subnet-'+getName();
	    	this.dataForm.sub_type.value=this.dataForm.sub_type.values ? '负载均衡' : '普通子网';
	    	if(this.regionType == 'cs'){
	    		this.dataForm.Acl.value='acl-'+getName();
	    		this.dataForm.Acl.values=this.dataForm.Acl.value;
	    	}
	  	}
    },
    /*created(){
    },*/
    computed:{
    	regionType(){
    		let regionType = this.$store.getters.regionType;
    		return this.$store.getters.regionType;
    	}
    },
    created(){
		if(this.$store.getters.regionType=='cs'){
	    	let Acl = {
	          	type:'使用默认',
	          	label:'acl名称：',
	          	aclid:'',
	          	value:'',
	          	values:'',
	         };
	        let aclDisplaytext={
	          	value:''
	         };
	        let aclRule={
	          	num:10,
	          	data:[]
	         };
    		this.$set(this.dataForm,"Acl",Acl);
    		this.$set(this.dataForm,"aclDisplaytext",aclDisplaytext);
    		this.$set(this.dataForm,"aclRule",aclRule);
    		console.log(this.dataForm)
    	}
    	this.setname();
    }
  }
</script>

<style lang="scss" scoped="scoped">
  .main{

    background: #f6f8fb;
    .el-form{
    	margin-right: 20px;
	    padding-left: 20px;
	    padding-top: 20px;
	    background-color: #fff;
    }
    .create_title_right{
    	padding-top: 20px;
    	background-color: #fff;
    	padding-left: 6px;
    }
    .el-aside{
		background-color: #fff;
	}
    .currentInfo{
		background-color: #fff;
	    height: 360px;
	    padding: 10px;
	    padding-top: 18px;
	    border-radius: 3px;
	    .el-button{
	    	padding: 7px 15px;
	    }
	    .message{
			margin-top: 20px;
			dl{
				/*height: 27px;*/
				font-size: 12px;
				color: #333333;
				padding-bottom: 15px; 
				&:after{
					display: table;
					content: '';
				}
				dt{
					float: left;
					font-size: 12px;
					color: #afbfda;
				}
				dd{
					white-space: normal;
					overflow: hidden;
					/*text-overflow: ellipsis;*/
					font-size: 12px;
				}
			}
		}	
	}
  }
  .content{
    .content_top{
      background: #ffffff;
      padding-top: 30px;
      padding-left: 20px;
      padding-bottom: 20px;
    }
  }
  .configure_tab{
    display: flex;
    flex-direction: row;
    /*.vpc-ruleForm{
      width: 75%;
    }*/
   	.padding-left{
		padding-left: 0px;
	}
   	.el-main{
		padding-left: 20px;
		background-color: #fff;
		padding-right: 0px;
		margin-right: 20px;
	}
    .configure_tab .create_title_right{

    }
    .create_title_right{
      width: 25%;
      .el-col-6{
        width: 100%;
      }
      .message{
        margin-top: 20px;
        dl{
          display: flex;
          flex-direction: row;
          font-size: 12px;
          /*line-height: 12px;*/
          color: #333333;
          padding-bottom: 15px;
          &:after{
            display: table;
            content: '';
          }
          dt{
            margin-right: 10px;
            font-size: 12px;
            color: #333333;
          }
          dd{
          	width: 72%;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
            font-size: 13px;
          }
        }
      }
      .show_money{
        margin-top: 30px;
        .money_icon{
          color: #f9cd76;
          font-size: 17px;
          font-weight: 600;
        }
      }
      .link{
        color: #3dc7fa;
        cursor: pointer;
        font-size: 12px;
        margin-top: 15px;
      }
    }
  }
	.Agreement_component{
	    font-size: 12px;
	    margin-top: 20px;
      	.agreement {
			color: #3dc7fa;
			cursor: pointer;
			font-weight: 500;
		}
	}
</style>
<style lang="scss">
	.create-main{
		.checkedTip{
			.el-checkbox__label{
				font-size: 12px;
			}
		}
		.tiny_input_ip_container{
	    	background: #fff;
	    }
	  .el-form-item__content{
	    /*width: 40%;*/
	  }
	  .el-input__inner{
	    background-color: #fff;
	  }
	  .el-form-item--small{
	    label{
	      text-align: left;
	      font-size:12px;
	    }
	  }
		.create_title_right{
			.el-col-6{
	    		padding-left: 16px!important;
	    	}
	    }
	}

</style>



