<template>
	<div class="pwd-change">
		<el-row class="action-tip bg">
			<el-row class="action-tip-title"  v-html="actionTip[0]">
				
			</el-row>
			<el-row class="action-tip-title action-danger" v-if=" actionTip[1] " v-html="actionTip[1]">
			
			</el-row>
			
		</el-row>
		<el-row class="form-content">
			<el-form :model="pwdForm" :rules="rules" label-width="80px" size="mini" ref="pwdForm">
				<el-form-item label="密码 :" prop="pwd">
					<el-input type="password" v-model="pwdForm.pwd"></el-input>
					<div class="passGrade">
				    	<span class="gradeTabs">
				    		<span class="gradeTab" :class="{ passSimple: passGrade>0 }"></span>
				    		<span class="gradeTab" :class="{ passModerate: passGrade>1 }"></span>
				    		<span class="gradeTab" :class="{ passComplex: passGrade>2 }"></span>
				    	</span>
				    </div>
					<el-tooltip popper-class='editSnapTip' content="密码必须为8-20位英文（区分大小写）、数字和符号的组合，不能是纯数字或字母" placement="left"><i class="el-icon-question"></i></el-tooltip>
				</el-form-item>
				<el-form-item label="确认密码 :" prop="newPwd">
					<el-input type="password" v-model="pwdForm.newPwd"></el-input>
				</el-form-item>
			</el-form>
		</el-row>
	</div>
</template>

<script>
	import { analyzePasswordSecurityLevel } from '@/utils/validator'
	export default {
		props:{
			form:[Object],
			tableData:[Object,Array],
			actionTip:[Array]
		},
		data(){
			let _this = this;
			let checkNewPwd1 = (rule, value, callback)=>{
				if(value){
					if(value.length < 8 || value.length >20 ){
						callback(new Error('密码长度在8-20位之间'))
					}else{
						let level = analyzePasswordSecurityLevel(value,8);
						_this.passGrade =  level
						if(level > 2){
							callback()
						}else{
							callback(new Error('密码强度较低，请重新输入'))
						}
					}
					
					
				}else{
					callback(new Error('密码不能为空'))
				}
			}
			let checkNewPwd2 = (rule, value, callback)=>{
				if(value == ''){
					callback(new Error('请再次输入密码'))
				}else if(value != this.pwdForm.pwd){
					callback(new Error('两次密码不一致'))
				}else{
					callback()
				}
			}
			return {
				pwdForm:{
					pwd:'',
					newPwd:''
				},
				passGrade:0,
				rules:{
					pwd:[
						{
							required:true,
							message:'请输入密码',
							trigger:'change,blur'
						},{
							validator:checkNewPwd1,
							trigger:'change,blur'
						}
					],
					newPwd:[
						{
							required:true,
							message:'请再次输入密码',
							trigger:'change,blur'
						},{
							validator:checkNewPwd2,
							trigger:'change,blur'
						}
					]
				}
			}
		},
		
		methods:{
			validateForm(){
				this.form.new_password = this.pwdForm.newPwd;
				return this.$refs.pwdForm.validate()
			}
		}
	
	}
</script>

<style lang="scss" scoped="scoped">
	.pwd-change{
		max-width:500px;
	}
	.action-tip{
		font-size: 12px;
		line-height: 18px;
	}
	.bg{
		background: #fff9ee;
		padding:10px;
		border:1px solid #F9CD76;
		.action-danger{
			color:#f6585e;
		}
	}
	.form-content{
		padding:20px 10px 0;
		.el-input{
			width:50%;
		}
	}
	
	.passGrade{
		display: inline-block;
		.infoTip {
			vertical-align: middle;
		    color: #606266;
		}
		.gradeTabs {
			padding-left: 10px;
			.gradeTab {
				width: 35px;
			    height: 5px;
			    display: inline-block; 
			    margin-right: 4px;
			    background-color: #eee;
			}
		}
	}
	.passSimple {
		background-color: #eb7473 !important;
	}
	.passModerate {
		background-color: #f5b840 !important;
	}
	.passComplex {
		background-color: #55ca95 !important;
	} 
</style>
<style lang="scss">
	.pwd-change{
		.el-form-item{
			
			label.el-form-item__label{
				font-size:12px;
			}	
		}
	} 
	.editSnapTip{
		width:100px;
	}
</style>


