<template>
  <div class="main">
    <RegionList :name ='name' :regionData='regionData' :buttonText ='buttonText' @create='create' @changeRegion = 'changeRegion' type="cs"></RegionList>
    <div class="content">
      <div class="content_top">
        <!--<div class="acl_mark">您还可以创建99个ACL规则,4998个ACL规则</div>-->
        <!--<div class="acl_mark">您还可以创建{{aclRemainCount}}个ACL</div>-->
        <div class="acl_mark"></div>
        <ConsoleSearch
          class="acl_search"
          :options ='options'
          :optionsValue ='optionsValue'
          @searchEvent = 'search'
          :btnClose = true
          @clear = 'clear'
          @refreshEvent = 'refresh'
        ></ConsoleSearch>
      </div>
      <div>
        <ConsoleAclTable class="aclList" :checkBox='checkBox'
                      :tableData='acllist'
                      :tableOptions="tableOptions"
                      :selectOrNot=true
                      :noHead="true"
                      :expand="true"
                      :addRule='addRule'
                      :expandTable="true"
                      @buttonClick = 'buttonClick'
                      :detilOptions="detilOptions"
                      :detilData="acllist['security_group_rules']"
                      :operationOrNot=true
                      :operationdetailOrNot=true
                      :loading = 'loading' 
                      :OperationEvents='aclOperation'
                      :OperationdetailEvents='OperationdetailEvents'
                         :Pagination = 'Pagination'
                         @pagingEvent = 'pagingChange'
        ></ConsoleAclTable>
      </div>
    </div>
  </div>
</template>

<script>
	import service from '@/utils/request'
  import RegionList from '@/components/RegionList'
  import ConsoleSearch from '@/components/ConsoleSearch'
  import ConsoleAclTable from '@/components/ConsoleAclTable'
  import aclDilogView from './aclDilogView'
  import { getRegionId, setRegionId } from '@/utils/auth'
  import i18n from '@/lang'
  import { GetRegionNum } from '@/api/ecm'
  import { getAclList,CreateAcl,deleteAcl,editAcl,queryAsyncJob,addRule,deleteRule,queryQuota } from '@/api/vpc'
  export default {
    props:{
      name:[String],
      buttonText:[String]
    },
    components:{
      RegionList,
      ConsoleSearch,
      ConsoleAclTable
    },
    data(){
      return {
        checkBox:false,
        loading:false,
        statusOnOff:true,
        totalCount:0,
//      aclTotalCount:'--',
//      aclExistCount:'--',
//      aclRemainCount:'--',
        Pagination:{
          'pagingData':[10,20,50],
          'total':0,
          'show':false
        },
        acllist:[],
        regionData:[],
        multipleSelection: [],
        filter:{
          status:'',
          limit:10,
          page:1,
          name:'',
          id:'',
          search:'',
        },
        detilOptions:[
            {'label':'方向',prop:'traffictype'},
            {'label':'类型',prop:'action'},
            {'label':'协议',prop:'protocol'},
            {'label':'端口范围/ICMP类型',prop:'port'},
            {'label':'CIDR',prop:'cidrlist'},
        ],
        detilData:[
          {
            traffictype:'',
            type:'2',
            protal:'23',
            port:'223',
            romate:'45',
          },
        ],
        options: [{
          value: 'name',
          label: 'name'
        }],
        optionsValue: i18n.common.allStatus,
        tableOptions:[{
          prop:'name',
          label:i18n.vpc.name,
          temp:true,
          detail:true,
        },{
          prop:'Egress',
          label:i18n.vpc.Egress,
          direction:'out',
          temp:true,
          rule:true,
          describe:i18n.vpc.Egress,
          num:'',
        },{
          prop:'Ingress',
          label:i18n.vpc.Ingress,
          direction:'in',
          describe:i18n.vpc.Ingress,
          temp:true,
          rule:true,
          num:'',
        },{
          prop:'vpcname',
          label:i18n.vpc.vpcname,
          temp:true,
          rule:true,
          vpc:true,
        }],
        tabledetailOptions:[{
          prop:'name',
          label:i18n.vpc.name,
          temp:true,
          detail:true,
          noEffect:true
        },{
          prop:'Ingress',
          label:i18n.vpc.Ingress,
          direction:'in',
          describe:i18n.vpc.Ingress,
          temp:true,
          rule:true,
          num:'',
        },{
          prop:'Egress',
          label:i18n.vpc.Egress,
          direction:'out',
          temp:true,
          rule:true,
          describe:i18n.vpc.Egress,
          num:'',
        }],
        aclOperation:[{
          value:i18n.vpc.delete,
          fun:(data)=>{
            return this.delete(data);
          }
        },
        ],
        OperationdetailEvents:[{
          value:i18n.vpc.delete,
          fun:(data)=>{
            return this.deleteRule(data);
          }
        }]
      }
    },
    methods:{
      telnet(){
        console.log('telnet')
      },
      handleSelectionChange(val) {
        this.multipleSelection = val;
      },
      handleClick(){

      },
      buttonClick(funName,data){
        funName(data);
      },
      create(){
//    	if(this.aclRemainCount < 0){
//    		alertMsg('ACL配额不足');
//    	}else{
      		let alertMsg=this.$message;
	        let view = aclDilogView;
	        let data = {};
	        data.type='create';
	        data.title="创建ACL";
	        data.regionId=this.regionId;
	        let form = {
	          "name": "",
	          "desc": "",
	          "vpcid": ""
	        }
	        let option = {
	          data,
	          view,
	          form
	        }
	        this.$alertDialog(option).then((res)=>{
	          let data={
	            "desc": res.desc,  //#描述
	            "name": res.name,
	            "network_uuid": res.vpcid,//#上面zone列表中的第一个id
	          }
	          CreateAcl(data).then((response)=>{
	            let acl=response.data.results;
	            alertMsg('ACL创建成功');
	            this.refresh();
	
	          }).catch((error)=>{
	            console.log(error);
	//              this.$message('删除VPC失败，此VPC下存在子网！');
	          })
	        });
//    	}
      },
      delete(arg){
        let alertMsg=this.$message;
        let view = aclDilogView;
        let data = {}
        data.type='delete';
        data.title="删除ACL";
        let form ={
          name:arg.name
        }
        let option = {
          data,
          view,
          form
        }
        let aclId=arg.id;
        this.$alertDialog(option).then((res)=>{
          deleteAcl(aclId).then((response)=>{
              if(response.data.errcode=='SUCCESS'){
                alertMsg('ACL刪除成功');
                this.refresh();
              }else{
                alertMsg('ACL刪除失敗');
                this.refresh()
              }
          }).catch((error)=>{
            this.refresh()
          })
        });
      },
      edit(arg){
        let alertMsg=this.$message;
        let refresh=this.refresh;
        let view = aclDilogView;
        let data = {}
        data.type='edit';
        data.title="修改ACL";
        let form ={
          name:arg.name
        }
        let option = {
          data,
          view,
          form
        }
        let aclId=arg.id;
        this.$alertDialog(option).then((res)=>{
          let data={
            "name":res.name,
            "desc":res.name
          }
          editAcl(aclId,data).then((response)=>{
            let jobid=response.data.results[0].jobid;
            queryAsyncJob(jobid).then((result)=>{
              let status=result.data.results[0].jobstatus;
              if(status==1){
                clearInterval(timer);
                alertMsg('ACL修改成功');
                refresh();
              }else if(status==2){
                clearInterval(timer);
                alertMsg('ACL修改失败');
              }
            });
            var timer=setInterval(function(){
              queryAsyncJob(jobid).then((result)=>{
                let status=result.data.results[0].jobstatus;
                if(status==1){
                  clearInterval(timer);
                  alertMsg('ACL修改成功');
                  refresh();
                }else if(status==2){
                  clearInterval(timer);
                  alertMsg('ACL修改失败');
                }
              });
            },5000)
          }).catch((error)=>{
            console.log(error);
//              this.$message('删除VPC失败，此VPC下存在子网！');
          })
        });
      },
      addRule(arg){
        let alertMsg=this.$message;
        let view = aclDilogView;
        let refreshData=this.refreshData;
        let data = {};
        let refresh=this.refresh;
        data.type='createRule';
        data.title="增加ACL规则";
        data.regionId=this.regionId;
        let form = {
          "direction": "ingress",
          "port_range_min": "33",
          "ethertype": "IPv4",
          "port_range_max": "33",
          "remote_ip_prefix": "0.0.0.0/0",
          "security_group_id": arg.id,
          "protocol": "tcp",
          "action":""
        }
        let aclid=arg.id;
        let option = {
          data,
          view,
          form
        }
        this.$alertDialog(option).then((res)=>{
          console.log('>>>>>');
            console.log(res);
            let data;
            if(res.protocol=='icmp'){
              data={
                "direction": res.direction,
                "port_range_min": res.port_range_min,
                "remote_ip_prefix":res.remote_ip_prefix,
                "ethertype": "IPv4",
                "code": res.icmpcode,
                "type":res.icmptype,
                "security_group_id": arg.id,
                "protocol": res.protocol,
                "action":res.action
              }
            }else if(res.protocol=='all'){
              data={
                "direction": res.direction,
                "ethertype": "IPv4",
                "remote_ip_prefix":res.remote_ip_prefix,
                "security_group_id": arg.id,
                "protocol": res.protocol,
                "action":res.action
              }
            }else{
              data={
                "direction": res.direction,
                "port_range_min": res.port_range_min,
                "ethertype": "IPv4",
                "port_range_max": res.port_range_max,
                "remote_ip_prefix":res.remote_ip_prefix,
                "security_group_id": arg.id,
                "protocol": res.protocol,
                "action":res.action
              }
            }
          addRule(data).then((response)=>{
//          let jobid=response.data.results[0].jobid;
//          queryAsyncJob(jobid).then((result)=>{
//            let status=result.data.results[0].jobstatus;
//            if(status==1){
//              clearInterval(timer);
//              alertMsg('ACL规则成功');
//              refreshData(result.data.results[0].jobresult['networkacl'],aclid)
//            }else if(status==2){
//              clearInterval(timer);
//              alertMsg('ACL规则失败');
//            }
//          });
//          var timer=setInterval(function(){
//            queryAsyncJob(jobid).then((result)=>{
//              let status=result.data.results[0].jobstatus;
//              if(status==1){
//                clearInterval(timer);
//                alertMsg('ACL规则成功');
//                refreshData(result.data.results[0].jobresult['networkacl'],aclid)
//              }else if(status==2){
//                clearInterval(timer);
//                alertMsg('ACL规则失败');
//              }
//            });
//          },5000)
 						refreshData(response.data.results[0],aclid)
						alertMsg('ACL规则成功');
//						refresh();
          }).catch((error)=>{
            console.log(error);
//              this.$message('删除VPC失败，此VPC下存在子网！');
          })
        });
      },
      deleteRule(arg){
          console.log(arg);
        let alertMsg=this.$message;
        let view = aclDilogView;
        let data = {}
        data.type='delete';
        data.title="删除ACL规则";
        let form ={
          name:arg.name
        }
        let option = {
          data,
          view,
          form
        }
        let ruleId=arg.id;
        console.log(arg);
        this.$alertDialog(option).then((res)=>{
          deleteRule(ruleId).then((response)=>{
            if(response.data.errcode=='SUCCESS'){
              alertMsg('ACL规则刪除成功');
              this.acllist = this.acllist.filter(function (acl) {
              	if(acl.id == arg.aclid){
              		if(arg.traffictype == '入方向'){
              			acl['rule_in_num']--;
              		}else{
              			acl['rule_out_num']--;
              		}
	                if (acl.security_group_rules) {
	                  acl.security_group_rules=acl.security_group_rules.filter(function (rule) {
	                    return rule.id !== ruleId;
	                  })
	                }
              	}
                return true;
              });
            }else{
              alertMsg('ACL规则刪除失敗');
              this.refresh()
            }
          }).catch((error)=>{
            console.log(error);
            this.refresh()
//              this.$message('删除VPC失败，此VPC下存在子网！');
          })
        });
      },
      refreshData(data,id){
      	let _this = this;
          this.acllist.filter(function(acl){
            if(acl.id===id){
              if(data.traffictype==="Ingress"){
                acl['rule_in_num']++
                data.traffictype='入方向';
              }else if(data.traffictype==="Egress"){
                acl['rule_out_num']++
                data.traffictype='出方向';
              }
              data['type']='IPv4';
              data['port']=data.protocol==='icmp'?data.icmptype+'/'+data.icmpcode:data.protocol==='all'?'':data.startport+'/'+data.endport;
              acl['security_group_rules'].push(data);
              _this.addOperation(acl);
            }
            return;
        })
      },
      changeRegion(uuid){
      	service.cancle('CancelToken');
        this.regionId = uuid;
        this.refresh();
//      this.GetRegionNums()
        //this.queryQuotas();
//      this.getAclList()
      },
      getRegion(){
        let timer1;
        if(getRegionId()){
          clearTimeout(timer1)
          this.changeRegion();
        }else{
          let _this = this;
          timer1 = setTimeout(function(){
            _this.getRegion()
          },300)
        }
      },
      clear(){
				this.filter.keyword='';
				this.filter.name='';
				this.filter.id='';
			},
      getAclList(){
        let params =this.filter;
        this.loading =true;
        getAclList(params).then((res)=>{
          let acllist = res.data.results;
          let data=[];
          this.filterServers(acllist,data);
//          console.log(acllist);
          this.acllist=acllist;
          let totalCount = res.data.total_count;
          this.totalCount = totalCount;
//        this.aclRemainCount = 100 - this.totalCount;
          this.Pagination.total = this.totalCount;
          if(totalCount>this.Pagination.pagingData[0]){
            this.Pagination.show = true
          }
          this.loading=false
        }).catch((error)=>{
          if(error.message == 'CancelToken'){
  					
  				}else{
  					this.loading=false
  				}
          console.log(error)
        })
      },
      filterServers(list,data){
//        {name:'moren',rule_in_num:34,rule_out_num:9,describe_in:'in',describe_out:'out'}
        let _this = this;
        list.filter(function(acl){
            let Ingress=0;
            let Egress=0;
            if(acl.security_group_rules && acl.security_group_rules.length > 0){
              acl.security_group_rules.filter(function(rule){
                acl['traffictype']=rule.traffictype;
                rule['type']='IPv4';
                rule['port']=rule.protocol==='icmp'?rule.icmptype+'/'+rule.icmpcode:rule.protocol==='all'?'':rule.startport+'/'+rule.endport;
                if(rule.traffictype==="Ingress"){
                  rule.traffictype='入方向';
                  Ingress++;
                }else if(rule.traffictype==="Egress"){
                  rule.traffictype='出方向';
                  Egress++;
                }
                return true;
              });
            }else{
            	acl.security_group_rules = [];
            }
          acl['rule_in_num']=Ingress;
          acl['rule_out_num']=Egress;
          acl['describe_in']=i18n.vpc.Ingress;
          acl['describe_out']=i18n.vpc.Egress;
          acl['expendStatus']=false;
          if(acl.do_delete == '0'){
          	acl.addButton = false;
          }else if(acl.do_delete == '1'){
          	acl.addButton = true;
          }
          acl.netName = '';
          	if(acl.network.length > 0){
          		acl.network.forEach(function(item){
          			acl.netName += item.name + '、'; 
          		})
          	}
          _this.addOperation(acl);
          return true;
        })
      },
      addOperation(server){
//      	console.log(server);
        let deleteDisable = ['delete'];
        let name=server.name;
        let _this = this;
        let Operation = [{
            id: 'delete',
            value: i18n.vpc.delete,
            fun: (data) => {
              return this.delete(data);
            }
          },
          ];
        let serverDisable;
        Operation.map(item=>{
	          if(server.do_delete == '0'){
	            item.disabled = true;
	            serverDisable = true
	          }else if(server.do_delete == '1'){
	          	item.disabled = false;
	          	serverDisable = false
	          }
        });
        this.$set(server,'aclOperation',Operation)
        if(server.security_group_rules.length > 0){
        	server.security_group_rules.filter(function(items){
        			let Operationdetail = [{
			          value:i18n.vpc.delete,
			          id: 'deleteRule',
			          fun:(data)=>{
			            return _this.deleteRule(data);
			          }
			        }];
        			Operationdetail.map(item=>{
			          if(serverDisable){
			            item.disabled = true;
			          }else{
			          	item.disabled = false;
			          }
		       	 	});
		       	 	_this.$set(items,'OperationdetailEvents',Operationdetail)
        	});
        }
//        console.log(server.security_group_rules);
      },
      search(filter){
      	if(filter.name == 'id'){
      		this.filter.status = ''
					this.filter.name=''
					this.filter.search='search';
					this.filter.id=filter.value;
	//			this.filter[filter.name] = filter.value;
					this.filter.keyword = '';
      	}else{
					this.filter.status = ''
	        this.filter.name=''
	        this.filter.id=''
	        this.filter.search='';
	//      this.filter[filter.name] = filter.value;
	        this.filter.keyword=filter.value;
      	}
        console.log(filter);
        this.getAclList()
      },
      refresh(){
        this.filter.status = ''
//      this.filter.name=''
        this.filter.search='';
//      this.filter.id=''
//      this.filter.keyword=''
        this.GetRegionNums()
        this.getAclList()
      },
      pagingChange(type,val){
        if(type == 'num'){
          this.filter.limit = val;
        }else if(type == 'page'){
          this.filter.page = val;
        }
        this.loading =true;
        this.getAclList();
      },
      queryQuotas(){
      	queryQuota().then((res)=>{
          console.log(res.data.results);
          let Quota = res.data.results[0];
          this.aclTotalCount = 100;
        	this.aclExistCount = this.totalCount;
//      	this.aclRemainCount = Quota.networkavailable;
//      	this.aclRemainCount = 100 - this.totalCount;
        }).catch((error)=>{
          console.log(error)
        })
      },
      GetRegionNums(){
				let params ={
					resource:'acl',
					regionid:getRegionId()
				}
				GetRegionNum(params).then( res => {
					let data = res.data.results[0].ACLLIST;
					console.log(data)
					if(data && data.detail){
						this.regionData = data.detail;
					}
					
				})
			},
    },
    computed:{
      changeRowSty(row,index){
        return{'background':'#f0f2f7'}
      },
      changethSty(row,index){
        return{'background':'#f0f2f7','color':'#333333','font-size':'12px','font-weight':'600',}
      },
    },
    mounted(){
      this.getRegion();
//    this.queryQuotas();
    }
  }
</script>

<style lang="scss" scoped="scoped">
  .main{
    background: #f6f8fb;
  }


  .content{
    .content_top{
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      background: #ffffff;
      padding-top: 30px;
      padding-left: 20px;
      padding-bottom: 10px;
      .acl_mark{
        /*display: inline-block;*/
        /*width: 45%;*/
        /*position: relative;*/
        /*top:15px;*/
       line-height: 35px;
        font-size: 12px;
      }
      .acl_search{
        /*display: inline-block;*/
        width: 60%;
        text-align: right;
        .el-row{
          text-align: right;
          .el-col{
            float: right;
          }
        }
      }
    }
  }
</style>
<style lang="scss">
	.aclList{
    	.el-table__expanded-cell{
    		background-color: #fbfcfd;
    	}
    	.el-table td, .el-table th{
    		padding: 5px 0px;
    	}
    	.el-table .el-table__expanded-cell{
				padding: 20px 50px;
			}
    	.el-table__expanded-cell:hover{
    		background-color: #fbfcfd!important;
    	}
    }
  .acl_search{
    /*display: inline-block;*/
    width: 60%;
    text-align: right;
    .el-row{
      text-align: right;
      .el-col-14{
        float: right;
      }
    }
  }
  .aclList{
    tr{
      td:last-child{
        text-align: right;
        button{
          margin-right: 30px;
        }

      }
    }
  }
</style>



