<template>
	<div class="eipAction-content">
		<el-row class="action-tip">
			<el-row class="action-tip-title">
				{{actionTip[0]}}
			</el-row>
		</el-row>
		<el-row class="action-select">
			<el-form :model="eipData" label-width='100px'>
				<el-form-item label="带宽：" required  prop="bandwidth">
						<!--<el-slider v-model="eipData.bandwidth" @change="bandwidthChange" :max="eipData.broadbandMax" :show-input="eipData.showInput" :step="broadbandStep"></el-slider>
						<div class="slider-mark">
							<div class="scale">
								<span v-for="number in temp2" :style="'width:' + 100/temp2 + '%'" :key="number">|</span>
								<span>|</span>
							</div>
							<div class="scaleInfo">
								<span style="margin-left: -2px;">0M</span>
								<span style="margin-left: 27%;">100M</span>
								<span style="margin-left: 26%;">200M</span>
								<span style="margin-top: 0;margin-right: -10px;">300M</span>
							</div>
						</div>-->
						<slideScale @ofterCallback="bbCallback" :bandwidths = 'bandwidth' :isOften=false></slideScale>
					</el-form-item>
			</el-form>
		</el-row>
		<el-row v-show='warnTip'>
			<div style="color: red;">弹性公网IP不支持降级操作！</div>
		</el-row>
		<!--<el-row>
			<div class="price">
				<span>价格：</span><span>共计</span><span class="RMB_color">￥0.00</span>
			</div>
		</el-row>
		<el-row>
			<div class="help">
				如需了解价格详情，<a class="link" @click="openHelp">请查看计费规则</a>
			</div>
		</el-row>-->
	</div>
</template>


<script>
	import slideScale from '@/components/SlideScale'
	
	export default {
		props:{
			actionTip:[Array],
			form:[Object],
			tableData:[Object],
		},
		components:{
			slideScale
		},
		data(){
			return {
				temp2:30,
				warnTip:false,
				bandwidth:this.tableData.data[0].bandwidthNum,
				eipData:{
					bandwidth: this.tableData.data[0].bandwidthNum,
					broadbandMax: 300,
		      		showInput: true,
				},
			}
		},
		methods:{
			bbCallback (val) {
      			if(val < this.tableData.data[0].bandwidthNum){
					this.warnTip = true;
					this.eipData.bandwidth = this.tableData.data[0].bandwidthNum;
				}else{
					this.warnTip = false;
					this.form.data = val;
				}
      		},
      		openHelp(){
	      		open("http://www.ctyun.cn/help");
	      	}
		},
		computed:{
			
		},
		
	}
</script>

<style lang="scss" scoped="scoped">
	.eipAction-content{
		min-width: 600px;
		.action-tip-title{
			line-height: 30px;
		}
		.link{
			color: #3dc7fa;
			cursor: pointer;
			font-size: 12px;
		}
		/*.slider-mark {
			margin-right: 160px;
		    margin-top: -15px;
		    color: #afbfda;
		}
		.slider-mark > .scale {
			height: 20px;
		}
		.slider-mark > .scale > span {
			display: inline-block;
			height:100%;
		}
		.slider-mark > .scale > span:last-child {
			width: auto !important;
			display: inline;
		    float: right;
		    margin-top: -28px;
		}
		.slider-mark > .scaleInfo {
			font-size: 12px;
		}
		.slider-mark > .scaleInfo > span{
			display: inline-block;
			text-align: center;
		}
		.slider-mark > .scaleInfo > span:last-child {
		    float: right;
		    margin-top: -40px;
		    margin-right: -25px;
		}*/
		.price{
			span{
				display: inline-block;
				line-height: 20px;
			}
			.RMB_color{
				font-size: 20px;
				color: #f8d285;
			}
		}
		.help{
			margin-top: 8px;
			padding-left: 43px;
			a{
				color: #83dafa;
			}
		}
	}
</style>
<style lang="scss">
	.eipAction-content{
		/*.el-form-item{
			.el-form-item__label{
				position: relative;
				float: none;
				
			}
			.el-form-item__content{
				margin-top: 15px;
				.el-slider{
					.el-slider__input{
						position: absolute;
						left: 0;
						top: -30px;
					}
					.el-slider__runway{
						width: 100%;
					}
				}
				.slider-mark{
					width: 100%;
				}
			}
		}*/
	}
	
</style>


