<template>
	<div>
		<console-detail
			:detailList = 'detailList'
			:detail_image = 'detailImg'
			:detail_label='detailLabel'
			:btnMsg= 'btnFun2'
			@deatilBtnClick = 'action'
			>

		</console-detail>

		<detail-page :tabOption='tabOption' @click="switchTab" ref="detaiPage">
			<div slot="detailMain">
				<div class="detail-main" >
					<component v-for="item in tabOption.slice(0,1)" :is='item.name' :serverId = 'serverId' :serverData='serverData' :key="item.name" :ref="item.name"></component>
					<component v-for="item in tabOption.slice(1)" :is='item.name' :serverId = 'serverId' :serverData='serverData' :key="item.name" v-if="loadAfter" :ref="item.name"></component>
					<div class="detail-more-btn" v-if="!loadAfter">
						<el-button @click='dropDownLoad' type="text">下拉加载更多选项</el-button>
					</div>
				</div>
			</div>
		</detail-page>
	</div>
</template>

<script>
	import detailImg from '@/assets/ecm/ecmDetail.png'
	import ConsoleDetail from '@/components/ConsoleDetail'
	import DetailPage from '@/components/DetailPage'

	import { timeService,timeExpired } from '@/utils/timeformat'

	import { statFormat } from '@/utils/statformat'
	import { GetVnc } from '@/utils/auth'

	import { GetJobDetail } from '@/api/console'
	import { GetEcmDetail, GetEbsDetailById, EcmAction, TelnetVnc,GetNetworkById } from '@/api/ecm'
	import { HandScroll,getScrollTop,getScrollHeight,getWindowHeight } from '@/utils/shareFunction'

	import ecmDetailVolume from './ecmDetailVolume'
	import ecmDetailNic from './ecmDetailNic'
	import ecmDetailEip from './ecmDetailEip'
	import ecmDetailSec from './ecmDetailSec'

	export default {
		components:{
			ConsoleDetail,
			DetailPage,
			ecmDetailVolume,
			ecmDetailNic,
			ecmDetailEip,
			ecmDetailSec
		},
		methods:{
			action(value,label){
				let actionTip = '确认要对当前主机进行'+label+'操作吗'
				let option ={
					data:{
						actionTip:[actionTip],
						title:label,
						warn:true
					}
				}

				let server = this.serverData.servers;
				let loadingState = ['Starting','Stopping','Restarting'];
				let activeState = ['Running','ACTIVE'];
				let shutoffState = ['Stopped','SHUTOFF']
				let stateStatus = server.state || server.status
				if(loadingState.includes(stateStatus)){
					let msg = {
						Starting:'开机中',
						Stopping:'关机中',
						Restarting:'重启中'
					}
					this.$message({
						message:msg[stateStatus]+',请稍后再进行此操作',
						type:'error'
					})
					return
				}
				if(value == "start" && activeState.includes(stateStatus)){
					this.$message({
						message:'请先关闭云服务器再进行此操作',
						type:'error'
					})
					return
				}
				console.log(value)
				if((value == 'stop' || value=='reboot' || value== 'loginVnc') && shutoffState.includes(stateStatus) ){
					this.$message({
						message:'请先对云服务器开机再进行此操作',
						type:'error'
					})
					return
				}
				this.$alertDialog(option).then(()=>{
					let params  = {
						servers:[this.serverId],
						action:value
					}
					console.log(value)
					if(value == 'loginVnc'){
						let win = window.open()
						TelnetVnc(this.serverId).then(res=>{
							console.log(res)
							let data = res.data.results;
							let vncurl = this._.head(data).vnc_url || '' ;
							win.location.href = vncurl
						})
						.catch(error=>{
							console.log(error)
							win.close();
						})
					}else{
						EcmAction(params.servers,params).then((res)=>{
							this.$message({
								message:'正在进行'+label+'，请稍后',
								type:'warning'
							})
							this.getEcmDetail(this.serverId,value)
						}).catch((error)=>{
							console.log(error)
						})
					}

				})
			},
			getEcmDetail(id,value){

				GetEcmDetail(id).then((res)=>{
					//console.log(res)
					let data = res.data.results[0];

					this.filterServer(data,value)
					data.jobid && this.autoRefresh(data.jobid) 
					this.detailLabel =  data.name;

					this.serverData.servers = data;
					data.nic && (this.serverData.nics = data.nic);
				}).catch((req)=>{
					console.log(req)
				})
			},
			filterServer(data,value){
				let detailList = this.detailList;
				let flavor,nics,metaDate;
				metaDate = data.metadata;
				if('cs' == this.regionType){
					flavor = data.cpunumber + ' CPU  * ' + data.memory /1024 + 'G';
					nics = data.nic ? (data.nic.length + ' 个') : '--';
					'reboot' == value && (data.state = 'Restarting');
				}
				if( 'os' == this.regionType){
					let flavors = data.flavor;
					flavor = flavors ? (flavors.vcpus + '核 | ' + flavors.ram /1024 + 'G') :'--';
					//nics = data.attached_volumes ? (data.attached_volumes.length + ' 个') : '--'
				}
				data.flavorDetail = flavor;
				data.nicsNum = nics;
				data.expired = timeExpired(data.expire_date);				
				data.expire_date = timeService(data.expire_date);
				data.created = data.created && data.created !='--' ? timeService(data.created) : data.created_at && data.created_at != '--' ? timeService(data.created_at) : '--';
				data.vpc = metaDate && metaDate.vpcname || '--';
				if(data.metadata && data.metadata.fake_vpc && data.metadata.fake_vpc == 1){
					if(data.metadata.vpcname == 'fake_vpc' || data.metadata.vpc_name == 'fake_vpc'){
						data.vpc = '默认网络';
					}
				}
				data.vpcId = metaDate && metaDate.vpcid || '--';
				data.ims = metaDate && metaDate.image_name || '--';
				data.system = metaDate && metaDate.ostypename || '--';
				data.stateTxt = statFormat(data.state || data.status)

				detailList.map((item)=>{
					if(item.name == 'diskNum'){
						return
					}
					if('os'==this.regionType && item.name == 'nicsNum'){
						return
					}
					item.intro = data[item.name]
				})
				if(data.state == 'Running'){
					this.btnFun.map(function(item){
						if(item.state == 'RunState'){
							item.disabled = true
						}else{
							item.disabled = false
						}
					});
				}else if(data.state == 'Starting'){
					this.btnFun.map(function(item){
						item.disabled = false
					});
				}else{
					this.btnFun.map(function(item){
						if(item.state == 'ShutState'){
							item.disabled = true
						}else{
							item.disabled = false
						}
					});
				}
			},
			getebsDetailById(id){
				let detailList = this.detailList;
				GetEbsDetailById(id).then((res)=>{
					let volumes = res.data.results;
					let count = res.data.total_count;
					detailList.map((item)=>{
						if(item.name == 'diskNum'){
							item.intro = count +' 个'
						}
					})
					this.serverData.volumeDetail = [];
					this.serverData.volumeDetail = volumes
				}).catch((req)=>{
					console.log(req)
				})
			},
			getNicsDetailById(id){
				GetNetworkById(id).then(res=>{
					let data = res.data.results;
					let count = res.data.total_count;
					this.serverData.nics = data;
					this.detailList.map(item=>{
						if(item.name == 'nicsNum'){
							item.intro = count;
						}
					})
				}).catch(error=>{
					console.log(error)
				})
			},
			switchTab(tab,index){
				console.log(tab)
				if(index){
					this.detailMore()
					if(this.loadAfter && this.$refs[tab.name]){
						let el = this.$refs[tab.name][0].$el.offsetTop;
						window.scrollTo(0,el);
					}else{
						let that = this
						setTimeout(function(){
							let el = that.$refs[tab.name][0].$el.offsetTop;
							window.scrollTo(0,el);
						},1000)
					}
				}else{
					let el = this.$refs[tab.name][0].$el.offsetTop;
					window.scrollTo(0,el);
				}
			},
			dropDownLoad(){
				this.detailMore()
				if(this.loadAfter){
					this.tabActive()
				}
			},
			hanScroll(){
				//console.log('dd')
				//console.log(HandScroll())
				if(HandScroll()){
					this.detailMore()
				}
				if(this.loadAfter){
					this.tabActive()
				}

			},
			tabActive(){
//				let scrollTop = getScrollTop();
				let scrollTop = document.documentElement.scrollTop || window.pageYOffset || document.body.scrollTop;
				let scrollHeight = getScrollHeight();
				let windowHeight=getWindowHeight();
				let index;
				if(scrollHeight==windowHeight){
		          index=0
		        }else{
		          let m = (scrollHeight-windowHeight) / this.tabOption.length;
		
		          index=  Math.floor(scrollTop / m);
		          if(index >= this.tabOption.length){
		            index = this.tabOption.length -1
		          }
		        }
				this.$refs.detaiPage.switchActive(this.tabOption[index])
			},
			detailMore(){
				this.loadAfter = true;
			},
			autoRefresh(jobIds){
      			if(jobIds){
      				if(typeof(jobIds)=='string'){

      					this.getJobDetail(jobIds)

	      			}else if(Array.isArray(jobIds)){
	      				jobIds.map(item =>{
	      					console.log(item)
	      					item.jobid && this.getJobDetail(item.jobid)
	      				})
	      			}else{
	      				console.log(jobIds)
	      			}
      			}
      		},
      		getJobDetail(id){
      			let _this = this;
      			if(this.detroyAutoRefresh) return
      			GetJobDetail(id).then(res=>{
  					let result = res.data.results[0];
  					if(result.jobstatus == 1){
  						let server = result.jobresult.virtualmachine;
  						this.serverData.servers.state = server.state;
  						this.filterServer(this.serverData.servers)
  						this.$message({
  							type:'success',
  							message:'操作成功！'
  						})
  						console.log(server)
  					}else if(result.jobstatus == 2){
  						clearTimeout(timer)
  						console.log('失败')
  						this.getEcmDetail(this.serverId)
  					}else{
  						setTimeout(()=>{
  							_this.getJobDetail(result.jobid)
  						},5000)
  					}

  				}).catch(error=>{
  					console.log(error)
  					this.getEcmDetail(this.serverId)
  				})
      		},
		},
		computed:{
			btnFun2(){
				if(this.serverData.servers.expired){
					if(GetVnc() == 1){
					}else{
						this.btnFun.splice(3,1)
					}
					this.btnFun.map(function(item){
						item.disabled = true
					});
					return  this.btnFun;
				}else{
					if(GetVnc() == 1){
						return  this.btnFun;
					}else{
						this.btnFun.splice(3,1)
						return this.btnFun;
					}
				}
			},
			regionType(){
				let regionType = this.$store.getters.regionType;
				if('os'== regionType){
					this.getNicsDetailById(this.$route.params.serverId)
					let sec = {
							label:'安全组',
							name:'ecmDetailSec',
							view:'',
						}
					this.tabOption.splice(2,0,sec)
				} 
				return regionType;
			}
		},
		data (){
			return {
				detroyAutoRefresh:false,
				detailImg:detailImg,
				detailLabel:'',
				diskCount:0,
				currentView:'ecmDetailVolume',
				serverId:'',
				loadAfter:false,
				serverData:{
					servers:{},
					nics:[],
					volumeDetail:'--'
				},
				btnFun:[
					{
						value:'开机',
						disabled:false,
						btnCli:'start',
						state:'ShutState'
					},{
						value:'关机',
						disabled:false,
						btnCli:'stop',
						state:'RunState'
					},{
						value:'重启',
						disabled:false,
						btnCli:'reboot',
						state:'RunState'
					},{
						value:'远程登录',
						disabled:false,
						btnCli:'loginVnc',
						state:'RunState'
					}
				],
				detailList:[
					{
						name:'name',
						label:'名称',
						intro:'--'
					},
					{
						name:'vpc',
						label:'虚拟私有云',
						intro:'--'
					},
					{
						name:'stateTxt',
						label:'状态',
						intro:'--'
					},
					{
						name:'ims',
						label:'镜像',
						intro:'--'
					},
					{
						name:'id',
						label:'ID',
						intro:'--',
						title:true
					},
					{
						name:'nicsNum',
						label:'网卡',
						intro:'0'
					},
					{
						name:'diskNum',
						label:'磁盘',
						intro: 0
					},
					{
						name:'system',
						label:'操作系统',
						intro:'--'
					},
					/*{
						name:'zonename',
						label:'可用分区',
						intro:'--'
					},*/
					{
						name:'created',
						label:'创建时间',
						intro:'--'
					},
					{
						name:'flavorDetail',
						label:'主机配置',
						intro:'--'
					},
					{
						name:'expire_date',
						label:'到期时间',
						intro:'--'
					}

				],
				tabOption:[
					{
						label:'云硬盘',
						name:'ecmDetailVolume',
						view:''
					},
					{
						label:'网卡',
						name:'ecmDetailNic',
						view:''
					},
					{
						label:'弹性公网IP',
						name:'ecmDetailEip',
						view:''
					}
					
				]


			}
		},
		created(){
			this.getEcmDetail(this.$route.params.serverId)
			this.getebsDetailById(this.$route.params.serverId)
			this.serverId = this.$route.params.serverId
//			window.addEventListener('scroll',this.hanScroll)
			//判断是否有滚动条执行下来加载
	    	if(document.addEventListener){
	        	document.addEventListener('DOMMouseScroll',this.hanScroll,false);
	      	}
	      	window.onmousewheel = document.onmousewheel=this.hanScroll;

		},
		destroyed(){
			this.detroyAutoRefresh=true;
		    if(document.removeEventListener){
		        document.removeEventListener('DOMMouseScroll',this.hanScroll,false);
		    }
		    window.onmousewheel = document.onmousewheel=false;
//			window.removeEventListener('scroll', this.hanScroll);
		}


	}
</script>

<style scoped="scoped" lang="scss">
	.detail-main{
		padding-bottom:40px;
		background: #FFFFFF;
		.detail-more-btn{
			text-align: center;
		}
	}
</style>



