
import Layout from '@/views/Layout'
import Order from '@/views/cost/order'
import OrderInfo from '@/views/cost/orderInfo'
import Bill from '@/views/cost/bill'
import BillInfo from '@/views/cost/billInfo'
import Renew from '@/views/cost/renew'
import EipRenew from '@/views/cost/IPRenew'

let Router ={
	path:'/cost',
	name:'cost',
	component: Layout,
	redirect: '/cost/order',
	children:[
		{
      		path: 'order',
      		name: 'order',
      		component: Order,
      	},{
      		path: 'bill',
      		name: 'bill',
      		component: Bill,
      	},
      	{
      		path: 'billInfo',
      		name: 'billInfo',
      		component: BillInfo,
      	},
      	{
      		path: 'renew',
      		name: 'renew',
      		component: Renew,
      	},
      	{
      		path: 'EipRenew',
      		name: 'EipRenew',
      		component: EipRenew,
      	},
            {
                  path: 'orderInfo/:orderId',
                  name: 'orderInfo',
                  component: OrderInfo,  
            }
	]
};
export default Router;


