<template>
	<div class="box main">
		<ConsoleDetail
			 :detail_label = 'detail_label'
			  :detail_image='detail_image' 
			  :detailList = 'detailList' 
			  :btnMsg='btnMsg'
			  :changeName='changeEbsName'
			  @deatilBtnClick = 'deatilBtnClick'></ConsoleDetail>
		
		<detail-tab :tabOption='tabOption' @click="swtichTab">
			<div :slot="currentView">
				<component :is='currentView' @AttachRefresh = 'refresh' :ebsType='ebsType' :expired='expired' :ebsDetailData='ebsDetailData' :attachShow='attachShow' :serverId = 'serverId' :ecmId = 'ecmId' :ecmDataList='ecmDataList'></component>
			</div>
		</detail-tab>
	</div>
</template>

<script>
	import i18n from '@/lang'
	import { attachPoint } from '@/baseConfig/consoleList'
	import ConsoleTable from '@/components/ConsoleTable'
	import ConsoleDetail from '@/components/ConsoleDetail'
	import DetailTab from '@/components/DetailTab'
	
	import ebsDetailAttach from './ebsDetailAttach'
	import ebsDetailBackup from './ebsDetailBackup'
	import ebsDetailLog from './ebsDetailLog'
	import ecmRenew from '@/views/ecm/ecmRenew'
	import ConsoleAction from '@/components/ConsoleAction'
	
	import { getRegionId } from '@/utils/auth'
	import { timeService,timeExpired } from '@/utils/timeformat'
	import { statFormat,diskType } from '@/utils/statformat'
	import { GetEbsDetail,renewEbs,changeEbsName,getEbsList } from '@/api/ebs'
	import { nameRule } from '@/utils/validator';
	import { GetEcmDetail } from '@/api/ecm'
	import { GetJobDetail } from '@/api/console'
	export default{
		components:{
			ConsoleTable:ConsoleTable,
			ConsoleDetail:ConsoleDetail,
			DetailTab:DetailTab,
			ebsDetailAttach:ebsDetailAttach,
			ebsDetailBackup:ebsDetailBackup,
			ebsDetailLog:ebsDetailLog,
			ecmRenew:ecmRenew,
		},
		data(){
			return {
				checkBox:true,
				selectOrNot:true,
				operationOrNot:true,
				attachPoint,
				attachShow:true,
				ebsType:false,
				expired:false,
				detail_label:'云硬盘详情',
				detail_image:require('../../assets/ecm/ebsDetail.png'),
				currentView:'ebsDetailAttach',
				serverId:'',
				ecmId:'',
				ebsDetailData:[],
				ecmDataList:[],
		        detailList:[
		        {
		        	name:'name',
					label:i18n.common.name,
					intro:'--',
					change:true
		        },
//		        {
//		        	name:'sharedStorage_or_not',
//					label:i18n.ebs.sharedStorage_or_not,
//					intro:'--'
//		        },
		        {
		        	name:'status',
					label:i18n.common.status,
					intro:'--'
		        },
		        {
		        	name:'ims',
					label:i18n.common.ims,
					intro:'--'
		        },
		        {
		        	name:'diskId',
					label:i18n.ebs.diskId,
					intro:'--'
		        },
//		        {
//		        	name:'deviceidNum',
//					label:i18n.ebs.deviceidNum,
//					intro:'--'
//		        },
		        {
		        	name:'ecmId',
					label:i18n.ebs.ecmId,
					intro:'--'
		        },
//		        {
//		        	name:'chargeType',
//					label:i18n.ebs.chargeType,
//					intro:'--'
//		        },
		        {
		        	name:'diskCapacityGB',
					label:i18n.ebs.diskCapacityGB,
					intro:'--'
		        },
//		        {
//		        	name:'orderDetails',
//					label:i18n.ebs.orderDetails,
//					intro:'--'
//		        },
		        {
		        	name:'storageType',
					label:i18n.ebs.storageType,
					intro:'--'
		        },
		        {
		        	name:'createAt',
					label:i18n.common.createAt,
					intro:'--'
		        },
		        {
		        	name:'type',
					label:i18n.ebs.diskModel,
					intro:'--'
		        },
		        {
		        	name:'finishAt',
					label:i18n.common.finishAt,
					intro:'--'
		        },
		        ],
		        btnMsg:[{
//					value:i18n.ebs.capacityExpansion,
//					btnCli:()=>{
//      				return this.capacityExpansion();
//      			}
//				},{
					value:i18n.common.renew,
					disabled:false,
					title:'',
					btnCli:()=>{
        				return this.renew();
        			}
//				},{
//					value:i18n.common.refresh,
//					btnCli:()=>{
//      				return this.refresh();
//      			}
				}
				],
				tabOption:[
					{
						label:'挂载点',
						name:'ebsDetailAttach',
						view:''
//					},
//					{
//						label:'云硬盘快照',
//						name:'ebsDetailBackup',
//						view:''
//					},
//					{
//						label:'操作日志',
//						name:'ebsDetailLog',
//						view:''
					}
				],
			}
		},
		methods:{
//			capacityExpansion(){
//				console.log('调用扩容');
//			},
			changeEbsName(detail,val,fn){
				let _this = this;
				if(detail.intro.length > 63 || detail.intro.length < 2){
					this.$message({
			          	message: '名称长度为2-63字符',
			          	type: 'warning'
			        });
      			}else if(nameRule(detail.intro)){
      				this.$message({
			          	message: '名称不能使用中文',
			          	type: 'warning'
			        });
		      	}else{
		      		let params = {
						status:'',
						page:1,
						name:detail.intro,
						attached:'',
					}
			        getEbsList(params).then((res)=>{
	      				let servers = res.data.results;
	      				if(servers.length > 0){
	      					this.$message({
					          	message: '云硬盘名称重复，请重新输入！',
					          	type: 'warning'
					        });
	      				}else{
	      					let data={
	      						name:detail.intro,
	      						volume_uuid:_this.serverId
	      					};
	      					changeEbsName(data).then((res)=>{
	      						this.$message({
						          	message: '云硬盘名称修改成功！',
						          	type: 'success'
						        });
						        fn();
	      						_this.refresh();
	      					}).catch((error)=>{
			      				console.log(error);
			      				this.$message({
						          	message: '云硬盘名称修改失败，请稍后重试！',
						          	type: 'warning'
						        });
			      			})
	      				}
	      			}).catch((error)=>{
	      				console.log(error);
	      				this.$message({
				          	message: '云硬盘名称查重失败，请稍后重试！',
				          	type: 'warning'
				        });
	      			})
		      	}
			},
			renew(){
				console.log('调用续订');
				console.log(this.ebsDetailData);
				if(this.ebsDetailData.with_vm){
					this.$message({
			          	message: '该云硬盘与云服务器一起购买,不支持单独续订',
			          	type: 'success'
			        });
			        return
				}
				let renewActionOptions = [{
        			prop:'name',
        			label:i18n.common.name,
        			temp:true,
        			detail:true,
        		},{
        			prop:'diskCapacityGBs',
        			label:i18n.ebs.capacityGB,
        		},{
        			prop:'storageType',
        			label:i18n.ebs.type,
        		},{
        			prop:'created',
        			label:i18n.common.createAt,
        		},{
        			prop:'status',
        			label:i18n.common.status,
        			temp:true,
        			status:true
        		}];
				let option = {
      				'data':{
      					'option':renewActionOptions,
      					'title':'续订磁盘',
      					'actionTip':['以下云硬盘将进行续订操作，续订将会产生续订订单，请于一日内支付订单，否则续订操作失败数据将被清空。'],
      					'data':[[this.ebsDetailData]],
      				},
      				'view':ecmRenew,
      				'form':{}
      			};
      			this.$alertDialog(option).then((res)=>{
  					res.id = [this.ebsDetailData.id];
      				let data = res;
      				renewEbs(data).then((res)=>{
	      				console.log(res)
	      				this.$message({
				          	message: '命令下发成功',
				          	type: 'success'
				        });
				        setTimeout(()=>{
  							this.refresh();
  						},3000)
	      			}).catch((error)=>{
	      				console.log(error);
	      				this.$message({
				          	message: '命令下发失败',
				          	type: 'error'
				        });
	      			})
  				})
			},
			refresh(val){
      			this.getEbsDetail(this.$route.params.serverId,val);
      		},
			attach(){
      			console.log('调用挂载');
      		},
      		detach(){
      			console.log('调用卸载');
      		},
			deatilBtnClick(funName){
				funName()
			},
			handleClick(tab, event) {
        		console.log(tab, event);
      		},
      		handleSelectionChange(val) {
        		this.multipleSelection = val;
      		},
      		swtichTab(tab){
				this.currentView = tab.name
			},
      		getEbsDetail(id,val){
				let detailList = this.detailList;
				GetEbsDetail(id).then((res)=>{
					let data = res.data.results[0];
					this.ecmId = data.virtualmachineid ? data.virtualmachineid : (data.attachments && data.attachments.length) ? data.attachments[0].server_id : '';;
					if(val == 'attach' && !this.ecmId){
						setTimeout(()=>{
  							this.refresh('attach');
  						},3000)
						return
					}else if(val == 'detach' && this.ecmId){
						setTimeout(()=>{
  							this.refresh('detach');
  						},3000)
						return
					}else{
						if(this.ecmId){
							this.getecmDetailById(this.ecmId);
						}else{
							data.ims = '--';
							this.ecmDataList = [];
						}
					}
					data.name = data.name || '--';
					data.sharedStorage_or_not = data.storagetype == 'shared' ? '是' : '否';
					data.state = this.ecmId ? 'mount' : 'uninstall';
					data.status = statFormat(data.state)
					this.attachShow = !this.ecmId;
					data.diskId = data.id;
					this.ebsType = data.type == 'DATADISK' ? false : true;
					data.type = data.type == 'DATADISK' ? '数据盘' : '系统盘';
//					data.deviceidNum = data.deviceid ? data.deviceid + '个' : '0个';
					data.ecmId = this.ecmId || '--';
					data.chargeType = data.chargeType || '--';
					let size = data.size;
					if(this.regionType == 'cs'){
						size = data.size/1024/1024/1024;
					}
					data.diskCapacityGB = size ? size + 'GB' : '--';
					data.diskCapacityGBs = size || '--';
					data.orderDetails = data.orderDetails || '--';
					data.storageType = data.disk_types ? diskType(data.disk_types) : data.volume_type ? diskType(data.volume_type) : '--';
					data.createAt = data.created ? timeService(data.created) : data.created_at ? timeService(data.created_at) : '--';
					data.diskModel = data.diskModel || '--';
					data.finishAt = data.expire_date ? timeService(data.expire_date) : '--';
					this.expired = timeExpired(data.finishAt);
					detailList.map((item)=>{
						if(item.name == 'ims'){
							return
						}
						item.intro = data[item.name]
					})
					this.detail_label = data.name; 
					this.ebsDetailData = data;
					if(!this.ebsDetailData.with_vm){
						this.btnMsg[0].disabled = true;
					}else{
						if(this.ebsDetailData.type == '系统盘'){
							this.btnMsg[0].title = '系统盘不可单独续订';
						}else{
							this.btnMsg[0].title = '与云主机一起购买的磁盘不可单独续订';
						}
						this.btnMsg[0].disabled = false;
					}
				}).catch((req)=>{
					console.log(req)
				})
			},
			getecmDetailById(id){
				let detailList = this.detailList;
				GetEcmDetail(id).then((res)=>{
					let server = res.data.results[0];
					if(this.regionType == 'os'){
						let ims = server.metadata && server.metadata.image_name || '--';
						server.templatedisplaytext = ims;
					}
					detailList.map((item)=>{
						if(item.name == 'ims'){
							item.intro =server.templatedisplaytext;
						}
					});
					server.jobid && this.autoRefresh(server.jobid)
					this.ecmDataList = res.data.results;					
					
				}).catch((req)=>{
					console.log(req)
				})
			},
			autoRefresh(jobIds){
      			if(jobIds){
      				if(typeof(jobIds)=='string'){

      					this.getJobDetail(jobIds)

	      			}else if(Array.isArray(jobIds)){
	      				jobIds.map(item =>{
	      					console.log(item)
	      					item.jobid && this.getJobDetail(item.jobid)
	      				})
	      			}else{
	      				console.log(jobIds)
	      			}
      			}
      		},
      		getJobDetail(id){
      			let _this = this;
      			if(this.detroyAutoRefresh) return
      			GetJobDetail(id).then(res=>{
  					let result = res.data.results[0];
  					if(result.jobstatus == 1){
  						let server = result.jobresult.virtualmachine;
  						this.$set(this.ecmDataList,state,server.state)
//						this.serverData.servers.state = server.state;
  						console.log(server)
  					}else if(result.jobstatus == 2){
  						clearTimeout(timer)
  						console.log('失败')
  						this.getEbsDetail(this.serverId)
  					}else{
  						setTimeout(()=>{
  							_this.getJobDetail(result.jobid)
  						},5000)
  					}

  				}).catch(error=>{
  					console.log(error)
  					this.getEbsDetail(this.serverId)
  				})
      		},
		},
		computed:{
			regionType(){
				return this.$store.getters.regionType;
			}
		},
		created(){
			this.getEbsDetail(this.$route.params.serverId);
			this.serverId = this.$route.params.serverId;
		},
	}
</script>

<style lang="scss" scoped="scoped">
    .main{
    	background: #f6f8fb;
    	.detail_child{
    		background: #ffffff;
    		margin-top: 20px;
    		margin-bottom: 30px;
    		
    	}
    }
</style>
<style lang="scss" >
	.detail_child{
		.el-tabs--card{
			.el-tabs__header{
				.el-tabs__nav-scroll{
					background: #f6f8fb;
				}
				.el-tabs__nav{
					border: none;
				}
				.el-tabs__item{
					height: 36px;
					padding: 0;
					width: 121px;
					text-align: center;
					line-height: 36px;
					font-size: 12px;
					color: #666666;
					background: #f0f2f7;
					border-radius: 5px;
					margin-right: 10px;
					&.is-active{
						background: #3ac0f1;
						color: #ffffff;
					}
				}	
			}
		}
	}
	.main{
		.el-table__header{
			.has-gutter{
				tr{
					background: #f0f2f7;
					th{
						background: #f0f2f7;
						color: #333333;
						font-size: 12px;
						font-weight: 600;
					}
				}
			}
		}
		tr,th{
			text-align: center;
		}
	}
</style>



