<template>
	<div  class="action-content">
		<el-row class="action-tip" v-if="!warn">
			<el-row class="action-tip-title" v-if=" typeCom=='info' ">
				{{actionTip[0]}}
			</el-row>
			<el-row class="action-tip-waring" v-if=" typeCom=='warn' ">
				<el-col :span='2' class="action-img">
					<svg-icon icon-class="warn"></svg-icon>
				</el-col>
				<el-col :span="21" class="action-tip-title">
					<p>
						{{actionTip[0]}}
					</p>
					
				</el-col>
				
			</el-row>
		</el-row>
		<el-row class="action-tip" v-if="warn" id='warnStyle'>
			<el-row class="action-tip-waring">
				<el-col class="action-img">
					<svg-icon icon-class="warn"></svg-icon>
				</el-col>
				<el-col class="action-tip-title">
					<span>
						{{actionTip[0]}}
					</span>
					
				</el-col>
			</el-row>
		</el-row>
		<el-row>
			<p class="action-tip-subtip">
						{{actionTip[1]}}
					</p>
		</el-row>
		<el-row class='CreateSnaSearch'>
			<el-col :span="24" style="">
				<ConsoleSearch :statusOnOff=true 
					:optionsValue ='optionsValue' 
					@searchEvent='search' 
					@refreshEvent='refresh' 
					@clear = 'clear'
					:btnClose = 'btnClose'
					></ConsoleSearch>
			</el-col>
		</el-row>
		<el-row class='action-table'>
			<el-col>
				<console-table :tableData='fliterData.data'
					:operationOrNot=false
					:tableOptions = 'fliterData.option'
					:noEffect=true
					:key='fliterData.date'
					:radioShow=true
					:loading='loading'
					@radioChange='radioChange'
					:Pagination = 'Pagination'
					@pagingEvent = 'pagingChange'
					:height = '280'
					></console-table>
				
			</el-col>
			
		</el-row>
		<el-row class="action-select">
			<el-form :model="snapshot" label-width='100px' size="mini" ref="actionForm" :rules='rules'>
				
				<el-form-item :label="i18n.ecm.snapshotName+'：'" prop='name'>
					<el-input  v-model.trim="snapshot.name" style="width:200px;" @change="changeForm"></el-input>	
					<el-tooltip content="不能使用中文,且长度为2-63字符" placement="right"><i class="el-icon-question"></i></el-tooltip>
				</el-form-item>
				
			</el-form>
			
		</el-row>	
	
	
	
	</div>
</template>


<script>
	import ConsoleTable from '@/components/ConsoleTable'
	import ConsoleSearch from '@/components/ConsoleSearch'
	import i18n from '@/lang'
	import { getEcmList,GetSnapshotList } from '@/api/ecm'
	import { nameRule } from '@/utils/validator'
	
	export default {
		props:{
			type:[String],
			warn:[Boolean],
			attachOperation:[Boolean],
			tableData:[Array,Object],
			actionTip:[Array],
			action:[String],
			form:[Object]
		},
		components:{
			ConsoleTable,
			ConsoleSearch
		},
		data(){
			let that = this
			let checkName = function(rule,value,callback){
//				let nameRule = /^[a-zA-Z][a-zA-Z0-9\-]*[a-zA-Z0-9]$/;
      			if(value.length > 63 || value.length < 2){
		      		callback(new Error('长度为2-63字符'));
      			}else if(nameRule(value)){
      				callback(new Error('不能使用中文'));
		      	}else{
      				let params ={
						name:value
					}
					GetSnapshotList(params).then(res=>{
						let data = res.data.results;
						if(data.length){
							callback(new Error('名字有重复，请重新输入'))
						}else{
							callback()
						}
						
					}).catch(error=>{
						console.log(error)
					})
      			}
				
			}
			return {
				i18n,
				btnClose:true,
				optionsValue:'',
				tableData1:{
					data:[],
					option:[{
						prop:'name',
						label:i18n.common.name,
						temp:true,
						linkProp:true
					},{
						prop:'status',
						label:i18n.common.status,
						temp:true,
						status:true
					},{
						prop:'expire_date',
						label:i18n.common.finishAt,
					}]
					
				},
				snapshot:{
					name:''
				},
				filter:{
					status:'',
					limit:10,
					page:1,
					keyword:'',
					id:''
				},
				Pagination:{
					'pagingData':[10,20,50],
		        	'total':0,
		        	'show':false
				},
				rules:{
					name:[
						{ required: true, message: i18n.ecm.snapshotNameTip, trigger: 'blur' },
//          			{ min: 2, max: 16, message: i18n.ecm.snapshotLengthTip, trigger: 'blur,change' },
            			{validator:checkName,trigger: 'blur'}
					]
				},
			}
		},
		methods:{
			search(filter){
	  			this.filter.status = ''
				this.filter.keyword=''
				this.filter.id=''
				if(filter.name == 'name'){
					filter.name = 'keyword'
				}
				console.log(filter)
				this.filter[filter.name] = filter.value;
				this.getEcmList()
	  		},
	  		refresh(){
	  			this.filter.keyword=''
				this.filter.id=''
	  			this.getEcmList()
	  		},
	  		clear(){
      			this.filter.keyword='';
      			this.filter.name='';
      		},
			changeForm(){
				this.form.name = this.snapshot.name;
			},
			getEcmList(){
				this.loading = true;
				getEcmList(this.filter).then((res)=>{
					let data = res.data;
					let servers = data.results;
					console.log('aaa')
					this.tableData1.data = this.filterServer(servers);
					this.Pagination.total = data.total_count;
      				if(this.Pagination.total > this.Pagination.pagingData[0]){
      					this.Pagination.show = true
      				}
					this.loading= false
				}).catch((error)=>{
					this.loading = false;
					console.log(error)
				})
			},
			filterServer(servers){
				let server =  servers.filter(item=>{
					return (item.state == 'Stopped' || item.state == 'Running')
				})
				console.log(server)
				return server
			},
			radioChange(rowData){
				this.form.rowData = rowData;
			},
			pagingChange(type,val){
      			if(type == 'num'){
      				this.filter.limit = val;
      			}else if(type == 'page'){
      				this.filter.page = val;
      			}
      			this.getEcmList();
      		},
      		validateForm(){
      			if(!this.form.rowData){
      				this.$message({
      					message:"请选择一个云服务器",
      					type:'error'
      				})
      				return new Promise((resolve,reject)=>{
      					reject()
      				})
      			}
      			return this.$refs['actionForm'].validate()
      		},
		},
		computed:{
			typeCom(){
				let type = this.type || 'warn'
				return type 
			},
			
			fliterData(){
				let date = (new Date()).getTime();
				this.$set(this.tableData1,'date',date)
				return this.tableData1
			},
			
		},
		created(){
			this.getEcmList()
		}
		
		
	}
</script>

<style lang="scss" scoped="scoped">
.action-content{
	min-width: 600px;
}
	.action-tip{
		/*margin-top: -20px;*/
		padding-bottom: 10px;
		font-size:12px;
		min-height: 30px;
		color: #666666;
		.action-tip-title{
			line-height: 30px;
		}
		.action-tip-waring{
			.action-img{
				font-size:30px;
				color:#F9CD76;
				text-align: left;
				padding-left:2px;
			}
			.action-tip-title{
				p{
					line-height: 20px;
				}
			}
		}	
		
		
	}
	.action-table{
		padding:10px 0;
	}
	#warnStyle{
		.action-tip-waring{
			.action-img{
				font-size:40px;
				text-align: center;
				padding-left:0px;
			}
			.action-tip-title{
				text-align: center;
				span{
					font-size: 18px;
				}
				i{
					font-size: 18px;
				}
				.action-tip-subtip{
					color:#999999;
				}
			}
		}
	}
	.action-tip-subtip{
		color:#F9CD76,;
		font-size:12px;
	}
	
</style>
<style lang="scss">
	.action-content{
		.CreateSnaSearch{
			.el-input__suffix{
				transform: translateX(-35px)!important;
			}	
		}
	}
</style>


