<template>
	<div>
		<div class="attach_content">
    		<ul class="ebs_operate">
    			<li class="left_title">备份</li>
    		</ul>
    	</div>
    	<ConsoleTable 
    		:operationOrNot = 'operationOrNot' 
    		:checkBox='checkBox' 
    		:selectOrNot='selectOrNot' 
    		:tableData = 'tableData3' 
    		:tableOptions = 'tableOptions' 
    		:OperationEvents='ebsAttachPointOperation' 
    		@buttonClick = 'buttonClick'></ConsoleTable>
	</div>
</template>

<script>
	import DetailBtn from '@/components/DetailBtn'
	import ConsoleTable from '@/components/ConsoleTable'
	import i18n from '@/lang'
	
	export default{
		props:{
			serverId:[String],
			ecmId:[String]
		},
		components:{
			DetailBtn,
			ConsoleTable,
		},
		data(){
			return{
				checkBox:true,
				selectOrNot:true,
				operationOrNot:true,
				tableData3: [{
		          	status: '可用',
		          	name: '王小虎',
		          	ID: '123213123123',
		          	equipment:"adsads",
		          	IPAddress:'172.18.143.236'
		        }],
				tableOptions:[{
        			prop:'name',
        			label:i18n.ebs.type,
        			width:'190px'
        		},{
        			prop:'status',
        			label:i18n.ebs.description,
        			width:'190px'
        		},{
        			prop:'ID',
        			label:i18n.ebs.operactionPeople,
        			width:'220px'
        		}],
	    		ebsAttachPointOperation:[
		        	{
        				value:i18n.ebs.monitorData,
        				fun:'monitorData'
        			}
		        ],
	    	}
		},
		methods:{
			attach(){
      			console.log('调用挂载');
      		},
      		detach(){
      			console.log('调用卸载');
      		},
			buttonClick(buttonName){
      			switch(buttonName){
					case 'monitorData':
						this.monitorData();
						break;
				}
      		},
		},
	}
</script>

<style lang="scss" scoped="scoped">
	.attach_content{
		padding: 20px 0;
		line-height: 30px;
		.left_title{
			font-size: 12px;
			padding-left: 20px;
			height: 30px;
			line-height: 30px;
		}
	}
</style>


