<template>
	<div class="box main">
		<ConsoleDetail
			 :detail_label = 'detail_label'
			  :detail_image='detail_image' 
			  :detailList = 'detailList' 
			  :btnMsg='btnMsg' :elbshow=true
			  ></ConsoleDetail>
		
		<detail-tab :tabOption='tabOption' @click="swtichTab">
			<div :slot="currentView">
				<component :is='currentView' @AttachRefresh = 'refresh'></component>
			</div>
		</detail-tab>
	</div>
</template>

<script>
	import i18n from '@/lang'
	import { attachPoint } from '@/baseConfig/consoleList'
	import ConsoleTable from '@/components/ConsoleTable'
	import ConsoleDetail from '@/components/ConsoleDetail'
	import DetailTab from '@/components/DetailTab'
	import ElbDetailMonitor from './ElbDetailMonitor'
	
	import { getRegionId,getZoneId } from '@/utils/auth'
	import { timeService } from '@/utils/timeformat'
	import { statFormat } from '@/utils/statformat'
	import { GetEbsDetail,renewEbs } from '@/api/ebs'
	import { GetElbDetail } from '@/api/elb'
	export default{
		components:{
			ConsoleTable:ConsoleTable,
			ConsoleDetail:ConsoleDetail,
			DetailTab:DetailTab,
			ElbDetailMonitor:ElbDetailMonitor,
		},
		data(){
			return {
				checkBox:true,
				selectOrNot:true,
				operationOrNot:true,
				attachPoint,
				attachShow:true,
				detail_label:'负载均衡',
				detail_image:require('../../assets/ecm/elbDetail.png'),
				currentView:'ElbDetailMonitor',
				serverId:'',
				elbId:'',
				elbDetailData:[],
				elbDataList:[],
		        detailList:[
		        {
		        	name:'name',
					label:i18n.common.name,
					intro:'--'
		        },
//		        {
//		        	name:'status',
//					label:i18n.common.status,
//					intro:'--'
//		        },
		        {
		        	name:'vpcid',
					label:i18n.elb.belongsToVpcId,
					intro:'--'
		        },
//		        {
//		        	name:'type',
//					label:i18n.elb.type,
//					intro:'--'
//		        },
		        {
		        	name:'vpcname',
					label:i18n.elb.belongsToVpcName,
					intro:'--'
		        },
		        {
		        	name:'serviceAddress',
					label:i18n.elb.serviceAddress,
					intro:'--'
		        },
		        {
		        	name:'subnet',
					label:i18n.vpc.subnet,
					intro:'--'
		        },
		        {
		        	name:'chargeMode',
					label:i18n.common.chargeMode,
					intro:'--'
		        },
		        {
		        	name:'createAt',
					label:i18n.common.createAt,
					intro:'--'
		        },
		        {
		        	name:'bw',
					label:i18n.vpc.bw,
					intro:'--'
		        }
		        ],
		        btnMsg:[],
				tabOption:[
					{
						label:'监听器',
						name:'ElbDetailMonitor',
						view:''
					}
				],
			}
		},
		methods:{
			refresh(val){
//    			this.getEbsDetail(this.$route.params.serverId,val);
      		},
      		swtichTab(tab){
				this.currentView = tab.name
			},
      		GetElbDetail(id,val){
				let detailList = this.detailList;
				GetElbDetail(id).then((res)=>{
					let data = res.data.results[0];
					data.name = data.name || '--';
//					data.status = data.virtualmachineid ? statFormat('mount') : statFormat('uninstall');
					data.createAt = data.create_date ? timeService(data.create_date) : '--';
//					data.diskModel = data.diskModel || '--';
					detailList.map((item)=>{
						if(item.name == 'ims'){
							return
						}
						item.intro = data[item.name]
					})
					this.detail_label = data.name; 
				}).catch((req)=>{
					console.log(req)
				})
			},
		},
		created(){
			this.GetElbDetail(this.$route.params.serverId);
		},
	}
</script>

<style lang="scss" scoped="scoped">
    .main{
    	background: #f6f8fb;
    	.detail_child{
    		background: #ffffff;
    		margin-top: 20px;
    		margin-bottom: 30px;
    		
    	}
    }
</style>
<style lang="scss" >
	.detail_child{
		.el-tabs--card{
			.el-tabs__header{
				.el-tabs__nav-scroll{
					background: #f6f8fb;
				}
				.el-tabs__nav{
					border: none;
				}
				.el-tabs__item{
					height: 36px;
					padding: 0;
					width: 121px;
					text-align: center;
					line-height: 36px;
					font-size: 12px;
					color: #666666;
					background: #f0f2f7;
					border-radius: 5px;
					margin-right: 10px;
					&.is-active{
						background: #3ac0f1;
						color: #ffffff;
					}
				}	
			}
		}
	}
	.main{
		.el-table__header{
			.has-gutter{
				tr{
					background: #f0f2f7;
					th{
						background: #f0f2f7;
						color: #333333;
						font-size: 12px;
						font-weight: 600;
					}
				}
			}
		}
		tr,th{
			text-align: center;
		}
	}
</style>



