<template>
	<div class="Re_detail">
		<p class="title">绑定资源详情</p>
		<ConsoleDetail
			  :show = true
			  :eipPort=eipPort
			  :detailList = 'resourcesList' ></ConsoleDetail>
			
		<el-col v-if='eipPorts' style="padding-bottom: 10px; padding-top: 10px;" :span='24'>
			<el-col :span='18'><span class="addRules">端口映射规则：</span></el-col>
			<el-col :span='6' style='text-align: right;'><span class="addRule" @click="addPort">添加规则</span></el-col>
		</el-col>
		<el-col v-if='eipPorts'>
			<ConsoleTable :checkBox=false :eipStatus=false 
			:operationOrNot =true :selectOrNot=false
			:tableData = 'PortListData' :tableOptions = 'tableOptions' 
			:OperationEvents='eipOperation' 
			@buttonClick = 'buttonClick'></ConsoleTable>
		</el-col>
		<!--<p class="title">带宽</p>
		<ConsoleDetail
			  :detailList = 'bwList' 
			  :show = true></ConsoleDetail>-->
</div>
</template>

<script>
	import ConsoleDetail from '@/components/ConsoleDetail'
	import ConsoleTable from '@/components/ConsoleTable'
	import ConsoleAction from '@/components/ConsoleAction'
	import EipBind from '@/views/vpc/EipBind'
	import i18n from '@/lang'
	import { getEipList,GetPortDetail,deletePort,portMappingEip } from '@/api/vpc'
	import { timeService,timeExpired } from '@/utils/timeformat'
	import { GetEcmDetail,GetNetworkDetail } from '@/api/ecm'
	export default{
		props:{
			eipDetailData:[Object,Array],
			eipPort:[Boolean,String],
			vpcname:[String],
		},
		components:{
			ConsoleDetail:ConsoleDetail,
			ConsoleTable:ConsoleTable,
			ConsoleAction:ConsoleAction,
			EipBind:EipBind,
		},
		data(){
			return{
				eipPorts:false,
				PortListData:[],
				subnet_id:'',
				filter:{
					satus:'',
					limit:5,
					page:1,
					ip:'',
					keyword:'',
					id:'',
					attached:'',
				},
				resourcesList:[
		        {
		        	name:'name',
					label:i18n.vpc.serverName,
					intro:'--'
		        },
		        {
		        	name:'vpc',
					label:i18n.common.vpc,
					intro:'--'
		        },
//		        {
//		        	name:'selectCorrectAZ',
//					label:i18n.common.selectCorrectAZ,
//					intro:'--'
//		        },
		        {
		        	name:'serverId',
					label:i18n.vpc.serverId,
					intro:'--'
		        },
		        {
		        	name:'networkCard',
					label:i18n.vpc.networkCard,
					intro:'--'
		        }],
		        bwList:[
		        {
		        	name:'bandwidthName',
					label:i18n.vpc.bandwidthName,
					intro:'--'
		        },
		        {
		        	name:'bindEip',
					label:i18n.vpc.bindEip,
					intro:'--'
		        },
		        {
		        	name:'bwID',
					label:i18n.vpc.bwID,
					intro:'--'
		        },
		        {
		        	name:'pricingModel',
					label:i18n.vpc.pricingModel,
					intro:'--'
		        },
		        {
		        	name:'bandwidth',
					label:i18n.vpc.bandwidth,
					intro:'--'
		        },
		        {
		        	name:'bwOrder',
					label:i18n.vpc.bwOrder,
					intro:'--'
		        }],
		        tableOptions:[{
        			prop:'publicport',
        			label:i18n.vpc.netStartPort,
        		},{
        			prop:'publicendport',
        			label:i18n.vpc.netEndPort,
        		},{
        			prop:'privateport',
        			label:i18n.vpc.privateStartPort,
        		},{
        			prop:'privateendport',
        			label:i18n.vpc.privateEndPort,
        		},{
        			prop:'protocol',
        			label:i18n.vpc.protocol,
        		},{
        			prop:'virtualmachinedisplayname',
        			label:i18n.vpc.ecmName,
        		},{
        			prop:'vmguestip',
        			label:i18n.vpc.privateIp,
        		}],
        		eipOperation:[{
        			value:i18n.bks.deleter,
        			fun:(data)=>{
        				return this.deleter(data);
        			}
        		}],
			}
		},
		methods:{
			deleter(arg){
				let option = {
      				'data':{
      					'type':'warn',
      					'option':'',
      					'title':'删除规则',
      					'actionTip':['确定要删除该规则吗？'],
      					'data':'',
      					'warn':true
      				},
      				'view':ConsoleAction,
      				'form':{},
      			};
      			console.log(arg);
      			this.$alertDialog(option).then((res)=>{
  					let portId = arg.id;
  					let _this = this;
					deletePort(portId).then((res)=>{
	      				console.log(res)
	      				this.$message({
				          	message: '规则删除成功。',
				          	type: 'success'
				        });
				        let count = 0;
			            for(var i =0;i < _this.PortListData.length;i++){
			            	if(portId == _this.PortListData[i].id){
			            		count = i;
			            		break;
			            	}
			            }
			            _this.PortListData.splice(count,1);
			            if(!_this.PortListData.length > 0){
			            	_this.$emit('ecmIp','refresh');
			            }
	      			}).catch((error)=>{
	      				console.log(error);
	      				this.$message({
				          	message: '命令下发失败',
				          	type: 'error'
				        });
	      			})
  				})
			},
			buttonClick(funName,data){
      			funName(data);
      		},
      		addPort(){
      			let tableOption=[];
      			let option = {
      				'data':{
      					'option':tableOption,
      					'title':'端口映射',
      					'actionTip':i18n.vpc.bindingTip,
      					'data':'',
      					'port':true,
      					'table':true,
      					'vpcid':this.PortListData[0].virtualmachineid,
      					'vpcName':this.PortListData[0].virtualmachinedisplayname ? this.PortListData[0].virtualmachinedisplayname : this.PortListData[0].virtualmachinename ? this.PortListData[0].virtualmachinename : '--',
      				},
      				'view':EipBind,
      				'form':{},
      			};
      			this.$alertDialog(option).then((res)=>{
  					console.log(res);
  					let dataMsg = res;
  					dataMsg.id = this.eipDetailData.id;
  					dataMsg.instance_uuid = this.PortListData[0].virtualmachineid;
  					dataMsg.subnet_uuid = this.subnet_id;
					portMappingEip(dataMsg).then((res)=>{
	      				console.log(res)
	      				this.$message({
				          	message: '命令下发成功',
				          	type: 'success'
				        });
				        this.PortListData.push(res.data.results[0].portforwardingrule);
	      			}).catch((error)=>{
	      				console.log(error);
	      				this.$message({
				          	message: '命令下发失败',
				          	type: 'error'
				        });
	      			})
				})
      		},
			GetEcmDetail(serverId,Data){
				let _this = this;
				let Detail = {};
				if(serverId){
					GetEcmDetail(serverId).then((res)=>{
						let data = res.data.results[0];
							Detail.name = data.name;
							Detail.serverId = data.id;
							Detail.vpc = Data.vpcname || '--';
							Detail.selectCorrectAZ = Data.zonename || '--';
							_this.subnet_id = data.nic[0].networkid;
							_this.$emit('ecmIp',data.nic[0].ipaddress);
						GetNetworkDetail(data.nic[0].networkid).then((res)=>{
							let nicData = res.data.results[0];
							console.log(nicData);
							let resourcesList = _this.resourcesList;
							Detail.networkCard = nicData.name;
							resourcesList.map((item)=>{
								item.intro = Detail[item.name]
							});
						}).catch((req)=>{
							console.log(req);
							this.$message({
					          	message: '命令下发失败',
					          	type: 'error'
					        });
						});
					}).catch((req)=>{
						console.log(req);
						this.$message({
				          	message: '命令下发失败',
				          	type: 'error'
				        });
					})
				}else{
					Detail.name = '--';
					Detail.serverId = '--';
					Detail.vpc = '--';
					Detail.selectCorrectAZ = '--';
					Detail.networkCard = '--';
					let resourcesList = _this.resourcesList;
					resourcesList.map((item)=>{
						item.intro = Detail[item.name]
					});
				}
				
			},
			GetPortDetail(data){
				console.log(data);
				let params = this.filter;
				params.ipaddressid = data.id;
				GetPortDetail(params).then((res)=>{
					let nicData = res.data.results;
					console.log(nicData);
					this.PortListData = nicData;
					let data = this.eipDetailData;
					data.vpc = data.vpcname || '--';
					this.GetEcmDetail(this.PortListData[0].virtualmachineid,data);
				}).catch((req)=>{
					console.log(req);
					this.$message({
			          	message: '命令下发失败',
			          	type: 'error'
			        });
				});
			}
		},
		watch:{
//			eipDetailData:function(val,oldval){
////				let resourcesList = this.resourcesList;
//				let data = this.eipDetailData;
//				let serverID = this.eipDetailData.virtualmachineid;
//				console.log(this.eipDetailData);
//				data.vpc = data.vpc_name || '--';
////				data.selectCorrectAZ = data.zonename || '--';
////				resourcesList.map((item)=>{
////					item.intro = data[item.name]
////				});
//				this.GetEcmDetail(serverID,data);
//			},
			eipPort:function(val,oldval){
				console.log(val);
				let data = this.eipDetailData;
				if(this.eipPort == '2'){
					this.eipPorts = true;
					this.GetPortDetail(data);
				}else{
					this.eipPorts = false;
					let serverID = this.eipDetailData.virtualmachineid;
					console.log(this.eipDetailData);
					data.vpc = data.vpcname || '--';
					if(serverID){
						this.GetEcmDetail(serverID,data);
					}else{
						this.resourcesList.map((item)=>{
							if(item.name == 'vpc'){
								item.intro = this.vpcname;
							}else{
								item.intro = '--';
							}
						});
					}
				}
			}
		},
	}
</script>

<style lang="scss">
	.Re_detail{
		background: #ffffff;
		.title{
			padding-left: 10px;
			line-height: 40px;
			font-size: 16px;
			border-bottom: 1px solid #f0f2f7;
		}
	}
</style>
<style lang="scss" scoped="scoped">
	.Re_detail{
		.addRules{
			font-size:16px;
		}
		.addRule{
			color: #3ac0f1;
			cursor: pointer;
			font-size: 14px;
		}
	}
</style>


