<template>
	<div class="create_title">
		<el-container>
		  	<el-main class="padding-left">
		  		<el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" label-position="left" class="demo-ruleForm" size="small">
					<el-form-item label="名称：" prop="name">
    					<el-input style="width: 180px;" v-model.trim="ruleForm.name"></el-input>
  					</el-form-item>
					<el-form-item label="磁盘：" prop="diskName">
    					<el-input style="width: 180px;" disabled='disabled' v-model="ruleForm.diskName"></el-input>
  						<el-button type="warning" @click='chooseDisk'>选择磁盘</el-button>
					</el-form-item>
  					<el-form-item label="描述" prop="desc">
					    <el-input type="textarea" v-model="ruleForm.desc"></el-input>
					</el-form-item>
				</el-form>
		  	</el-main>
		  	<el-aside width="200px">
		  		<div class="currentInfo">
		  			<h4>当前配置</h4>
			  		<div class="message">
			  			<dl>
				  			<dt>名称：</dt>
				  			<dd>{{ruleForm.name ? ruleForm.name : '--'}}</dd>
				  		</dl>
				  		<dl>
				  			<dt>磁盘：</dt>
				  			<dd>{{ruleForm.diskName ? ruleForm.diskName :'--'}}</dd>
				  		</dl>
				  		<dl>
				  			<dt>描述：</dt>
				  			<dd>{{ruleForm.desc ? ruleForm.desc :'--'}}</dd>
				  		</dl>	
			  		</div>
			  		<a class="link" @click='openHelp'>了解云硬盘快照功能</a>
		  		</div>
		  	</el-aside>
		</el-container>
	</div>
</template>

<script>
	import AttachAction from '@/views/bks/AttachAction'
	import { getName } from '@/utils/shareFunction'
	import i18n from '@/lang'
	import { CreateBks,getBksList } from '@/api/bks'
	import { listZone } from '@/api/ebs'
	export default{
		components:{
			AttachAction,
		},
		data(){
			var nameCheck = (rule, value, callback) => {
				let params = {
					status:'',
					page:1,
					name:value,
					attached:'',
				}
		        getBksList(params).then((res)=>{
      				let servers = res.data.results;
      				if(servers.length > 0){
      					callback(new Error('云硬盘快照名称重复，请重新输入！'));
      				}else{
      					callback();
      				}
      			}).catch((error)=>{
      				console.log(error);
      			})
		    };
			return {
				ebsId:'',
				name:'',
				ruleForm:{
		      		name: '',
		      		diskName: '',
		      		desc:'',
		      		ebsId:''
		      	},
				rules:{
		      		name: [
	            		{ required: true, message: '请输入磁盘名称', trigger: 'blur' },
	            		{ min: 3, max: 16, message: '长度在 3 到 16 个字符', trigger: 'blur' },
	            		{ validator: nameCheck, trigger: 'blur' }
	          		],
	          		diskName: [
	            		{ required: true, message: '请选择磁盘', trigger: 'blur' }
	          		],
	          	},
			}
		},
		methods:{
			chooseDisk(){
	      		console.log('选择磁盘');
      			let DataOption = [{
	          		prop:'name',
        			label:i18n.common.nameOrID,
        			temp:true,
        			detail:true,
	          	},{
	          		prop:'status',
        			label:i18n.common.status,
        			temp:true,
        			status:true
	          	},{
	          		prop:'size',
        			label:i18n.ebs.capacityGB,
	          	},{
	          		prop:'created',
        			label:i18n.common.createAt,
	          	}];
	          	let SelectOptions =[{
		          	value: '',
		          	label: i18n.common.allStatus
		        }, {
		          	value: 'allocated',
		          	label: i18n.ebs.allocated
		        }, {
		          	value: 'ready',
		          	label: i18n.ebs.ready
		        }];
				let option = {
      				'data':{
      					'option':DataOption,
      					'title':'选择磁盘',
      					'actionTip':'',
      					'data':'',
      					'selectOptions':SelectOptions,
      					'statusOnOff':true,
      					'optionsValue':'',
      				},
      				'view':AttachAction,
      				'form':{}
      			};
      			this.$alertDialog(option).then((res)=>{
  					console.log(res);
  					this.ruleForm.diskName = res.data.name + '(' + res.data.id +')';
  					this.ruleForm.ebsId = res.data.id;
      			})
	      	},
	      	order(){
	      		var basicInfo_valid = false;
	      		this.$refs['ruleForm'].validate((valid) => {
		          if (valid) {
		          	basicInfo_valid = true;
		          	this.$emit('orderResult',true,this.ruleForm);
		          }else{
		          	this.$emit('orderResult',false,'');
		          }
		        });
	      	},
	      	addEbs(ebsId,name){
	      		this.ruleForm.diskName = name + '(' + ebsId +')';
	      		this.ruleForm.ebsId = ebsId;
	      	},
	      	openHelp(){
	      		open("http://www.ctyun.cn/help");
	      	}
		},
		created(){
			this.ebsId = this.$route.query.ebsId;
			this.name = this.$route.query.name;
			if(this.ebsId){
				this.addEbs(this.ebsId,this.name);
			}
			this.ruleForm.name = 'bks-' + getName();
		},
	}
</script>

<style lang="scss" scoped="scoped">
	.create_title{
		/*width: 300px;*/
		padding: 0 0px 20px 0px;
		min-height: 260px;
		.padding-left{
			padding-left: 0px;
		}
		.el-main{
			padding-left: 20px;
			background-color: #fff;
			padding-right: 0px;
			margin-right: 20px;
		}
		.el-aside{
			background-color: #fff;
		}
		.currentInfo{
			background-color: #fff;
		    height: 360px;
		    padding: 10px;
		    padding-top: 18px;
		    border-radius: 3px;
		}
		.message{
			margin-top: 20px;
			dl{
				font-size: 12px;
				color: #333333;
				padding-bottom: 15px; 
				&:after{
					display: table;
					content: '';
				}
				dt{
					float: left;
					font-size: 12px;
					color: #333333;
				}
				dd{
					white-space: nowrap;
					overflow: hidden;
					text-overflow: ellipsis;
					font-size: 13px;
				}
			}
		}	
		.show_money{
			margin-top: 30px;
			.money_icon{
				color: #f9cd76;
				font-size: 17px;
				font-weight: 600;
			}
		}
		.link{
			color: #3dc7fa;
			font-size: 12px;
			margin-top: 15px;
			cursor: pointer;
		}
	}
</style>
<style lang="scss">
	.create_title{
		.el-form-item--small{
			label{
				text-align: left;
			}
		}
		.el-textarea{
			width: 180px;
			.el-textarea__inner{
				min-height: 80px!important;
			}
		}
	}
</style>


