<template>
	<div class="del_board">
		<div class="action-img">
			<svg-icon icon-class="warn"></svg-icon>
			<span class="tipContent">{{tableData.actionTip}}</span>
		</div>
		<div class="delName">{{tableData.delName}}</div>
	</div>
</template>


<script>
	export default {
		props:{
			tableData:[Object,Array],
		},
		data(){
			return {
			}
		},
		methods:{
			
		},
	}
</script>

<style lang="scss" scoped="scoped">
.del_board{
	.action-img{
		font-size:40px;
		color:#F9CD76;
		text-align: left;
		padding-left:2px;
	}
	.tipContent{
		font-size: 14px;
    	color: #666;
    	vertical-align: middle;
	}
	.delName{
		background: #f4f5f6;
    	height: 25px;
    	line-height: 25px;
    	padding: 0 10px;
    	margin-top: 10px;
	}
}
	
	
</style>


