<template>
	<div class="create-sec">
		<div class="createfirst" v-if="!success">
			<el-row class="create-title">
				<span>创建安全组</span>
			</el-row>
			<el-row class="create-form">
				<el-form :model="sec" label-width="80px" size="mini" class="secForm" ref="secForm" :rules="rules" label-position="left">
					<el-form-item label="名称 :"  class="secForm-item" prop="sec_name">
						<el-input v-model="sec.sec_name"></el-input>
					</el-form-item>
					<el-form-item label="描述 :" class="content-required">
						<el-input type="textarea" v-model="sec.desc" :rows="4" resize="none"></el-input>
					</el-form-item>
					<!--<el-row class="create-title">
						<span>增加规则</span>
					</el-row>
					<el-form-item>
						<el-radio v-model="sec.ruleType">使用默认规则</el-radio>
						<el-radio v-model="sec.ruleType">自定义规则</el-radio>
					</el-form-item>-->
				</el-form>
				
			</el-row>
			<el-row class="create-btn">
				<el-button @click="cancel" size="medium">取消</el-button>
				<el-button @click='createSec' size="medium" class="iscreate">立即创建</el-button>
			</el-row>
		</div>
		<div class="createSecond submit_title" v-if="success">
			<ConsoleSuccess 
				:system_type = "successTip.system_type"
				:return_url = "successTip.returnUrl"
				:system_name = 'successTip.system_name'
				:params = "successTip.params"
				:orderWay=true
				></ConsoleSuccess>
		</div>
	</div>
</template>

<script>
	import { CreateAcl } from '@/api/vpc'
	import ConsoleSuccess from '@/components/ConsoleSuccess'
	import {getName} from '@/utils/shareFunction';
	export default {
		components:{
			ConsoleSuccess
		},
		data(){
			return {
				successTip:{
					system_type:'安全组',
					returnUrl:'/vpc/sec/secList',
					system_name:''
				},
				success:false,
				sec:{
					ruleType:'default',
					ruleOption:[{
						label:'使用默认规则',
						value:'default'
					}],
					sec_name:'',
					desc:''
				},
				rules:{
					sec_name:[{
						required:true,
						message:'请填写安全组名称',
						trigger:'blur,change'
					}]
				}
			}
		},
		methods:{
			validateForm(){
				return this.$refs.secForm.validate()
			},
			createSec(){
				this.validateForm().then(res=>{
					let params = {
						name:this.sec.sec_name,
						desc:this.sec.desc
					}
					const loading = this.$loading({
							lock: true,
					        text: '正在下发命令，请稍后……',
					        spinner: 'el-icon-loading',
					        background: 'rgba(0, 0, 0, 0.7)'
					    })	
					CreateAcl(params).then(res=>{
						loading.close()
						this.success = true
						this.successTip.system_name = this.sec.sec_name;
					}).catch(error=>{
						console.log(error)
						loading.close()
					})
				}).catch(error=>{
					console.log(error)
				})
			},
			cancel(){
				this.$router.push({path:'/vpc/sec/secList'});
			}
		},
		computed:{
			
		},
		mounted(){
			this.sec.sec_name = 'sgs-' + getName();
		}
	}
</script>

<style lang="scss" scoped="scoped">
.create-sec{
	background: #FFFFFF;
	padding:0 20px 20px;
	height: 100%;;
	.create-title{
		font-size: 14px;
	    color: #333;
	    font-family: "microsoft yahei";
	    padding: 20px 0px
	}
	.secForm{
		width:40%;
		.secForm-item{
			.el-input{
				width:48%;
				display: inline-block;
			}
		}
	}
	.chooseBtn{
		margin-left: 15px;
		border: 1px solid #f9cd76;
	    border-radius: 4px!important;
		background: #f9cd76;
		color: #ffffff;
		
	}
	.create-btn{
		.el-button{
			padding: 8px 20px;
			background: #f0f2f7;
			border: 1px solid #f0f2f7;
			color: #666666;
		}
		.iscreate{
			background: #3ac0f1;
			border-color: #3ac0f1;
			color: #FFFFFF;
		}
	}
	.ecm-detail{
		border:1px solid #dcdfe6;
		border-radius: 4px;
		padding:10px;
		font-size:12px;
	}
}
</style>
<style lang="scss">
	.secForm{
		.el-form-item__label{
			font-size:12px;
		}
	}
	
</style>


