<template>
	<div>
		<RegionList :name ='name' :buttonText ='buttonText' @create='create' @changeRegion = 'changeRegion' :regionData='regionData' type ='os'></RegionList>
		<div class="content">
			<div class="content_top">
				<ConsoleSearch 
					:optionsValue ='optionsValue' 
					@searchEvent = 'search'
					:statusOnOff=false 
					@selectChange = "selectChange"
					:PublicOperation="PublicOperation"
					@PublicOperEvent="aciton"
					:disableData='changeTableData'
					@refreshEvent = 'refresh'
					:NameOptions='NameOptions'
					></ConsoleSearch>
			</div>
			<div>
				<ConsoleTable 
					:tableData='keyPairList'
					:tableOptions="tableOptions"
					:checkBox=true
					:operationOrNot=true 
					:OperationEvents='PublicOperation'
					@buttonClick="handleClick"
					@changeTable="changeTable"
					:loading = 'loading'
					:dragable=true
					:Pagination = 'Pagination'
					@pagingEvent = 'pagingChange'
					></ConsoleTable>
			</div>
			
		</div>
	</div>
</template>

<script>
	import service from '@/utils/request'
	import {SaveTextAs} from '@/utils/saveFile'
	import RegionList from '@/components/RegionList'
	import ConsoleSearch from '@/components/ConsoleSearch'
	import ConsoleTable from '@/components/ConsoleTable'
	
	import i18n from '@/lang'
	import { getEcmOsKeypairs, CreateOsKeypairs, DeleteKeypairs, GetRegionNum } from '@/api/ecm'
	import { getRegionId} from '@/utils/auth'
	
	import createOnlyName from './createOnlyName'
	import importKey from './importKey'
	export default {
		components:{
			RegionList,
			ConsoleSearch,
			ConsoleTable
		},
		data (){
			return {
				name :'密钥对',
				loading:false,
				regionData:[],
				buttonText:[{
					label:'创建秘钥对',
					id:'createKey',
					fun:()=>{
						return this.createKey()
					}
				},{
					label:'导入秘钥对',
					id:'importKey',
					fun:()=>{
						return this.importKey()
					}
				}],
				optionsValue:'',
				NameOptions:[{
		          	value: 'name',
		          	label: i18n.common.name,
		        }],
				PublicOperation:[
        			{
        				value:i18n.common.delete,
        				fun:(data)=>{
        					this.deleteKey(data)
        				}
        			}
        		],
				changeTableData:[],
				filter:{
					page:1,
					limit:10,
					id:'',
					name:''
				},
				tableOptions:[{
        			prop:'name',
        			label:i18n.common.name,
        			temp:true,
					linkProp:true,
        		},{
        			prop:'fingerprint',
        			label:"指纹",
        		}],
				keyPairList:[],
				Pagination:{
					'pagingData':[10,20,50],
		        	'total':0,
		        	'show':false
				},
			}
		},
		methods:{
			changeRegion(){
				service.cancle('CancelToken');
				this.getEcmKeypairList()
				this.getRegionNums()
			},
			refresh(){
				this.filter.name = ''
				this.filter.id = ''
				this.getEcmKeypairList()
				this.getRegionNums()
			},
			createKey(){
				let option = {
					data:{
						title:"创建密钥对",
						path:'KeyPair',
						data:this.keyPairList,
					},
					form:{},
					view:createOnlyName,
				}
				this.$alertDialog(option).then(form=>{
					const loading = this.$loading({
						lock: true,
				        text: '正在下发命令，请稍后……',
				        spinner: 'el-icon-loading',
				        background: 'rgba(0, 0, 0, 0.7)'
				    })	
					CreateOsKeypairs(form).then(res=>{
						let data = res.data.results[0];
						let private_key = data.private_key;
						loading.close()
						if(SaveTextAs(private_key,form.name +'.pem',"ISO-8859-1")){
							let options = {
								data:{
									title:'提示',
									type:'warn',
									actionTip:['创建密钥对成功，请确认密钥已成功下载到本地。为保证云主机安全，私钥只能下载一次，请妥善保管']
								},
								showCancelButton:false
							}
							this.$alertDialog(options).then(res=>{
								console.log(res)
								this.refresh()
							})
							
						}
					}).catch(error=>{
						loading.close()
						console.log(error)
					})
				})
			},
			importKey(){
				let option = {
					data:{
						title:'导入密钥对',
						data:this.keyPairList,
						path:'KeyPair'
					},
					view:importKey,
					form:{
						name:'',
						public_key:''
					},
				}
				this.$alertDialog(option).then(form=>{
					const loading = this.$loading({
						lock: true,
				        text: '正在下发命令，请稍后……',
				        spinner: 'el-icon-loading',
				        background: 'rgba(0, 0, 0, 0.7)'
				    })				
					
					CreateOsKeypairs(form).then(res=>{
						loading.close()
						this.$message({
							type:'sucess',
							message:'密钥对导入成功'
						})
						this.refresh()
					}).catch(error=>{
						loading.close()
						console.log(error)
					})
					
				})
			},
			create(fun){
				fun()
			},
			deleteKey(rowData){
				let option ={
					data:{
						title:i18n.common.delete,
						actionTip:["确认删除密钥对吗？"],
						warn:true,
					}
				}
				this.$alertDialog(option).then(form=>{
					const loading = this.$loading({
						lock: true,
				        text: '正在下发命令，请稍后……',
				        spinner: 'el-icon-loading',
				        background: 'rgba(0, 0, 0, 0.7)'
				    })
					DeleteKeypairs(rowData.name).then(res=>{
						loading.close()
						this.$message({
							message:'密钥对删除成功！',
							type:'success'
						});
						this.refresh()
					}).catch(error=>{
						loading.close()
						console.log(error)
					})
				})
				
			},
			search(val){
				this.filter[val.name] = val.value;
				this.getEcmKeypairList()
			},
			selectChange(){
				
			},
			aciton(value,label){
				let option ={
					data:{
						title:i18n.common.delete,
						type:'warn',
						data:[this.changeTableData,[]],
						actionTip:['确定要删除以下密钥对吗'],
						option:this.tableOptions
					}
				}
				let name = [];
				this.changeTableData.map(item=>{
					name.push(item.name)
				});
				this.$alertDialog(option).then(form=>{
					const loading = this.$loading({
						lock: true,
				        text: '正在下发命令，请稍后……',
				        spinner: 'el-icon-loading',
				        background: 'rgba(0, 0, 0, 0.7)'
				    })
					DeleteKeypairs(name).then(res=>{
						loading.close()
						this.$message({
							message:'密钥对！',
							type:'success'
						});
						this.refresh()
					}).catch(error=>{
						loading.close()
						console.log(error)
					})
				})
			},
			changeTable(val){
				this.changeTableData = val;
			},
			handleClick(funName,rowData){
      			funName(rowData)
      		},
			getEcmKeypairList(){
				this.loading = true;
				let params = this.filter;
				getEcmOsKeypairs(params).then(res=>{
					let data = res.data.results;
					let count = res.data.total_count;
					this.Pagination.total = count;
					if(count > this.Pagination.pagingData[0]){
						this.Pagination.show = true
					}
					this.keyPairList = data;
					this.loading=false
				}).catch(error=>{
					if(error.message == 'CancelToken'){
      					
      				}else{
      					this.loading=false
      				}
					console.log(error)
				})
			},
			filterKeys(key){
				let operation = [{
        			value:i18n.common.delete,
        			id:'deleteKey',
        			fun:(data)=>{
        				return this.deleteKey(data)
        			}
        		}];
        		
			},
			pagingChange(type,val){
				if(type == 'num'){
      				this.filter.limit = val;
      			}else if(type == 'page'){
      				this.filter.page = val;
      			}
      			this.loading =true;
      			this.refresh();
			},
			getRegionNums() {
				let params = {
					resource: 'keypair',
					regionid: getRegionId()
				}
				GetRegionNum(params).then(res => {
					let data = res.data.results[0].KEYPAIR;
					console.log(data)
					if(data && data.detail) {
						this.regionData = data.detail;
					}

				})
			},
		},
		mounted(){
			this.changeRegion()
		}
	}
</script>

<style scoped="scoped" lang="scss">
	.content{
	  	.content_top{
	  		background: #ffffff;
	  		padding-top: 30px;
	  		padding-left: 20px;
	  		padding-bottom: 20px;
	  	}
	}
</style>


