<template>
	<div class="importKey-content">
		<el-row class="key-words">
			<el-col class="key-type">
				导入公钥有两种方式：</br>
				1、选择文件导入，导入成功后，可以修改公钥名称。</br>
				2、复制内容到“公钥内容”文本框，手动输入公钥名称。
			</el-col>
			<el-col :span="2" class="key-desc">说明：</el-col>
			<el-col :span="21" class="key-desc">
				1、最多支持1024字符长度（包括1024字符）的公钥导入；</br>
				2、仅支持RSA类型的密钥。
			</el-col>
			
			
		</el-row>
		<el-form label-width="90px" :model="keyForm" ref="keyForm" :rules = 'rules' size="mini">
			<el-form-item label="文件 :">
				<div class="fileContent">
					<span class="file-name">{{fileDisplayname}}</span>
					<span class="file-btn">选择文件</span>
					<input type="file" @change='changeFile($event)' />
					
				</div>
				
				
			</el-form-item>
			<el-form-item label="名称 :" prop="filename">
				<el-input v-model="keyForm.filename"></el-input>
			</el-form-item>
			<el-form-item label="公钥内容 :" prop="text">
				<el-input type="textarea" v-model="keyForm.text" class="areaWidth" :autosize="{ minRows: 2, maxRows: 6 }" resize='none' :rows="6"></el-input>
			</el-form-item>
			
		</el-form>
		
	</div>
</template>

<script>
	import {readFile} from '@/utils/readFile'
	import {getName} from '@/utils/shareFunction';
	export default {
		props:{
			form:[Object],
			tableData:[Object]
		},
		data(){
			let checkFilename = (rule,value,callback)=>{
				let hasName = this.tableData.data.find(item=>{
					return item.name == value
				})
				if(hasName){
					callback(new Error('名称重复，请重新输入'))
				}else{
					callback()
				}
			}
			let checkKey = (rules,value,callback)=>{
				let hasKey = this.tableData.data.find(item=>{
					let key = item.public_key;
					key = key.substr(0, key.length - " Generated-by-Nova\n".length);
					return key == value
				});
				if(hasKey){
					callback(new Error('密钥已存在，请重新导入'))
				}else{
					callback()
				}
			}
			return {
				keyForm:{
					filename:'',
					text:'',
					name:''
				},
				rules:{
					filename:[
						{
							required:true,trigger:'blur,change',message:'请输入密钥对名称'
						},
						{
							validator:checkFilename,
							trigger:'blur,change'
						}
					],
					text:[
						{
							required:true,trigger:'blur,change',message:'请输入密钥'
						},
						{min:1,max:1024,trigger:'blur,change',message:'长度在1到1024个字符'},
						{
							validator:checkKey,
							trigger:'blur,change'
						}
					]
				}
			}
		},
		methods:{
			validateForm(){
				this.form.name = this.keyForm.filename;
				this.form.public_key = this.keyForm.text;
				return this.$refs['keyForm'].validate()
			},
			changeFile(event){
				//读取文件有延迟 所以使用当前方法来保证文件可以直接获取到
				this.keyForm = readFile(event)
			}
		},
		computed:{
			fileDisplayname(){
				return this.keyForm.name || '未选择任何文件'
			}
		},
		mounted(){
			if(this.tableData.path){
				this.keyForm.filename = this.tableData.path + '-' + getName();
			}
		}
		
	}
</script>

<style lang="scss" scoped="scoped">
	.importKey-content{
		max-width:450px;
		padding:10px 20px;
	}
	.key-words{
		font-size:12px;
		color:#666666;
		line-height: 18px;
		padding-bottom: 15px;
		.key-type{
		
		}
		.key-desc{
			color:#F9CD76;
		}
	}
	.fileContent{
		position: relative;
		width:200px;
		height:30px;
		line-height: 30px;
		overflow: hidden;
		input{
			position: absolute;
			font-size:100%;
			top:0;
			right:0;
			opacity: 0;
			height: 100%;
			cursor: pointer;
		}
		span{
			display: inline-block;
			font-size:12px;
		}
		.file-name{
			width:100px;
			overflow: hidden;
			white-space: nowrap;
			text-overflow: ellipsis;
		}
		.file-btn{
			width:80px;
			height:30px;
			border-radius: 4px;
			background: #f9cd76;
			text-align: center;
			color:#FFFFFF;
			float: right;
		}
	}
</style>
<style lang="scss">
	.importKey-content{
		.areaWidth{
			.el-textarea__inner{
				min-width:350px;
				/*min-height: 80px !important;*/
			}
		}
		.el-form-item__label{
			font-size:12px;
		}
	}
	
	
</style>


