<template>
	<div class="action-ims">
		<el-row class="action-tip bg">
			<el-row class="action-tip-title" v-html="actionTip[0]">
				
			</el-row>
			
		</el-row>
		<el-row>
				<console-table :tableData='filterData'
					:loading = loading
					:operationOrNot=false
					:tableOptions = 'option'
					:noEffect=true
					:radioShow=true
					@radioChange='radioChange'
					:Pagination = 'Pagination'
					@pagingEvent = 'pagingChange'
					></console-table>
				
				<p class="unOperationTip" v-if="unOperationData.length > 0">
					以下云服务器不可{{actionTip[2]}},
					<span v-if='!unOperationShow' @click="OpenData">点击查看</span>
					<span v-if='unOperationShow' @click="OpenData">点击关闭</span>
				</p>
				<console-table v-if='unOperationShow' :tableData='unOperationData' class='unOperationTable'
				:operationOrNot=false
				:tableOptions = 'option'
				:noEffect=true
				></console-table>
			</el-row>
	</div>
</template>

<script>
	import ConsoleTable from '@/components/ConsoleTable'
	import {getEcmList} from '@/api/ecm'
	export default {
		components:{
			ConsoleTable
		},
		props:{
			actionTip:[Array],
			form:[Object]
		},
		data(){
			return {
				loading:false,
				ecmListData:[],
				filter:{
					status:'',
					limit:10,
					page:1,
					keyword:'',
					id:''
				},
				Pagination:{
					'pagingData':[10,20,50],
		        	'total':0,
		        	'show':false
				},
				option:[{
					prop:'name',
					label:'名称',
					temp:true,
					linkProp:true
				},{
					prop:'stateTxt',
					label:'状态',
					temp:true,
					status:true
					
				},{
					prop:'templatedisplaytext',
					label:'镜像'
				},{
					prop:'privateIp',
					label:'私有IP地址'
				}],
				unOperationShow:false
			}
		},
		methods:{
			OpenData(){
				this.unOperationShow = !this.unOperationShow;
			},
			radioChange(rowData){
				console.log(rowData)
				/*if(rowData.status){
				//if(rowData.status.toLowerCase() == 'active'){
					let options = {
						data:{
							title:'关机',
							type:'warn',
							actionTip:['创建镜像需关闭云主机，确认关机单机确定，否则单击取消']
						}
						//showCancelButton:false
					}
					this.$alertDialog(options).then(res=>{
						console.log(res)
						//this.refresh()
					})
				}else{
				}*/
				this.form.rowData = rowData;
				
			},
			filterServers(servers){
				
				servers.map(item=>{
					let nics = item.addresses[item.metadata.vpcname]
					let addr = _.find(nics,{'OS-EXT-IPS:type':'fixed'})
	      			item.privateIp = addr ? addr.addr : '--'
	      			item.templatedisplaytext = item.metadata && item.metadata.image_name || '--';
				})
			},
			getEcmList(){
				let params = this.filter;
      			this.loading =true;
      			getEcmList(params).then((res)=>{
      				let servers = Array.from(res.data.results);
      				let totalCount = res.data.total_count;
      				this.filterServers(servers)
      				this.ecmListData = servers;
      				this.Pagination.total = this.totalCount;
      				if(totalCount>this.Pagination.pagingData[0]){
      					this.Pagination.show = true
      				}
      				this.loading=false
      			}).catch((error)=>{
      				if(error.message == 'CancelToken'){
      					
      				}else{
      					this.loading=false
      				}
      				console.log(error)
      			})
			},
			pagingChange(type,val){
      			if(type == 'num'){
      				this.filter.limit = val;
      			}else if(type == 'page'){
      				this.filter.page = val;
      			}
      			this.loading =true;
      			this.getEcmList();
      		},
      		validateForm(){
      			return new Promise((resolve,reject)=>{
      				if(!this.form.rowData){
		      			this.$message({
		      				message:"请选择一台云服务器",
		      				type:'error'
		      			})
      					reject()
      				}else{
      					resolve(this.form)
      				}
      			})
      		}
		},
		computed:{
			filterData(){
				
				return this.ecmListData.filter(item=>{
					return item.status.toLowerCase() == 'shutoff'
				})
			},
			unOperationData(){
				return this.ecmListData.filter(item=>{
					return item.status.toLowerCase() !== 'shutoff'
				})
			}
		},
		mounted(){
			this.getEcmList()
		}
	}
</script>

<style lang="scss" scoped="scoped">
	.action-ims{
		max-width:500px;
	}
	.action-tip{
		/*margin-top: -20px;*/
		padding-bottom: 10px;
		font-size:12px;
		min-height: 30px;
		color: #666666;
		.action-tip-title{
			line-height: 20px;
		}
		
		.action-tip-waring{
			.action-img{
				font-size:30px;
				color:#F9CD76;
				text-align: left;
				padding-left:2px;
			}
			.action-tip-title{
				p{
					line-height: 20px;
				}
				.action-tip-subtip{
					color:#999999;
				}
			}
		}	
		
		
	}
	.bg{
		background: #fff9ee;
		padding:10px;
		border:1px solid #F9CD76;
		.action-danger{
			color:#f6585e;
		}
	}
	.unOperationTip{
		font-size: 12px;
		padding: 5px;
		span{
			color: #409EFF;
			cursor: pointer;
		}
	}
</style>


