<template>
	<div :loading='loading'>
		<el-row class='action-table ebsAttach'>
			<el-col :span="24" style="margin-bottom: 20px;">
				<ConsoleSearch :statusOnOff='tableData.statusOnOff' 
					:options ='tableData.selectOptions' 
					:optionsValue ='tableData.optionsValue' 
					:PublicOperation='tableData.ebsPublicOperation' 
					@searchEvent='search' 
					@refreshEvent='refresh' 
					@clear = 'clear'
					:disableData='tableData.changeTableData'
					@selectChange = "selectChange"
					:btnClose = 'btnClose'
					></ConsoleSearch>
			</el-col>
		</el-row>
		<el-row class='action-table ebsAttach'>
			<el-col>
				<console-table :tableData='ecmListData'
				:operationOrNot=false
				:tableOptions = 'fliterData.option'
				:noEffect=true
				:radioShow=true
				:height='360'
				@radioChange = 'radioChange'
				:Pagination = 'Pagination' @pagingEvent = 'pagingChange'
				></console-table>
			</el-col>
			
		</el-row>
	</div>
</template>

<script>
	import ConsoleTable from '@/components/ConsoleTable'
	import ConsoleSearch from '@/components/ConsoleSearch'
	import { statFormat } from '@/utils/statformat'
	import i18n from '@/lang'
	import { getEcmList, EcmAction } from '@/api/ecm'
	export default {
		props:{
			tableData:[Object],
			loading:[Boolean],
			form:[Object,String]
		},
		components:{
			ConsoleTable,
			ConsoleSearch
		},
		data(){
			return {
				btnClose:true,
				Pagination:{
		        	'pagingData':[10,20,50],
		        	'total':0,
		        	'show':false
		        },
				filter:{
					satus:'',
					limit:10,
					page:1,
					name:'',
					id:'',
					show_attached_volumes_number:1,
				},
				ecmListData:[]
			}
		},
		methods:{
			search(filter){
	  			console.log(filter)
	  			if(filter.name == 'id'){
      				this.filter.status = ''
					this.filter.name=''
					this.filter.id=filter.value;
	//				this.filter[filter.name] = filter.value;
					this.filter.keyword = '';
      			}else{
      				this.filter.status = ''
					this.filter.name=''
					this.filter.id=''
	//				this.filter[filter.name] = filter.value;
					this.filter.keyword = filter.value;
      			}
//				this.filter.status = ''
//				this.filter.displayname=''
//				this.filter.id=''
//				if(filter.name == 'name'){
//					filter.name = 'displayname'
//				}
//				this.filter[filter.name] = filter.value;
				this.getEcmList()
	  		},
			selectChange(value){
				this.filter.status =value;
				this.getEcmList()
			},
	  		refresh(){
	  			this.getEcmList()
	  		},
	  		clear(){
      			this.filter.keyword='';
      			this.filter.name='';
      		},
	  		changeTable(val){
				console.log(val)
				this.changeTableData = val
			},
			radioChange(val){
				console.log(val);
				this.form.data = val;
			},
			validateForm(){
				return new Promise((resolve,reject)=>{
					if(this.form.data){
						resolve(this.form);
	      			}else{
		      			this.$message({
				          	message: '请选择一台要挂载的云服务器',
				          	type: 'error'
				        });
				        reject();
					}
	      		})
			},
			getEcmList(){
	  			let params = this.filter;
	  			getEcmList(params).then((res)=>{
	  				let servers = Array.from(res.data.results);
	  				let totalCount = res.data.total_count;
	  				servers = this.filterServers(servers)
	  				this.ecmListData = servers;
	  				this.Pagination.total = totalCount;
      				if(totalCount > this.Pagination.pagingData[0]){
      					this.Pagination.show = true;
      				}
	  			}).catch((error)=>{
	  				console.log(error)
	  			})
	  		
	  		},
	  		filterServers(servers){
      			servers = servers.filter((item)=>{
      				if(item.state == 'Starting'){
      					return true;
      				}else{
      					//item.privateIp = item.nic.map
	      				//获取私有IP
	      				//let privateIp = item.nic ? item.nic.length && item.nic[0].ipaddress : item.addresses ? item.metadata && this._.find(item.addresses[item.metadata.vpcname],{'OS-EXT-IPS:type':'fixed'}).addr : '--'
	      				let privateIp = '--'
	      				item.privateIp = privateIp || '--';
	      				//item.name =item.displayname ? item.displayname : item.name;
	      				return true;
      				}
      			})
      			return servers
      		},
	      	pagingChange(type,val){
	      			if(type == 'num'){
	      				this.filter.limit = val;
	      			}else if(type == 'page'){
	      				this.filter.page = val;
	      			}
	      			this.getEcmList();
	      		}
			},
		computed:{
			fliterData(){
				return this.tableData
			}
		},
		mounted(){
			this.getEcmList(this.filter)
		}
	}
</script>

<style lang="scss">
	.ebsAttach{
		.el-input__suffix{
			transform: translateX(-35px)!important;
		}
		.el-table{
			/*max-height: 380px;
			overflow-y: auto;
			overflow-x: hidden;*/
		}
	}
</style>


