<template>
	<div class='monitor_userGroup'>
		<h4 class="header_title">{{name}}</h4>
		<div class="tab">
			<el-radio-group v-model="user_type" size="small">
			    <el-radio-button label="user">告警联系人</el-radio-button>
			    <el-radio-button label="group">告警联系组</el-radio-button>
			</el-radio-group>
		</div>
		<div class="user_content" v-show="isUser">
			<div class="">
				<el-button type="success" size="small">添加联系人</el-button>
				<el-button type="success" size="small">删除</el-button>
				<el-button icon="el-icon-refresh" size="small" class="fl_r m_l_10"></el-button>
			    <el-input placeholder="请输入内容" v-model="input5" class="input-with-select fl_r" size="small" style="width:350px">
				    <el-select v-model="select" slot="prepend" placeholder="请选择">
				      <el-option label="餐厅名" value="1"></el-option>
				      <el-option label="订单号" value="2"></el-option>
				      <el-option label="用户电话" value="3"></el-option>
				    </el-select>
				    <el-button slot="append" icon="el-icon-search"></el-button>
				</el-input>
			</div>
			<div class="user_table">
				<ConsoleTable 
					:checkBox=true
					:tableData='userListData'
					:tableOptions="userTableOptions"
					:operationOrNot=true
					:OperationEvents='userOperation'
					@buttonClick="handleClick"
					:loading = 'u_loading'
					:dragable=true
					:Pagination = 'Pagination'
					@pagingEvent = 'pagingChange'
					@changeTable="changeTable"
					></ConsoleTable>
			</div>
		</div>
		<div class="group_content" v-show="!isUser">
			<div class="">
				<el-button type="success" size="small">添加联系组</el-button>
				<el-button type="success" size="small">删除</el-button>
				<el-button icon="el-icon-refresh" size="small" class="fl_r m_l_10"></el-button>
			    <el-input placeholder="请输入内容" v-model="input5" class="input-with-select fl_r" size="small" style="width:350px">
				    <el-select v-model="select" slot="prepend" placeholder="请选择">
				      <el-option label="餐厅名" value="1"></el-option>
				      <el-option label="订单号" value="2"></el-option>
				      <el-option label="用户电话" value="3"></el-option>
				    </el-select>
				    <el-button slot="append" icon="el-icon-search"></el-button>
				</el-input>
			</div>
			<div class="group_table">
				<el-table
				    :data="tableData5"
				    style="width: 100%">
				    <el-table-column type="expand">
				      <template slot-scope="props">
				        <el-form label-position="left" inline class="demo-table-expand">
				          <el-form-item label="商品名称">
				            <span>{{ props.row.name }}</span>
				          </el-form-item>
				          <el-form-item label="所属店铺">
				            <span>{{ props.row.shop }}</span>
				          </el-form-item>
				          <el-form-item label="商品 ID">
				            <span>{{ props.row.id }}</span>
				          </el-form-item>
				          <el-form-item label="店铺 ID">
				            <span>{{ props.row.shopId }}</span>
				          </el-form-item>
				          <el-form-item label="商品分类">
				            <span>{{ props.row.category }}</span>
				          </el-form-item>
				          <el-form-item label="店铺地址">
				            <span>{{ props.row.address }}</span>
				          </el-form-item>
				          <el-form-item label="商品描述">
				            <span>{{ props.row.desc }}</span>
				          </el-form-item>
				        </el-form>
				      </template>
				    </el-table-column>
				    <el-table-column
				      type="selection"
				      width="55">
				    </el-table-column>
				    <el-table-column
				      label="组名称"
				      prop="id">
				    </el-table-column>
				    <el-table-column label="操作">
				      <template slot-scope="scope">
				        <el-button
				          size="mini"
				          type="text"
				          @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
				        <el-button
				          size="mini"
				          type="text"
				          @click="handleDelete(scope.$index, scope.row)">删除</el-button>
				      </template>
				    </el-table-column>
				  </el-table>
			</div>
		</div>
	</div>
</template>

<script>
import ConsoleTable from '@/components/ConsoleTable'
	export default {
		components:{
			ConsoleTable,
		},
		data (){
			return {
				name :'告警联系人管理',
				user_type: 'user',
				input5: '',
				select: '',
				u_loading: false,
				Pagination:{
					'pagingData':[10,20,50],
		        	'total':0,
		        	'show':false
				},
				userListData: [],
				userTableOptions:[{
        			prop:'displayname',
        			label: '姓名',
        		},{
        			prop:'status',
        			label: '手机号码',
        		},
        		{
        			prop:'templatedisplaytext',
        			label: '邮箱',
        			
        		},{
        			prop:'privateIp',
        			label: '所属报警组',
        		}],
        		userOperation: [{
        			value: '编辑',
        			id:'telnet',
        			fun:(data)=>{
        				return this.telnet(data);
        			}
        		},{
        			value: '删除',
        			id:'changeSpc',
        			fun:(data)=>{
        				return this.changeSpc(data)
        			}
        		}],
        		tableData5: [{
		          id: '12987122',
		          name: '好滋好味鸡蛋仔',
		          category: '江浙小吃、小吃零食',
		          desc: '荷兰优质淡奶，奶香浓而不腻',
		          address: '上海市普陀区真北路',
		          shop: '王小虎夫妻店',
		          shopId: '10333'
		        }, {
		          id: '12987123',
		          name: '好滋好味鸡蛋仔',
		          category: '江浙小吃、小吃零食',
		          desc: '荷兰优质淡奶，奶香浓而不腻',
		          address: '上海市普陀区真北路',
		          shop: '王小虎夫妻店',
		          shopId: '10333'
		        }, {
		          id: '12987125',
		          name: '好滋好味鸡蛋仔',
		          category: '江浙小吃、小吃零食',
		          desc: '荷兰优质淡奶，奶香浓而不腻',
		          address: '上海市普陀区真北路',
		          shop: '王小虎夫妻店',
		          shopId: '10333'
		        }, {
		          id: '12987126',
		          name: '好滋好味鸡蛋仔',
		          category: '江浙小吃、小吃零食',
		          desc: '荷兰优质淡奶，奶香浓而不腻',
		          address: '上海市普陀区真北路',
		          shop: '王小虎夫妻店',
		          shopId: '10333'
		        }]
			}
		},
		mounted(){
			
		},
		
		methods:{
			handleClick(funName,rowData){
      			funName(rowData)
      		},
      		pagingChange(type,val){
      			if(type == 'num'){
      				this.filter.limit = val;
      			}else if(type == 'page'){
      				this.filter.page = val;
      			}
      			this.getEcmList();
      		},
      		changeTable(data) {
      			this.checkData = data;
      		},
		},
		computed: {
			isUser:function(){
				return this.user_type === 'user';
			}
		},
		
	}
</script>

<style scoped lang="scss">
.monitor_userGroup{
	background: #fff;
    height: 100%;
    padding: 20px;
    .header_title{
    	font-size: 14px;
    	color: #333;
	}
	.fl_r{
		float: right;
		vertical-align: middle;
	}
	.m_l_10{
		margin-left: 10px;
	}
	.demo-table-expand {
    	font-size: 0;
  	}
  	.demo-table-expand label {
    	width: 90px;
    	color: #99a9bf;
  	}
  	.demo-table-expand .el-form-item {
    	margin-right: 0;
    	margin-bottom: 0;
    	width: 50%;
  	}
  	.tab{
  		padding: 20px 0;
  	}
  	.user_table,.group_table{
  		padding-top: 20px;
  	}
}
</style>
<style lang="scss">
.monitor_userGroup{
	.el-select .el-input {
		width: 100px;
	}
	.input-with-select .el-input-group__prepend {
		background-color: #fff;
	}
}
.group_table{
	.el-table th{
	  	padding: 6px 0;
	  	background-color: #f0f2f7;
	 }
	 .el-table td{
	  	padding: 10px 0;
	 }
}
</style>


