<template>
	<div class='monitor_rule'>
		<div class="header">
			<h4 class="header_title">{{name}}</h4>
			<el-row>
				<el-col :span="20">
					<p class="header_tip">
						<i class="el-icon-question"></i>
						<span>添加告警规则时使用原始值可以提高告警触发速度1-5倍。</span>
					</p>
				</el-col>
				<el-col :span="4" class="t_a_c">
					<el-button class="createButton" @click='create'>创建告警规则</el-button>
				</el-col>
			</el-row>
		</div>
		<div class="line"></div>
		<div class="content">
			<div class="rule_title">
				<el-button type="success" size="small">启用</el-button>
				<el-button type="success" size="small">停用</el-button>
				<el-button type="success" size="small">删除</el-button>
			    <el-button icon="el-icon-refresh" size="small" class="fl_r m_l_10"></el-button>
			    <el-input placeholder="请输入内容" v-model="input5" class="input-with-select fl_r" size="small" style="width:350px">
				    <el-select v-model="select" slot="prepend" placeholder="请选择">
				      <el-option label="餐厅名" value="1"></el-option>
				      <el-option label="订单号" value="2"></el-option>
				      <el-option label="用户电话" value="3"></el-option>
				    </el-select>
				    <el-button slot="append" icon="el-icon-search"></el-button>
				</el-input>
				<el-select v-model="value" placeholder="请选择" class="fl_r" size="small">
				    <el-option
				      v-for="item in options"
				      :key="item.value"
				      :label="item.label"
				      :value="item.value">
				    </el-option>
				  </el-select>
			</div>
			<p class="tip_num">您还可以创建7个告警规则</p>
			<div>
				<ConsoleTable 
					:checkBox=true
					:tableData='ruleListData'
					:tableOptions="tableOptions"
					:operationOrNot=true
					:OperationEvents='ruleOperation'
					@buttonClick="handleClick"
					:loading = 'loading'
					:dragable=true
					:Pagination = 'Pagination'
					@pagingEvent = 'pagingChange'
					@changeTable="changeTable"
					></ConsoleTable>
			</div>
		</div>
	</div>
</template>

<script>
import ConsoleTable from '@/components/ConsoleTable'
	export default {
		components:{
			ConsoleTable,
		},
		data (){
			return {
				name :'告警规则',
				input5: '',
				select: '',
				value: '',
				tableData5: [],
				loading: false,
				checkData: [],
				options: [{
		          value: '选项1',
		          label: '黄金糕'
		        }, {
		          value: '选项2',
		          label: '双皮奶'
		        }, {
		          value: '选项3',
		          label: '蚵仔煎'
		        }, {
		          value: '选项4',
		          label: '龙须面'
		        }, {
		          value: '选项5',
		          label: '北京烤鸭'
		        }],
		        Pagination:{
					'pagingData':[10,20,50],
		        	'total':0,
		        	'show':false
				},
		        ruleListData: [],
		        tableOptions:[{
        			prop:'displayname',
        			label: '名称',
        		},{
        			prop:'status',
        			label: '服务',
        		},
        		{
        			prop:'templatedisplaytext',
        			label: '监控对象',
        			
        		},{
        			prop:'privateIp',
        			label: '监控对象ID',
        		},{
        			prop:'privateIp',
        			label: '阀值',
        		},{
        			prop:'privateIp',
        			label: '状态',
        		},{
        			prop:'privateIp',
        			label: '最近状态改变时间',
        		}],
        		ruleOperation: [{
        			value: '停用',
        			id:'telnet',
        			fun:(data)=>{
        				return this.telnet(data);
        			}
        		},{
        			value: '修改',
        			id:'changeSpc',
        			fun:(data)=>{
        				return this.changeSpc(data)
        			}
        		},{
        			value: '删除',
        			id:'changeSpc',
        			fun:(data)=>{
        				return this.changeSpc(data)
        			}
        		}]
			}
		},
		mounted(){
			
		},
		methods:{
			create(){
				this.$router.push('/monitor/createTemplate');
			},
			handleClick(funName,rowData){
      			funName(rowData)
      		},
      		pagingChange(type,val){
      			if(type == 'num'){
      				this.filter.limit = val;
      			}else if(type == 'page'){
      				this.filter.page = val;
      			}
      			this.getEcmList();
      		},
      		changeTable(data) {
      			this.checkData = data;
      		},
		},
		computed: {
			
		},
		
	}
</script>

<style scoped lang="scss">
.monitor_rule{
	.header{
		background: #fff;
		padding: 20px;
		border-radius: 5px;
	}
	.header_title{
		padding-bottom: 20px;
    	font-size: 14px;
    	color: #333;
	}
	.header_tip{
		background: #fbf9f2;
    	color: #f5b840;
    	height: 36px;
    	line-height: 36px;
    	font-size: 14px;
    	padding-left: 10px;
	}
	.t_a_c{
		text-align: center;
	}
	.createButton{
		width: 146px;
		height: 36px;
		background: #3ac0f1;
		border-color: #3ac0f1;
		color: #ffffff;
		font-size: 12px;
		&:hover{
			background: #5ed4ff;
		}
	}
	.line{
		height: 10px;
		width: 100%;
		background: #f0f2f7;
		background-size: 50% 100%;
		background-repeat: no-repeat;
	}
	.content{
		background: #fff;
		padding: 20px;
		border-radius: 5px;
	}
	.template_rule{

	}
	.fl_r{
		float: right;
		vertical-align: middle;
	}
	.m_l_10{
		margin-left: 10px;
	}
	.p_t_20{
		padding-top: 20px;
	}
	.p_b_20{
		padding-bottom: 20px;
	}
	.tip_num{
		padding: 20px 0;
    	font-size: 12px;
    	color: #666;
	}
}
</style>
<style lang="scss">
.monitor_rule{
	.el-select .el-input {
		width: 100px;
	}
	.input-with-select .el-input-group__prepend {
		background-color: #fff;
	}
}
</style>


