<template>
    <div id="drag">
        <div class="drag_bg weui-btn_primary" :style="{width:curW+'px'}"></div>
        <div class="drag_text" onselectstart="return false;" unselectable="on":class="[isDragOk ? 'whitecolor':'']">{{text}}</div>
        <div class="handler" :style="{left:curW+'px'}" :class="[isDragOk ? 'handler_ok_bg':'handler_bg']"></div>
    </div>
</template>

<script>
export default{
    name:'slider',
    props:{
        isDragOk: {
            type: Boolean,
            default: false //是否拖动成功
        }
    },
    data(){
        return{
            curW:0,
            isMove:false, //是否在运动
            //isDragOk:false, //是否拖动成功
            maxWidth:0, //拖动的最大宽度
            element:{},
            text:'拖动滑块验证',
            currentPos:{
                x: 0,
                y: 0
            }
        }
    },
    created(){
        
    },
    mounted () {
        var self = this;
        this.element = document.querySelector('.handler');
        this.getMaxWidth();
        //window.addEventListener('resize',function(){self.getMaxWidth()});
        //window.addEventListener('orientationchange',function(){self.getMaxWidth()});
        window.addEventListener('resize',self.getMaxWidth);
        window.addEventListener('orientationchange',self.getMaxWidth);
        
        
        this.element.addEventListener('touchstart',self.touchstartFun,false);
        document.querySelector('body').addEventListener('touchmove',self.touchmoveFun,false);
        document.querySelector('body').addEventListener('touchend',self.touchendFun,false);
        
        this.element.addEventListener('mousedown',self.touchstartFun,false);
        document.querySelector('body').addEventListener('mousemove',self.touchmoveFun,false);
        document.querySelector('body').addEventListener('mouseup',self.touchendFun,false);
        
        (function drawAnimate() {
            if( self.curW <= self.maxWidth){
            	//window.requestAnimationFrame
//              window.requestAnimationFrame(drawAnimate,1000/60);
				window.requestAnimFrame = (function () {
					if(window.requestAnimationFrame){
						return  window.requestAnimationFrame(drawAnimate,1000/60) ||
			             		window.webkitRequestAnimationFrame(drawAnimate,1000/60) ||
			             		window.mozRequestAnimationFrame(drawAnimate,1000/60)
					}else{
						return function (callback) {
                 				window.setTimeout(drawAnimate, 1000 / 60);
             				};
					}
             		
     			})();
                self.curW = self.currentPos.x;
            }else{
                self.curW = self.currentPos.x = self.maxWidth;
            }
        })();
    },
    watch:{
        isDragOk:function(val,oldval){  
            var self = this;
            if(!val){
                this.curW = 0;
                this.isMove = false;
                this.maxWidth = 0;
                this.text = '拖动滑块验证';
                this.currentPos = {x: 0,y: 0};
                this.getMaxWidth();
                (function drawAnimate() {
                    if( self.curW <= self.maxWidth){
                        window.requestAnimationFrame(drawAnimate,1000/60);
                        self.curW = self.currentPos.x;
                    }else{
                        self.curW = self.currentPos.x = self.maxWidth;
                    }
                })();
            }
        }
    },
    beforeDestroy () {
        console.log('滑动组建销毁');
        window.removeEventListener('resize',this.getMaxWidth);
        window.removeEventListener('orientationchange',this.getMaxWidth);
    },
    methods:{
        touchstartFun(e){
            if(this.isDragOk){
                  e.preventDefault();
                  return;
              }
            this.isMove = true;
            this.curW = this.currentPos.x = this.getCurrentPosition(e).x;
        },
        touchmoveFun(e){
            if(this.isMove && this.curW > 0 && this.curW < this.maxWidth){
                this.currentPos.x = this.getCurrentPosition(e).x;
            }
            else if(this.isMove && this.curW >= this.maxWidth){
                this.curW = this.currentPos.x = this.maxWidth;
                //this.isDragOk = true;
                this.text = "验证通过";
                this.$emit('introduce',true);
            }
        },
        touchendFun(e){
            this.isMove = false;
            if(this.curW < this.maxWidth){
                this.curW = this.currentPos.x = 0;
            }
        },
        
        getCurrentPosition(event){
            var xPos, yPos, rect;
            rect = document.getElementById('drag').getBoundingClientRect();
            //event = event.originalEvent;
            //判断是touch，还是鼠标事件
            if (event.type.indexOf('touch') !== -1) {
                xPos = event.touches[0].clientX - rect.left;
                yPos = event.touches[0].clientY - rect.top;
            }
            
            //鼠标事件
            else {
                xPos = event.clientX - rect.left;
                yPos = event.clientY - rect.top;
            }
            return {
                x: xPos,
                y: yPos
            }
        },
        getMaxWidth(){
            this.maxWidth = document.querySelector("#drag").clientWidth - document.querySelector(".handler").scrollWidth;
        }
    }
}
</script>

<style scoped>
#drag {
    position: relative;
    background-color: #e8e8e8;
    width: 100%;
    height: 36px;
    line-height: 36px;
    text-align: center;
}
#drag .drag_bg {
    height: 36px;
    width:0;
}
#drag .drag_text {
    position: absolute;
    top: 0px;
    width: 100%;
    -moz-user-select: none;
    -webkit-user-select: none;
    user-select: none;
    -o-user-select: none;
    -ms-user-select: none;
}
.drag_text.whitecolor{
    color:#fff;
}
#drag .handler {
    position: absolute;
    top: 0px;
    left: 0px;
    width: 40px;
    height: 34px;
    border: 1px solid #ccc;
    cursor: move;
}
.handler_bg {
    background:#fff url("data:image/png;base64,") no-repeat center
}
.handler_ok_bg {
    background:#fff url("data:image/png;base64,") no-repeat center
}
</style>


