<template>
	<div id="vpcBindChoose" class="vpcBindChoose">
		<ul>
			<li @click="change(index,item)" v-for="(item,index) in tableData.data" :class="{'active':index==current}">
				<div>
					<div class="svg_bg"><svg-icon :icon-class="item.svg"></svg-icon></div>
					<div class="title">{{item.title}}</div>
					<div class="msg">{{item.msg}}</div>
				</div>
			</li>
		</ul>
	</div>
</template>

<script>
	export default{
		props:{
			tableData:[Object],
			form:[Object],
		},
		data(){
			return{
				current:'0',
			}
		},
		methods:{
			change(val,item){
				if(val == '0'){
					console.log(this)
					this.current = val;
					this.form.data = item;
/*<<<<<<< HEAD
					this.$root.visible = false;
					setTimeout(function(){
						window.open('/#/vpc/creatVpc')
					},300)
=======*/
					this.$parent.doClose();
					setTimeout(function(){
						let tempWin = window.open("","_blank");
						tempWin.location = '/#/vpc/creatVpc';
					},300)
				}
			},
		},
		mounted(){
			this.form.data = this.tableData.data[0];
		}
	}
</script>

<style lang="scss">
	.vpcBindChoose{
		&:before{
			clear: both;
			content: "";
			display: inline-block;
		}
		&:after{
			clear: both;
			content: "";
			display: block;
		}
		li{
			float: left;
			border-right: 1px dashed #666666;
			/*width: 180px;*/
			width: 250px;
			height: 190px;
			text-align: center;
			margin-bottom: 20px;
			padding-top: 15px;
			cursor: pointer;
			&.active{
				.svg_bg{
					color: #f9cd76;
				}
				.title{
					color: #333333;
				}
			}
			.svg_bg{
				font-size: 102px;
				color: #666666;
			}
			.title{
				margin-top: 40px;
				font-size: 14px;
				color: #666666;
			}
		}
		li:last-child{
			cursor: pointer;
			border: none;
			.svg_bg{
				font-size: 65px;
			}
			.title{
				font-size: 12px;
				margin-top: 30px;
			}
			.msg{
				padding: 0 10px;
				margin-top: 30px;
				font-size: 12px;
				color: #666666;
			}
		}
	}
</style>


