<template>
	<div :loading='loading' class="action-content">
		<el-row class="action-tip bg">
			<el-row class="action-tip-title" v-if=" typeCom=='info' " v-html="actionTip[0]">
				
			</el-row>
			<el-row class="action-tip-title action-danger" v-if=" typeCom=='info' && actionTip[1] " v-html="actionTip[1]">
			
			</el-row>
			
		</el-row>
		
		<el-row class="action-form">
			<el-col>
				云服务器名称 : {{data.name }}
			</el-col>
			<el-col>
				IP地址： 内网： {{ data.privateIp }} 外网：{{ data.publicip || '--'}}
			</el-col>
			<el-col>

				配置： {{ data.flavorTxt }} 镜像：{{data.templatedisplaytext || data.templatename}}

			</el-col>
			
			<el-col class="form-content"   v-if="!tableData.view">
				<el-form :model="actionForm" :rules="rules" label-width="80px" size="mini" ref="actionForm" label-position="left">
					<el-form-item class="ims-type" v-if="regionType == 'os'" label="镜像类型 :" required>
						<el-radio-group v-model="actionForm.visibility" size="mini" @change = 'changeIms'>
							<el-radio-button v-for="item in imsTypes" :label="item.label" :key="item.value">
								{{item.value}}
							</el-radio-button>
						</el-radio-group>
					</el-form-item>
					<el-form-item class="ims-choose" label="重装选择 :" prop="image_id">
						<el-select v-model="actionForm.osType" @change = "changeIms" class="action-select" size='mini' v-if="actionForm.visibility">
							<el-option v-for="item in osTypes" :value="item.osType" :label="item.label" :key="item.label"></el-option>
						</el-select>
						<el-select v-model="actionForm.image_id" class="action-select" size='mini' :disabled="disabled">
							<el-option v-for="item in imsList" :label="item.name" :value="item.id" :key="item.id"></el-option>
						</el-select>
					</el-form-item>
					<el-form-item label="密码 :" prop="pwd">
						<el-input type="password" v-model="actionForm.pwd"></el-input>
						<div class="passGrade">
					    	<span class="gradeTabs">
					    		<span class="gradeTab" :class="{ passSimple: passGrade>0 }"></span>
					    		<span class="gradeTab" :class="{ passModerate: passGrade>1 }"></span>
					    		<span class="gradeTab" :class="{ passComplex: passGrade>2 }"></span>
					    	</span>
					    </div>
						<el-tooltip popper-class='editSnapTip' content="密码必须为8-20位英文（区分大小写）、数字和符号的组合，不能是纯数字或字母" placement="left"><i class="el-icon-question"></i></el-tooltip>
					</el-form-item>
					<el-form-item label="确认密码 :" prop="newPwd">
						<el-input type="password" v-model="actionForm.newPwd"></el-input>
					</el-form-item>
				</el-form>
			</el-col>
			<el-col v-if="tableData.view">
				<el-col class="orderNum">
					所属订单号：--
					
				</el-col>
				<el-col>
					订单完成时间：--
				</el-col>
				<el-col :span=4>
					问题描述及建议：
				</el-col>
				<el-col :span=19>
					<el-input type="textarea" v-model="desc" @change="changeDes" resize='none' :rows="3" :placeholder='textArea'></el-input>
				</el-col>
				
			</el-col>
			
		</el-row>
	
	
	
	</div>
</template>


<script>
	import { analyzePasswordSecurityLevel } from '@/utils/validator'
	import { getEcmImages } from '@/api/ecm'
	export default {
		props:{
			type:[String],
			actionTip:[Array],
			tableData:[Object],
			action:[String],
			loading:[Boolean],
			form:[Object],
			thatVue:[Object]
		},
		components:{
		},
		data(){
			let _this = this;
			let checkNewPwd1 = (rule, value, callback)=>{
				if(value){
					if(value.length < 8 || value.length >20 ){
						callback(new Error('密码长度在8-20位之间'))
					}else{
						let level = analyzePasswordSecurityLevel(value,8);
						_this.passGrade =  level
						if(level > 2){
							callback()
						}else{
							callback(new Error('密码强度较低，请重新输入'))
						}
					}
					
					
				}else{
					callback(new Error('密码不能为空'))
				}
			}
			let checkNewPwd2 = (rule, value, callback)=>{
				if(value == ''){
					callback(new Error('请再次输入密码'))
				}else if(value != this.actionForm.pwd){
					callback(new Error('两次密码不一致'))
				}else{
					callback()
				}
			}
			return {
				imsTypes:[{
					value:'公共镜像',
					label:1
				},{
					value:'私有镜像',
					label:0
				}],
				osTypes:[{
					label:'Linux',
					osType:1
				},{
					label:'Windows',
					osType:2
				},{
					label:'Redhat',
					osType:3
				}],
				ims:'',
				actionForm:{
					image_id:'',
					visibility:1,
					osType:1,
					pwd:'',
					newPwd:''
				},
				disabled:false,
				imsList:[],
				passGrade:0,
				rules:{
					image_id:[{
						required:true,
						message:'请选择镜像',
						trigger:'change,blur'
					}],
					pwd:[
						{
							required:true,
							message:'请输入密码',
							trigger:'change,blur'
						},{
							validator:checkNewPwd1,
							trigger:'change,blur'
						}
					],
					newPwd:[
						{
							required:true,
							message:'请再次输入密码',
							trigger:'change,blur'
						},{
							validator:checkNewPwd2,
							trigger:'change,blur'
						}
					]
				},
				desc:'',
				textArea:'请简要说明您的退订理由'
			}
		},
		methods:{
			changeImage(){
				this.form.image_id = this.actionForm.image_id;
				this.form.password = this.actionForm.pwd;
			},
			changeIms(){
				this.getEcmImages()
			},
			changeDes(){
				console.log('ddd')
				this.form.describe = this.desc;
			},
			validateForm(){
				this.changeImage()
				return this.$refs.actionForm.validate()
			},
			getEcmImages(){
				let params;
				if(this.regionType == 'os'){
					params = {
						visibility : this.actionForm.visibility
					}
					this.actionForm.visibility && (params.osType = this.actionForm.osType);
				}
				if(this.regionType == 'cs'){
					params = {
						osType:this.actionForm.osType
					}
				}
				this.disabled = true;
				getEcmImages(params).then((res)=>{
					let data = res.data;
					this.imsList = data.results;
					this.disabled =false;
				}).catch(error=>{
					console.log(error)
					this.disabled = false;
				})
			}
		},
		computed:{
			typeCom(){
				let type = this.type || 'warn'
				return type 
			},
			actionCom(){
				if(this.action == 'start' || this.action == 'stop' || this.action == 'reboot' || this.action == 'delete'){
					return 'action'
				}
			},
			data(){
				
				if(Array.isArray(this.tableData.data)){
					return this.tableData.data[0]
				}else{
					return this.tableData.data
				}
				
				
			},
			regionType(){
				console.log(this.thatVue.$store.getters.regionType)
				return this.thatVue.$store.getters.regionType;
			}
		},
		created(){
			if(this.tableData.view == "ubsubscribe"){
				
			}else{
				this.getEcmImages()
			}
			console.log(this.tableData)
			
		},
		
	}
</script>

<style lang="scss" scoped="scoped">
.action-content{
	max-width:600px;
}
	.action-tip{
		/*margin-top: -20px;*/
		padding-bottom: 10px;
		font-size:12px;
		min-height: 30px;
		color: #666666;
		.action-tip-title{
			line-height: 20px;
		}
		
		.action-tip-waring{
			.action-img{
				font-size:30px;
				color:#F9CD76;
				text-align: left;
				padding-left:2px;
			}
			.action-tip-title{
				p{
					line-height: 20px;
				}
				.action-tip-subtip{
					color:#999999;
				}
			}
		}	
		
		
	}
	.bg{
		background: #fff9ee;
		padding:10px;
		border:1px solid #F9CD76;
		.action-danger{
			color:#f6585e;
		}
	}
	.action-form{
		padding:20px 20px 0;
		.el-col{
			font-size:12px;
			line-height: 25px;
			padding:10px 0 0;
		}
		.action-btn,.action-select{
			height: 30px;
			
			font-size:12px;
		}
		.form-content{
			.el-input{
				width:50%;
			}
		}
		.el-textarea{
			font-size:12px;
		}
	}
	.orderNum{
		color:#3AC0F1;
	}
	
	.passGrade{
		display: inline-block;
		.infoTip {
			vertical-align: middle;
		    color: #606266;
		}
		.gradeTabs {
			padding-left: 10px;
			.gradeTab {
				width: 35px;
			    height: 5px;
			    display: inline-block; 
			    margin-right: 4px;
			    background-color: #eee;
			}
		}
	}
	.passSimple {
		background-color: #eb7473 !important;
	}
	.passModerate {
		background-color: #f5b840 !important;
	}
	.passComplex {
		background-color: #55ca95 !important;
	} 
</style>
<style lang="scss">
	.action-content{
		.el-form-item{
			
			label.el-form-item__label{
				font-size:12px;
			}	
		}
	} 
	.editSnapTip{
		width:100px;
	}
</style>


