<template>
  <div>
    <console-detail
      :detailList = 'detailList'
      :detail_image = 'detailImg'
      :detail_label='detailLabel'
      :btnMsg= 'btnFun'
      :changeName='changeVpcName'
      @deatilBtnClick = 'action'
    >

    </console-detail>

    <detail-tab :tabOption='tabOption' @click="swtichTab">
      <div :slot="currentView">
        <component :is='currentView' :serverId = 'serverId' @subNum = 'changeNum' :subTotalCount='subTotalCount' :subnetList="subnetList" :vpcinfo="vpcinfo"></component>
      </div>
    </detail-tab>
  </div>
</template>

<script>
  import detailImg from '@/assets/ecm/vpcDetail.png'
  import ConsoleDetail from '@/components/ConsoleDetail'
  import DetailTab from '@/components/DetailTab'

  import { getRegionId } from '@/utils/auth'
	import { nameRule } from '@/utils/validator';
  import { GetVpcDetail,queryQuota,getVpcList,editVpc} from '@/api/vpc'

  import SubnetDetail from './subnetDetail'
  import Topology from './topology'

  export default {
    components:{
      ConsoleDetail,
      DetailTab,
      SubnetDetail,
      Topology,
    },
    methods:{
      action(value){
        console.log(value)
      },
      changeVpcName(detail,val,fn){
				let _this = this;
				if(val == 'name'){
					if(detail.intro.length > 63 || detail.intro.length < 2){
					this.$message({
		          	message: '名称长度为2-63字符',
		          	type: 'warning'
		        });
	  			}else if(nameRule(detail.intro)){
	  				this.$message({
			          	message: '名称不能使用中文',
			          	type: 'warning'
			        });
		      	}else{
		      		let params = {
								status:'',
								page:1,
								name:detail.intro,
								attached:'',
							}
			        getVpcList(params).then((res)=>{
	      				let servers = res.data.results;
	      				if(servers.length > 0){
	      					this.$message({
					          	message: 'VPC名称重复，请重新输入！',
					          	type: 'warning'
					        });
	      				}else{
	      					let data={
	      						"name":detail.intro,
	      						"desc":this.VpcDetail.displaytext
	      					};
	      					editVpc(this.serverId,data).then((res)=>{
	      						this.$message({
						          	message: 'VPC名称修改成功！',
						          	type: 'success'
						        });
						        fn();
	      						_this.refresh();
	      					}).catch((error)=>{
			      				console.log(error);
//			      				this.$message({
//						          	message: 'VPC名称修改失败，请稍后重试！',
//						          	type: 'warning'
//						        });
			      			})
	      				}
	      			}).catch((error)=>{
	      				console.log(error);
	      				this.$message({
				          	message: 'VPC名称查重失败，请稍后重试！',
				          	type: 'warning'
				        });
	      			})
		      	}
				}else{
					let data={
						"desc":detail.intro,
						"name":this.VpcDetail.name
					};
					editVpc(this.serverId,data).then((res)=>{
						this.$message({
				          	message: 'VPC描述修改成功！',
				          	type: 'success'
				        });
				        fn();
						_this.refresh();
					}).catch((error)=>{
	      				console.log(error);
	      				this.$message({
				          	message: 'VPC描述修改失败，请稍后重试！',
				          	type: 'warning'
				        });
	      			})
				}
			},
			refresh(val){
  			this.getVpcDetail(this.$route.params.serverId)
  		},
      autoRefresh(id){
      	setTimeout(()=>{
					this.getVpcDetail(id)
				},5000)
      },
      getVpcDetail(id){
        let detailList = this.detailList;
        let detail;
        if(this.regionType == 'os'){
        	detail = 1;
        }
        GetVpcDetail(id,detail).then((res)=>{
          console.log(res);
          let data = res.data.results[0];
          this.VpcDetail = data;
          console.log('data');
          console.log(data);
          let fakeVpc = data.fake_vpc && (data.fake_vpc == 1 );
          data.name = fakeVpc ? (data.name == 'fake_vpc' ? '默认网络' : data.name) : data.name;
          if(this.regionType == 'os'){
          	data.displaytext = data.description;
          	this.subnetList = data.subnet_list;
          	data['net_num']= ''+data.subnet_list.length;
          }
          if(this.regionType == 'cs'){
          	this.subnetList=data.network;
          	data['net_num']= ''+data.network.length;
          }
          data.cidr = fakeVpc && '--' || data.cidr;
          this.vpcinfo['vpcid']=data.id;
          this.vpcinfo['vpcname']=data.name;
          this.vpcinfo['zoneid']=data.zoneid;
          this.vpcinfo['fake_vpc']=data.fake_vpc ? data.fake_vpc : null;
          this.vpcinfo['cidr'] = data.cidr;
          this.subTotalCount=this.diskCount;
          detailList.map((item)=>{
            if(item.name == 'diskNum'){
              return
            }
            if(fakeVpc && item.name == 'cidr'){
            	item.hide = true;
            }
            if(fakeVpc){
            	if(item.name == 'name' || item.name == 'displaytext'){
            		item.noChange = true;
            	}else{
            		item.noChange = false;
            	}
            	
            }
//          if(item.name == 'displaytext'){
//          	if(data[item.name].length <= 40) {
//					      item.intros = data[item.name];
//					    }else{
//					    	let res=parseInt(data[item.name]).toString();  
//							  let len=res.length;  
//							  if(len<=40){return res;}  
//							  let r=len%40;  
//							  item.intros = r>0?res.slice(0,r)+"&#10;"+res.slice(r,len).match(/\d{3}/g).join("&#10;"):res.slice(r,len).match(/\d{3}/g).join("&#10;"); 
//					    }
//          }
            item.intro = data[item.name]
          })
          this.detailLabel =  data.name;
          if(data.subnet_creating){
  						this.autoRefresh(data.id);
  					}
        }).catch((req)=>{
          console.log(req)
        })
      },
      changeNum(val){
      	console.log(val);
//    	this.detailList.map((item)=>{
//	        if(item.name == 'net_num'){
//	          item.intro = ''+val;
//	          console.log(item);
//	        }
//	      })
//    	this.queryQuotas();
      	this.getVpcDetail(this.$route.params.serverId)
      },
      swtichTab(tab){
        this.currentView = tab.name
      },
      queryQuotas(){
      	queryQuota().then((res)=>{
          console.log(res.data.results);
          let Quota = res.data.returnObj;
//        this.diskCount = Quota.network_limit_each_vpc - 0;
					this.diskCount =Quota.network_limit_each_vpc ? Quota.network_limit_each_vpc - 0 : '';
        }).catch((error)=>{
          console.log(error)
        })
      }
    },
    data (){
      return {
        detailImg:detailImg,
        detailLabel:'虚拟私有云',
        diskCount:0,
        subTotalCount:'--',
        currentView:'SubnetDetail',
        serverId:'',
        vpcinfo:{},
        VpcDetail:{},
        subnetList:[],
        btnFun:[],
        detailList:[
          {
            name:'name',
            label:'名称',
            intro:'--',
            change:true,
            noChange:'',
          },
          {
            name:'net_num',
            label:'子网',
            intro:'--',
            noChange:'',
          },
          {
            name:'displaytext',
            label:'描述',
            intro:'--',
            intros:'',
            changeDesc:true,
            noChange:'',
          },
          {
            name:'id',
            label:'ID',
            intro:'--',
            noChange:'',
          },
          {
            name:'cidr',
            label:'VPC网段',
            intro:'--',
            hide:false,
            noChange:'',
          }
        ],
        tabOption:[
          {
            label:'子网',
            name:'SubnetDetail',
            view:''
          },
//        {
//          label:'拓扑图',
//          name:'Topology',
//          view:''
//        }
        ]


      }
    },
    computed:{
    	regionType(){
    		return this.$store.getters.regionType;
    	}
    },
    created(){
//  	this.queryQuotas();
//      this.getebsDetailById(this.$route.params.serverId)
			let quotas = this.$store.state.user.user.quotas;
			if(quotas.network_limit_each_vpc){
				this.diskCount = quotas.network_limit_each_vpc - 0;
			}else{
				this.queryQuotas();
			}
			
      this.serverId = this.$route.params.serverId
      this.getVpcDetail(this.$route.params.serverId)
    },
    updated(){

    }



  }
</script>

<style>
</style>



