<template>
	<div :loading='loading' class="action-bks">
		<el-row class='action-table'>
			<el-col :span="24" style="margin-bottom: 20px;">
				<ConsoleSearch :statusOnOff='tableData.statusOnOff' 
					:options ='tableData.selectOptions' 
					:optionsValue ='tableData.optionsValue' 
					:PublicOperation='tableData.ebsPublicOperation' 
					@searchEvent='search' 
					@refreshEvent='refresh'
					@clear = 'clear'
					:disableData='tableData.changeTableData'
					@selectChange = "selectChange"
					:btnClose = 'btnClose'
					></ConsoleSearch>
			</el-col>
		</el-row>
		<el-row class='action-table bksAction'>
			<el-col>
				<console-table :tableData='ebsListData'
				:operationOrNot=false
				:tableOptions = 'fliterData.option'
				:noEffect=true
				:radioShows=true
				:height='280'
				@radioChange="radioChange"
				:Pagination = 'Pagination' @pagingEvent = 'pagingChange'
				></console-table>
			</el-col>
			
		</el-row>
	</div>
</template>

<script>
	import ConsoleTable from '@/components/ConsoleTable'
	import ConsoleSearch from '@/components/ConsoleSearch'
	import i18n from '@/lang'
	import { getEbsList } from '@/api/ebs'
	export default {
		props:{
			tableData:[Object],
			loading:[Boolean],
			form:[Object,String],
		},
		components:{
			ConsoleTable,
			ConsoleSearch
		},
		data(){
			return {
				Pagination:{
		        	'pagingData':[10,20,50],
		        	'total':0,
		        	'show':false
		        },
				btnClose:true,
				filter:{
					satus:'',
					limit:10,
					page:1,
					name:'',
					id:'',
					attached:'',
					keyword:''
				},
				ebsListData:[],
			}
		},
		methods:{
			clear(){
      			this.filter.keyword='';
      			this.filter.name='';
      		},
			search(filter){
	  			console.log(filter)
				if(filter.name == 'id'){
      				this.filter.status = ''
					this.filter.name=''
					this.filter.id=filter.value;
	//				this.filter[filter.name] = filter.value;
					this.filter.keyword = '';
      			}else{
      				this.filter.status = ''
					this.filter.name=''
					this.filter.id=''
	//				this.filter[filter.name] = filter.value;
					this.filter.keyword = filter.value;
      			}
				this.getEbsList()
	  		},
			selectChange(value){
				if(value == 'ready'){
      				this.filter.attached = 0;
      			}else if(value == 'allocated'){
      				this.filter.attached = 1;
      			}else{
      				this.filter.attached = '';
      			}
      			this.filter.page = 1;
				this.filter.status ='';
				this.getEbsList()
			},
	  		refresh(){
	  			this.filter.status = ''
		        this.filter.keyword = '',
		        this.filter.name=''
		        this.filter.id=''
		        this.filter.attached=''
	  			this.getEbsList()
	  		},
	  		radioChange(val){
				console.log(val)
				this.form.data = val;
				console.log(this.form);
			},
			getEbsList(){
	  			let params = this.filter;
	  			getEbsList(params).then((res)=>{
	  				let servers = res.data.results;
      				let totalCount = res.data.total_count;
      				this.filterServers(servers);
      				this.ebsListData = servers;
      				this.totalCount = totalCount;
      				this.Pagination.total = totalCount;
      				if(totalCount > this.Pagination.pagingData[0]){
      					this.Pagination.show = true;
      				}
	  			}).catch((error)=>{
	  				console.log(error)
	  			})
	  		
	  		},
	  		filterServers(servers){
      			servers.forEach(function(item){
      				item.storagetype = item.storagetype == 'shared' ? '是' : '否';
      				item.type = item.type == 'DATADISK' ? '数据盘' : '系统盘';
      				item.states = item.state;
      				if(item.state == 'Error'){
      					item.state = 'error';
      				}else{
      					item.state = item.virtualmachineid ? 'mount' : 'uninstall';
      				}
      				item.size = item.size/1024/1024/1024;
      			})
      		},
      		pagingChange(type,val){
      			if(type == 'num'){
      				this.filter.limit = val;
      			}else if(type == 'page'){
      				this.filter.page = val;
      			}
      			this.getEbsList();
      		}
		},
		computed:{
			fliterData(){
				return this.tableData
			}
		},
		mounted(){
			this.getEbsList(this.filter)
		}
	}
</script>

<style lang="scss">
	.action-bks{
		.bksAction{
			.el-table__body-wrappe{
				.el-table__body{
					max-height: 350px;
					overflow-y: auto;
				}
			}
		}
	}
</style>


