<template>
	<div class="orderInfo">
		<el-container>
			<el-main>
				<h4 class="p_b_20">订单编号  {{order_id}}</h4>
				<div class="tab_content p_b_20">
					<div class="tab_title">订单概要</div>
					<div class="tab_content_row">
						<el-row :gutter="20">
						  <el-col :span="8">
						  	<div class="grid-content bg-purple f_z_12 p_l_30">
						  		<label>订单编号：</label>
						  		<span>{{order_id}}</span>
						  	</div>
						  </el-col>
						  <el-col :span="8">
						  	<div class="grid-content bg-purple f_z_12">
						  		<label>创建时间：</label>
						  		<span>{{orderData.create_date}}</span>
						  	</div>
						  </el-col>
						  <el-col :span="8">
						  	<div class="grid-content bg-purple f_z_12">
						  		<label>支付时间：</label>
						  		<span>{{orderData.eff_date}}</span>
						  	</div>
						  </el-col>
						</el-row>
					</div>
					<div class="tab_content_row">
						<el-row :gutter="20">
						  <el-col :span="8">
						  	<div class="grid-content bg-purple f_z_12 p_l_30">
						  		<label>支付状态：</label>
						  		<span class="state_c">{{orderData.state_str}}</span>
						  	</div>
						  </el-col>
						  
						</el-row>
					</div>
					<div></div>
				</div>
				<div class="tab_content">
					<div class="tab_title tab_title_border_b">订单详情</div>
					<ConsoleConfirm :tableData="tableData"></ConsoleConfirm>
				</div>
			</el-main>
		</el-container>
		<div class="return">
			<el-button plain size="small" @click.stop="returnList">返回订单列表</el-button>
		</div>
	</div>
</template>

<script>
	import ConsoleConfirm from '@/components/ConsoleConfirm'
	import {getOrderList} from '@/api/cost'
	import i18n from '@/lang'
	export default {
		components:{
			ConsoleConfirm,
		},
		data (){
			return {
				tableData:{
		          	system_name: '北京资源池',//资源名称
		          	elseData: [],
		          	tableOptions: [
		          	{
		    			prop:'length',
		    			label:i18n.ebs.length,
		    		},{
		    			prop:'cost',
		    			label:'费用',
		    		},{
		    			prop:'num',
		    			label:i18n.ebs.Number,
		    		},{
		    			prop:'total',
		    			label:i18n.ebs.totalPrice,
		    		}],
		        },
		        order_id: '',
		        orderData: {}
			}
		},
		methods: {
			getOrderInfo(){
				getOrderList({keyword: this.order_id}).then(res => {
					console.log(res);
					if(res.status == 200){
						this.orderData = res.data.returnObj.orders[0];
						this.tableData.elseData.push({
			             	length: '5',
			             	cost: '¥23.6/小时',
			      			num: '9',
			      			total:'￥'+ this.orderData.price,
			      			system_type: this.orderData.product_type_str,
			      			confirmData:[{
						        	label: 'CPU',
						        	value: '4核',
					      		},{
					      			label: '内存',
					      			value: '4GB'
					      		},{
					      			label: '系统盘',
					      			value:	'100GB'
					      		},{
					      			label: '数据盘总和',
					      			value:	'200GB'
					      		},{
					      			label: '操作系统',
					      			value: 'windows/64位'
					      		}
			      			],
			            });
					}else{

					}
				},error => {
					console.log(error);
				});
			},
			returnList() {
				this.$router.push('/cost/order');
			}
		},
		mounted: function () {
			this.order_id = this.$route.params.orderId;
			this.getOrderInfo();
		}
	}
</script>

<style scoped lang="scss">
.orderInfo {
	background: #fff;
    height: 100%;
    .p_b_20 {
    	padding-bottom: 20px;
    	color: #333;
    }
    .tab_title {
    	background: #f0f2f7;
	    height: 30px;
	    line-height: 30px;
	    padding-left: 20px;
	    color: #333;
    	font-weight: bold;
    }
    .tab_title_border_b {
    	border-bottom: 1px solid #d7deec;
    }
    .tab_content_row {
    	border-bottom: 2px solid #ecf0f6;
    	height: 30px;
    	line-height: 30px;
    }
    .f_z_12 {
    	font-size: 12px;
    }
    .p_l_30 {
    	padding-left: 30px;
    }
    .state_c {
    	color: #9bd9cf;
    }
    .return {
    	padding-left: 20px;
    }
}
</style>
<style lang="scss">
.orderInfo .tableHeaderbg {
	background-color: #f0f2f6 !important;
	color: #635e5e;
	padding: 4px 0;
}
.orderInfo .el-table__row:hover > td {
	background-color: #fff !important;
}
</style>


