<template>
	<div class="create_rule">
		<el-header height="20px">
			<h4 class="rule_title">{{name}}</h4>
		</el-header>
		<el-container>
			  <el-main class="padding-left">
			  		<el-steps direction="vertical" :active="5" space='auto'>
						   <el-step title="选择监控对象" ref="_obj" class="info-step">
							   	<div slot="description" class="stepContent">
							   		<div class=" stepIcon">
										<div class="stepIcon-title">1</div>
										<div class="stepIcon-rectangle">
											<div class="stepIcon-triangle"></div>
										</div>
									</div>
							   		<el-form ref="monitor_obj" :model="monitor_obj" label-width="140px" label-position="left" size="small">
										<el-form-item label="云服务：" prop="server" required>
										    <el-select v-model="monitor_obj.server" placeholder="请选择活动区域" class="width_430">
										      <el-option label="云服务1" value="shanghai"></el-option>
										      <el-option label="云服务2" value="beijing"></el-option>
										    </el-select>
										  </el-form-item>
										  <el-form-item label="维度：" prop="server" required>
										    <el-select v-model="monitor_obj.server" placeholder="请选择活动区域" class="width_430">
										      <el-option label="维度1" value="shanghai"></el-option>
										      <el-option label="维度2" value="beijing"></el-option>
										    </el-select>
										  </el-form-item>
										  <el-form-item label="监控对象：" prop="m_obj" required>
										    <el-input placeholder="请输入内容" v-model="monitor_obj.obj_list_sr" clearable :readonly=true class="width_430"></el-input>
										    <el-button type="primary" @click.stop="" size="small">选择</el-button>
										  </el-form-item>
							   		</el-form>
							   	</div>
						   </el-step>
						   <el-step title="选择监控指标" ref="_index" class="network-step">
						   		<div slot="description" class="stepContent">
						   			<div class=" stepIcon">
										<div class="stepIcon-title">2</div>
										<div class="stepIcon-rectangle">
											<div class="stepIcon-triangle"></div>
										</div>
									</div>
						   			<el-form ref="monitor_index" :model="monitor_index" label-width="140px" label-position="left" size="small">
						   				<el-form-item label="选择类型：" prop="" required>
										    <el-radio-group v-model="monitor_index.rule_type" size="small" fill="#3dc7fa">
										      <el-radio-button label="default">从模版导入</el-radio-button>
										      <el-radio-button label="custom">自定义创建</el-radio-button>
										    </el-radio-group>
										</el-form-item>
										<template v-if="isDefault">
											<el-form-item label="模版：" prop="" required>
										    	<el-select v-model="monitor_obj.server" placeholder="请选择活动区域" class="width_430">
											      <el-option label="维度1" value="shanghai"></el-option>
											      <el-option label="维度2" value="beijing"></el-option>
											    </el-select>
											</el-form-item>
											<el-form-item>
												<el-table
											      :data="tableData"
											      class="width_430">
											      <el-table-column
											        prop="date"
											        label="监控指标"
											        width="160">
											      </el-table-column>
											      <el-table-column
											        prop="name"
											        label="阀值"
											        width="160">
											      </el-table-column>
											      <el-table-column label="操作">
											      	<template slot-scope="scope">
												        <el-button
												          size="mini"
												          type="text"
												          @click="handleDelete(scope.$index, scope.row)">删除</el-button>
												      </template>
											      </el-table-column>
											    </el-table>
											</el-form-item>
											<el-form-item label="发送通知：" prop="" required>
										    	<el-switch v-model="monitor_index.value2" active-color="#13ce66"
												  inactive-color="#d7deec">
												</el-switch>
											</el-form-item>
										</template>
										<template v-if="!isDefault">
											<el-form-item label="监控指标：" prop="" required>
										    	<el-select v-model="monitor_obj.server" placeholder="请选择活动区域" class="width_430">
											      <el-option label="维度1" value="shanghai"></el-option>
											      <el-option label="维度2" value="beijing"></el-option>
											    </el-select>
											</el-form-item>
											<el-form-item label="阀值：" prop="" required>
										    	<el-select v-model="monitor_obj.server" placeholder="请选择活动区域" class="width_160">
											      <el-option label="维度1" value="shanghai"></el-option>
											      <el-option label="维度2" value="beijing"></el-option>
											    </el-select>
											    <el-select v-model="monitor_obj.server" placeholder="请选择活动区域" class="width_160">
											      <el-option label="维度1" value="shanghai"></el-option>
											      <el-option label="维度2" value="beijing"></el-option>
											    </el-select>
											    <el-input-number v-model="monitor_index.num8" :controls=false :min="1" :max="100" class="width_100"></el-input-number>%
											</el-form-item>
											<el-form-item label="出现次数：" prop="" required>
										    	<el-input-number v-model="monitor_obj.num8" controls-position="right" :min="1" :max="10" class="width_430"></el-input-number>
											</el-form-item>
											<el-form-item label="出现周期：" prop="" required>
										    	<el-input-number v-model="monitor_obj.num8" controls-position="right" :min="1" :max="10" class="width_430"></el-input-number>
											</el-form-item>
											<el-form-item label="发送通知：" prop="" required>
										    	<el-switch v-model="monitor_index.value2" active-color="#13ce66"
												  inactive-color="#d7deec">
												  </el-switch>
											</el-form-item>
											<el-form-item label="选择告警联系组：" prop="" required>
										    	<el-input placeholder="请输入内容" v-model="monitor_obj.obj_list_sr" clearable :readonly=true class="width_430"></el-input>
										    	<el-button type="primary" @click.stop="" size="small">选择</el-button>
											</el-form-item>
											<el-form-item label="沉默时间：" prop="" required>
										    	<el-input-number v-model="monitor_obj.num8" controls-position="right" :min="1" :max="10" class="width_430"></el-input-number>
										    	<p class="display_inline">
										    		<i class="el-icon-question c_yellow"></i>
										    		<label class="f_c_6">指告警发生后如果未恢复正常，间隔多久重复发送一次告警通知</label>
										    	</p>
											</el-form-item>
											<el-form-item label="触发场景：" prop="" required>
												<div class="width_430 display_inline">
													<el-checkbox v-model="monitor_index.checked">出现告警</el-checkbox>
										    		<el-checkbox v-model="monitor_index.checked">恢复正常</el-checkbox>
										    		<el-checkbox v-model="monitor_index.checked">数据不足</el-checkbox>
												</div>
										    	<p class="display_inline">
										    		<i class="el-icon-question c_yellow"></i>
										    		<label class="f_c_6">连续三个小时未有监控数据数据上报，则判断当前状态为数据不足</label>
										    	</p>
											</el-form-item>
										</template>
						   			</el-form>
						   		</div>
						   </el-step>
						   <el-step title="规则信息" ref="_rule" class="create-step">
						   		<div slot="description" class="stepContent">
						   			<div class=" stepIcon">
										<div class="stepIcon-title">3</div>
										<div class="stepIcon-rectangle">
											<div class="stepIcon-triangle"></div>
										</div>
									</div>
						   			<el-form ref="monitor_rule" :model="monitor_rule" label-width="140px" label-position="right">
						   				<el-form-item label="名称：" prop="" required>
										    <el-input placeholder="请输入内容" v-model="monitor_obj.obj_list_sr" clearable  class="width_430"></el-input>
										</el-form-item>
										<el-form-item label="描述：" prop="">
										    <el-input
											  type="textarea"
											  :rows="4"
											  placeholder="请输入内容"
											  v-model="monitor_rule.textarea"
											  class="width_430">
											</el-input>
										</el-form-item>
						   			</el-form>
						   		</div>
						   </el-step>
						</el-steps>
			  </el-main>
		</el-container>
		<el-footer height="40px">
			<el-button type="info" @click.stop="cancel">取消</el-button>
			<el-button type="primary" @click.stop="order('indirect')">确定</el-button>
		</el-footer>
	</div>
</template>

<script>
export default {
	components:{
		
	},
	data () {
		return {
			name: '创建告警规则',
			tableData: [{
	            date: '内存使用率',
	            name: '原始值>=90%',
	        }, {
	            date: '内存使用率',
	            name: '原始值>=90%',
	        }],
			monitor_obj: {
				server: 'shanghai',
				obj_list_sr: 'sdfs',
			},
			monitor_index: {
				rule_type: 'default',
				value2: true,
				num8: 2,
				checked: true,
			},
			monitor_rule: {
				textarea: '',
			}

		}
	},
	methods: {
		handleDelete: function(){

		}
    },
    computed: {
    	isDefault:function(){
			return this.monitor_index.rule_type === 'default';
		}
    },
    mounted: function () {
    	
    },
    beforeDestroy: function () {

    }
}
</script>

<style lang="scss">
.create_rule{
	.el-step__main {
		background-color: #fff;
	    margin-left: 10px;
	    margin-bottom: 20px;
	    border-radius: 3px;
	    padding-top: 10px;
	}
	.padding-left{
		padding-left:0;
	}
	.el-button--text{
		color: #3dc7fa;
	}
	.el-button--primary{
		background-color: #3dc7fa;
	    border-color: #3dc7fa;
	}
	.el-button--primary.is-disabled{
		background-color: #a0cfff;
	    border-color: #a0cfff;
	}
	.el-step__head.is-finish{
		color: #3dc7fa;
	    border-color: #3dc7fa;
	}
	.network-step > .el-step__head.is-finish,.login-step > .el-step__head.is-finish,.create-step > .el-step__head.is-finish,.config-step > .el-step__head.is-finish {
		padding-top: 10px;
	}
	.info-step > .el-step__head.is-finish {
		margin-top: 10px;
	}
	.el-step:last-of-type .el-step__line{
		display : block;
		bottom: 20px;
		margin-right: 0px;
	}
	.el-step:last-of-type .el-step__line > i{
		transition-delay: 450ms;
	    border-width: 1px;
	    height: 100%;
	}
	.el-step__title {
		color: #3a3232 !important;
		padding-left: 30px;
	}
	.stepContent{
		padding-left: 18px;
		padding-right: 20px;
	    padding-bottom: 20px;
	    margin-bottom: 20px;
	}
	.stepIcon{
		position: absolute;
	    z-index: 1;
	    left: 0;
	    top: 9px;
	    border-radius: 50%;
	    border: 2px solid #3dc7fa;
	    align-items: center;
	    width: 24px;
	    height: 24px;
	    font-size: 14px;
	    background-color: #3dc7fa;
	    line-height: 2px;
	}
	.stepIcon-round{
		border-radius: 50%;
	    border: 2px solid #3dc7fa;
	    align-items: center;
	    width: 24px;
	    height: 24px;
	    font-size: 14px;
	    background-color: #3dc7fa;
	    line-height: 2px;
	    position: relative;
	}
	.stepIcon-title{
		position: absolute;
	    top: 10px;
	    left: 7px;
	    color: #fff;
	    text-align: center;
	}
	.stepIcon-rectangle{
		background-color: #3dc7fa;
	    width: 30px;
	    height: 20px;
	    margin-left: 20px;
	    position: relative;
	        top: 2px;
	}
	.stepIcon-triangle{
		width: 0;
	    height: 0;
	    border-top: 10px solid transparent;
	    border-left: 15px solid #3dc7fa;
	    border-bottom: 10px solid transparent;
	    position: absolute;
	    right: -15px;
	}
	.el-step__description {
		padding-right: initial;
	}
	.width_430 {
		width:430px;
	}
	.width_160 {
		width:160px;
	}
	.width_100 {
		width:100px;
	}
	.el-table th{
	  	padding: 0;
	  	background-color: #f0f2f7;
	 }
	 .el-table td{
	  	padding: 5px 0;
	 }
	 .rule_title {
	 	font-size: 14px;
    	color: #333;
    	padding-left: 12px;
	 }
	 .display_inline{
	     display: inline-block;
	 }
	 .f_c_6{
	 	color: #666;
	 }
	 .c_yellow{
	 	color: #face83;
	 }
}
</style>


