
import Layout from '@/views/ecm/ecm'
import BksList from '@/views/bks/BksList'
import BksCreate from '@/views/bks/BksCreate'

import i18n from '@/lang'

let Router ={
	path:'bks',
	name:'ecmEks',
	component:Layout,
	redirect:'bks/bksList',
	children:[
		{
      		path:'bksList',
      		name:'ecmEksList',
      		component:BksList,
      	},
		{
      		path:'bksCreate',
      		name:'ecmEksCreate',
      		component:BksCreate,
      	},
	]
};

export default Router;



