<template>
	<div :loading='loading' class="action-content" >
		<el-row class="action-tip" v-if="!warn">
			<el-row class="action-tip-title" v-if=" typeCom=='info' ">
				{{actionTip[0]}}
			</el-row>
			<el-row class="action-tip-waring" v-if=" typeCom=='warn' ">
				<el-col :span='2' class="action-img">
					<svg-icon icon-class="warn"></svg-icon>
				</el-col>
				<el-col :span="21" class="action-tip-title">
					<p>
						{{actionTip[0]}}
					</p>
					<p v-if="actionTip[1]" class="action-tip-subtip">
						{{ actionTip[1] }}
					</p>
				</el-col>
				
			</el-row>
		</el-row>
		
		<el-row class='action-table' v-if="Array.isArray(tableData.data) && !warn">
			<el-col>
				<console-table :tableData='fliterData.data'
					:operationOrNot=false
					:tableOptions = 'fliterData.option'
					:noEffect=true
					:height='280'
					:key='fliterData.date'
					
					></console-table>
				
			</el-col>
			
		</el-row>
		<el-row class="action-select">
			<el-form :model="snapshot" label-width='30%' size="mini" :rules='rules' ref="actionForm">
				<el-form-item :label="i18n.ecm.ecmName+ '：'">
					{{tableData.data.name}}
				</el-form-item>
				<el-form-item :label="i18n.ecm.snapshotName +'：'" prop='name'>
					<el-input  v-model.trim="snapshot.name" style="width:200px;" @change="changeForm"></el-input>
					<el-tooltip popper-class='editSnapTip' content="不能使用中文,且长度为2-63字符" placement="top"><i class="el-icon-question"></i></el-tooltip>
				</el-form-item>
				
			</el-form>
			
		</el-row>	
	
	
	
	</div>
</template>


<script>
	import ConsoleTable from '@/components/ConsoleTable'
	import ConsoleSearch from '@/components/ConsoleSearch'
	
	import i18n from '@/lang'
	
	import { GetSnapshotList } from '@/api/ecm'
	import { nameRule } from '@/utils/validator'
	
	export default {
		props:{
			type:[String],
			warn:[Boolean],
			attachOperation:[Boolean],
			actionTip:[Array],
			tableData:[Object],
			action:[String],
			loading:[Boolean],
			form:[Object]
		},
		components:{
			ConsoleTable,
			ConsoleSearch
		},
		data(){
			let CheckName = function(rule,value,callback){
//				let nameRule = /^[a-zA-Z][a-zA-Z0-9\-]*[a-zA-Z0-9]$/;
      			if(value.length > 63 || value.length < 2){
		      		callback(new Error('长度为2-63字符'));
      			}else if(nameRule(value)){
      				callback(new Error('不能使用中文'));
		      	}else{
				
					let params ={
						name:value
					}
					GetSnapshotList(params).then(res=>{
						let data = res.data.results;
						if(data.length){
							callback(new Error('名字有重复，请重新输入'))
						}else{
							callback()
						}
						
					}).catch(error=>{
						console.log(error)
					})
				}
			}
			return {
				i18n,
				snapshot:{
					name:''
				},
				rules:{
					name:[
						{ required: true, message: i18n.ecm.snapshotNameTip, trigger: 'blur' },
            			{validator:CheckName,trigger: 'blur'}
					]
				},
				
				
			}
		},
		methods:{
			changeForm(){
				this.form.name = this.snapshot.name
			},
			validateForm(){
				return this.$refs['actionForm'].validate()
			},
		},
		computed:{
			typeCom(){
				let type = this.type || 'warn'
				console.log(this.$refs.actionForm)
				return type 
			},
			
			fliterData(){
				let date = (new Date()).getTime();
				this.$set(this.tableData,'date',date)
				return this.tableData
			}
		}
		
		
	}
</script>

<style lang="scss" scoped="scoped">
/*.action-content{
	min-width: 600px;
}*/
	.action-tip{
		/*margin-top: -20px;*/
		padding-bottom: 10px;
		font-size:12px;
		min-height: 30px;
		color: #666666;
		.action-tip-title{
			line-height: 30px;
		}
		.action-tip-waring{
			.action-img{
				font-size:30px;
				color:#F9CD76;
				text-align: left;
				padding-left:2px;
			}
			.action-tip-title{
				p{
					line-height: 20px;
				}
				.action-tip-subtip{
					color:#999999;
				}
			}
		}	
		
		
	}
	#warnStyle{
		.action-tip-waring{
			.action-img{
				font-size:40px;
				text-align: center;
				padding-left:0px;
			}
			.action-tip-title{
				text-align: center;
				span{
					font-size: 18px;
				}
				i{
					font-size: 18px;
				}
				.action-tip-subtip{
					color:#999999;
				}
			}
		}
	}
	.action-select{
		.el-form-item{
			margin-bottom: 10px;
			label.el-form-item__label{
				font-size:12px;
			}	
		};
	}
	
</style>
<style lang="scss">
	.action-select{
		.el-form-item{
			
			label.el-form-item__label{
				font-size:12px;
			}	
		};
	}
	
	.editSnapTip{
		width: 100px;
	}
	
</style>


