<template>
	<div :loading='loading'>
		<el-row class='action-table'>
			<el-col :span="24" style="margin-bottom: 20px;">
				<ConsoleSearch :statusOnOff='tableData.statusOnOff' 
					:options ='tableData.selectOptions' 
					:optionsValue ='tableData.optionsValue' 
					:PublicOperation='tableData.ebsPublicOperation' 
					@searchEvent='search' 
					@clear = 'clear'
					@refreshEvent='refresh' 
					:disableData='tableData.changeTableData'
					@selectChange = "selectChange"
					:btnClose = 'btnClose'
					></ConsoleSearch>
			</el-col>
		</el-row>
		<el-row class='action-table'>
			<el-col>
				<console-table :tableData='bksListData'
				:operationOrNot=false
				:tableOptions = 'fliterData.option'
				:noEffect=true
				:height='380'
				:bksradioShow=true
				@radioChange="radioChange"
				:Pagination = 'Pagination' @pagingEvent = 'pagingChange'
				></console-table>
			</el-col>
			
		</el-row>
	</div>
</template>

<script>
	import ConsoleTable from '@/components/ConsoleTable'
	import ConsoleSearch from '@/components/ConsoleSearch'
	import { statFormat } from '@/utils/statformat'
	import i18n from '@/lang'
	import { getBksList } from '@/api/bks'
	export default {
		props:{
			tableData:[Object],
			loading:[Boolean],
			form:[Object,String],
		},
		components:{
			ConsoleTable,
			ConsoleSearch
		},
		data(){
			return {
				Pagination:{
		        	'pagingData':[10,20,50],
		        	'total':0,
		        	'show':false
		        },
				btnClose:true,
				filter:{
					satus:'',
					limit:10,
					page:1,
					name:'',
					id:'',
					attached:'',
					keyword:''
				},
				bksListData:[],
			}
		},
		methods:{
			search(filter){
	  			console.log(filter)
				this.filter.status = ''
				this.filter.name=''
				this.filter.id=''
//				this.filter[filter.name] = filter.value;
				this.filter.keyword = filter.value;
				this.getBksList()
	  		},
	  		clear(){
      			this.filter.keyword='';
      			this.filter.name='';
      		},
			selectChange(value){
				if(value == 'ready'){
      				this.filter.attached = 0;
      			}else if(value == 'allocated'){
      				this.filter.attached = 1;
      			}else{
      				this.filter.attached = '';
      			}
      			this.filter.page = 1;
				this.filter.status ='';
				this.getBksList()
			},
	  		refresh(){
	  			this.filter.status = ''
		        this.filter.keyword = '',
		        this.filter.name=''
		        this.filter.id=''
		        this.filter.attached=''
	  			this.getBksList()
	  		},
	  		radioChange(val){
				console.log(val)
				this.form.data = val;
				console.log(this.form);
			},
			getBksList(){
	  			let params = this.filter;
	  			getBksList(params).then((res)=>{
	  				let servers = res.data.results;
      				let totalCount = res.data.total_count;
      				this.filterServers(servers);
      				this.bksListData = servers;
      				this.totalCount = totalCount;
      				this.Pagination.total = totalCount;
      				if(totalCount > this.Pagination.pagingData[0]){
      					this.Pagination.show = true;
      				}
	  			}).catch((error)=>{
	  				console.log(error)
	  			})
	  		
	  		},
	  		filterServers(servers){
      			servers.forEach(function(item){
      				item.size = (item.size/1024/1024/1024).toFixed(2);
      				item.statusDetail = statFormat(item.state) || item.state;
      				item.selectCorrectAZ = item.zonename || '--';
      				item.restartOrNot = item.zonename || '--';
      				item.describe = item.describe || '--';
      			})
      		},
      		pagingChange(type,val){
      			if(type == 'num'){
      				this.filter.limit = val;
      			}else if(type == 'page'){
      				this.filter.page = val;
      			}
      			this.getBksList();
      		}
		},
		computed:{
			fliterData(){
				return this.tableData
			}
		},
		mounted(){
			this.getBksList(this.filter)
		}
	}
</script>

<style lang="scss" scoped="scoped">

</style>


