<template>
	<div class="ecm_monitor">
		<RegionList :name ='name' :buttonText ='buttonText' @create='create' @changeRegion = 'changeRegion'></RegionList>
		<div class="content">
			<div class="content_top">
				<el-input placeholder="请输入内容" v-model="input5" class="input-with-select" size="small" style="width:350px">
				    <el-select v-model="select" slot="prepend" placeholder="请选择">
				      <el-option label="餐厅名" value="1"></el-option>
				      <el-option label="订单号" value="2"></el-option>
				      <el-option label="用户电话" value="3"></el-option>
				    </el-select>
				    <el-button slot="append" icon="el-icon-search"></el-button>
				  </el-input>
			</div>
			<div>
				<ConsoleTable 
					:tableData='ecmListData'
					:tableOptions="tableOptions"
					:operationOrNot=true
					:OperationEvents='ecmOperation'
					@buttonClick="handleClick"
					:loading = 'loading'
					:dragable=true
					:Pagination = 'Pagination'
					@pagingEvent = 'pagingChange'
					></ConsoleTable>
			</div>
			
		</div>
	</div>
</template>

<script>
	import RegionList from '@/components/RegionList'
	import ConsoleTable from '@/components/ConsoleTable'
	import i18n from '@/lang'
	
	export default {
		components:{
			RegionList,
			ConsoleTable,
		},
		data(){
			return {
				name: '弹性公网IP',
				buttonText: '导出监控数据',
				loading:false,
				input5: '',
      			select: '',
				filter:{
					status:'',
					limit:10,
					page:1,
					keyword:'',
					id:''
				},
				Pagination:{
					'pagingData':[10,20,50],
		        	'total':0,
		        	'show':false
				},
				ecmListData: [],
        		tableOptions:[{
        			prop:'displayname',
        			label:i18n.common.name,
        		},{
        			prop:'status',
        			label:i18n.common.status,
        			temp:true,
        			status:true
        		},
        		{
        			prop:'templatedisplaytext',
        			label: 'ID',
        			
        		},{
        			prop:'privateIp',
        			label: '指标个数',
        		}],
        		ecmOperation:[{
        			value:i18n.ecm.telnet,
        			id:'telnet',
        			fun:(data)=>{
        				return this.telnet(data);
        			}
        		},{
        			value:i18n.ecm.changeSpc,
        			id:'changeSpc',
        			fun:(data)=>{
        				return this.changeSpc(data)
        			}
        		}]
			}
		},
		methods:{
      		create(){
      			this.$router.push('/ecm/ecmCreate')
      		},
      		changeRegion(uuid){
      			this.getEcmList()
      		},
      		getEcmList(action){
      			let params = this.filter;
      			this.loading =true;
      			getEcmList(params).then((res)=>{
      				let servers = Array.from(res.data.results);
      				let totalCount = res.data.total_count;
      				this.filterServers(servers,action)
      				this.ecmListData = servers;
      				this.totalCount = totalCount;
      				this.Pagination.total = this.totalCount;
      				if(totalCount>this.Pagination.pagingData[0]){
      					this.Pagination.show = true
      				}
      				this.loading=false
      			}).catch((error)=>{
      				this.loading=false
      				console.log(error)
      			})
      		
      		},
      		handleClick(funName,rowData){
      			funName(rowData)
      		},
      		pagingChange(type,val){
      			if(type == 'num'){
      				this.filter.limit = val;
      			}else if(type == 'page'){
      				this.filter.page = val;
      			}
      			this.getEcmList();
      		},
		},
		
		mounted(){
			
		},
		beforeDestroy(){
			
		}
		
	}
</script>

<style lang="scss" scoped="scoped">
	.ecm_monitor{
  		background: #f6f8fb;
		 .content{
		  	.content_top{
		  		background: #ffffff;
		  		padding-top: 30px;
		  		padding-left: 20px;
		  		padding-bottom: 20px;
		  	}
		}
  	}
</style>
<style lang="scss">
.ecm_monitor{
	.el-select .el-input {
		    width: 100px;
		  }
		  .input-with-select .el-input-group__prepend {
		    background-color: #fff;
		  }
}
</style>



