
import Layout from '@/views/ecm/ecm'
import KeyPairList from '@/views/ecm/KeyPairList'


let Router ={
	path:'keypair',
	name:'keypair',
	component:Layout,
	redirect:'keypair/keypairList',
	children:[
		{
      		path:'keypairList',
      		name:'keypairList',
      		component:KeyPairList
      	}
	]
};

export default Router;



