<template>
	<div class="main">
		<RegionList 
			:name ='name' 
			:buttonText ='buttonText' 
			:buttonTexts ='buttonTexts'
			type="os"
			@create='create' @strategy='strategy' @changeRegion = 'changeRegion'></RegionList>
		<div class="content">
			<div class="content_top">
				<ConsoleSearch 
					:statusOnOff=true 
					:options ='options' 
					@searchEvent='search' 
					@clear = 'clear'
					@refreshEvent='refresh'
					:NameOptions='NameOptions'
					@selectChange = "selectChange"
					:optionsValue ='optionsValue'></ConsoleSearch>
			</div>
			<div>
				<ConsoleTable 
					:checkBox=true 
					:expand=true 
					:operationOrNot = true 
					:selectOrNot=true 
					:tableData = 'bksListData' 
					:tableOptions = 'tableOptions' 
					:detilOptions = 'detilOptions'
					:noEffect = true
					:OperationEvents='ebsOperation' 
					@buttonClick = 'buttonClick'
					@detailClick = 'detailClick'
					:loading = 'loading'
					:Pagination = 'Pagination' @pagingEvent = 'pagingChange'></ConsoleTable>
			</div>
		</div>
	</div>
</template>

<script>
	import RegionList from '@/components/RegionList'
	import ConsoleTable from '@/components/ConsoleTable'
	import ConsoleSearch from '@/components/ConsoleSearch'
	import ConsoleAction from '@/components/ConsoleAction'
	import i18n from '@/lang'
	
	import { getBksList,GetBksDetail,DeleteBks,restoreBks } from '@/api/bks'
	import { getRegionId, setRegionId,getZoneId } from '@/utils/auth'
	import { statFormat } from '@/utils/statformat'
	import { timeService } from '@/utils/timeformat'
	import { queryAsyncJob } from '@/api/vpc'
	import { getEcmPlatforms } from '@/api/ecm'
	import { GetEbsDetail } from '@/api/ebs'
	let timer;
	export default{
		components:{
			RegionList : RegionList,
			ConsoleTable:ConsoleTable,
			ConsoleSearch:ConsoleSearch,
			ConsoleAction:ConsoleAction,
		},
		data(){
			return {
				name:i18n.bks.backupList,
				buttonText:i18n.bks.createBackup,
//				buttonTexts:i18n.bks.strategy_manager,
				buttonTexts:'',
				loading:false,
				detailRefresh:false,
				bksListData: [],
		        multipleSelection: [],
		        Pagination:{
		        	'pagingData':[10,15,20],
		        	'total':0,
		        	'show':false
		        },
		        NameOptions:[{
		          	value: 'name',
		          	label: i18n.common.disname,
		        }, {
		          	value: 'id',
		          	label: i18n.common.ID,
		        }],
		        filter:{
					satus:'',
					limit:10,
					page:1,
					keyword:'',
					name:'',
					id:''
				},
		        options: [{
		          	value: '',
		          	label: i18n.common.allStatus
		        }, {
		          	value: 'BackedUp',
		          	label: i18n.bks.available
		        }, {
		          	value: 'Error',
		          	label: i18n.common.errorStatus
		        }],
        		optionsValue: '',
        		tableOptions:[{
        			prop:'name',
        			label:i18n.common.name,
        			temp:true,
        			detail:true,
        		},{
        			prop:'status',
        			label:i18n.common.status,
        			temp:true,
        			status:true
        		},{
        			prop:'volumename',
        			label:i18n.bks.diskName,
        		},{
        			prop:'size',
        			label:i18n.bks.diskCapacityGB,
        		},{
        			prop:'created',
        			label:i18n.common.createAt,
        		}],
        		detilOptions:[{
        			prop:'name',
        			label:i18n.common.name,
        		},{
        			prop:'volumename',
        			label:i18n.bks.diskName,
        		},{
        			prop:'id',
        			label:i18n.common.ID,
        		},{
        			prop:'volumeid',
        			label:i18n.ebs.diskId,
        		},{
        			prop:'statusDetail',
        			label:i18n.common.status,
        		},{
        			prop:'size',
        			label:i18n.bks.diskCapacityGB,
        		},{
//      			prop:'selectCorrectAZ',
//      			label:i18n.common.selectCorrectAZ,
//      		},{
//      			prop:'restartOrNot',
//      			label:i18n.ebs.restartOrNot,
//      		},{
        			prop:'created',
        			label:i18n.common.createAt,
        		},{
        			prop:'describe',
        			label:i18n.bks.describe,
        		}],
        		ebsOperation:[{
        			value:i18n.bks.restore,
        			fun:(data)=>{
        				return this.restore(data);
        			}
        		},{
//      			value:i18n.bks.createDisk,
//      			fun:(data)=>{
//      				return this.createDisk(data);
//      			}
//      		},{
        			value:i18n.bks.deleter,
        			fun:(data)=>{
        				return this.deleter(data);
        			}
        		}]
			}
		},
		methods:{
      		handleClick(){
      			
      		},
      		create(){
      			this.$router.push({path:'/ebs/bks/bksCreate'});
      		},
      		strategy(){
      			console.log('备份策略');
      		},
      		clear(){
      			this.filter.keyword='';
      			this.filter.name='';
      		},
      		buttonClick(funName,data){
      			funName(data);
      		},
      		restore(data){
      			console.log(data);
      			console.log('调用恢复');
      			GetEbsDetail(data.volumeid).then((res)=>{
					console.log(res)
					if(res.data.results[0].vmstate == 'Stopped'){
						let restoreData = [];
		      			restoreData.push(data);
		      			let restoreOptions = [{
		        			prop:'name',
		        			label:i18n.common.name,
		        			temp:true,
		        			detail:true,
		        		},{
		        			prop:'status',
		        			label:i18n.common.status,
		        			temp:true,
		        			status:true
		        		},{
		        			prop:'volumename',
		        			label:i18n.bks.diskName,
		        		},{
		        			prop:'size',
		        			label:i18n.bks.diskCapacityGB,
		        		},{
		        			prop:'created',
		        			label:i18n.common.createAt,
		        		}];
		      			let option = {
		      				'data':{
		      					'option':restoreOptions,
		      					'title':'恢复数据',
		      					'type':'warn',
		      					'actionTip':['确定要用一下备份恢复磁盘数据吗？','该操作将使用备份时间点数据覆盖磁盘数据，一旦执行无法取消！'],
		      					'data':restoreData,
		      				},
		      				'view':ConsoleAction,
		      				'form':{}
		      			};
		      			this.$alertDialog(option).then((res)=>{
		      				console.log('huifu');
		      				let restoreId = {'volume_id':data.volumeid};
		      				restoreBks(restoreId,data.id).then((res)=>{
								console.log(res)
								this.$message({
									message: '命令下发成功',
						          	type: 'success'
								})
							}).catch((req)=>{
								this.$message({
						          	message: '命令下发失败',
						          	type: 'error'
						        });
							})
		      			});
					}else{
						this.$message({
				          	message: '该磁盘挂载的云服务器未处在关机状态，请执行关机操作再进行恢复数据操作',
				          	type: 'warning'
				        });
					}
				}).catch((req)=>{
					this.$message({
			          	message: '请求云服务器状态失败，请稍后再试',
			          	type: 'warning'
			        });
				})
      		},
      		createDisk(data){
      			console.log('调用创建');
      			console.log(data);
      			this.$router.push({path:'/ebs/ebsCreate',query:{'name':data.name,'id':data.id}});
      		},
      		deleter(rowData){
      			console.log('调用删除');
      			let option ={
					data:{
						title:'删除',
						actionTip:['确认要删除备份'],
						warn:true,
						data:rowData
					}
				}
				if(Array.isArray(rowData)){
					option.data.warn = false;
				}
				let _this = this;
				let bksId = rowData.id;
				this.$alertDialog(option).then((formres)=>{
					console.log(formres);
					DeleteBks(bksId).then((res)=>{
						console.log(res)
						this.$message({
							message:'快照删除成功。',
							type:'warning'
						})
						let count = 0;
			            for(var i =0;i < _this.bksListData.length;i++){
			            	if(bksId == _this.bksListData[i].id){
			            		count = i;
			            		break;
			            	}
			            }
			            _this.bksListData.splice(count,1);
					}).catch((req)=>{
						console.log(req)
					})
				})
      		},
      		addOperation(server){
      			let deleterDisable = ['deleter'];
      			let restoreDisable = ['restore'];
//    			let createDiskDisable = ['createDisk'];
      			let Operation = [{
        			value:i18n.bks.restore,
        			id:'restore',
        			fun:(data)=>{
        				return this.restore(data);
        			}
        		},{
//      			value:i18n.bks.createDisk,
//      			id:'createDisk',
//      			fun:(data)=>{
//      				return this.createDisk(data);
//      			}
//      		},{
        			value:i18n.bks.deleter,
        			id:'deleter',
        			fun:(data)=>{
        				return this.deleter(data);
        			}
        		}];
      			Operation.map(item=>{
      				'BackingUp' == server.state && deleterDisable.includes(item.id) && (item.disabled = true)
      				'BackingUp' == server.state && restoreDisable.includes(item.id) && (item.disabled = true)
//    				'BackingUp' == server.state && createDiskDisable.includes(item.id) && (item.disabled = true)
      			})
      			this.$set(server,'operation',Operation)
      		},
      		changeRegion(uuid){
      			this.regionId = uuid;
      			this.getBksList()
      		},
      		getRegion(){
      			let timer1;
      			if(getRegionId()){
      				clearTimeout(timer1)
					this.changeRegion();
				}else{
					let _this = this;
					timer1 = setTimeout(function(){
						_this.getRegion()
					},300)
				}
      		},
      		autoRefresh(jobIds){
      			if(jobIds){
      				jobIds.map(item =>{
      					console.log(item)
      					this.getJobDetail(item)
      				})
      			}
      		},
      		getJobDetail(id){
      			if(this.detailRefresh){
      				return
      			}
      			let _this = this;
      			timer && clearTimeout(timer);
      			let params={
	      			regionid:getRegionId()
	      		};
        		params.zoneId = getZoneId();
        		params.id = id;
        		console.log(params);
      			GetBksDetail(params).then(res=>{
  					let result = res.data.results[0];
  					console.log(result);
  					if(!(result.state == 'BackingUp')){
  						result.size = (result.size/1024/1024/1024).toFixed(2);
	      				result.statusDetail = statFormat(result.state) || result.state;
	      				result.selectCorrectAZ = result.zonename || '--';
	      				result.restartOrNot = result.zonename || '--';
	      				result.describe = result.description || '--';
	      				_this.addOperation(result);
  						_this.bksListData.map((item,index) =>{
  							if(item.id == result.id){
  								console.log(result);
  								_this.$set(_this.bksListData,index,result)
  								console.log(index,server)
  								return true;
  							}
  						})
  					}else{
						timer = setTimeout(()=>{
							_this.getJobDetail(id)
						},5000)
  					}
  					
  				}).catch(error=>{
  					console.log('error',error)
  					this.detailRefresh = true;
  					timer = setTimeout(()=>{
						this.refresh()
					},5000)
					
  				})
      		},
      		getBksList(){
      			let params = this.filter;
      			this.loading =true;
      			getBksList(params).then((res)=>{
      				console.log(res)
      				let servers = res.data.results;
      				let totalCount = res.data.total_count;
      				this.filterServers(servers);
      				this.bksListData = servers;
      				this.totalCount = totalCount;
      				this.Pagination.total = totalCount;
      				if(totalCount > this.Pagination.pagingData[0]){
      					this.Pagination.show = true;
      				}
      				this.loading=false;
      				let jobIds = [];
      				servers.filter((item)=>{
      					if(item.state == 'BackingUp'){
      						jobIds.push(item.id);
      					}else {
      						return false
      					}
      				});
      				if(jobIds.length > 0){
      					this.autoRefresh(jobIds);
      				}
      			}).catch((error)=>{
      				this.loading=false
      				console.log(error)
      			})
      		
      		},
      		filterServers(servers){
      			let _this = this;
      			servers.forEach(function(item){
//    				item.size = (item.size/1024/1024/1024).toFixed(2);
      				item.size = (item.size/1024/1024/1024).toFixed(2);
      				item.statusDetail = statFormat(item.state) || item.state;
      				item.selectCorrectAZ = item.zonename || '--';
      				item.restartOrNot = item.zonename || '--';
      				item.describe = item.description || '--';
      				_this.addOperation(item);
      			})
      		},
      		detailClick(id){
      			
      		},
      		search(filter){
      			console.log(filter)
      			if(filter.name == 'id'){
      				this.filter.status = ''
					this.filter.name=''
					this.filter.id=filter.value;
	//				this.filter[filter.name] = filter.value;
					this.filter.keyword = '';
      			}else{
      				this.filter.status = ''
					this.filter.name=''
					this.filter.id=''
	//				this.filter[filter.name] = filter.value;
					this.filter.keyword = filter.value;
      			}
				this.getBksList()
      		},
      		selectChange(value){
				this.filter.status =value;
				this.filter.page = 1;
				this.getBksList()
			},
      		refresh(){
      			this.filter.status = ''
		        this.filter.keyword = '',
		        this.filter.name=''
		        this.filter.id=''
      			this.getBksList()
      		},
      		pagingChange(type,val){
      			if(type == 'num'){
      				this.filter.limit = val;
      			}else if(type == 'page'){
      				this.filter.page = val;
      			}
      			this.getBksList();
      		}
		},
		computed:{
			
		},
		mounted(){
			this.getRegion()
		},
		beforeDestroy(){
			this.detailRefresh = true;
		}
	}
</script>

<style lang="scss" scoped="scoped">
  	.main{
  		background: #f6f8fb;
  	}
	.content{
	  	.content_top{
	  		background: #ffffff;
	  		padding-top: 30px;
	  		padding-left: 20px;
	  		padding-bottom: 20px;
	  	}
	}
</style>
<style lang="scss">
	.main{
		
	}
</style>


