<template>
	<div class="order">
		<Order-search :name='name' :btnText='btnText'></Order-search>
		<div class="list">
			<el-table :data="tableData"  style="width: 100%" :header-row-style = 'changeRowSty'
		    :header-cell-style = 'changethSty'>
			    <el-table-column prop="date" label="工单编号"></el-table-column>
			    <el-table-column prop="name" label="标题"></el-table-column>
			    <el-table-column prop="province" label="问题类型"></el-table-column>
			    <el-table-column
			      	prop="city" label="处理进度"
			      	:filters="[ { text: '待您反馈', value: '待您反馈' }, { text: '待您确认', value: '待您确认' }, { text: '正在处理', value: '正在处理' }, { text: '处理完成', value: '处理完成' }]"
      				:filter-method="filterTag"
      				filter-placement="bottom-end">
			    </el-table-column>
			    <el-table-column prop="address" label="提交时间"></el-table-column>
			    <el-table-column prop="zip" label="提交账号"></el-table-column>
			    <el-table-column label="操作">
			      	<template slot-scope="scope">
			        	<el-button @click="handleClick(scope.row)" type="text" size="small">查看</el-button>
			        	<el-button type="text" size="small">删除</el-button>
			      	</template>
			    </el-table-column>
			</el-table>
		</div>
	</div>
</template>

<script>
	import OrderSearch from '@/components/OrderSearch'
	import i18n from '@/lang'
	export default{
		components:{
			OrderSearch:OrderSearch
		},
		methods: {
      		handleClick(row) {
        		console.log(row);
      		},
      		filterTag(value, row) {
      			console.log(value,row);
      			return row.city === value;
      		}
    	},
		data(){
			return{
				name: i18n.order.submit_order,
				btnText:i18n.order.my_order,
				tableData:[
					{
			          	date: '2016-05-01',
			          	name: '王小虎',
			          	province: '上海',
			          	city: '待您确认',
			          	address: '上海市普陀区金沙江路 1518 弄',
			          	zip: 200333
        			},
        			{
			          	date: '2016-05-01',
			          	name: '王小虎',
			          	province: '上海',
			          	city: '待您反馈',
			          	address: '上海市普陀区金沙江路 1518 弄',
			          	zip: 200333
        			},
        			{
			          	date: '2016-05-01',
			          	name: '王小虎',
			          	province: '上海',
			          	city: '正在处理',
			          	address: '上海市普陀区金沙江路 1518 弄',
			          	zip: 200333
        			},
        			{
			          	date: '2016-05-01',
			          	name: '王小虎',
			          	province: '上海',
			          	city: '处理完成',
			          	address: '上海市普陀区金沙江路 1518 弄',
			          	zip: 200333
        			}
				],
			}
		},
    	computed:{
    		changeRowSty(row,index){
      			return{'background':'#f0f2f7'}
      		},
      		changethSty(row,index){
      			return{'background':'#f0f2f7','color':'#333333','font-size':'12px','font-weight':'600',}
      		},
    	}
	}
</script>

<style lang="scss" scoped="scoped">
	.order{
		.list{
			margin-top: 20px;
		}
	}
</style>
<style lang="scss">
	.order{
		background: #ffffff;
		padding-left: 20px;
		.el-table th>.cell.highlight{
			color: #000000!important;
		}
	}
</style>


