import i18n from '@/lang'

import Layout from '@/views/vpc/vpc'
import AclList from '@/views/vpc/aclList'


let Router ={
  path:'acl',
  name:'acl',
  component:Layout,
  redirect:'acl/aclList',
  children:[
    {
      path:'aclList',
      name:'aclList',
      component:AclList,
      props: { name: i18n.vpc.acl, buttonText: i18n.vpc.creatACL}
    },
  ]
};

export default Router;



