import Vue from 'vue'
import Router from 'vue-router'
import Layout from '@/views/Layout'
import Login from '@/views/login'
import ConsoleCenter from '@/views/dashboard/consoleCenter'
import Error from '@/views/dashboard/error'

import i18n from '@/lang'

import ecmRouters from './ecm'
import ebsRouters from './ebs'
import vpcRouters from './vpc'
import orderRouters from './order'
import elbRouters from './elb'
import logRouters from './log'
import costRouters from './cost'
import userRouters from './user'
import monitorRouter from './monitor'

Vue.use(Router)

//定义控制中心 登录 404 页面路由
let routers = [
    {
      path: '/',
      component: Layout,
      redirect:'/console',
      children:[
      	{
	    	path:'console',
	    	name:'console',
	    	component:ConsoleCenter
      	}
      ]
    },
    {
    	path:'/login',
    	name:'login',
    	component:Login
    },
    {
    	path:'*',
    	redirect:'/error/404',
    },
    {
      path:'/error/:errorCode',
      name:'error',
      component:Error
    }
];
//添加云服务器路由
routers.push(ecmRouters)
//添加云硬盘路由
routers.push(ebsRouters)
//添加虚拟私有云路由
routers.push(vpcRouters)
//添加订单路由
routers.push(orderRouters)
//添加负载均衡路由
routers.push(elbRouters)
//添加日志路由
routers.push(logRouters);
//添加费用中心路有
routers.push(costRouters);
//添加用户中心
routers.push(userRouters);
//云监控
routers.push(monitorRouter);

export default new Router({
  //mode:'history',
  scrollBehavior: () => ({ y: 0 }),
  routes: routers
})



