<template>
	<div style="background: #ffffff; min-height: 330px;">
		<div class="attach_content">
    		<ul class="ebs_operate">
    			<li class="left_title pull_left"></li>
    			<li class="pull_right">
    				<span class="btns_basic" @click="attach" v-if='attachShow'>挂载</span><span v-if='!attachShow' class="btns_basic" @click="detach">卸载</span>
    			</li>
    		</ul>
    	</div>
    	<!--<ConsoleTable 
    		:operationOrNot = 'operationOrNot' 
    		:checkBox='checkBox' 
    		:expand='expand' 
    		:selectOrNot='selectOrNot' 
    		:tableData = 'renderDetail' 
    		:tableOptions = 'tableOptions' 
    		:OperationEvents='ebsAttachPointOperation' 
    		@buttonClick = 'buttonClick'></ConsoleTable>-->
    		<ConsoleTable 
    		:operationOrNot = false
    		:checkBox='checkBox' 
    		:expand='expand' 
    		:selectOrNot='selectOrNot' 
    		:tableData = 'renderDetail' 
    		:tableOptions = 'tableOptions'></ConsoleTable>
	</div>
</template>

<script>
	import DetailBtn from '@/components/DetailBtn'
	import ConsoleTable from '@/components/ConsoleTable'
	import ConsoleAction from '@/components/ConsoleAction'
	import AttachAction from '@/views/ebs/AttachAction'
	import i18n from '@/lang'
	import { EbsDetach,EbsAttach } from '@/api/ebs'
	
	export default{
		props:{
			serverId:[String],
			ecmId:[String],
			attachShow:[Boolean],
			ebsType:[Boolean],
			expired:[Boolean],
			ecmDataList:[Object,Array],
			ebsDetailData:[Object,Array],
		},
		components:{
			DetailBtn,
			ConsoleTable,
			ConsoleAction,
			AttachAction,
		},
		data(){
			return{
				checkBox:true,
				selectOrNot:true,
//				operationOrNot:true,
				expand:false,
				attachDataList: [{
					name: '--',
		          	state: '--',
		          	id: '--',
		          	equipment:"--",
		          	addresses:'--',
		          	publicip:'--'
		        }],
				tableOptions:[{
	    			prop:'name',
	    			label:i18n.common.name,
	    			temp:true,
        			detail:true,
        			path:'/ecm/ecmDetail',
	    		},{
	    			prop:'stateTxt',
	    			label:i18n.common.status,
	    			temp:true,
        			status:true
	    		},{
	    			prop:'id',
	    			label:i18n.common.ID,
	    		},{
//	    			prop:'equipment',
//	    			label:i18n.ebs.equipment,
//	    		},{
//	    			prop:'addresses',
//	    			label:i18n.ebs.IPAddress,
//	    		},{
	    			prop:'publicip',
	    			label:i18n.common.eip,
	    		}],
//	    		ebsAttachPointOperation:[
//		        	{
//      				value:i18n.ebs.monitorData,
//      				fun:(data)=>{
//      					return this.monitorData(data);
//      				}
//      			}
//		        ],
	    	}
		},
		methods:{
			attach(){
      			console.log('调用挂载');
      			if(this.expired){
  					this.$message({
			          	message: '当前云硬盘已过期，请续订后再执行操作！',
			          	type: 'warning'
			        });
  					return
      			}
      			let attachDataOption = [{
        			prop:'name',
        			label:i18n.common.name,
        			temp:true,
        			detail:true,
        		},{
        			prop:'status',
        			label:i18n.common.status,
        			temp:true,
        			status:true
        		},{
        			prop:'templatedisplaytext',
        			label:i18n.common.ims,
        		},{
        			prop:'privateIp',
        			label:i18n.common.privateIp,
        		}];
				let attachDataSelectOptions = [{
		          	value: '',
		          	label: i18n.common.allStatus
		        }, {
		          	value: 'Running',
		          	label: i18n.common.active
		        }, {
		          	value: 'Stopped',
		          	label: i18n.common.shutOff
		        }];
      			let option = {
      				'data':{
      					'option':attachDataOption,
      					'title':'挂载磁盘',
      					'actionTip':'',
      					'data':'',
      					'selectOptions':attachDataSelectOptions,
      					'statusOnOff':true,
      					'optionsValue':'',
      				},
      				'view':AttachAction,
      				'form':{}
      			};
      			this.$alertDialog(option).then((res)=>{
  					console.log(res);
  					let volumed_id = this.$route.params.serverId;
  					let id = res.data.id;
  					EbsAttach(volumed_id,id).then((res)=>{
	      				console.log(res)
	      				this.$message({
				          	message: '命令下发成功',
				          	type: 'success'
				        });
				        setTimeout(()=>{
  							this.$emit('AttachRefresh','attach');
  						},3000)
	      			}).catch((error)=>{
	      				console.log(error);
	      			})
  				})
      		},
      		detach(){
      			console.log('调用卸载');
      			console.log(this.attachDataList);
      			if(!this.attachDataList){
      				return
      			}
      			if(this.expired){
  					this.$message({
			          	message: '当前云硬盘已过期，请续订后再执行操作！',
			          	type: 'warning'
			        });
  					return
      			}
      			if(this.ebsType){
  					this.$message({
				          	message: '系统盘不允许卸载！',
				          	type: 'warning'
				        });
  					return
  				}
      			
      			if(this.ebsDetailData.with_vm){
  					this.$message({
				          	message: '与云主机一起购买的云硬盘，不可卸载！',
				          	type: 'warning'
				        });
  					return
  				}
  				if(this.attachDataList[0].state == 'Stopped'){
  					let option = {
	      				'data':{
	      					'type':'warn',
	      					'option':'',
	      					'title':'卸载磁盘',
	      					'actionTip':['确定要卸载磁盘: ',this.ebsDetailData.name],
	      					'data':'',
	      					'warn':true
	      				},
	      				'view':ConsoleAction,
	      				'form':{},
	      			};
	      			this.$alertDialog(option).then((res)=>{
	  					let volumed_id = this.$route.params.serverId;
		              	let id = this.attachDataList[0].id;
						EbsDetach(volumed_id,id).then((res)=>{
		      				console.log(res)
		      				this.$message({
					          	message: '命令下发成功',
					          	type: 'success'
					        });
//					    	this.attachDataList.splice(0,1);
							setTimeout(()=>{
	  							this.$emit('AttachRefresh','detach');
	  						},3000)
					    	
		      			}).catch((error)=>{
		      				console.log(error);
//		      				this.$message({
//					          	message: '命令下发失败',
//					          	type: 'error'
//					        });
		      			})
	  				})
	      		}else{
  					this.$message({
			          	message: '该磁盘挂载的云服务器未处在关机状态,请先进行关机操作,再进行卸载操作。',
			          	type: 'error'
			        });
  				}
      		},
//    		monitorData(){
//				console.log('跳转查看监控数据');
//			},
//			buttonClick(funName,data){
//    			funName(data);
//    		},
		},
		computed:{
			renderDetail(){ 
				this.attachDataList = this.ecmDataList;
				console.log(this.attachDataList);
				return this.attachDataList;
			}
		}
	}
</script>

<style lang="scss" scoped="scoped">
	.attach_content{
		padding: 20px 0;
		line-height: 30px;
		.ebs_operate{
			display: table;
			content: '';
			width: 100%;
			.pull_left{
				float: left;
			}
			.left_title{
				font-size: 12px;
				padding-left: 20px;
				height: 30px;
				line-height: 30px;
			}
			.pull_right{
				float: right;
				.btns_basic{
					display: inline-block;
					width: 80px;
					height: 30px;
					background: #f9cd76;
					color: #ffffff;
					cursor: pointer;
					vertical-align: middle;
					text-align: center;
					line-height: 30px;
					outline: none;
					box-sizing: border-box;
					border-radius: 5px;
					font-size: 12px;
					margin-right: 10px;
				}
			}
		}
	}
</style>


