<template>
	<div>
		<RegionList :name ='name' :buttonText ='buttonText' @create='create' @changeRegion = 'changeRegion' :regionData='regionData' type ='os'></RegionList>
		<div class="content">
			<div class="content_tab">
				<el-radio-group v-model="filter.visibility" @change="changIms" size="medium">
					<el-radio-button v-for="item in imsTypeList" :label="item.value" :key="item.value">{{item.label}}</el-radio-button>
				</el-radio-group>
			</div>
			<div class="content_top">
				<ConsoleSearch 
					:optionsValue ='optionsValue' 
					@searchEvent = 'search'
					:statusOnOff=false 
					:PublicOperation="PublicOperation"
					@PublicOperEvent="aciton"
					:disableData='changeTableData'
					@refreshEvent = 'refresh'
					:NameOptions='NameOptions'
					:btnText='btnText'
					></ConsoleSearch>
			</div>
			<div v-if="quotaShow" class="content-quota">
				你还可以创建0个镜像
			</div>
			<div>
				<ConsoleTable 
					:checkBox='!filter.visibility' 
					:expand=true 
					:operationOrNot = true 
					:selectOrNot=true 
					:tableData = 'imsList' 
					@changeTable = 'changeTable'
					:tableOptions = 'options.tableOptions' 
					:detilOptions = 'options.detailOptions'
					:noEffect = true
					:OperationEvents='options.operation' 
					@buttonClick = 'handleClick'
					:loading = 'loading'
					:Pagination = 'Pagination' @pagingEvent = 'pagingChange'></ConsoleTable>
			</div>
			
		</div>
	</div>
</template>

<script>
	import service from '@/utils/request'
	import RegionList from '@/components/RegionList'
	import ConsoleSearch from '@/components/ConsoleSearch'
	import ConsoleTable from '@/components/ConsoleTable'
	
	import i18n from '@/lang'
	import { getEcmImages, EditImagesByOs, DeleteImageByOs, GetRegionNum } from '@/api/ecm'
	import {getRegionId } from '@/utils/auth'
	
	import createOnlyName from './createOnlyName'
	export default {
		components:{
			RegionList,
			ConsoleSearch,
			ConsoleTable
		},
		data (){
			return {
				name :'镜像',
				loading:false,
				quotaShow:false,
				regionData:[],
				buttonText:[{
					label:'创建私有镜像',
					id:'createPrivateIms',
					fun:()=>{
						return this.createPrivateIms()
					}
				}],
				imsTypeList:[{
					label:'公共镜像',
					value:1
				},{
					label:'私有镜像',
					value:0
				}],
				optionsValue:'',
				NameOptions:[{
		          	value: 'name',
		          	label: i18n.common.name,
		        }],
				PublicOperation:[
        			{
        				value:i18n.common.delete,
        				fun:(data)=>{
        					this.deleteKey(data)
        				}
        			}
        		],
				changeTableData:[],
				filter:{
					page:1,
					limit:10,
					id:'',
					name:'',
					visibility:1
				},
				imsList:[],
				Pagination:{
					'pagingData':[10,20,50],
		        	'total':0,
		        	'show':false
				},
			}
		},
		methods:{
			changeRegion(){
				service.cancle('CancelToken');
				this.getEcmImsList()
				this.getRegionNums()
			},
			refresh(){
				this.filter.name = ''
				this.filter.id = ''
				this.getEcmImsList()
				this.getRegionNums()
			},
			createPrivateIms(){
				window.open('/#/ecm/ims/createPrivateIms')
			},
			createEbs(rowData){
				console.log('申请云服务器')
				console.log(rowData);
				let win = window.open();
				win.opener = null;
				win.location.href = `/#/ecm/ecmCreate?imageid=${rowData.id}&visibility=${this.filter.visibility}`;
				//this.$router.push({path:'/ecm/ecmCreate',query:{imageid:rowData.id,visibility:this.filter.visibility}});
			},
			changIms(){
				this.quotaShow = !this.filter.visibility;
				this.getEcmImsList()
			},
			create(fun){
				fun()
			},
			editIms(rowData){
				let option = {
					data:{
						title:"修改镜像",
						path:'KeyPair',
						data:this.imsList,
						rowData,
						desc:true
					},
					form:{},
					view:createOnlyName,
				}
				this.$alertDialog(option).then(form=>{
					if(rowData.name == form.name && (rowData.description ? rowData.description == form.desc : form.desc == '')){
						return
					}
					form.id = rowData.id;
					console.log(form)
					//return
					const loading = this.$loading({
						lock: true,
				        text: '正在下发命令，请稍后……',
				        spinner: 'el-icon-loading',
				        background: 'rgba(0, 0, 0, 0.7)'
				    })
					EditImagesByOs(form).then(res=>{
						loading.close()
						this.$message({
							message:'镜像修改成功！',
							type:'success'
						})
						this.refresh()
					}).catch(error=>{
						loading.close()
						console.log(error)
					})
				})
			},
			deleteIms(rowData){
				let option ={
					data:{
						title:i18n.common.delete,
						actionTip:["确认删除镜像"+rowData.name+"吗？"],
						warn:true,
					}
				}
				this.$alertDialog(option).then(form=>{
					const loading = this.$loading({
						lock: true,
				        text: '正在下发命令，请稍后……',
				        spinner: 'el-icon-loading',
				        background: 'rgba(0, 0, 0, 0.7)'
				    })
					DeleteImageByOs(rowData.id).then(res=>{
						loading.close()
						this.$message({
							message:'镜像删除成功！',
							type:'success'
						});
						this.refresh()
					}).catch(error=>{
						loading.close()
						console.log(error)
					})
				})
				
			},
			search(val){
				this.filter[val.name] = val.value;
				this.getEcmImsList()
			},
			aciton(value,label){
				let deleteOption =[{
        			prop:'name',
        			label:i18n.common.name,
        			temp:true,
					linkProp:true,
        		},{
        			prop:'system',
        			label:i18n.ecm.systemName
        		},{
        			prop:'disk_size',
        			label:i18n.ebs.systemDiskSize
        		},{
        			prop:'created_at',
        			label:i18n.common.createAt
        		}];
				
				let option ={
					data:{
						title:i18n.common.delete,
						type:'warn',
						data:[this.changeTableData,[]],
						actionTip:['确定要删除一下镜像吗？','此镜像删除之后不可恢复，也不能用于共享和创建云主机'],
						option:deleteOption
					}
				}
				let imsList = [];
				this.changeTableData.map(item=>{
					imsList.push(item.id)
				});
				this.$alertDialog(option).then(form=>{
					const loading = this.$loading({
						lock: true,
				        text: '正在下发命令，请稍后……',
				        spinner: 'el-icon-loading',
				        background: 'rgba(0, 0, 0, 0.7)'
				    })
					DeleteImageByOs(imsList).then(res=>{
						loading.close()
						this.$message({
							message:'镜像删除成功！',
							type:'success'
						});
						this.refresh()
					}).catch(error=>{
						loading.close()
						console.log(error)
					})
				})
			},
			changeTable(val){
				this.changeTableData = val;
			},
			handleClick(funName,rowData){
      			funName(rowData)
      		},
			getEcmImsList(){
				this.loading = true;
				let params = this.filter;
				getEcmImages(params).then(res=>{
					let data = res.data.results;
					let count = res.data.total_count;
					this.Pagination.total = count;
					if(count > this.Pagination.pagingData[0]){
						this.Pagination.show = true
					}
					this.filterIms(data);
					this.imsList = data;
					this.loading=false
				}).catch(error=>{
					if(error.message == 'CancelToken'){
      					
      				}else{
      					this.loading=false
      				}
					console.log(error)
				})
			},
			getRegionNums() {
				let params = {
					resource: 'image',
					regionid: getRegionId()
				}
				GetRegionNum(params).then(res => {
					let data = res.data.results[0].IMAGE;
					console.log(data)
					if(data && data.detail) {
						this.regionData = data.detail;
					}

				})
			},
			filterIms(ims){
				/*let operation = [{
        			value:i18n.common.delete,
        			id:'deleteKey',
        			fun:(data)=>{
        				return this.deleteKey(data)
        			}
        		}];*/
        		let stateFormate = {
        			'active':'正常',
        			'queed':'创建中',
        		}
        		Array.isArray(ims) && ims.map(item=>{
        			item.size = item.size/1024/1024/1024;
        			item.stateTxts = item.status ? stateFormate[item.status] :'--'
        			item.system = item.os_type ? item.os_type == 1 ? 'Linux' : 'Windows' :'--';
        			item.source = item.instance_uuid ? `云服务器(<a class="linkTag" href="/#/ecm/ecmDetail/${item.instance_uuid}">${item.instance_name}</a>)` : '云服务器';
        			item.showNum = 2
        		})
        		
			},
			pagingChange(type,val){
				if(type == 'num'){
      				this.filter.limit = val;
      			}else if(type == 'page'){
      				this.filter.page = val;
      			}
      			this.loading =true;
      			this.refresh();
			}
		},
		computed:{
			options(){
				let option = {
					tableOptions:[],
					detailOptions:[],
					operation:[]
				}
				let tableOptions = [{
        			prop:'name',
        			label:i18n.common.name,
        			temp:true,
					linkProp:true,
        		},{
        			prop:'stateTxts',
        			label:i18n.common.status,
        		},{
        			prop:'system',
        			label:i18n.ecm.systemName
        		},{
        			prop:'disk_size',
        			label:i18n.ebs.systemDiskSize
        		}];
        		let detailOptions = [{
        			prop:'name',
        			label:i18n.common.name
        		},{
        			prop:'id',
        			label:i18n.ecm.imsUuid
        		},{
        			prop:'disk_size',
        			label:i18n.ebs.systemDiskSize
        		},{
        			prop:'system',
        			label:i18n.ecm.systemName
        		},{
        			prop:'created_at',
        			label:i18n.common.createAt
        		},{
        			prop:'description',
        			label:i18n.vpc.displaytext
        		}]
        		let operation = [{
        			value:'申请云服务',
        			fun:(data)=>{
        				return this.createEbs(data);
        			}
        		}];
        		let privateOperation=[{
        			value:'修改',
        			fun:(data)=>{
        				return this.editIms(data)
        			}
        		},{
        			value:'删除',
        			fun:(data)=>{
        				return this.deleteIms(data);
        			}
        		}]
        		let privateOption = {
        			prop:'created_at',
        			label:i18n.common.createAt
        		};
        		let privateDetailOption = [{
        			prop:'size',
        			label:i18n.ecm.imsSize
        		},{
        			prop:'source',
        			html:true,
        			label:i18n.ecm.imsSourece
        		}];
				if(this.filter.visibility==0){
					tableOptions.push(privateOption)
					operation = operation.concat(privateOperation);
					privateDetailOption.unshift(4,0)
					Array.prototype.splice.apply(detailOptions,privateDetailOption)
				}
				option.tableOptions = tableOptions;
				option.detailOptions = detailOptions;
				option.operation = operation;
				return option
			},
			btnText(){
				let text;
				if(this.filter.visibility == 1){
					text = "公共镜像说明<a href='#'>定价说明</a>"
				}
				return text
			}
		},
		mounted(){
			
			if(this.$route.query.visibility == 0){
				this.filter.visibility = this.$route.query.visibility
			}
			this.changeRegion()
		}
	}
</script>

<style scoped="scoped" lang="scss">
	.content{
		background: #ffffff;
		.content_tab{
			padding-left: 20px;
			padding-top: 30px;
			padding-bottom: 20px;
			
		}
	  	.content_top{
	  		padding-left: 20px;
	  		padding-bottom: 20px;
	  	}
	  	.content-quota{
	  		line-height: 30px;
	  		font-size:12px;
	  		padding-left:20px;
	  		padding-bottom: 10px;
	  	}
	}
</style>
<style lang="scss"> 
	.content_tab{
		.el-radio-button__orig-radio:checked+.el-radio-button__inner{
			background: #3ac0f1;
			border-color: #3ac0f1;
		}
	}
	
</style>


