
import Layout from '@/views/vpc/vpc'
import EipList from '@/views/vpc/EipList'
import EipCreate from '@/views/vpc/EipCreate'
import EipDetail from '@/views/vpc/EipDetail'



let Router ={
	path:'eip',
	name:'eip',
	component:Layout,
	redirect:'eip/eipList',
	children:[
	    {
	      path: 'eipList',
	      name: 'eipList',
	      component: EipList
	    },
	    {
	      path: 'eipCreate',
	      name: 'eipCreate',
	      component: EipCreate,
	      meta:{
      		dependOn:'vpcEipSub',
      		aisdeHide:true
      	  }
	    },
	    {
	      path: 'eipDetail/:serverId',
	      name: 'eipDetail',
	      component: EipDetail,
	      meta:{
      		dependOn:'vpcEipSub'
      	  }
	    }
	]
};

export default Router;



