<template>
	<div class="create-private">
		<div class="createfirst" v-if="!success">
			<el-row class="create-title">
				<span>创建镜像</span>
			</el-row>
			<el-row class="create-form">
				<el-form :model="ims" label-width="80px" size="mini" class="imsForm" ref="imsForm" :rules="rules" label-position="left">
					<el-form-item label="源 :" prop='sourceType'>
						<el-radio  v-model="ims.sourceType" v-for="item in ims.sourceTypes" :label="item.value" :key="item.value">{{item.label}}</el-radio>
					</el-form-item>
					<el-form-item label='云主机 :' class="imsForm-item" prop="ecm">
						<el-input v-model="ims.ecm.name" disabled @click.native="chooseEcm"></el-input>
						<el-button  class="chooseBtn" size="mini" @click.native="chooseEcm">选择云服务器</el-button>
					</el-form-item>
					<el-form-item v-if="ims.ecm.name" class="imsForm-item">
						<el-row class="ecm-detail">
							<el-col>云主机详情</el-col>
							<el-col>名称：{{ims.ecm.name || '--'}}</el-col>
							<el-col>操作系统：{{ims.ecm.templatedisplaytext || '--'}}</el-col>
							<el-col>系统盘：{{ims.ecm.rootSize || '--'}}</el-col>
						</el-row>
					</el-form-item>
					<el-form-item label="名称 :"  class="imsForm-item" prop="image_name">
						<el-input v-model="ims.image_name"></el-input>
					</el-form-item>
					<el-form-item label="描述 :" class="content-required">
						<el-input type="textarea" v-model="ims.desc" :rows="4" resize="none"></el-input>
					</el-form-item>
				</el-form>
			</el-row>
			<el-row class="create-btn">
				<el-button @click="cancel" size="medium">取消</el-button>
				<el-button @click='createPrivate' size="medium" class="iscreate">立即创建</el-button>
			</el-row>
		</div>
		<div class="createSecond submit_title" v-if="success">
			<ConsoleSuccess 
				:system_type = "successTip.system_type"
				:return_url = "successTip.returnUrl"
				:system_name = 'successTip.system_name'
				:params = "successTip.params"
				:orderWay=true
				></ConsoleSuccess>
		</div>
	</div>
</template>

<script>
	import ChooseEcm from './chooseEcm'
	import { GetEbsDetailById, CreateImageByOs,GetEcmDetail } from '@/api/ecm'
	import ConsoleSuccess from '@/components/ConsoleSuccess'
	export default {
		components:{
			ConsoleSuccess
		},
		data(){
			return {
				successTip:{
					system_type:'私有镜像',
					returnUrl:'/ecm/ims/imsList',
					params:{
						visibility:0
					},
					system_name:''
				},
				success:false,
				ims:{
					sourceType:'ecm',
					sourceTypes:[{
						value:'ecm',
						label:'云服务器'
					}],
					image_name:'',
					ecm:'',
					desc:''
				},
				rules:{
					sourceType:[{
						required:true,
						message:'请选择源',
						trigger:'blur,change'
					}],
					ecm:[{
						required:true,
						message:'请选择云服务器',
						trigger:'blur,change'
					}],
					image_name:[{
						required:true,
						message:'请填写镜像名称',
						trigger:'blur,change'
					}]
				}
			}
		},
		methods:{
			validateForm(){
				return this.$refs.imsForm.validate()
			},
			chooseEcm(){
				let option = {
					data:{
						title:'选择云服务器',
						actionTip:['1.当前关机状态的云主机才可以用来创建私有镜像，如果选择的云主机状态是"运行中",请先关机。请勿在创建镜像过程中对选择的云主机及相关联资源进行其他操作。<br/>2.如需使用一键式密码重置功能，需要安装密码重置插件。',"","用来创建私有镜像"]
					},
					view:ChooseEcm,
					form:{}
				}
				this.$alertDialog(option).then(form=>{
					console.log(form)
					this.ims.ecm  = form.rowData;
					this.getebsDetailById(form.rowData.id);
				})
				
			},
			getebsDetailById(id){
				GetEbsDetailById(id).then((res)=>{
					let volumes = res.data.results;
					let count = res.data.total_count;
					let root = volumes.find(item=>{
						return item.type !== 'DATADISK'
					});
					let rootsize = root ? (root.size + ' GB') : '--';
					this.$set(this.ims.ecm,'rootSize',rootsize)
				}).catch((req)=>{
					console.log(req)
				})
			},
			getEcmDetailById(id){
				GetEcmDetail(id).then(res=>{
					let server = res.data.results[0];
					server.templatedisplaytext = server.metadata && server.metadata.image_name;
					this.ims.ecm=server;
				})
			},
			createPrivate(){
				this.validateForm().then(res=>{
					let params = {
						instance_uuid:this.ims.ecm.id,
						image_name:this.ims.image_name,
						desc:this.ims.desc
					}
					const loading = this.$loading({
							lock: true,
					        text: '正在下发命令，请稍后……',
					        spinner: 'el-icon-loading',
					        background: 'rgba(0, 0, 0, 0.7)'
					    })	
					CreateImageByOs(params).then(res=>{
						loading.close()
						this.success = true
						this.successTip.system_name = this.ims.image_name;
					}).catch(error=>{
						console.log(error)
						loading.close()
					})
				}).catch(error=>{
					console.log(error)
				})
			},
			cancel(){
				this.$router.push({
					path:'/ecm/ims/imsList',
					query:{
						visibility:0
					}
				});
			}
		},
		computed:{
			
		},
		mounted(){
			let serverid = this.$route.query.instance_uuid
			if(serverid){
				this.getEcmDetailById(serverid)
				this.getebsDetailById(serverid)
			}
			
		}
	}
</script>

<style lang="scss" scoped="scoped">
.create-private{
	background: #FFFFFF;
	padding:0 20px 20px;
	height: 100%;;
	.create-title{
		font-size: 14px;
	    color: #333;
	    font-family: "microsoft yahei";
	    padding: 20px 0px
	}
	.imsForm{
		width:40%;
		.imsForm-item{
			.el-input{
				width:48%;
				display: inline-block;
			}
		}
	}
	.chooseBtn{
		margin-left: 15px;
		border: 1px solid #f9cd76;
	    border-radius: 4px!important;
		background: #f9cd76;
		color: #ffffff;
		
	}
	.create-btn{
		.el-button{
			padding: 8px 20px;
			background: #f0f2f7;
			border: 1px solid #f0f2f7;
			color: #666666;
		}
		.iscreate{
			background: #3ac0f1;
			border-color: #3ac0f1;
			color: #FFFFFF;
		}
	}
	.ecm-detail{
		border:1px solid #dcdfe6;
		border-radius: 4px;
		padding:10px;
		font-size:12px;
	}
}
</style>
<style lang="scss">
	.imsForm{
		.el-form-item__label{
			font-size:12px;
		}
	}
	
</style>


