<template>
	<div class='monitor_overview'>
		<el-container>
			<el-main class="p_t_l_0">
				<el-row :gutter="20">
				  <el-col :span="8" v-for="item in dashboarList" class="monitor-product" :key='item.bg'>
				  	<router-link tag="div" :to='item.path' class="grid-content bg-purple server m_b_20">
							<div class="monitor-product-grid">
								<i class="dashboar_icon monitor-product-grid-icon" :class="'monitor-img-'+item.bg"></i>
								<label class="monitor-product-grid-title">{{item.name}}</label>
								<span class="monitor-product-grid-number">
									{{item.num || 0}}
								</span>
							</div>
						</router-link>
				  </el-col>
				</el-row>
				<el-row :gutter="20">
				  <el-col :span="24"><div class="grid-content bg-purple alarm">
				  	<h4 class="alarm_title"><span class="p_l_20">告警概览</span></h4>
				  	<el-row :gutter="20">
				  		<el-col :span="8" class="scanning">
				  			<div class="scanning_chart">
						  		<div class="radar scanning_gif"><span class="scanning_num">5</span></div>
						  	</div>
						  	<label class="scanning_chart_tip">近7日报警总数</label>
				  		</el-col>
				  		<el-col :span="8" class="scanning">
				  			<div class="scanning_chart">
						  		<div class="radar scanning_gif"><span class="scanning_num">7</span></div>
						  	</div>
						  	<label class="scanning_chart_tip">正在报警</label>
				  		</el-col>
				  		<el-col :span="8" class="scanning">
				  			<div class="scanning_chart">
						  		<div class="radar scanning_gif"><span class="scanning_num">9</span></div>
						  	</div>
						  	<label class="scanning_chart_tip">数据不足</label>
				  		</el-col>
				  	</el-row>
				  </div></el-col>
				</el-row>
				<div class="p_t_b_15">资源概览</div>
				<el-row :gutter="20">
				  <el-col :span="12">
				  	<div class="grid-content bg-purple resources">
				  		<h4 class="alarm_title">
				  			<span class="p_l_20">主机</span>
				  			<el-button type="text" icon="el-icon-delete" class="f_right p_r_20 p_l_5"></el-button>
				  			<el-button type="text" icon="el-icon-edit" class="f_right"></el-button>
				  		</h4>
				  		<el-row :gutter="20" class="m_t_10">
				  			<el-col :span="8">
				  				<progress-circle class="center" :radius="radius" :percent="percent_cpu">
				  					<div class="percentage font_z_c">{{percentage_cpu}}</div>
				  					<div class="font_z_c m_t_10">CPU使用率（%）</div>
				  				</progress-circle>
				  			</el-col>
				  			<el-col :span="8">
				  				<progress-circle class="center" :radius="radius" :percent="percent_memory">
				  					<div class="percentage font_z_c">{{percentage_memory}}</div>
				  					<div class="font_z_c m_t_10">内存使用率（%）</div>
				  				</progress-circle>
				  			</el-col>
				  			<el-col :span="8">
				  				<progress-circle class="center" :radius="radius" :percent="percent_disk">
				  					<div class="percentage font_z_c">{{percentage_disk}}</div>
				  					<div class="font_z_c m_t_10">磁盘使用率（%）</div>
				  				</progress-circle>
				  			</el-col>
				  		</el-row>
				  	</div>
				  </el-col>
				  <el-col :span="12">
				  	<div class="grid-content bg-purple resources add_resources">
				  		<el-button type="text" icon="el-icon-circle-plus">添加资源项</el-button>
				  	</div>
				  </el-col>
				</el-row>
				<div class="p_t_b_15">实例监控预览（告警中）</div>
				<el-row :gutter="20">
				  <el-col :span="12">
				  	<div class="grid-content bg-purple lineChart">
				  		<echarts id="myChart" width='408px' height='280px' :monitorData="monitorData"></echarts>
				  	</div>
				  </el-col>
				  <el-col :span="12">
				  	<div class="grid-content bg-purple lineChart">
				  		<echarts id="myChart1" width='408px' height='280px' :monitorData="monitorData"></echarts>
				  	</div>
				  </el-col>
				</el-row>
			</el-main>
			<el-aside>
				<MainRight></MainRight>
			</el-aside>
		</el-container>
	</div>
</template>

<script>
import echarts from '@/components/Echarts'
import progressCircle from '@/components/progressCircle'
import MainRight from '@/views/dashboard/mainRight'
	export default {
		components:{
			echarts,
			progressCircle,
			MainRight
		},
		data (){
			return {
				msg :'云监控概览界面',
				radius: 75,
				percent_cpu: 0.0384,
				percent_memory: 0.2803,
				percent_disk: 0.0600,
				monitorData: {
					metric_name: '网络连接数',
					data: {
						xAxis_data: ['周一','周二','周三','周四','周五','周六','周日'],
						chartData: [220, 332, 901, , 1290, 130, 1320]
					},
					id: new Date().getTime()
				},
				dashboarList: [
					{
						name:'云服务器',
						bg:'ecm',
						path:'/ecm/ecmList',
						id:"VM"
					},
					{
						name:'弹性负载均衡',
						bg:'elb',
						path:'/elb/elbList',
						id:'elbs',
					},
					{
						name:'云硬盘',
						bg:'ebs',
						path:'/ebs/ebsList',
						id:"Volume"
					}
				],
			}
		},
		mounted(){
			
		},
		
		methods:{
			
		},
		computed: {
			percentage_cpu: function() {
				return (Math.round(this.percent_cpu * 10000) / 100.00 + "%");
			},
			percentage_memory: function() {
				return (Math.round(this.percent_memory * 10000) / 100.00 + "%");
			},
			percentage_disk: function() {
				return (Math.round(this.percent_disk * 10000) / 100.00 + "%");
			},
		},
		
	}
</script>

<style scoped lang="scss">
@import  './style.css';
.monitor_overview{
	.p_t_l_0 {
		padding-left: 0;
    	padding-top: 0;
	}
	.server {
		background: #fff;
    	height: 130px;
	}
	.alarm {
		background: #fff;
    	height: 210px;
	}
	.p_t_20 {
		padding-top: 20px;
	}
	.p_l_20 {
		padding-left: 20px;
	}
	.p_l_5 {
		padding-left: 5px;
	}
	.p_r_20 {
		padding-right: 20px;
	}
	.m_b_20 {
		margin-bottom: 20px;
	}
	.m_t_20 {
		margin-top: 20px;
	}
	.m_t_10 {
		margin-top: 10px;
	}
	.p_t_b_15 {
		padding: 15px 0;
	}
	.resources {
		background: #fff;
    	height: 160px;
	}
	.lineChart {
		background: #fff;
    	height: 280px;
	}
	.monitor-img-ebs{
		background: url(../../assets/ecm/ebsIcon.png) no-repeat center center;
	}
	.monitor-img-ecm{
		background: url(../../assets/ecm/ecmIcon.png) no-repeat center center;
	}
	.monitor-img-elb{
		background: url(../../assets/ecm/elbIcon.png) no-repeat center center;
	}
	.dashboar_icon {
		width: 40px;
    	height: 40px;
    	background-size: contain;
    	display: inline-block;
	}
	.monitor-product {
		line-height: 130px;
	}
	.monitor-product-grid {
		text-align: center;
	}
	.monitor-product-grid-icon {
		vertical-align: middle;
	}
	.monitor-product-grid-title {
		font-size: 16px;
	}
	.monitor-product-grid-number {
		font-size: 30px;
		vertical-align: sub;
		display: inline-block;
    	width: 60px;
    	text-align: right;
	}
	.scanning {
		height: 169px;
    	text-align: center;
	}
	.scanning_chart {
		width: 81.38px;
		margin: auto;
		padding-top: 30px;
	}
	.alarm_title {
		height: 40px;
    	line-height: 40px;
    	border-bottom: 1px solid #d5deed;
	}
	.scanning_chart_tip {
		padding-top: 20px;
		display: block;
	}
	.percentage {
		position: absolute;
	    width: 100%;
	    text-align: center;
	    top: 50%;
	    margin-top: -19px;
	}
	.center {
		text-align: center;
	}
	.font_z_c {
		font-size: 12px;
    	color: #333;
	}
	.add_resources {
		text-align: center;
    	line-height: 160px;
    	background: #fbfcfd;
	}
	.scanning_num {
		position: absolute;
	    margin-left: -40px;
	    top: 50%;
	    width: 80px;
	    margin-top: -8px;
	}
	.f_right{
		float: right;
		color: #d5deed;
	}
}
</style>


