<template>
	<div :loading='loading' class="action-content_createSub">
		<div v-if="type==='delete'">
      <svg-icon class="vpcDelete" icon-class="warn"></svg-icon>
      <div class="content">确定要删除{{form.name}}吗？</div>
    </div>
		<div v-if="type==='edit'">
			<el-form v-if="type==='edit'" size='small' :model="dataform" :rules='rules1' ref="dataform" label-width="100px" class="demo-ruleForm">
			    <el-form-item class="editName" label="子网名称 :" prop="name" required>
			        <el-input type="age" v-model.trim="dataform.name" auto-complete="off"></el-input>
			        <el-tooltip popper-class='editSubTip' content="不能使用中文,且长度为2-63字符" placement="bottom"><i class="el-icon-question"></i></el-tooltip>
			    </el-form-item>
		      	<el-form-item class="editName" label="描述 :" >
					<el-input v-model.trim="dataform.desc"></el-input>
			  	</el-form-item>
			  	<el-form-item class="editName" label="ACL :" prop="aclId" required v-if="regionType == 'cs'">
			        <el-select v-model='dataform.aclId' :disabled = "seleceCheckAcl">
			          	<el-option v-for = '(item,index) in changeAcllist'
			             :label='item.name' :value='item.id' :key="index">
			          	</el-option>
			        </el-select>
  				</el-form-item>
	    	</el-form>
		</div>
		<div v-if="type==='create'">
			<el-form :rules='rules' size='small' :model="dataform" ref="dataform" label-width="100px" class="subnet-create">
				<el-form-item class="vpc" label="VPC :" v-if="form.vpc">
		        	<el-input type="text" v-model="form.vpcname" auto-complete="off" disabled></el-input>
		        	<div class="mark">当前VPC网段 :{{vpcinfo.cidr ? vpcinfo.cidr : regionType == 'os' ? vpcinfo.description : '--'}}</div>
		      	</el-form-item>
				<el-form-item class="vpc" label="VPC :" prop="vpcid" v-if="!form.vpc">
					<el-select v-model='dataform.vpcid' :disabled = "vpcSeleceCheck" @change="getAclList">
						<el-option v-for = 'item in dataform.vpclist'
			                :label='item.name'
			                :value='item.id' :key="item.id"></el-option>
					</el-select>
					<div class="mark">当前VPC网段 :{{vpcinfo.cidr ? vpcinfo.cidr : '--'}}</div>
				</el-form-item>
				
				<el-form-item class="subType" label="子网类型：" prop="subType" required>
					<el-radio-group v-model="dataform.subType" size="medium">
				      	<el-radio :label="0">普通子网</el-radio>
				      	<!--<el-radio v-if='elbExist' :label="1">负载均衡</el-radio>-->
					</el-radio-group>
				</el-form-item>
  					
				<el-form-item class="name" label="子网名称 :" prop="sub_name">
					<!--<div class="subName_box">-->
						<el-input v-model.trim="dataform.sub_name"></el-input>
						<el-tooltip popper-class='createSubTip' content="不能使用中文,且长度为2-63字符" placement="top"><i class="el-icon-question"></i></el-tooltip>
					<!--</div>-->
				</el-form-item>
				
				<el-form-item class="desc" label="描述 :" >
					<el-input v-model.trim="dataform.desc"></el-input>
				</el-form-item>
				
				<el-form-item label="网段 :" prop="net">
					<div class="tiny-net">
			          <span class="tiny_input_ip_container valid_error_input" id="CidrIp">
			            <el-input 
			            	 v-on:keydown.native ="onkeydowns($event,0,'CidrIp')" :maxlength=3
			            	@change='changeSubnet'
			            	v-model="dataform.net.first" type="text" class="tiny_input_ip_anchor tiny_input_ip_octet"></el-input>
			                <span class="tiny_input_ip_division">.</span>
			            <el-input 
			            	 v-on:keydown.native ="onkeydowns($event,1,'CidrIp')" :maxlength=3
			            	@change='changeSubnet'
			            	v-model="dataform.net.second" type="text" class="tiny_input_ip_anchor tiny_input_ip_octet"></el-input>
			                <span class="tiny_input_ip_division">.</span>
			            <el-input 
			            	 v-on:keydown.native ="onkeydowns($event,2,'CidrIp')" :maxlength=3
			            	@change='changeSubnet'
			            	v-model="dataform.net.third"  type="text" class="tiny_input_ip_anchor tiny_input_ip_octet"></el-input>
			                <span class="tiny_input_ip_division">.</span>
			            <el-input 
			            	 v-on:keydown.native ="onkeydowns($event,3,'CidrIp')" :maxlength=3
			            	@change='changeSubnet'
			            	v-model="dataform.net.four" type="text" class="tiny_input_ip_anchor tiny_input_ip_octet"></el-input>
			           </span>
         				<span class="ng-scope">/</span>
			          	<span class="tiny-textbox-container valid_error_input" id="VpcPortal" style="width: 50px;">
				            <el-input 
				            	@keypress.native="replaceNum($event)"
				            	@blur='changeSubnet' :maxlength=2
				            	v-model="dataform.net.last" type="text" value="24" spellcheck="false" class="tiny-input-text " validator="true"></el-input>
			            </span>
		        	</div>
		        	
				</el-form-item>
				
				<el-form-item label="网关 :"prop="gateway">
					<div class="tiny-net">
			          <span class="tiny_input_ip_container valid_error_input" id="gateway">
			            <el-input 
			            	 v-on:keydown.native ="onkeydowns($event,0,'gateway')" :maxlength=3
			            	v-model="dataform.gateway.first" value="192" type="text" class="tiny_input_ip_anchor tiny_input_ip_octet"></el-input>
			                <span class="tiny_input_ip_division">.</span>
			            <el-input 
			            	 v-on:keydown.native ="onkeydowns($event,1,'gateway')" :maxlength=3
			            	v-model="dataform.gateway.second" value="168" type="text" class="tiny_input_ip_anchor tiny_input_ip_octet"></el-input>
			                <span class="tiny_input_ip_division">.</span>
			            <el-input 
			            	 v-on:keydown.native ="onkeydowns($event,2,'gateway')" :maxlength=3
			            	v-model="dataform.gateway.third" value="0" type="text" class="tiny_input_ip_anchor tiny_input_ip_octet"></el-input>
			                <span class="tiny_input_ip_division">.</span>
			            <el-input 
			            	 v-on:keydown.native ="onkeydowns($event,3,'gateway')" :maxlength=3
			            	v-model="dataform.gateway.four" value="0" type="text" class="tiny_input_ip_anchor tiny_input_ip_octet"></el-input>
			              </span>
			        </div>
					
				</el-form-item>
				
				<el-form-item class="acl" label="ACL :" prop="aclId" v-if="regionType == 'cs' && !form.acl">
					<el-select v-model='dataform.aclId' :disabled = "aclSeleceCheck">
			          <el-option v-for = 'item in dataform.acllist'
			            :label='item.name'
			            :value='item.id' :key="item.id"></el-option>
			        </el-select>
			        <!--<el-button @click="createACl" class="createACl">创建ACL</el-button>-->
				</el-form-item>
				
			</el-form>
			
			
		</div>
		
	</div>

</template>


<script>
  import ConsoleTable from '@/components/ConsoleTable'
  import { getName } from '@/utils/shareFunction'
  import aclDilogView from './aclDilogView'
  import { getAclList,getVpcList,getNetList,CreateAcl,queryQuota} from '@/api/vpc'
  
  import { ipValidatorSelf, maskAndSubnetValidator, prefix2mask, broadcastValidator, subnetValidator, getIpValue, ipValue2Str, nameRule  } from '@/utils/validator';
  
  export default {
    props:{
      type:[String],
      regionId:[String],
      actionTip:[Array],
      tableData:[Object],
      action:[String],
      loading:[Boolean],
      name:[String],
      form:[Object,String],
      thatVue:[Object]
    },
    components:{
      ConsoleTable
    },
    data(){
    	let _this = this;
    	let checkVpcCidr = (vpcCidr,ip,port)=>{
    		let cidr = vpcCidr.split('/');
	      	let mask = prefix2mask(cidr[1]);
	      	if(port < parseInt(cidr[1],10)) return !1;
	      	
	      	return subnetValidator(mask,ip,cidr[0])
	    }
        var checkCidr=(rule, value, callback)=>{
        	console.log(value)
        	
        	let vpc = _this.vpcinfo;
        	let vpcMask = parseInt(vpc.cidr.split('/')[1],10);
	        //let ip = value.first + '.' + value.second + '.' + value.third + '.' + value.four;
	        let sub_cidr = _this.sub_cidr;
	        let ip = sub_cidr.split('/')[0];
	        let port1 = sub_cidr.split('/')[1];
	        //检测输入的ip是否合法
            if(ipValidatorSelf(ip)){
        	//检测网段是否合理
        	
    		  //检测port取值范围是否合理
    		  let port = port1 && parseInt(port1);
    		  
    		  if(port>28 || port < vpcMask){
    			callback(new Error('掩码取值范围是'+vpcMask+'与28之间'))
    		  }else{
    			//子网ip与掩码匹配检测
    			
				let mask = prefix2mask(parseInt(port));
	        	let cidrResult = maskAndSubnetValidator(mask, ip);
	        	if(cidrResult){
	        		/*let vpc =  _this.dataform.vpclist.find((item)=>{
				      	return item.id == _this.dataform.vpcid
				    });*/
			      	//_this.vpcinfo = vpc;
			      	if(vpc && checkVpcCidr(vpc.cidr,ip,port)){
			      		callback()
			      		
			      	}else{
			      		callback(new Error('子网IP地址不在可用网段内。'))
			      	}
				      	
	        	}else{
	        		callback(new Error('网段IP地址与掩码不匹配。'))
	        	}
        	
    		}
        	
          }else{
        	  callback(new Error('IP地址不合法'))
          }
      };
      
      let checkGateway = (rule,value,callback)=>{
        let ip = _this.gateway_ip;
        //检测输入的ip是否合法
        if(ipValidatorSelf(ip)){
        	let sub_cidr = _this.sub_cidr.split('/');
        	let sub_cidrPort = parseInt(sub_cidr[1])
        	//校验网关是否合法
        	let mask = prefix2mask(sub_cidrPort)
					let subnet =sub_cidr[0];
					if (ip == subnet){
						callback(new Error('网关地址不在子网范围内。'))
					}else{
						let cidrResult = subnetValidator(mask, ip,subnet)
						console.log(cidrResult)
			        	if(cidrResult){
			        		let broadResut = broadcastValidator(subnet,sub_cidrPort,ip);
	            			if(broadResut){
	            				callback();
	            			}else{
	            				callback(new Error('广播地址不能作为网关。'))
	            			}
			        	}else{
			        		callback(new Error('网关地址不在子网范围内。'))
			        	}
					}
        }else{
        	callback(new Error('IP地址不合法'))
        }
        
      };
      var checkName=(rule, value, callback)=>{
      	
      	if(!value){
      		callback(new Error('请输入子网名称'))
      	}
      	
      	console.log(this.oldSubName);
      	console.log(value);
//    	let nameRule = /^[a-zA-Z][a-zA-Z0-9\-]*[a-zA-Z0-9]$/;
      	if(value.length > 63 || value.length < 2){
      		callback(new Error('长度为2-63字符'));
		}else if(nameRule(value)){
			callback(new Error('不能使用中文'));
      	}else{
	      	if(this.oldSubName == value){
	      		callback();
	      		this.form.edited = false;
	      	}else{
	      		this.form.edited = true;
	      		let params= {
		          id: '',
		          vpcid: this.dataform.vpcid,
		          name: value
		        }
		        console.log(value)
		        getNetList(params).then((res)=>{
		          let netlist = res.data.results;
		          if(netlist.length>0){
		            callback(new Error('同一vpc下的子网名称不能重复'));
		          }else{
		            callback();
		          }
		        }).catch((error)=>{
		          console.log(error)
		        })
	      	}
	    }
      };
      
      let checkAcl = (rule,value,callback)=>{
      	if(_this.dataform.aclId){
      		callback()
      	}else{
      		callback(new Error('所选ACL不能为空'))
      	}
      };
      let chooseVpc = (rule,value,callback)=>{
      	console.log(value);
      		let selectVpc = this._.find(this.dataform.vpclist, { 'id': value});
		    if(selectVpc){
		    	if(selectVpc.network.length < this.allowSubCount){
		    		callback()
		    	}else{
		    		callback(new Error('该vpc下子网配额已满！'))
		    	}
		    }
      };
      return {
        aclId:'',
        vpcSeleceCheck:true,
        aclSeleceCheck:true,
        seleceCheckAcl:true,
        elbExist:true,
        allowSubCount:0,
        SubCount:0,
        aclRemainCount:10,
        oldSubName:'',
        vpcinfo:'',
        changeAcllist: [],
        dataform:{
          name:'',
          desc:'',
          sub_name:'',
          subType:0,
          vpcid:'',
          aclId:'',
          net:{
            first:'',
            second:'',
            third:'',
            four:'',
            last:'24'
          },
          gateway:{
            first:'',
            second:'',
            third:'',
            four:'',
          },
          vpclist: [],
          acllist: [],
        },
        rules:{
        	vpcid:[
        		{ required: true, message: '所选VPC不能为空', trigger: 'blur,change' },
        		{validator: chooseVpc, trigger: 'blur'},
        	],
	        
          sub_name:[
            {validator: checkName, trigger: 'blur'},
            {required: true, message: '名称不能为空', trigger: 'blur' },
            {min: 3, max: 16, message: '长度在 3 到 16 个字符', trigger: 'blur'},
          ],
          net:[
            {validator: checkCidr, trigger: 'change,blur'},
            { required: true},
          ],
          gateway:[
            {validator: checkGateway, trigger: 'change,blur'},
            { required: true},
          ],
          /*acl:[
            {
            	required: true, message: '所选ACL不能为空', trigger: 'change'
            }
          ]*/
         aclId:[
         	{validator:checkAcl,trigger:'change,blur'},
         	{ required: true},
         ]
        },
        rules1:{
        	name:[
	          {validator: checkName, trigger: 'blur'},
	          {required: true, message: '名称不能为空', trigger: 'blur' },
	        ],
        }
      }
    },
    methods:{
	    setname(){
	    	if(this.type =='create'){
	    		this.dataform.sub_name='subnet-'+getName();
	    	}
	        if(this.type=='edit'){
	        	this.dataform.name = this.form.name;
	        	this.dataform.desc = this.form.desc;
	        	this.dataform.aclId = this.form.aclId;
	        }
	    },
	    changeForm(){
	      	this.form.sub_cidr = this.sub_cidr;
	      	this.form.gateway_ip = this.gateway_ip;
	      	this.form.vpcid = this.dataform.vpcid;
	      	this.form.desc=this.dataform.desc;
	        this.form.sub_name=this.dataform.sub_name;
	        this.form.aclid = this.dataform.aclId;
	        this.form.subType = this.dataform.subType;
	        if(this.type=='edit'){
	        	this.form.name = this.dataform.name;
	        	this.form.desc=this.dataform.desc;
	        	this.form.aclId=this.dataform.aclId;
	        }
	      },
	    validateForm(data){
	    	console.log(data);
    		this.changeForm()
		    if(this.type!=='delete'){
		        return this.$refs['dataform'].validate();
		    }else{
		        return new Promise((resolve,reject)=>{
		          resolve();
		        })
		    }
	      	
	    },
	    changeSubnet(){
					//自动填写子网和网关
			let _this = this;
			this.$refs['dataform'].validateField('net',function(res){
				if(!res){
					console.log(_this.dataform.net)
					let sub_cidr = _this.dataform.net
					let ip = sub_cidr.first + '.' + sub_cidr.second + '.' + sub_cidr.third + '.' + sub_cidr.four;
					_this.subnetAndGateway2Str(ip)
				}
				
			})
			
		},
		changeCidr(){
			let sub_cidr = _this.vpcinfo.cidr;
			let ip = sub_cidr.first + '.' + sub_cidr.second + '.' + sub_cidr.third + '.' + sub_cidr.four;
			_this.subnetAndGateway2Str(ip)
		},
		subnetAndGateway2Str(ip){
			let value = getIpValue(ip);
			//this.dataForm.sub_cidr.value = ipValue2Str(value);
			//this.replaceIp('sub_cidr',value)
			//this.dataForm.gateway_ip.value = ipValue2Str(value + 1);
			this.replaceIp('gateway',value+1)
		},
		replaceIp(opt,value){
			let val = ipValue2Str(value).split('.')
			//let val = this.dataForm[opt].value.split('.');
			this.dataform[opt].first = val[0];
			this.dataform[opt].second = val[1];
			this.dataform[opt].third = val[2];
			this.dataform[opt].four = val[3];
		},
		replaceNum(event){
			let code = window.event ? event.keyCode : event.which;
			if(!window.event){
				let arr = [8,9,110,37,39,0];
				if(arr.includes(code)) return
			}
			if(code==46){
				let next = event.target.parentElement.nextElementSibling ? event.target.parentElement.nextElementSibling.nextElementSibling.firstElementChild :'';
				next && next.focus()
				next && next.setSelectionRange(0,3);
			}
			code>=48 && code<=57 || event.preventDefault()
		},
		createACl(){
//			if(this.aclRemainCount < 0){
//	      		alertMsg('ACL配额不足');
//	      	}else{
	      		let alertMsg=this.$message;
		        let view = aclDilogView;
		        let data = {};
		        data.type='create';
		        data.title="创建ACL";
		        data.regionId=this.regionId;
		        let form = {
		          "name": "",
		          "desc": "",
		          "vpcid": ""
		        }
		        let option = {
		          data,
		          view,
		          form
		        }
		        this.$alertDialog(option).then((res)=>{
		          let data={
		            "desc": res.desc,  //#描述
		            "name": res.name,
		            "network_uuid": res.vpcid,//#上面zone列表中的第一个id
		          }
		          CreateAcl(data).then((response)=>{
		            let acl=response.data.results;
		            alertMsg('ACL创建成功');
		            this.refresh();
		
		          }).catch((error)=>{
		            console.log(error);
		//              this.$message('删除VPC失败，此VPC下存在子网！');
		          })
		        });
//	      	}
		},
		getAclList(vpcid){
			let data={
	          id:vpcid ||this.dataform.vpcid
	        };
	        if(this.form.edit){
	        	this.seleceCheckAcl = true;
	        }else{
	        	this.aclSeleceCheck = true;
		        this.elbExist = true;
		        if(this.form.vpc){
		        	if(this.form.vpcSubData.length > 0){
		        		this.form.vpcSubData.filter(item=>{
									if(item.networkofferingname == 'DefaultIsolatedNetworkOfferingForVpcNetworks'){
										this.elbExist = false;
							        }
								});
		        	}else{
		        		this.elbExist = true;
		        	}
		        }else{
		        	let vpcData = this._.find(this.dataform.vpclist,data);
			        vpcData['network'] && vpcData['network'].filter(item=>{
						if(item.networkofferingname == 'DefaultIsolatedNetworkOfferingForVpcNetworks'){
							this.elbExist = false;
					   	}
					});
		        }
		        console.log('vpcinfo')
		        console.log(this.form)
		        if(this.form.vpc){
		        	this.vpcinfo = this.form.vpcinfo;
		        }else{
		        	let vpc =  this.dataform.vpclist.find((item)=>{
			      		return item.id == this.dataform.vpcid
			      	});
							this.vpcinfo = vpc;
		        }
	        }
	        
		    getAclList(data).then((res)=>{
		        if(this.form.edit){
		        	this.changeAcllist = res.data.results;
		        	this.seleceCheckAcl = false
		        }else{
		        	this.dataform.acllist=res.data.results;
		        	this.aclSeleceCheck = false;
		        }
		    });
		},
		getVpcList(){
			this.vpcSeleceCheck = true;
			let params = {}
			this.regionType == 'os' && (params.detail = 1)
			getVpcList(params).then((res)=>{
		        let vpclist = res.data.results;
		        let vpcs = this.filterFakeVpc(vpclist);
		        if(!vpcs.length) return
		        this.dataform.vpclist= vpcs
		        this.dataform.vpcid = vpcs[0].id;
		        this.getAclList()
		        this.vpcSeleceCheck = false;
		        this.loading=false
		    }).catch((error)=>{
		        this.loading=false
		        console.log(error)
		    })
		},
		filterFakeVpc(vpcs){
			vpcs = vpcs.filter(item=>{
				return !(item.fake_vpc && item.fake_vpc == 1)
			})
			return vpcs
		},
		onkeydowns(event,num,target){
			let e = window.event || event; 
			let inputs=document.getElementById(target).getElementsByTagName("input");
			let valueLength = e.target.value.length;
			let InputValue = e.target.value - 0;
　 			let code = e.keyCode||e.which||e.charCode; 
			if((code >= 48 && code<=57) || (code >= 96 && code<=105)){
				if(valueLength >= 3){
					if(inputs[num].selectionStart == '0' && valueLength == inputs[num].selectionEnd && inputs[num].value ){
						return true
					}else{
						if(num <3){
							inputs[num+1].focus();
						}
					}
				}
				return true
			}else if((code == 39) || (code == 190)){
				let cursurPosition=-1;
				if(inputs[num].value || inputs[num].value == '0'){
					if(inputs[num].selectionStart || inputs[num].selectionStart == '0'){//非IE浏览器
						cursurPosition= inputs[num].selectionStart;
					}else{//IE
						let range = document.selection.createRange();
						range.moveStart("character",-inputs[num].value.length);
						cursurPosition=range.text.length;
					}
				}else{
					cursurPosition = 0;
				}
				console.log(inputs[num].selectionStart);
				if(cursurPosition == inputs[num].value.length){
					if(num <3){
						let pos = inputs[num+1].value.length;
						if(inputs[num+1].setSelectionRange){
							inputs[num+1].focus()
							inputs[num+1].setSelectionRange(0,0);
						}else if (inputs[num+1].createTextRange) {
							let range = obj.createTextRange();
							range.collapse(!0);
							range.moveEnd("character", pos);
							range.moveStart("character", pos);
							range.select();
						}
					}
				}
				if(code == 190){
					event.preventDefault()
				}
			}else if(code == 37){
				let cursurPosition=-1;
				if(inputs[num].value || inputs[num].value == '0'){
					if(inputs[num].selectionStart || inputs[num].selectionStart == '0'){//非IE浏览器
						cursurPosition= inputs[num].selectionEnd;
					}else{//IE
						let range = document.selection.createRange();
						range.moveStart("character",-inputs[num].value.length);
						cursurPosition=range.text.length;
					}
				}else{
					cursurPosition = 0;
				}
				console.log(cursurPosition,inputs[num].selectionEnd);
				if(cursurPosition == '0'){
					if(num > 0){
						let ele = inputs[num-1];
						let pos = ele.value.length;
						if(ele.setSelectionRange){
							setTimeout(function(){
								ele.focus()
								ele.selectionStart = pos;
								ele.selectionEnd = pos;
								ele.setSelectionRange(pos,pos);
							},10)
						}else if (ele.createTextRange) {
							setTimeout(function(){
								let range = obj.createTextRange();
								range.collapse(!0);
								range.moveEnd("character", pos);
								range.moveStart("character", pos);
								range.select();
							},10)
						}
					}
				}
			}else if(code == 8){
				let cursurPosition=-1;
				let inputVaLen = inputs[num].value.length;
				if(inputs[num].value || inputs[num].value == '0'){
					if(inputs[num].selectionStart || inputs[num].selectionStart == '0'){//非IE浏览器
						cursurPosition= inputs[num].selectionStart;
					}else{//IE
						let range = document.selection.createRange();
						range.moveStart("character",-valueLength);
						cursurPosition=range.text.length;
					}
				}else{
					cursurPosition = 0;
				}
				
				if(cursurPosition == 0){
					if(inputs[num].selectionStart == '0' && inputVaLen == inputs[num].selectionEnd && inputs[num].value ){
						return true
					}else{
						if(num > 0){
							inputs[num-1].selectionStart = inputs[num-1].value.length;
							inputs[num-1].focus()
						}
					}
				}
				return true
			}else if(code == 9){
				return true
			}else{
				event.preventDefault()
			}
		},
		changValue(event,num,target){
			let e = window.event || E;
			console.log(e);
			let valueLength = e.target.value.length;
			let InputValue = e.target.value - 0;
//			if(InputValue > 255){
//				setTimeout(function(){
//					inputs[num].value = '255';
//				},10);
//			}
		},
		queryQuotas(){
	      	queryQuota().then((res)=>{
	          console.log(res.data.results);
	          let Quota = res.data.returnObj;
//	          this.allowSubCount = Quota.network_limit_each_vpc - 0;
				this.allowSubCount =Quota.network_limit_each_vpc ? Quota.network_limit_each_vpc - 0 :'';
	        }).catch((error)=>{
	          console.log(error)
	        })
	    },	
    },

    computed:{
		sub_cidr(){
			let net = this.dataform.net
			let sub_cidr = net.first + '.' + net.second + '.' +net.third +'.' + net.four + '/' +net.last;
			return sub_cidr;
		},
		gateway_ip(){
			let gateway = this.dataform.gateway;
			let gateway_ip = gateway.first + '.' + gateway.second + '.' +gateway.third +'.' + gateway.four;
			return gateway_ip;
		},
		regionType(){
			return this.thatVue.$store.getters.regionType;
		}
    },
    created(){
    	console.log(this.form)
    	console.log(this.type)
    	if(this.type == 'delete'){
    		return
    	}
    	this.setname();
    	if(this.type=='edit'){
    		this.oldSubName = this.form.name;
    		this.getAclList(this.form.vpcid);
    	}else if(this.type == 'create' && this.form.vpc){
    		this.getAclList(this.form.vpcid);
    		this.dataform.vpcid = this.form.vpcid;
    	}else{
    		this.getVpcList();
//  		this.queryQuotas();
//			let quotas = this.thatVue.$store.state.user.user.quotas;
			let quotas = this.form.quotas;
			if(quotas.network_limit_each_vpc){
				this.allowSubCount = quotas.network_limit_each_vpc - 0;
			}else{
				this.queryQuotas();
			}
			
    	}
    }
  }
</script>

