<template>
	<div class="billInfo">
		<el-container v-if="!isShowEcs&&!isShowBatchEcs">
			<el-header height="80px">
				<h4 class="bill_info_title">云服务器续费</h4>
				<el-date-picker
					v-model="filter.expire_time"
					placeholder="选择日期" 
					value-format="yyyy-MM-dd" 
					size="small" style="width:200px">
				</el-date-picker>
				<el-select v-model="node" size="small" placeholder="节点" style="width:150px;" @change="nodeChange">
					<el-option
					    v-for="item in nodeList"
					    :key="item.uuid"
					    :label="item.name"
					    :value="item.uuid">
					</el-option>
				</el-select>
				<el-select v-model="type" size="small" placeholder="类型" style="width:100px">
					<el-option
					    v-for="item in exampleList"
					    :key="item.value"
					    :label="item.label"
					    :value="item.value">
					</el-option>
				</el-select>
				<el-input v-model="filter.id" placeholder="输入实例ID精确查找" size="small" style="width:200px;"></el-input>
				<el-button type="primary" size="small" @click.stop="search">搜索</el-button>
				<el-button type="primary" size="small" class="right">导出续费清单</el-button>
				<el-button size="small" class="right" @click.stop="batchRenew">批量续费</el-button>
			</el-header>
			<el-main>
				<ConsoleTable :checkBox=true :operationOrNot =true :selectOrNot=true 
					:tableData = 'tableData' :tableOptions = 'tableOptions' 
					:OperationEvents='ebsOperation' :loading = 'loading' 
					:Pagination = 'Pagination' @pagingEvent = 'pagingChange' @buttonClick='options_callback' 
					@changeTable="changeTable"></ConsoleTable>
			</el-main>
		</el-container>
		<ecsRenew v-if="isShowEcs" :dataList="nodeItem" @callback="renewCallback"></ecsRenew>
		<ecsBatchRenew v-if="isShowBatchEcs" :dataList="checkData" @callback="batchRenewCallback"></ecsBatchRenew>
	</div>
</template>

<script>
import ConsoleTable from '@/components/ConsoleTable'
import ecsRenew from './renewPay'
import ecsBatchRenew from './batchRenewPay'
import {getEcsServerList} from '@/api/cost'
	export default {
		components:{
			ConsoleTable:ConsoleTable,
			ecsRenew,
			ecsBatchRenew
		},
		data (){
			return {
				isShowEcs: false,
				isShowBatchEcs: false,
				tableData: [],
				nodeItem: {},
				type: '',
				node: '',
				loading:false,
				checkData:[],
				Pagination:{
		        	'pagingData':[5,10,15],
		        	'total':0,
		        	'show':true
		        },
		        nodeList: [
			        {
			        	uuid: '',
						name: '全部节点'
			        },
		        ],
		        exampleList: [
			        {
			        	value: '',
						label: '实例ID'
			        },
		        ],
		        filter:{
					limit:5,
					page:1,
					regionid: '',
					zoneid: '',
					list_all: 1,
					expire_time: '',
					id:'',
				},
				tableOptions: [
					{
						prop: "displayname",
						label: "实例名称",
						temp: true,
						btn:true,
        				fun: (data) => {
        					return this.renew(data);
        				}
					},
					{
						prop: "dc_name",
						label: "节点"
					},
					{
						prop: "publicip",
						label: "公网IP",
						isEmpty: true,
						empty_st: '未绑定'
					},
					{
						prop: "nic[0].ipaddress",
						label: "内网IP",
						isIpaddress: true,
						empty_st: '未绑定'
					},
					{
						prop: "expire_date",
						label: "产品到期时间"
					},
					{
						prop: "expire_days",
						label: "倒计时",
						companyDay: true,
						company_st: '天',
					},
					{
						prop: "state",
						label: "自动续费／续费周期",
						width: 150,
						replace: true,
					}
				],
				ebsOperation:[
					{
	        			value: '续费',
	        			fun:(data) => {
	        				return this.renew(data);
	        			}
	        		},{
	        			value: '开通自动续费',
	        			fun:(data)=>{
	        				return this.autoRenew(data);
	        			}
	        		}
				]
			}
		},
		methods: {
			pagingChange(type,val){
      			if(type == 'num'){
      				this.filter.limit = val;
      			}else if(type == 'page'){
      				this.filter.page = val;
      			}
      			this.getEcsServerData();
      		},
      		getEcsServerData(){
      			this.loading = true;
      			getEcsServerList(this.filter).then(res => {
      				console.log(res);
      				if(res.status == 200){
						this.tableData = res.data.results;
						this.Pagination.total = res.data.total_count;
					}else{

					}
					this.loading = false;
      			},error => {
      				this.loading = false;
      				console.log("获取云服务续费列表错误");
      			})
      		},
      		renew(data) {
      			//this.$router.push('/cost/EipRenew');
      			this.nodeItem = {
      				titleName: '云服务器当前配置',
      				name: data.displayname,
      				expire_date: data.expire_date,
      				id: data.id,
      				nodeList: [
      					{
      						title: '实例ID',
							value: data.id,
      					},
      					{
      						title: '网络类型',
							value: '专用网络',
      					},
      					{
      						title: '系统盘',
							value: data.memory + 'GB',
      					},
      					{
      						title: '地域',
							value: data.dc_name,
      					},
      					{
      						title: '交换机',
							value: 'dads/dsfsd',
      					},
      					{
      						title: '操作系统',
							value: data.templatename,
      					},
      					{
      						title: '付费类型',
							value: '包年包月',
      					},
      					{
      						title: '内网IP',
							value: data.nic[0].ipaddress,
      					},
      					{
      						title: '数据盘',
							value: '无',
      					},
      					{
      						title: '实例规格',
							value: '无',
      					},
      					{
      						title: '公网IP',
							value: data.publicip,
      					},
      				]
      			};
      			this.isShowEcs = true;
      		},
      		autoRenew(data) {
      			console.log(data);
      		},
      		batchRenew() {
      			if(this.checkData.length == 0){
      				this.$message({
				          showClose: true,
				          message: '请选择批量续费的云服务器',
				          type: 'warning'
				        });
      			}else{
      				//this.$router.push('/cost/EcsRenew');
      				this.isShowBatchEcs = true;
      			}
      		},
      		renewCallback() {
      			this.isShowEcs = false;
      			this.getEcsServerData();
      		},
      		batchRenewCallback() {
      			this.isShowBatchEcs = false;
      			this.checkData = [];
      			this.getEcsServerData();
      		},
      		options_callback(fun,data){
      			fun(data);
      		},
      		changeTable(data) {
      			this.checkData = data;
      		},
      		nodeChange(val) {
      			if(val == ''){
      				this.filter.list_all = 1;
      			}else{
      				var nodeData = this._.find(this.nodeList,{uuid:val});
      				this.filter.regionid = nodeData.uuid;
      				this.filter.zoneid = nodeData.zoneid;
      				this.filter.list_all = undefined;
      			}
      			this.getEcsServerData();
      		},
      		search(){
      			if(this.id == ''){
      				this.filter.list_all = 1;
      			}else{
      				this.filter.list_all = undefined;
      			}
      			this.getEcsServerData();
      		}
		},
		mounted: function(){
			this.nodeList = this.nodeList.concat(this.$store.state.user.regionList);
			this.filter.regionid = this.nodeList[1].uuid;
			this.getEcsServerData();
		}
	}
</script>

<style lang="scss" scoped="scoped">
.billInfo {
	background-color: #fff;
	height: 100%;
	.paging {
		text-align: right;
	    margin-top: 20px;
	}
	.tableHeaderbg {
		background-color: #f0f2f6 !important;
		color: #333;
	}
	.bill_info_title {
		margin: 15px 0;
	}
	.bill_info_tip {
		color: #f9cd76;
		font-size: 12px;
		margin-bottom: 15px;
	}
	.right {
		float: right;
	}
	.el-button--primary {
		background-color: #3dc7fa;
	    border-color: #3dc7fa;
	}
}
</style>


