<template>
	<div class="nameCreate">
		<el-form :model="name" label-width='20%' size="mini" :rules='rules' ref="actionForm">
			<el-form-item :label='labelName +" :"' prop="name" >
				<el-input v-model.trim="name.name" style="width:200px;"></el-input>
				<el-tooltip popper-class='editSnapTip' content="不能使用中文,且长度为2-63字符" placement="bottom"><i class="el-icon-question"></i></el-tooltip>
			</el-form-item>
			<el-form-item v-if="tableData.desc" label="描述 :">
				<el-input v-model.trim="name.desc" style="width:200px;"></el-input>
			</el-form-item>
		</el-form>
		
	</div>
</template>

<script>
	import i18n from '@/lang';
	import { nameRule } from '@/utils/validator';
	import {getName} from '@/utils/shareFunction';
	export default {
		props:{
			form:[Object],
			tableData:[Object]
		},
		data(){
			let that = this;
			let CheckName = function(rule,value,callback){
				if(value.length > 63 || value.length < 2){
		      		callback(new Error('长度为2-63字符'));
      			}else if(nameRule(value)){
      				callback(new Error('不能使用中文'));
		      	}else{
		      		let nameCheck = false;
		      		if(that.tableData.desc){
		      			let rowData = that.tableData.rowData;
		      			if(rowData.description != that.name.desc && rowData.name == that.name.name){
		      				nameCheck = true;
		      			}
		      		}
		      		
		      		if(nameCheck){
		      			callback()
		      		}else{
		      			let hasName = that.tableData.data.find(item=>{
			      			return item.name == value
			      		});
			      		if(hasName){
			      			callback(new Error('名字有重复，请重新输入'))
			      		}else{
			      			callback()
			      		}
		      		}
		      	}
			}
			return {
				name:{
					name:'',
					desc:''
				},
				rules:{
					name:[
						{ required: true, message: "请输入名称", trigger: 'blur' },
						{ validator:CheckName,trigger: 'blur'}
					]
				}
			}
			
		},
		computed:{
			labelName(){
				return this.tableData.labelName || '名称'
			}
		},
		methods:{
			validateForm(){
				this.form.name = this.name.name;
				if(this.tableData.desc){
					this.form.desc = this.name.desc;
				}
				return this.$refs['actionForm'].validate()
			}
		},
		mounted(){
			if(this.tableData.path){
				this.name.name = this.tableData.path + '-' + getName();
			}
			if(this.tableData.desc){
				this.name.name = this.tableData.rowData.name;
				this.name.desc = this.tableData.rowData.description
			}
		}
	}
</script>
<style scoped="scoped" lang="scss">
/*	.nameCreate{
		min-height: 70px;
	}*/
</style>
<style lang="scss">
	.nameCreate{
		.el-form-item{
			
			label.el-form-item__label{
				font-size:12px;
			}	
		}
	} 
</style>


