

import Layout from '@/views/Layout'
import AllLog from '@/views/log/OperationLog'
import LogReport from '@/views/log/LogReport'

let Router ={
	path:'/log',
	name:'log',
	component: Layout,
	redirect: '/log/allLog',
	children:[
		{
      		path: 'allLog',
      		name: 'allLog',
      		component: AllLog,
      	},{
      		path:'logReport',
      		name:'logReport',
      		component:LogReport,
      	},
	]
};

export default Router;



