<template>
	<div class="seclist">
		<RegionList :name ='name' :buttonText ='buttonText' @create='create' @changeRegion = 'changeRegion' :regionData='regionData' type ='os'></RegionList>
		<div class="seclist-quota">
			您可以创建99个安全组，8888个安全组规则
		</div>
		<div class="seclist-top">
			<ConsoleSearch 
				:optionsValue ='searchModel.optionsValue' 
				@searchEvent = 'searchModel.fun'
				:statusOnOff=false 
				:PublicOperation="PublicOperation"
				@PublicOperEvent="aciton"
				:disableData='changeTableData'
				@refreshEvent = 'refresh'
				:NameOptions='searchModel.nameOptions'
				></ConsoleSearch>
		</div>
		<div class="seclist-table">
			<ConsoleTable 
				:checkBox=true
				:operationOrNot = true 
				:selectOrNot=true 
				:tableData = 'secList' 
				@changeTable = 'changeTable'
				:tableOptions = 'options.tableOptions' 
				@buttonClick = 'handleClick'
				:dragable=true
				:OperationWidth="250"
				:loading = 'loading'
				:Pagination = 'Pagination' @pagingEvent = 'pagingChange'></ConsoleTable>
		</div>
	</div>
</template>

<script>
	import service from '@/utils/request';
	import RegionList from '@/components/RegionList';
	import ConsoleSearch from '@/components/ConsoleSearch';
	import ConsoleTable from '@/components/ConsoleTable';
	
	import secAddrules from './secAddrules'
	
	import i18n from '@/lang';
	import { GetRegionNum } from '@/api/ecm';
	import { getAclList, addRule, editAcl, deleteAcls} from '@/api/vpc';
	import { getRegionId } from '@/utils/auth';
	
	import createOnlyName from '@/views/ecm/createOnlyName'
	export default {
		components:{
			RegionList,
			ConsoleSearch,
			ConsoleTable
		},
		data (){
			return {
				name :'安全组',
				loading:false,
				regionData:[],
				buttonText:[{
					label:'创建安全组',
					id:'createSec',
					fun:()=>{
						return this.createSec()
					}
				}],
				PublicOperation:[
        			{
        				value:i18n.common.delete,
        				disable:false,
        				fun:(data)=>{
        					this.deleteSec(data)
        				}
        			}
        		],
        		searchModel:{
					optionsValue:'',
					nameOptions:[{
			          	value: 'name',
			          	label: i18n.common.name,
			        },{
			          	value: 'id',
			          	label: i18n.common.ID,
			        }],
			        fun:(val)=>{
			        	return this.search(val)
			        }
				},
        		changeTableData:[],
				filter:{
					page:1,
					limit:10,
					id:'',
					name:'',
					visibility:1
				},
				secList:[],
				Pagination:{
					'pagingData':[10,20,50],
		        	'total':0,
		        	'show':false
				},
				options:{
					tableOptions:[{
						prop: 'name',
						label: i18n.vpc.secName,
						width: '135px',
						temp: true,
						detail: true,
						path: '/vpc/sec/secDetail',
					},{
						prop: 'id',
						label: i18n.common.ID,
					},{
						prop: 'ecms',
						label: i18n.vpc.connectEcm,
					},{
						prop: 'created_at',
						label: i18n.common.createAt,
					},{
						prop:'description',
						label: i18n.vpc.displaytext
					}]
				}
			}
		},
		methods:{
			changeRegion(){
				service.cancle('CancelToken');
				this.getSecList()
				this.getRegionNums()
			},
			refresh(){
				this.filter.name = ''
				this.filter.id = ''
				this.getSecList()
				this.getRegionNums()
			},
			create(fun){
				fun()
			},
			handleClick(funName,rowData){
      			funName(rowData)
      		},
			search(val){
				this.filter[val.name] = val.value;
				this.getSecList()
			},
			createSec(){
				window.open('/#/vpc/sec/secCreate')
			},
			deleteSec(rowData){
				let option={
					data:{
						title:'删除安全组',
						actionTip:[`确认删除安全组${rowData.name}吗?`],
						warn:true
					},
				}
				this.$alertDialog(option).then(form=>{
					let params = {
						firewall_uuid:[]
					}
					params.firewall_uuid.push(rowData.id)
					this.$message({
						type:"info",
						message:"正在删除安全组，请稍后……"
					})
					deleteAcls(params).then(res=>{
						this.$message({
							type:"success",
							message:"删除成功！"
						})
						this.refresh()
					}).catch(error=>{
						console.log(error)
					})
				})
			},
			editSec(rowData){
				let option ={
					data:{
						title:'修改安全组',
						rowData,
						data:this.secList,
						desc:true
					},
					view:createOnlyName,
					form:{},
				}
				this.$alertDialog(option).then(form=>{
					let params = {
						name:form.name,
						description:form.desc
					}
					this.$message({
						type:"info",
						message:"正在修改安全组，请稍后……"
					})
					editAcl(rowData.id,params).then(res=>{
						console.log(res)
						this.$message({
							type:"success",
							message:"修改成功！"
						})
						this.refresh()
					}).catch(error=>{
						console.log(error)
					})
				})
			},
			aciton(value,label){
				console.log(value,label)
				let actionDate = this.changeTableData.filter(item=>{
					return !item.default;
				})
				let forbidDate = this.changeTableData.filter(item=>{
					return item.default;
				})
				let deleteOption =[{
        			prop:'name',
        			label:i18n.common.name,
        		},{
        			prop:'description',
        			label:"描述"
        		},{
        			prop:'ecms',
        			label:"相关云服务器"
        		}];
				let option={
					data:{
						title:'删除安全组',
						type:'warn',
						actionTip:['确认删下安全组吗?','删除安全组，云服务器将不受此安全组规则的保护。'],
						data:[actionDate,forbidDate],
						option:deleteOption
					}
				}
				this.$alertDialog(option).then(form=>{
					let params = {
						firewall_uuid:[]
					}
					actionDate.map(item=>{
						params.firewall_uuid.push(item.id)
					})
					this.$message({
						type:"info",
						message:"正在删除安全组，请稍后……"
					})
					console.log(params)
					return
					deleteAcls(params).then(res=>{
						this.$message({
							type:"success",
							message:"删除成功！"
						})
						this.refresh()
					}).catch(error=>{
						console.log(error)
					})
				})
			},
			addRules(rowData,fast){
				console.log(rowData)
				let option = {
					data:{
						title:'增加规则',
						fast:false
					},
					view:secAddrules,
					ref:'addRest'
				}
				if(fast){
					option.fast = true;
					option.data.title="快速增加规则";
				}
				/*this.$dialog({
					view:secAddrules,
					title:'增加规则',
					validateName:'validateForm',
					props:{
						form:{}
					},
					dataName:'form'
				},(data)=>{
					console.log(data)
					this.$message({
						type:"info",
						message:"正在增加规则，请稍后……"
					})
				})*/
				/*this.$dialog({
					title:'开机',
					actionTips:[{
						template:'测试'
					}]
				},(data)=>{
					console.log(data)
					this.$message({
						type:"info",
						message:"正在增加规则，请稍后……"
					})
				})
				return*/
				this.$alertDialog(option).then(resForm=>{
					this.$message({
						type:"info",
						message:"正在增加规则，请稍后……"
					})
					let params = resForm.data;
					params.security_group_id = rowData.id;
					return 
					addRule(params).then(res=>{
						console.log(res);
						this.$message({
							type:'success',
							message:'规则增加成功'
						})
					})
				})
			},
			changeTable(val){
				this.changeTableData = val;
				let deleteData = val.filter(item=>{
					return !item.default
				})
				if(deleteData.length){
					this.PublicOperation.map(item=>{
						item.disable = true
					})
				}else{
					this.PublicOperation.map(item=>{
						item.disable = false
					})
				}
					
				
			},
			addOperation(sec){
				let defaultMenu = ['deleteSec','editSec'];
				let operation = [{
        			value:'快速添加规则',
        			fun:(data)=>{
        				return this.addRules(data,true)
        			}
        		},{
        			value:'添加规则',
        			fun:(data)=>{
        				return this.addRules(data)
        			}
        		},{
        			value:'修改',
        			id:'editSec',
        			fun:(data)=>{
        				return this.editSec(data)
        			}
        		},{
        			value:'删除',
        			id:'deleteSec',
        			fun:(data)=>{
        				return this.deleteSec(data);
        			}
        		}]
				operation.map(item=>{
					sec.default && defaultMenu.includes(item.id) && (item.disabled = true) && (item.title = "默认安全组不允许修改删除");
				})
				this.$set(sec, 'operation', operation)
			},
			filterSec(data){
				data.map(item=>{
					
					item.name == 'default' && (item.name ='默认安全组') && (item.default = true);
					this.addOperation(item);
				})
			},
			getSecList(){
				let params = this.filter;
				this.loading = true;
				getAclList(params).then(res=>{
					let data = res.data.results;
					let count = res.data.total_count;
					this.Pagination.total = count;
					if(count > this.Pagination.pagingData[0]){
						this.Pagination.show = true
					}
					this.filterSec(data);
					this.secList = data;
					this.loading = false;
				}).catch(error=>{
					this.loading = false;
					console.log(error)
				})
			},
			getRegionNums(){
				let params = {
					resource: 'firewall',
					regionid: getRegionId()
				}
				GetRegionNum(params).then(res => {
					let data = res.data.results[0].FIREWALL;
					console.log(data)
					if(data && data.detail) {
						this.regionData = data.detail;
					}
				})
			},
			pagingChange(type,val){
				if(type == 'num'){
      				this.filter.limit = val;
      			}else if(type == 'page'){
      				this.filter.page = val;
      			}
      			this.loading =true;
      			this.refresh();
			}
		},
		computed:{
			
		},
		mounted(){
			this.changeRegion()
		}
	}
</script>

<style scoped="scoped" lang="scss">
.seclist{
	background: #FFFFFF;	
	.seclist-quota{
		font-size:12px;
		padding:20px 20px 0;
	}
	.seclist-top{
		padding:20px;
	}
}
</style>


