<template>
  <div class="detail-content">
    <div class="attach_content">
      <ul class="ebs_operate">
        <li v-if="!fakeVpc" class="left_title pull_left">您还可以创建{{subRemainCount}}个子网</li>
        <li class="pull_right">
          <span class="btns_basic" @click="create" v-if="!fakeVpc">创建</span>
        </li>
      </ul>
    </div>
    <ConsoleTable
      :operationOrNot = 'operationOrNot'
      :checkBox='checkBox'
      :noEffect="true"
      :expand='expand'
      :selectOrNot='selectOrNot'
      :tableData = 'renderDetail'
      :tableOptions = 'tableOptions'
      :OperationEvents='vpcAttachPointOperation'
      @changeTable="changeTable"
      @buttonClick = 'buttonClick'></ConsoleTable>
  </div>
</template>

<script>
  import DetailBtn from '@/components/DetailBtn'
  import ConsoleTable from '@/components/ConsoleTable'
  import createSubnetView from './createSubnet'
  import { getEcmPlatforms } from '@/api/ecm'
  import { statFormat } from '@/utils/statformat'
  import { getRegionId,getZoneId } from '@/utils/auth'
  import {GetVpcDetail, deleteSubnet,editSubnet,queryAsyncJob,createSubnet,listZone,queryQuota,getNetList} from '@/api/vpc'
  import i18n from '@/lang'

  export default{
    props:{
      serverId:[String],
      subnetList:[Array],
      vpcinfo:[Object],
      subTotalCount:[Number,String]
    },
    components:{
      DetailBtn,
      ConsoleTable,
    },
    data(){
      return{
      	fakeVpc:false,
        checkBox:false,
        selectOrNot:true,
        operationOrNot:true,
        subdetailRefresh:false,
        expand:false,
//      subTotalCount:'--',
//      subExistCount:'--',
        subRemainCount:'--',
        vpcid:'',
        zoneid:'',
        vpcname:'',
        tableData:[],
        jobid:[],
        filter:{
          status:'',
          limit:10,
          page:1,
          name:'',
          id:'',
          vpcid:''
        },
        tableOptions:[{
          prop:'name',
          label:i18n.vpc.name,
          temp:true,
          detail:true
//          path:'/net/netList',
        },{
          prop:'stateTxt',
          label:i18n.vpc.state,
          temp:true,
          status:true
        },{
          prop:'cidr',
          label:i18n.vpc.subnet,
        },{
          prop:'gateway',
          label:i18n.vpc.gateway,
        },{
          prop:'subType',
          label:i18n.vpc.subType,
        }],
        vpcAttachPointOperation:[
          {
            value:i18n.vpc.delete,
            fun:(data)=>{
              return this.delete(data);
            }
          },{
            value:i18n.vpc.edit,
            fun:(data)=>{
              return this.edit(data);
            }
          },
//          {
//            value:i18n.ebs.monitorData,
//            fun:(data)=>{
//              return this.monitorData(data);
//            }
//          }
        ],
      }
    },
    methods:{
    	filterSubnet(subnets){
    		this.fakeVpc = true
    		subnets.map(item=>{
    			this.$set(item,'operation',[])
    		})
    	},
    	autoRefresh(){
      	if(this.subdetailRefresh){
  				return
  			}
      	let _this = this;
				if(this.jobid){
  				this.jobid.map((item,index) =>{
  					console.log(item);
						if(item.fjobid){
							this.getJobDetail(item.fjobid,index,()=>{
								item.fjob = true;
							})
						}
						this.getJobDetail(item.jobid,index,()=>{
								item.job = true;
						})
  				})
				}
			},
  		getJobDetail(id,index,fn){
				let _this = this;
				let timer;
				queryAsyncJob(id).then(res=>{
					console.log(res)
					let result = res.data.results[0];
					if(result.jobstatus == 1){
						fn();
						if(_this.jobid[index].fjobid){
								if(_this.jobid[index].fjob && _this.jobid[index].job){
									_this.filter.keyword=_this.jobid[index].name;
		        			let netlist;
		        			getNetList(_this.filter).then((res)=>{
		        				_this.filter.keyword= '';
		        			 	netlist = res.data.results[0];
		        			 	_this.setData(netlist);
		        			 	_this.tableData.map((item,index) =>{
				  							if(item.id == netlist.id){
					  								_this.$set(_this.tableData,index,netlist)
					  								console.log(index,netlist)
					  								return true;
					  							}
					  						})
		        			}).catch((error)=>{
						          console.log(error)
						      })
								}
						}else{
							if(_this.jobid[index].job){
								_this.filter.keyword=_this.jobid[index].name;
		        			let netlist;
		        			getNetList(_this.filter).then((res)=>{
		        				_this.filter.keyword= '';
		        			 	netlist = res.data.results[0];
		        			 	_this.setData(netlist);
		        			 	_this.tableData.map((item,index) =>{
				  							if(item.id == netlist.id){
					  								_this.$set(_this.tableData,index,netlist)
					  								console.log(index,netlist)
					  								return true;
					  							}
					  						})
		        			}).catch((error)=>{
						          console.log(error)
						      })
							}
						}
					}else if(result.jobstatus == 2){
						clearTimeout(timer)
						this.refresh()
					}else{
						timer = setTimeout(()=>{
							_this.getJobDetail(result.jobid,index,fn)
						},5000)
					}
				}).catch(error=>{
					this.subdetailRefresh = true;
					setTimeout(()=>{
						this.refresh()
					},5000)

				})
  		},
  		setData(data){
        	data.fake_subnet && data.fake_subnet == 1 && (data.disabled = true) && (data.vpcname = '默认网络')
          if(data.state==='Allocated'){
            data.state="已分配";
          }else{
            data.state = data.state ? statFormat(data.state) : '--';
          }
          data.deteler = false;
        return data;
      },
      create(arg){
      	if(this.subRemainCount < 1){
					this.$message({
	          	message: '子网配额不足！',
	          	type: 'warning'
	        });
	        return
				}else{
					let alertMsg=this.$message;
	        let view = createSubnetView;
	        let data = {};
	        data.type='create';
	        data.title="增加子网";
	        let params={
	      			regionid:getRegionId()
	      		};
          let form = {
            "vpc": true,
            "aclid": "",
            "desc": "",
            "sub_name": "",
            "sub_cidr": "",
            "gateway_ip": "",
            "vpcid": this.vpcinfo.vpcid,
            "vpcname": this.vpcinfo.vpcname,
            "zoneid":getZoneId(),
            'vpcinfo':this.vpcinfo,
            'vpcSubData':this.tableData,
          }
          let option = {
            data,
            view,
            form
          }
          let netId=arg.id;
          this.$alertDialog(option).then((res)=>{
            let data={
              "aclid": res.aclid,  //#acllist的id
              "desc": res.desc,  //#描述
              "sub_name":res.sub_name,
              "sub_cidr": res.sub_cidr,
              "is_lb": res.subType,
              "gateway_ip":res.gateway_ip,
              "vpcid": res.vpcid,
              "zoneid": res.zoneid,//#上面zone列表中的第一个id
            }
            createSubnet(data).then((response)=>{
              let network=response.data.results[0].network;
              alertMsg('子网创建成功');
//            this.tableData.push(network);
//            this.subRemainCount = this.subTotalCount - this.tableData.length;
              this.$emit('subNum',this.tableData.length)
            }).catch((error)=>{
              console.log(error);
//              this.$message('删除VPC失败，此VPC下存在子网！');
            })
          });
				}

      },
      delete(arg){
        let alertMsg=this.$message;
        let view = createSubnetView;
        let data = {};
        data.type='delete';
        data.title="删除子网";
        let form ={
          name:arg.name
        }
        let option = {
          data,
          view,
          form
        }
        let netId=arg.id;
        let vpcid=arg.vpcid;
        let _this = this;
        this.$alertDialog(option).then((res)=>{
          deleteSubnet(netId,vpcid).then((response)=>{
            alertMsg('删除子网成功');
//          let count = 0;
//          for(var i =0;i < _this.tableData.length;i++){
//          	if(netId == _this.tableData[i].id){
//          		count = i;
//          		break;
//          	}
//          }
//          _this.tableData.splice(count,1);
//          _this.subRemainCount = _this.subTotalCount - _this.tableData.length;
            _this.$emit('subNum',_this.tableData.length)
//          this.tableData=this.tableData.filter(function(net){
//              return net.id!=netId;
//          });
            console.log(response);
          }).catch((error)=>{
//          let result=error.response.data.code;
//          if(result=='SUBNET.004'){
//            this.$message({
//              message: '该子网下有云服务器，不能删除！',
//              type: 'error'
//            });
//          }else{
//            console.log(error);
//          }

//              this.$message('删除VPC失败，此VPC下存在子网！');
          })
        });
      },
      edit(arg){
        let alertMsg=this.$message;
        let refresh=this.refresh;
        let view = createSubnetView;
        let data = {}
        data.type='edit';
        data.title="修改子网";
        let form ={
          name:arg.name,
          aclName:arg.aclname,
          aclId:arg.aclid,
          vpcid:arg.vpcid,
          desc:arg.displaytext,
          edit:true
        }
        let option = {
          data,
          view,
          form
        }
        let netId=arg.id;
        let _this = this;
        this.$alertDialog(option).then((res)=>{
          console.log(res);
        		if(res.name == arg.name && res.aclId == arg.aclid && res.desc == arg.displaytext){
        			return
        		}else{
        			let data;
        			if(res.aclId == arg.aclid){
        				data={
		            	"name":res.name,
		            	"desc":res.desc,
		          	}
        			}else{
        				data={
		            	"name":res.name,
		            	"desc":res.desc,
		            	"firewall_uuid":res.aclId,
		          	}
        			}
		          editSubnet(netId,data).then((response)=>{
		          	alertMsg('修改子网成功');
		          	this.subdetailRefresh = false;
		          	_this.jobid.push({
		          		id:netId,
		          		jobid:response.data.results[0].jobid,
		          		fjob : false,
									job : false,
									name:data.name,
		          		fjobid:response.data.results[0].fjobid?response.data.results[0].fjobid : ''
		          		});
		            _this.autoRefresh(_this.jobid);
		          }).catch((error)=>{
		            console.log(error);
		          })
        		}
        });
      },
      buttonClick(funName,data){
        funName(data);
      },
      changeTable(val){
        this.changeTableData = val
      },
      getVpcDetail(id){
//        var _this=this;
        console.log('1111');
        console.log(this);
        GetVpcDetail(id).then((res)=>{
          let data = res.data.results[0];
          this.tableData=data.network || data.subnet_list;
          console.log(this.tableData);
          this.filterServers(this.tableData);
          this.zoneid=data.zoneid;
          this.vpcid=data.vpcid;
          this.vpcname=data.vpcname;
          data['net_num']=data.network ? data.network.length : data.subnet_list ? data.subnet_list.length : 0;
        }).catch((req)=>{
          console.log(req)
        });
      },
      changeData(data){
        let _this=this;
        this.tableData.map(function (item, index) {
          if (item.id == data.id) {
            data.state = data.state ? statFormat(data.state) : '--';
            _this.$set(_this.tableData, index, data)
          }
        });
      },
      filterServers(servers){
      			let _this = this;
        var timer;
        var Implementing=[];
        let filter=this.filter;
        let changeData=this.changeData;
      			console.log(Array.isArray(servers));
      			if(Array.isArray(servers)){
	      			servers.forEach(function(item){
	      				item.subType = item.is_lb ? '负载均衡' : '普通子网';
	      				item.aclname = item.aclname ? item.aclname : '--';
	      				if(item.state==='Allocated'){
			            item.state="Allocateds";
			          }else if(item.state==='Implementing'){
                  Implementing.push(item.name);
                  item.state = item.state ? statFormat(item.state) : '--';
                }else if(item.state==='Implemented'){
			            item.state="Implemented";
			          }
                if(_this.regionType == 'os'){
                	item.gateway = item.gateway_ip;
                	item.status = item.status == 'ACTIVE' && 'subnetactive';
                }
	      			})
	      		}else{
	      			servers.subType = servers.is_lb ? '负载均衡' : '普通子网';
	      			servers.aclname = servers.aclname ? servers.aclname : '--';
	      			if(servers.state==='Allocated'){
		            servers.state="Allocateds";
		          }else if(servers.state==='Implementing'){
                Implementing.push(servers.name);
                servers.state = servers.state ? statFormat(servers.state) : '--';
              }else if(servers.state==='Implemented'){
		            servers.state="Implemented";
		          }
	      		}
        if(Implementing.length>0){
          Implementing.map(function(val){
            let timer;
            timer=setInterval(function(){
              filter.name=val;
              getNetList(filter).then((res)=>{
                let netDetail = res.data.results[0];
                if(netDetail.state!=='Implementing'){
                  clearInterval(timer);
                  changeData(netDetail);
                }
              }).catch((error)=>{
                if(error.message == 'CancelToken'){

                }else{
                  this.loaded=false
                }
                console.log(error)
              })
            },9000);
          });
        }
      		},
      refresh(){
        this.getVpcDetail(this.$route.params.serverId)
      },
      queryQuotas(){
      	queryQuota().then((res)=>{
          console.log(res.data.results);
          let Quota = res.data.results[0];
          this.subTotalCount = Quota.networklimit;
        	this.subExistCount = Quota.networktotal;
//      	this.subRemainCount = Quota.networkavailable;
        }).catch((error)=>{
          console.log(error)
        })
      }
    },
    computed:{
      renderDetail(){
      	console.log(this.vpcinfo)
      	let fakeVpc = this.vpcinfo.fake_vpc ? this.vpcinfo.fake_vpc : null;
      	fakeVpc == 1 &&  this.filterSubnet(this.subnetList);
        this.tableData = this.subnetList;
        this.filterServers(this.tableData);
        if(this.subTotalCount=='--'){
        	this.subRemainCount = '--';
        }else{
        	this.subRemainCount = this.subTotalCount - this.tableData.length;
        	if(this.subRemainCount < 0 ){
        		this.subRemainCount = 0;
        	}
        }
        return this.tableData;
      },
      regionType(){
      	let regionType = this.$store.getters.regionType;
      	if(regionType == 'cs'){
      		this.tableOptions.push({
	          prop:'aclname',
	          label:i18n.vpc.aclName,
	        })
      	}
      	return this.$store.getters.regionType;
      }
    },
    mounted(){
//    this.queryQuotas();
			
    },
    beforeDestroy(){
			this.subdetailRefresh = true;
		}
  }
</script>

<style lang="scss" scoped="scoped">
  .attach_content{
    padding: 20px 0;
    line-height: 30px;
    .ebs_operate{
      display: table;
      content: '';
      width: 100%;
      .pull_left{
        float: left;
      }
      .left_title{
        font-size: 12px;
        padding-left: 20px;
        height: 30px;
        line-height: 30px;
      }
      .pull_right{
        float: right;
        .btns_basic{
          display: inline-block;
          width: 80px;
          height: 30px;
          background: #f9cd76;
          color: #ffffff;
          cursor: pointer;
          vertical-align: middle;
          text-align: center;
          line-height: 30px;
          outline: none;
          box-sizing: border-box;
          border-radius: 5px;
          font-size: 12px;
          margin-right: 10px;
        }
      }
    }
  }
  .detail-content{
    padding:0 20px;
    background:#FFFFFF;
    min-height:330px;
  }
</style>



