import vpcRouter from './vpc'

import eipRouter from './eip'
import secRouter from './sec'
import netRouter from './net'
import aclRouter from './acl'

/*因为左侧菜单栏缘故，故把vpc下面的所有路由放入vpc模块下面进行了嵌套*/
vpcRouter.children.push(eipRouter,secRouter,netRouter,aclRouter);

export default vpcRouter;



