<template>
	<div class="createEbs">
		<ConsoleCreate ref="createProcess" :returnUrl='returnUrl' :system_type='name' :switchPage = 'nextPage' :checked='!checked' :submitForm="submitForm" @orderEvent = 'orderEvent'>
			<div slot='create_title' class="create_title" v-show="page==0">
				<EbsCreateFirst ref="createOrder" @orderResult = 'orderResult'></EbsCreateFirst>
			</div>
			<div slot="confirm_title" class="confirm_title" v-show="page==1">
				<div style="padding: 20px 0;background-color: #fff; font-size: 12px;color: #333;">资源详情</div>
		        <ConsoleConfirm :tableData="tableData"></ConsoleConfirm>
		        <div class="Agreement_component">
				    <p>
				      	<el-checkbox class="checkedTip" v-model="checked">我已阅读并同意相关协议</el-checkbox>
				      	<span @click="goProtocol" class="agreement">《 天翼云弹性云服务器服务等级协议 》</span>
				    </p>
			  	</div>
			</div>
			<div slot="submit_title" class="submit_title"  v-if="page===2">
	        	<ConsoleSuccess :return_url='returnUrl' :order_number='order_number' :system_type='name' :system_name="tableData.system_name" ></ConsoleSuccess>
	      	</div>
		</ConsoleCreate>
	</div>
	
</template>

<script>
	import ConsoleCreate from '@/components/ConsoleCreate'
	import ConsoleConfirm from '@/components/ConsoleConfirm'
  	import ConsoleSuccess from '@/components/ConsoleSuccess'
  	import EbsCreateFirst from '@/views/ebs/EbsCreateFirst'
	import i18n from '@/lang'
	import { CreateEbs , listZone} from '@/api/ebs'
	import { getEcmPlatforms } from '@/api/ecm'
	import { getRegionId,getZoneId } from '@/utils/auth'
	export default {
		components:{
			ConsoleCreate:ConsoleCreate,
			ConsoleConfirm,
	     	ConsoleSuccess,
	      	EbsCreateFirst,
		},
	    data() {
	      	return {
		      	page:0,
		      	checked:false,
		      	name:'云硬盘',
		        returnUrl:'/ebs/ebsList',
		        order_number:'',
	      		tableData:{
		          	system_name:'',//资源名称
		          	elseData: [{
		          		system_type:'云硬盘',//资源类型
	             		length:'1',
	      				num:'1',
//	      				total:'￥0.00',
	      				confirmData:[],
		            }],
		          	tableOptions: [{
		    			prop:'length',
		    			label:i18n.ebs.length,
		    		},{
		    			prop:'num',
		    			label:i18n.ebs.Number,
//		    		},{
//		    			prop:'total',
//		    			label:i18n.ebs.totalPrice,
		    		}],
		        },
		        ruleFormData:'',
		    }
	    },
	    methods: {
	    	orderEvent(){
				this.$refs.createOrder.order();
	    	},
	    	goProtocol(){
	    		window.open('http://www.ctyun.cn/term/ecs');
	    	},
	    	orderResult(val,data){
	    		console.log(val);
				if(val){
					this.ruleFormData = data;
					console.log(this.ruleFormData);
					this.$refs.createProcess.next();
				}else{
					return
				}
	    	},
	      nextPage(index){
	      	console.log(index);
	      	this.page=index;
	      	if(index == 1){
	      		if(this.ruleFormData.bksName){
	      			this.tableData.elseData = [{
		      			system_type:'云硬盘',//资源类型
		      			length:this.ruleFormData.broadbandMsg,
		      			num:this.ruleFormData.ebsNumber,
//		      			total: '￥' + this.ruleFormData.price,
		      			confirmData:[{
			      			label: i18n.common.resourceNodes,
			      			value:this.ruleFormData.regionName
			      		},{
			      			label: i18n.common.name,
			      			value:this.ruleFormData.name
			      		},{
			      			label: i18n.ebs.bks,
			      			value:this.ruleFormData.bksName,
			      		},{
	//		      			label: i18n.common.selectCorrectAZ,
	//		      			value:this.ruleFormData.resource
	//		      		},{
			      			label: i18n.ebs.storageType,
			      			value:this.ruleFormData.storageType
			      		},{
			      			label: i18n.ebs.capacity,
			      			value:this.ruleFormData.size
			      		}],
		      		}];
	      		}else{
	      			this.tableData.elseData = [{
		      			system_type:'云硬盘',//资源类型
		      			length:this.ruleFormData.broadbandMsg,
		      			num:this.ruleFormData.ebsNumber,
//		      			total: '￥' + this.ruleFormData.price,
		      			confirmData:[{
			      			label: i18n.common.resourceNodes,
			      			value:this.ruleFormData.regionName
			      		},{
			      			label: i18n.common.name,
			      			value:this.ruleFormData.name
			      		},{
	//		      			label: i18n.common.selectCorrectAZ,
	//		      			value:this.ruleFormData.resource
	//		      		},{
			      			label: i18n.ebs.storageType,
			      			value:this.ruleFormData.storageType
			      		},{
			      			label: i18n.ebs.capacity,
			      			value:this.ruleFormData.size
			      		}],
		      		}];
	      		}
	      		this.tableData.system_name=this.ruleFormData.name;
	      	}
	      },
	      submitForm(fn) {
	      		let params={
	      			regionid:getRegionId()
	      		};
//	      		let NowPlatforms = '';
//	        	getEcmPlatforms(params).then((res)=>{
//	        		console.log(res.data.platforms);
//	        		let Platforms = res.data.platforms;
//	        		NowPlatforms = Platforms.filter((item)=>{
//	        			if(item.uuid == params.regionid){
//	        				return true
//	        			}else{
//	        				return false
//	        			}
//	        		});
//	        		console.log(NowPlatforms);
	          		let data={};
	          		if(this.ruleFormData.bksId){
	          			data={
							'datahd':this.ruleFormData.size,
							'dataType':this.ruleFormData.dataType,
							'volumeName':this.ruleFormData.name,
							'orderNum':this.ruleFormData.ebsNumber,
							'cycleType':this.ruleFormData.cycleType,
							'cycleCnt':this.ruleFormData.cycleCnt,
							'backupId':this.ruleFormData.bksId,
							'regionid':this.ruleFormData.uuid,
//				            "zoneId":NowPlatforms[0].details.zoneId
				            "zoneId":getZoneId()
			          	};
	          		}else{
	          			data={
							'datahd':this.ruleFormData.size,
							'dataType':this.ruleFormData.dataType,
							'volumeName':this.ruleFormData.name,
							'orderNum':this.ruleFormData.ebsNumber,
							'cycleType':this.ruleFormData.cycleType,
							'cycleCnt':this.ruleFormData.cycleCnt,
							'regionid':this.ruleFormData.uuid,
//				            "zoneId":NowPlatforms[0].details.zoneId
				            "zoneId":getZoneId()
			          	};
	          		}
	          		const loading = this.$loading({
				          lock: true,
				          text: '拼命创建中',
				          spinner: 'el-icon-loading',
				          background: 'rgba(0, 0, 0, 0.7)'
				        });
		          	CreateEbs(data).then((res)=>{
	      				console.log(res)
	      				loading.close();
	      				this.order_number = res.data.returnObj.so_number;
	      				this.$message({
				          	message: '命令下发成功',
				          	type: 'success'
				        });
				        fn();
	      			}).catch((error)=>{
	      				loading.close();
	      				console.log(error);
//	      				this.$message({
//				          	message: '命令下发失败',
//				          	type: 'error'
//				        });
	      			});
//		        }).catch((error)=>{
//		          this.loading=false;
//		          console.log(error)
//		        });
		
		    },
	    },
	}
</script>

<style lang="scss" scoped="scoped">
	.Agreement_component{
	    font-size: 12px;
	    margin-top: 20px;
      	.agreement {
			color: #3dc7fa;
			cursor: pointer;
		}
	}
</style>
<style lang="scss">
	.createEbs{
		.confirm_title{
			.mt5{
				display: block;
				margin-top: 5px;
			}
		}
		.checkedTip{
			.el-checkbox__label{
				font-size: 12px;
			}
		}
		.agreement{
			font-size: 12px;
			font-weight: 500;
		}
	}
	
</style>



