<template>
	<div class="detail-content">
		<el-row class="detail-title">
			网卡
		</el-row>
		<el-row class="detail-top" v-if="quotaShow">
			<el-col :span="20">
				您还可以添加 
					{{ addNicNum }}
				块网卡
			</el-col>
			<!--<el-col :span="4">
				<el-row type="flex" justify="end" >
					<detail-btn :btnMsg='btnMsg' @deatilBtnClick="addNic"></detail-btn>
				</el-row>
			</el-col>-->
			
		</el-row>
		
		<el-row class="detail-nic">
			<el-col :span="8" v-for="nic in nics" :key="nic.id">
				<detail-card :cardData='nic' className="nic" :opearationBtn='opearationBtn' @clickFun='action' @remove="remove">
					<div slot="detailMore">
						<detail-more :detailLabel='nicLabel' :Data='nic'></detail-more>
					</div>
				</detail-card>
			</el-col>
		</el-row>
		<el-row class="border-style"></el-row>
	</div>
		
		
		
	
</template>

<script>
	import DetailBtn from '@/components/DetailBtn'
	import DetailCard from '@/components/DetailCard'
	import DetailMore from '@/components/DetailCard/DetailMore'
	import { GetEcmDetail, GetNetworkDetail, GetACLListDetail  } from '@/api/ecm'
	export default {
		props:{
			serverId:[String],
			serverData:[Object]
		},
		components:{
			DetailBtn,
			DetailCard,
			DetailMore
		},
		methods:{
			addNic(){
				
			},
			action(fun,item){
				let id = item.id;
				fun(id)
			},
			switchMore(id){
				this.nics.map((item)=>{
					if(item.id == id){
						let bool = !item.showDetail;
						console.log(item)
						this.$set(item,'showDetail',bool)
					}
				})
			},
			changeAclList(id){
				console.log(id)
			},
			eipManager(id){
				console.log(id)
			},
			remove(id){
				console.log('remove',id)
			},
			getEcmDetailById(id){
				GetEcmDetail(id).then((res)=>{
					let data = res.data;
					let servers = data.results[0];
					let nics = servers.nic;
					console.log(nics);
					this.getAclName(nics)
					this.getNetWorkDetail(nics)
					
					this.nics = nics;
				}).catch((req)=>{
					console.log(req)
				})
			},
			
			addLabel(){
				
			},
			filterNicsByOs(nics){
				nics.map(item=>{
					let fiexd_ips = item.fixed_ips && item.fixed_ips.length &&item.fixed_ips[0];
					if(fiexd_ips){
						item.ipaddress = fiexd_ips.ip_address;
						GetNetworkDetail(fiexd_ips.subnet_id).then(res=>{
							let data = res.data.results.length &&  res.data.results[0];
							if(data){
								item.network = data.name + '(' + data.cidr +')';
							}
							
							
						}).then(error=>{
							console.log(error)
						})
					}
					item.macaddress = item.mac_addr;
					
				})
			},
			getAclName(nics){
				nics.map((item)=>{
					GetACLListDetail(item.networkid).then((res)=>{
						console.log(res)
						let data =res.data;
						let aclList = data.results[0]
						this.$set(item,'acllistname',aclList.name)
					}).catch((req)=>{
						console.log(req)
					})
					
				})
			},
			getNetWorkDetail(nics){
				nics.map((item,index)=>{
					GetNetworkDetail(item.networkid).then((res)=>{
						let data = res.data;
						let subnet = data.results[0];
						item.network = subnet.name + '(' + subnet.cidr +')';
					}).catch((req)=>{
						console.log(req)
					})
					
				})
				
			}
		},
		data (){
			return {
				addNicNum:0,
				quotaShow:false,
				btnMsg:[
					{
						value:'添加网卡',
						fun:'addNic'
					}
				],
				nics:[],
				opearationBtn:[
					/*{
						value:'管理私有IP地址',
						fun:(id)=>{
							return this.eipManager(id)
						}
					},{
						value:'更改ACLList',
						fun:(id)=>{
							return this.changeAclList(id)
						}
					},*/
					{
						value:'查看更多详情',
						id:'showDetail',
						fun:(id)=>{
							return this.switchMore(id)
						}
					}
				],
				nicLabel:[
					{
						label:'网卡ID',
						value:'id'
					},
					/*{
						label:'弹性公网IP',
						value:'publicip'
					},*/
					{
						label:'源/目的检查',
						hide:true
					},
					{
						label:'子网',
						value:'network'
					},
					{
						label:'MAC地址',
						value:'macaddress'
					},
					{
						label:'子网掩码',
						value:'netmask'
					},
					{
						label:'VLAN1',
						value:'',
						hide:true
					}
				]
			}
		},
		computed:{
			regionType(){
				let regionType = this.$store.getters.regionType;
				 'os' == regionType && this.nicLabel.map(item=>{
					(item.value == 'netmask') && (item.hide = true)
				})
				return regionType
			},
		},
		created(){
			let nics = this.serverData.nics;
			console.log(nics);
			if(this.regionType == 'os'){
				this.filterNicsByOs(nics)
				console.log(nics)
				this.nics = nics;
			}
			if(this.regionType == 'cs'){
				this.getAclName(nics)
				this.getNetWorkDetail(nics)
			}
			
			
		}
	}
</script>

<style scoped="scoped" lang="scss">
	.detail-title{
		padding:20px 0;
	}
	.detail-top{
		padding-bottom:20px;
		line-height: 30px;
		div{
			font-size:14px;
		}
		
	}
	.detail-content{
		padding:20px;
		background:#FFFFFF;
		/*min-height:300px;*/
	}
	.border-style{
		height:20px;
		border-bottom:3px solid #f6f8fb;
	}
</style>


