import ecmRouter from './ecm'
import ebsRouter from './ebs'
import bksRouter from './bks'
import imsRouter from './ims'
import keyparirRouter from './keypair'
import snapshotRouter from './snapshot'
import eipRouter from './eip'


//因为嵌套路由问题，把所有云服务器下面的路由嵌套到ecm下面
ecmRouter.children.push(ebsRouter,bksRouter,imsRouter,keyparirRouter,snapshotRouter,eipRouter);


export default ecmRouter;



