import i18n from '@/lang'

import Layout from '@/views/Layout'
import vpcList from '@/views/vpc/vpcList'
import creatVpc from '@/views/vpc/creatVpc'
import vpcDetail from '@/views/vpc/vpcDetail'


let Router ={
  path:'/vpc',
  name:'vpc',
  component:Layout,
  redirect:'/vpc/vpcList',
  children:[
    {
      path:'vpcList',
      name:'vpcList',
      component:vpcList,
      props: { name: i18n.common.vpc, buttonText: i18n.vpc.creatVPC }
    },
    {
      path:'creatVpc',
      name:'creatVpc',
      component:creatVpc,
      props: { name: i18n.vpc.creatVPC},
      meta:{
				dependOn:'vpcSub',
				aisdeHide:true
			}
    },
    {
      path:'vpcDetail/:serverId',
      name:'vpcDetail',
      component:vpcDetail,
      props: { name: i18n.vpc.vpcDetail},
      meta:{
  			dependOn:'vpcSub'
  		}
    },
  ]
};

export default Router;



