<template>
	<div class="box main">
		<ConsoleDetail
			 :detail_label = 'detail_label'
			  :detail_image='detail_image' 
			  :detailList = 'detailList' 
			  :btnMsg='btnFun2' 
			  :detailShow='detailShow'
			  @deatilBtnClick = 'deatilBtnClick'></ConsoleDetail>
		
		<detail-tab :tabOption='tabOption' @click="swtichTab">
			<div :slot="currentView">
				<component :is='currentView' @ecmIp='getEcmIp' :vpcname='vpcname' :eipPort='eipPort' :serverId = 'eipId' :eipDetailData = 'eipDetailData'></component>
			</div>
		</detail-tab>
	</div>
</template>

<script>
	import i18n from '@/lang'
	import { attachPoint } from '@/baseConfig/consoleList'
	import ConsoleTable from '@/components/ConsoleTable'
	import ConsoleDetail from '@/components/ConsoleDetail'
	import DetailTab from '@/components/DetailTab'
	
	import resourcesDetail from './resourcesDetail'
	
	import EipBindChoose from '@/views/vpc/EipBindChoose'
	import ConsoleAction from '@/components/ConsoleAction'
	import EipBind from '@/views/vpc/EipBind'
	
	import { getRegionId } from '@/utils/auth'
	import { timeService,timeExpired } from '@/utils/timeformat'
	import { statFormat } from '@/utils/statformat'
	import { GetEipDetail,renewEip,deleteEip,mappingEip,portMappingEip} from '@/api/vpc'
	export default{
		components:{
			ConsoleTable:ConsoleTable,
			ConsoleDetail:ConsoleDetail,
			DetailTab:DetailTab,
			ConsoleAction:ConsoleAction,
			resourcesDetail:resourcesDetail,
			EipBindChoose:EipBindChoose,
			EipBind:EipBind,
		},
		data(){
			return {
				checkBox:true,
				selectOrNot:true,
				operationOrNot:true,
				detailShow:true,
				eipPort:'',
				attachPoint,
				detail_label:'弹性公网IP',
				detail_image:require('../../assets/ecm/eipDetail.png'),
				eipId:'',
				vpcname:'',
				eipDetailData:[],
				currentView:'resourcesDetail',
		        detailList:[
		        {
		        	name:'id',
					label:i18n.common.ID,
					intro:'--'
		        },
//		        {
//		        	name:'flavor',
//					label:i18n.common.flavor,
//					intro:'--'
//		        },
		        {
		        	name:'status',
					label:i18n.common.status,
					intro:'--'
		        },
//		        {
//		        	name:'eipOrder',
//					label:i18n.vpc.eipOrder,
//					intro:'--'
//		        },
		        {
		        	name:'eipAddress',
					label:i18n.vpc.eipAddress,
					intro:'--'
		        },
		        {
		        	name:'createAt',
					label:i18n.common.createAt,
					intro:'--'
		        },
		        {
		        	name:'vmipaddress',
					label:i18n.vpc.bindPrivateIp,
					intro:'--'
		        },
		        {
		        	name:'finishAt',
					label:i18n.common.finishAt,
					intro:'--'
		        },
		        ],
		        btnMsg:[{
//					value:i18n.ebs.capacityExpansion,
//					btnCli:()=>{
//      				return this.capacityExpansion();
//      			}
//				},{
					value:i18n.vpc.binding,
					disabled:false,
					state:'bind',
        			btnCli:()=>{
        				return this.binding();
        			}
				},{
					value:i18n.vpc.unbunding,
					disabled:false,
					state:'unbind',
        			btnCli:()=>{
        				return this.unbunding();
        			}
				}
				],
				tabOption:[
					{
						label:'详情',
						name:'resourcesDetail',
						view:''
					}
				],
			}
		},
		computed:{
			btnFun2(){
				if(this.eipDetailData.expired){
					return  this.btnMsg;
				}else{
					return this.btnMsg;
				}
				
			}
		},
		methods:{
			unbunding(){
				if(this.eipDetailData.state == 'unbinding'){
					this.$message({
						type:'error',
						message:'请先绑定弹性公网IP再进行此操作'
					})
					return
				}
				if(this.eipDetailData.expired){
					return
				}
				if(!this.eipDetailData.resource_type){
      				return
      			}
				let _this = this;
				console.log('调用解绑');
				let tipMsg = '';
      			if(this.eipDetailData.resource_type == 'INSTANCE'){
      				tipMsg = '确定要解绑这个IP的静态NAT';
      			}else if(this.eipDetailData.resource_type  == 'PORTFORWARDING'){
      				tipMsg = '确定要解绑这个IP绑定的所有端口映射规则吗？'
      			}else if(!this.eipDetailData){
      				tipMsg = '解除负载均衡IP将会删除负载均衡？'
      			}
      			let option = {
      				'data':{
      					'type':'warn',
      					'option':'',
      					'title':'解绑',
      					'actionTip':[tipMsg],
      					'data':'',
      					'warn':true
      				},
      				'view':ConsoleAction,
      				'form':{},
      			};
      			this.$alertDialog(option).then((res)=>{
					deleteEip(this.eipDetailData.id).then((res)=>{
	      				console.log(res)
	      				this.$message({
				          	message: '弹性公网IP解绑成功',
				          	type: 'success'
				        });
				        setTimeout(()=>{
				        	_this.refresh();
  						},3000)
	      			}).catch((error)=>{
	      				console.log(error);
	      				this.$message({
				          	message: '命令下发失败',
				          	type: 'error'
				        });
	      			})
  				})
			},
			binding(){
				console.log('调用绑定');
				if(this.eipDetailData.state.toLowerCase() == 'instance' || this.eipDetailData.state.toLowerCase() == 'lb' || this.eipDetailData.state == 'PORTFORWARDING'){
					this.$message({
						type:'error',
						message:'请先解绑弹性公网IP再进行此操作'
					})
					return
				}
				if(this.eipDetailData.expired){
					return
				}
				if(this.eipDetailData.resource_type){
      				return
      			}
				let _this = this;
				let tableOption = [{
	      				prop:'name',
	        			label:i18n.common.name,
	        			detail:true,
	        			noEffect:true
	      			},{
	      				prop:'status',
	        			label:i18n.common.status,
	        			temp:true,
        				status:true
	      			},{
	      				prop:'privateIp',
	        			label:i18n.common.privateIp,
	      			}];
						let option = {
		      				'data':{
		      					'option':tableOption,
		      					'title':'一对一映射',
		      					'actionTip':i18n.vpc.bindingTip,
		      					'data':'',
		      					'vpcid':this.eipDetailData.vpcid
		      				},
		      				'view':EipBind,
		      				'form':{},
		      			};
		      			this.$alertDialog(option).then((res)=>{
		  					console.log(res.data);
		  					let server_id = res.data.id;
		  					let subnet_uuid = res.data.nic[0].networkid;
		  					mappingEip(server_id,this.eipDetailData.id,subnet_uuid).then((res)=>{
			      				console.log(res)
			      				this.$message({
						          	message: '命令下发成功',
						          	type: 'success'
						        });
						        _this.refresh();
			      			}).catch((error)=>{
			      				console.log(error);
			      				this.$message({
						          	message: '命令下发失败',
						          	type: 'error'
						        });
			      			})
						})
//				let tableData = [
//    				{
//    					svg:'mapping',
//    					title:'一对一映射',
//    					msg:'通过一对一映射绑定，每个公网IP可直接绑定到一台指定的云服务器上。'
//    				},
//    				{
