//    					title:'端口映射',
//    					msg:'端口有限制，一个公网IP可以绑定多个私网IP。'
////    				},
////    				{
////    					svg:'elb',
////    					title:'负载均衡',
////    					msg:'每个公网IP可通过负载均衡配置设置多条规则可绑定多台云服务器。'
//    				}
//    			];
//    			let option = {
//    				'data':{
//    					'option':'',
//    					'title':'选择云服务器',
//    					'actionTip':'',
//    					'data':tableData
//    				},
//    				'view':EipBindChoose,
//    				'form':{}
//    			};
//    			this.$alertDialog(option).then((res)=>{
//					console.log(res);
//					let tableOption = [{
//	      				prop:'name',
//	        			label:i18n.common.name,
//	        			detail:true,
//	        			noEffect:true
//	      			},{
//	      				prop:'status',
//	        			label:i18n.common.status,
//	        			status:true,
//	      			},{
//	      				prop:'privateIp',
//	        			label:i18n.common.privateIp,
//	      			}];
//					if(res.data.svg == 'mapping'){
//						let option = {
//		      				'data':{
//		      					'option':tableOption,
//		      					'title':res.data.title,
//		      					'actionTip':i18n.vpc.bindingTip,
//		      					'data':''
//		      				},
//		      				'view':EipBind,
//		      				'form':{}
//		      			};
//		      			this.$alertDialog(option).then((res)=>{
//		  					console.log(res);
//		  					let server_id = res.data.id;
//		  					let subnet_uuid = res.data.nic[0].networkid;
//		  					mappingEip(server_id,this.eipDetailData.id,subnet_uuid).then((res)=>{
//			      				console.log(res)
//			      				this.$message({
//						          	message: '命令下发成功',
//						          	type: 'success'
//						        });
//						        setTimeout(()=>{
//						        	_this.refresh();
//		  						},4000)
//			      			}).catch((error)=>{
//			      				console.log(error);
//			      				this.$message({
//						          	message: '命令下发失败',
//						          	type: 'error'
