//		      				},
//		      				'view':EipBind,
//		      				'form':{}
//		      			};
//					}
//				})
			},
			getEcmIp(val){
				if(val == 'refresh'){
					this.refresh();
				}else{
					this.$set(this.detailList[4],'intro',val);
				}
			},
			refresh(){
      			this.GetEipDetail(this.$route.params.serverId);
      		},
			deatilBtnClick(funName){
				funName()
			},
			handleClick(tab, event) {
        		console.log(tab, event);
      		},
      		handleSelectionChange(val) {
        		this.multipleSelection = val;
      		},
      		swtichTab(tab){
				this.currentView = tab.name
			},
      		GetEipDetail(id){
				let detailList = this.detailList;
				GetEipDetail(id).then((res)=>{
					let data = res.data.results[0];
					console.log(res);
					data.state = data.resource_type ? data.resource_type : 'unbinding';
					data.status = data.state ? statFormat(data.state) : '--';
					if(data.state == 'PORTFORWARDING'){
						this.eipPort = '2';
					}else{
						this.eipPort = '1';
					}
					this.vpcname = data.vpcname;
					data.id = data.id;
					data.eipAddress = data.ipaddress || '--';
					data.createAt = data.created_at ? timeService(data.created_at) : '--';
					data.chargeType = data.chargeType || '--';
					data.diskModel = data.diskModel || '--';
					data.finishAt = data.expire_date ? timeService(data.expire_date) : '--';
					data.expired = timeExpired(data.finishAt);
					detailList.map((item)=>{
						item.intro = data[item.name]
					})
					this.detail_label = data.ipaddress; 
					this.eipDetailData = data;
					this.btnMsg.map(function(item){
						if(item.state =='bind'){
							if(data.state == 'unbinding'){
								item.disabled = true;
							}else{
								item.disabled = false;
							}
						}else{
							if(data.state == 'unbinding'){
								item.disabled = false;
							}else{
								item.disabled = true;
							}
						}
						if(data.expired){
							item.disabled = false;
							item.title='该弹性ip已过期，不可执行该操作'
						}
						if(data.with_vm){
							item.disabled = false;
							item.title='与云主机一起购买的弹性ip，不可执行该操作'
						}
					})
					console.log(this.eipDetailData);
				}).catch((req)=>{
					console.log(req)
				})
			},
		},
		created(){
			this.GetEipDetail(this.$route.params.serverId);
			this.eipId = this.$route.params.serverId;
		},
	}
</script>

<style lang="scss" scoped="scoped">
    .main{
    	background: #f6f8fb;
    	.detail_child{
    		background: #ffffff;
    		margin-top: 20px;
    		margin-bottom: 30px;
    		
    	}
    }
</style>
<style lang="scss" >
	.detail_child{
		.el-tabs--card{
			.el-tabs__header{
				.el-tabs__nav-scroll{
					background: #f6f8fb;
				}
				.el-tabs__nav{
					border: none;
				}
				.el-tabs__item{
					height: 36px;
					padding: 0;
					width: 121px;
					text-align: center;
					line-height: 36px;
					font-size: 12px;
					color: #666666;
					background: #f0f2f7;
					border-radius: 5px;
					margin-right: 10px;
					&.is-active{
						background: #3ac0f1;
						color: #ffffff;
					}
				}	
			}
		}
	}
	.main{
		.el-table__header{
			.has-gutter{
				tr{
					background: #f0f2f7;
					th{
						background: #f0f2f7;
						color: #333333;
						font-size: 12px;
						font-weight: 600;
					}
				}
			}
		}
		tr,th{
			text-align: center;
		}
	}
</style>



