<template>
	<div class="detail-content">
		<el-row class="detail-top">
			<el-col :span="15">
				弹性公网IP
			</el-col>
			<el-col :span="9">
				<el-row type="flex" justify="end" >
					<detail-btn :btnMsg='btnMsg' @deatilBtnClick="action"></detail-btn>
				</el-row>
			</el-col>

		</el-row>
		<el-row>
			<ConsoleTable
				:loading="loading"
				:expand=true
				:operationOrNot = true
				:selectOrNot=true
				:tableData = 'eipList'
				:tableOptions = 'tableOptions'
				:detilOptions = 'detilOptions'
				:noEffect = true
				:OperationEvents='eipOperation'
				@buttonClick = 'buttonClick'
				@detailClick = 'detailClick'
				></ConsoleTable>

		</el-row>

	</div>

</template>

<script>
  	import i18n from '@/lang'
	import DetailBtn from '@/components/DetailBtn'
	import ConsoleTable from '@/components/ConsoleTable'
	import { getEcmEipsdata,EcmUnbindEip } from '@/api/ecm'
  	import { getEcmList,getEipList ,mappingEip} from '@/api/vpc'
	import { statFormat } from '@/utils/statformat'
  	import EipBind from '@/views/ecm/EipBind'
	export default {
		props:{
			serverId:[String],
			serverData:[Object,Array]
		},
		components:{
			DetailBtn,
			ConsoleTable
		},
		methods:{
			action(funname,label){
				funname()
			},
			filterType(eips){
				let _this = this;
				eips.length && eips.map(item=>{
					item.resourceType = statFormat(item.resource_type) || '--'
					item.isShared = item.isShare ? '共享' :'独享';
					_this.addOperation(item)
				})
			},
			addOperation(item){
				let Operation = [];
				if(item.with_vm){
					Operation =[
						{
							value:'解绑',
		        			disabled:true,
		        			title:i18n.vpc.withVmTip,
		        			fun:(data)=>{
		        				return this.ubbind(data)
		        			}
						}
					];
				}else{
					Operation =[
						{
							value:'解绑',
		        			disabled:false,
		        			fun:(data)=>{
		        				return this.ubbind(data)
		        			}
						}
					];
				}
				this.$set(item,'operation',Operation)
			},
			getEipById(id){
				let params ={
					id
				}
				this.loading = true;
				getEcmEipsdata(params).then((res)=>{
					console.log(res)

					let eips = res.data.results;
					this.filterType(eips)
					this.eipList = eips;
					this.loading = false;
				}).catch((error)=>{
					this.loading = false;
					console.log(error)
				})
			},
			ubbind(data){
				let option={
					data:{
						actionTip:['您确定要解绑弹性公网IP吗'],
						title:'解绑',
						warn:true
					}
				}

				this.$alertDialog(option).then(()=>{
					this.$message({
						message:'正在解绑弹性公网IP，请稍后',
						type:'warning'
					})
					EcmUnbindEip(data.id).then((res)=>{
						console.log(res)
						this.$message({
							message:'解绑成功',
							type:'success'
						})
						this.serverData.servers.publicipid = '';
						this.refresh()
					}).then((error)=>{
						console.log(error)
						this.refresh()
					})


				})
				console.log(data)
			},
			buttonClick(funName,data){
      			funName(data);
      		},
      		detailClick(id){

      		},
      		lookEip(){
      			this.$router.push('/vpc/eip/eipList')
      		},
      		bindEip(){
            	console.log('调用绑定');
	            if(this.serverData.servers.publicipid){
	              this.$message({
	                type:'error',
	                message:'请先解绑弹性公网IP再进行此操作'
	              })
	              return
	            }
	            let _this = this;
	            let tableOption = [{
	              prop:'ipaddress',
	              label:i18n.ecm.ipaddress,
	            },{
	              prop:'status',
	              label:i18n.common.status,
	              temp:true,
	              status:true
	            },{
	              prop:'virtualmachinename',
	              label:i18n.vpc.instanceName,
	              detail:true,
	              noEffect:true
	            }];
	            let option = {
	              'data':{
	                'option':tableOption,
	                'title':'一对一映射',
	                'actionTip':i18n.ecm.bindingTip,
	                'data':'',
	                'vpcid':this.serverData.servers.vpcId
	              },
	              'view':EipBind,
	              'form':{},
	            };
	            this.$alertDialog(option).then((res)=>{
	              console.log(this.eipDetailData);
	              console.log(res.data.id);
	              console.log('ffff');
	              let server_id = this.serverId;
	                let EipId=res.data.id;
	              mappingEip(server_id,EipId).then((res)=>{
	                console.log(res);
	                this.$message({
	                  message: '命令下发成功',
	                  type: 'success'
	                });
	                console.log('successeip');
	                this.serverData.servers.publicipid = EipId;
	                this.refresh();
	              }).catch((error)=>{
	                console.log(error);
	                this.$message({
	                  message: '命令下发失败',
	                  type: 'error'
	                });
	              })
	            })
      		},
      		refresh(){
      			this.serverData.servers.publicipid ? this.getEipById(this.serverData.servers.publicipid) : this.eipList = []
      		}
		},
		data (){
			return {
				msg:'弹性公网IP详情',
				eipList:[],
				loading:false,
				tableOptions:[{
					prop:'ipaddress',
					label:'弹性公网IP',
				},{
					prop:'vmipaddress',
					label:'已绑定内网IP'
				}],
				detilOptions:[{
					prop:'ipaddress',
					label:'弹性公网IP',
				},{
					prop:'vpcname',
					label:'虚拟私有云'
				},{
					prop:'resourceType',
					label:'状态'
				},{
					prop:'isShared',
					label:'宽带类型'
				},{
					prop:'vmipaddress',
					label:'已绑定内网IP'
				},{
					prop:'networkrate',
					label:'宽带大小'
				},{
					prop:'id',
					label:'ID'
				},{
					prop:'created_at',
					label:'创建时间'
				}],
				eipOperation:[{
        			value:'解绑',
        			fun:(data)=>{
        				return this.ubbind(data)
        			}
        		}],
				btnMsg:[
					{
						value:'绑定弹性公网IP',
						disabled:false,
						fun:()=>{
							return this.bindEip()
						}
					},
					{
						value:'查看弹性公网IP',
						disabled:false,
						fun:()=>{
							return this.lookEip()
						}
					}
				]
			}
		},
		created(){
			this.refresh()
			if(this.serverData.servers.state == 'Starting'){
	        	this.btnMsg.map(function(item){
	        		if(item.value == '绑定弹性公网IP'){
	        			item.disabled = true
	        		}
				});
	        }
			//this.serverData.publicipid && this.getEipById(this.serverData.publicipid)
		}
	}
</script>

<style scoped="scoped" lang="scss">
	.detail-top{
		padding:20px 0;
		line-height: 30px;
		div{
			font-size:14px;
		}

	}
	.detail-content{
		padding:0 20px;
		background:#FFFFFF;
		/*min-height:330px;*/
	}
</style>



