<template>
	<div class="orderContent">
		<el-container>
			<el-header height="80px">
				<h4 class="botton_15 top_10">订单列表</h4>
				<div class="">
					<el-select v-model="filter.productType" size="small" placeholder="类型" style="width:150px" @change="getOrderLists">
					    <el-option
					      v-for="item in productList"
					      :key="item.value"
					      :label="item.label"
					      :value="item.value">
					    </el-option>
					 </el-select>
					 <el-select v-model="type" size="small" placeholder="类型" style="width:180px" @change="getOrderLists">
					    <el-option
					      v-for="item in productInfoList"
					      :key="item.value"
					      :label="item.label"
					      :value="item.value">
					    </el-option>
					 </el-select>
					 <el-select v-model="filter.orderType" size="small" placeholder="类型" style="width:180px" @change="getOrderLists">
					    <el-option
					      v-for="item in orderTypeList"
					      :key="item.value"
					      :label="item.label"
					      :value="item.value">
					    </el-option>
					 </el-select>
					 <el-select v-model="filter.state" size="small" placeholder="类型" style="width:150px" @change="getOrderLists">
					    <el-option
					      v-for="item in payStateList"
					      :key="item.value"
					      :label="item.label"
					      :value="item.value">
					    </el-option>
					 </el-select>
					 <el-date-picker
				      v-model="filter.createDate"
				      placeholder="选择日期" 
				      value-format="yyyy-MM-dd"
				      size="small" style="width:200px">
				    </el-date-picker>
				    <el-button type="primary" size="small" @click.stop="getOrderLists">搜索</el-button>
				    <el-button type="primary" size="small" class="right">导出订单</el-button>
				</div>
			</el-header>
			<el-main>
				<ConsoleTable :operationOrNot =true :selectOrNot=true 
					:tableData = 'tableData' :tableOptions = 'tableOptions' 
					:OperationEvents='ebsOperation' :loading = 'loading' 
					:Pagination = 'Pagination' @pagingEvent = 'pagingChange' @buttonClick='options_callback'></ConsoleTable>
			</el-main>
		</el-container>
	</div>
</template>

<script>
import ConsoleTable from '@/components/ConsoleTable'
import {getOrderList} from '@/api/cost'

	export default {
		components:{
			ConsoleTable:ConsoleTable,
		},
		data (){
			return {
				loading:false,
				Pagination:{
		        	'pagingData':[5,10,15],
		        	'total':0,
		        	'show':true
		        },
		        filter:{
					pageSize:5,
					page:1,
					orderType: '',
					productType: '',
					state: '',
					createDate: '',
				},
				tableData: [],
				type: '1',
				productList: [
					{
						value: '',
						label: '所有产品'
					},
					{
						value: 'CVM',
						label: '云服务器'
					},
					{
						value: 'CSTO',
						label: '云存储'
					},
					{
						value: 'CNET',
						label: '云网络'
					}
				],
				productInfoList: [
					{
						value: '1',
						label: '所有产品明细'
					},
				],
				orderTypeList: [
					{
						value: '',
						label: '所有订单类型'
					},
					{
						value: '2HA',
						label: '订购'
					},
					{
						value: '2HB',
						label: '升级'
					},
					{
						value: '2HC',
						label: '退订'
					},
					{
						value: '2HD',
						label: '续订'
					},
					{
						value: '2HE',
						label: '添加硬盘'
					},
				],
				payStateList: [
					{
						value: '',
						label: '支付状态'
					},
					{
						value: '27A',
						label: '处理中'
					},
					{
						value: '27B',
						label: '已就绪(未激活)'
					},
					{
						value: '27C',
						label: '待审批'
					},
					{
						value: '27D',
						label: '预受理'
					},
					{
						value: '27E',
						label: '已注销'
					},
					{
						value: '27F',
						label: '已生效(已激活)'
					},
					{
						value: '27G',
						label: '已取消'
					},
					{
						value: '27Z',
						label: '异常'
					},
				],
				tableOptions: [
					{
						prop: "serial_number",
						label: "订单号",
						temp: true,
        				detail: true,
        				path: 'orderInfo',
        				key: 'serial_number',
					},
					{
						prop: "product_type_str",
						label: "产品"
					},
					{
						prop: "order_type_str",
						label: "类型"
					},
					{
						prop: "create_date",
						label: "创建时间"
					},
					//{
						//prop: "exp_date",
						//label: "支付／开通时间"
					//},
					{
						prop: "state_str",
						label: "状态"
					},
					{
						prop: "price",
						label: "价格"
					},
				],
				ebsOperation:[
					{
	        			value: '详情',
	        			fun:(data)=>{
	        				return this.details(data);
	        			}
	        		},
	        		//{
	        			//value: '支付',
	        			//fun:(data)=>{
	        			//	return this.pay(data);
	        			//}
	        		//},
	        		//{
	        			//value: '作废',
	        			//fun:(data)=>{
	        			//	return this.toVoid(data);
	        			//}
	        		//}
				]
			}
		},
		methods: {
			tableHeaderbg(obj){
				console.log(obj,'123');
			},
			pagingChange(type,val){
      			if(type == 'num'){
      				this.filter.pageSize = val;
      			}else if(type == 'page'){
      				this.filter.page = val;
      			}
      			this.getOrderLists();
      		},
      		getOrderLists(params){
      			this.loading = true;
      			getOrderList(this.filter).then(res => {
					if(res.status == 200){
						this.tableData = res.data.returnObj.orders;
						this.Pagination.total = res.data.returnObj.count;
					}else{

					}
					this.loading = false;
				},error => {
					this.loading = false;
					console.log("获取订单列表error");
				});
      		},
      		details(data) {
      			this.$router.push('/cost/orderInfo/'+data.serial_number);
      		},
      		pay(data) {

      		},
      		toVoid(dtat) {

      		},
      		options_callback(fun,data){
      			fun(data);
      		}
		},
		mounted: function(){
			this.getOrderLists(this.filter);
		}
	}
</script>

<style lang="scss" scoped="scoped">
.orderContent {
	background-color: #fff;
	.paging {
		text-align: right;
	    margin-top: 20px;
	}
	.botton_15 {
		margin-bottom: 15px;
	}
	.top_10 {
		margin-top: 10px;
	}
	.el-table__header>thead>tr>th {
		background-color: #f0f2f6 !important;
		color: #333;
	}
	.right {
		float: right;
	}
	.el-button--primary {
		background-color: #3ac0f1;
    	border-color: #3ac0f1;
	}
}
</style>


