<template>
	<div class="main">
		<el-row class="user-title-row">
			<el-col>
				<div class="user-title">
					基本信息
				</div>
			</el-col>
		</el-row>
		<el-row class="border-bottom">
			<el-col :span="22" class="user-type-title">
					<span  class="user-title">账号类型 </span>
					<span class="user-type-vip">
						<svg-icon icon-class="vip" style="margin-right:5px;font-size:14px"></svg-icon>
						{{userItems.userInfo ? 'VIP企业客户' : 'VIP用户'}}
					</span>
			</el-col>
			<el-col :span="2" class="editInfo" v-if="editModule">
				<svg-icon icon-class="edit"></svg-icon>
				<el-button type="text" @click='editInfo'>
					修改信息
				</el-button>
			</el-col>
		</el-row>
		<el-row class="user-type">
			<el-col :span="4">
				<div class="user-img">
					<img :src="userBig"/>
				</div>
				<div class="user-type-editImg" v-if="editModule">
					<el-button type='text' @click="editImg">
						修改头像
					</el-button>
				</div>
			</el-col>
			<el-col :span="20" class="user-type-items">
				<el-col :span="item.span || 12" v-for = '(item,index) in userItems.userType' :key="item.label" class="user-type-item">
					{{item.label}} : {{item.value}}
				</el-col>
			</el-col>
		</el-row>
		<el-row class="border-bottom" v-if="userItems.userInfo">
			<el-col :span="22" class="user-title">
				负责人信息 
			</el-col>
			<el-col :span="2" class="editInfo" v-if="editModule">
				<svg-icon icon-class="edit"></svg-icon>
				<el-button type="text" @click="editInfo">
					修改信息
				</el-button>
			</el-col>
		</el-row>
		<el-row class="user-info" v-if="userItems.userInfo">
			<el-col :span='12' v-for='item in userItems.userInfo' :key='item.label' class="user-info-item">
				{{item.label}} : {{ item.value}}
			</el-col>
		</el-row>
		<el-row class="border-bottom">
			<el-col  class="user-title">
				登录密码
			</el-col>
		</el-row>
		<el-row class="pwdRow" >
			<el-col class="pwdInfo">
				<div>
					当前密码强度
				</div>
				<div class="pwdProgress">
					<el-progress :percentage="password_strength ? 100 : 20 " :status="password_strength ? 'success' : 'exception'"></el-progress>
				</div>
				<div>
					<el-button type="text" class="editButton" @click="editPwd">
						修改密码
					</el-button>
				</div>
			</el-col>
			<el-col class="pwdTip">
				备注：安全性高的密码可以使账号更安全，建议您定期更换密码，设置一个包含英文字母（区分大小写）、符号和数字且长度为8-20位的密码。
			</el-col>
		</el-row>
		<el-row  class="border-bottom" v-if="editModule">
			<el-col :span="22" class="user-title">
				提醒设置 
			</el-col>
			<el-col :span="2" class="editInfo">
				<svg-icon icon-class="edit"></svg-icon>
				<el-button type="text">
					设置提醒
				</el-button>
			</el-col>
		</el-row>
		<el-row class="pwdRow" v-if="editModule">
			<el-col >
				提醒方式：
				<span v-for="item in tipList" class="tipSetting">
					<svg-icon icon-class="correctInCircle" :class="item.status ? 'correct' : 'error'" v-if="item.status"></svg-icon>
					<svg-icon icon-class="error" :class="item.status ? 'correct' : 'error'" v-if="!item.status">></svg-icon>
					{{item.value}}
				</span>
			</el-col>
			<el-col class="pwdTip">
				备注：开通提醒设置后会以相应方式发送提醒信息至预留账户。
			</el-col>
		</el-row>
	</div>
</template>

<script>
	import userBig from '@/assets/user-big.png';
	export default {
		data(){
			return {
				msg:'用户中心',
				user:{},
				userBig,
				password_strength:'',
				editModule:false,
				userType:'personal',
				userInfo:{
					company:{
						userType:[
							{
								label:'登录账号',
								id:'account',
								value:'--',
								span:24
							},
							{
								label:'公司名称',
								id:'company_name',
								value:'--'
							},
							{
								label:'公司类型',
								id:'company_type',
								value:'--'
							},
							{
								label:'账号ID',
								id:'account_id',
								value:'--'
							},
							{
								label:'注册时间',
								id:'create_date',
								value:'--'
							}
						],
						userInfo:[
							{
								label:'负责人',
								id:'customer_name',
								value:'--',
							},
							{
								label:'性别',
								id:'sex',
								value:'--',
							},
							{
								label:'注册邮箱',
								id:'email',
								value:'--',
							},
							{
								label:'手机号码',
								id:'mobile',
								value:'--',
							},
							{
								label:'联系地址',
								id:'address',
								value:'--',
							}
						]
					},
					personal:{
						userType:[
							{
								label:'姓名',
								id:'name',
								value:'--',
							},
							{
								label:'用户名',
								id:'customer_name',
								value:'--',
							},
							{
								label:'公司名称',
								id:'company_name',
								value:'--'
							},
							{
								label:'注册邮箱',
								id:'email',
								value:'--'
							},
							{
								label:'手机号码',
								id:'phone',
								value:'--'
							},
							{
								label:'联系地址',
								id:'address',
								value:'--'
							}
						]
					}
				},
				tipList:[
					{
						status:true,
						value:'邮箱提醒'
					},
					{
						status:true,
						value:'短信提醒'
					},
					{
						status:false,
						value:'微信提醒'
					}
				]
				
				
			}
		},
		methods:{
			
		},
		computed:{
			userItems(){
				let userTypeList = this.userInfo[this.userType].userType;
				userTypeList.map(item=>{
					item.value = this.user[item.id] || '--'
				})
				
				return this.userInfo[this.userType];
			}
		},
		created(){
			this.user = this.$store.state.user.user;
			this.password_strength = this.user.password_strength;
			console.log(this.user)
		},
		methods:{
			editImg(){
				console.log('修改头像')
			},
			editPwd(){
				console.log('修改密码')
				this.$router.push('/user/userInfo/editPwd')
			},
			editInfo(){
				console.log('修改信息')
			}
		}
		
		
	}
</script>

<style lang="scss" scoped="scoped">
	.main{
		background: #fff;
		height: 100%;
		font-size:12px;
		color:#333333;
		padding:0px 20px;
	}
	.correct{
		color:#56D6C4
	}
	.error{
		color:#fa767c;
	}
	.border-bottom{
		font-size:14px;
		line-height: 34px;;
		border-bottom:1px dashed #dce0e3;
	}
	.user-title-row{
		padding-top:10px;
		line-height:34px;
	}
	.user-title{
		font-size:14px;
		font-weight: 600;
	}
	.user-type-title{
		
	}
	.user-type-vip{
		font-size:12px;
		color:#F9CD76;
		margin-left:15px;
	}
	.user-type{
		padding:20px;
		.user-img{
			padding:0 20px;
			width:75%;
			box-sizing:border-box;
			img{
				width:100%;
			}
		}
		.user-type-editImg{
			width:75%;
			text-align: center;
			.el-button--text{
				color:#3dc7fa;
				font-size:12px;
			}
		}
		.user-type-items{
			padding-top:20px;
			.user-type-item{
				line-height: 32px;;
			}
		}
	}
	.user-info-title{
		
	}
	.user-info{
		padding-top:10px;
		.user-info-item{
			line-height: 32px;
			font-size:12px;
		}
		
	}
	.editInfo{
		font-size:14px;
		color:#3dc7fa;
		.el-button--text{
			color:#3dc7fa;
			font-size:12px;
		}
	}
	
	.editButton.el-button--text{
		color:#3dc7fa;
		font-size:12px;
	}
	.pwdRow{
		line-height: 32px;
		font-size:12px;
		padding:20px 0;
		.pwdInfo{
			>div{
				display: inline-block;
			}
			.pwdProgress{
				width:400px;
				padding:0 10px 0 20px;
			}
		}
		.pwdTip{
			color:#666666;
		}
	}
	.tipSetting{
		padding-right:10px;
	}
	
</style>


