import Layout from '@/views/ecm/ecm'
import EbsList from '@/views/ebs/EbsList'
import EbsDetail from '@/views/ebs/EbsDetail'
import EbsCreate from '@/views/ebs/EbsCreate'

import i18n from '@/lang'


let router = {
	path:'ebs',
	name:'ecmEbs',
	component:Layout,
	redirect:'ebs/ebsList',
	children:[
		{
      		path:'ebsList',
      		name:'ecmEbsList',
    		component:EbsList,
      	},
      	{
      		path:'ebsDetail/:serverId',
      		name:'ecmEbsDetail',
    		component:EbsDetail,
    		meta:{
      			dependOn:'ecmEbsSub'
      		}
      	},
      	{
      		path:'ebsCreate',
      		name:'ecmEbsCreate',
    		component:EbsCreate,
    		meta:{
      			dependOn:'ecmEbsSub',
      			aisdeHide:true
      		}
      	},
	]
}

export default router;



