import Layout from '@/views/Layout'
import EbsList from '@/views/ebs/EbsList'
import EbsDetail from '@/views/ebs/EbsDetail'
import EbsCreate from '@/views/ebs/EbsCreate'

import i18n from '@/lang'


let router = {
	path:'/ebs',
	name:'ebs',
	component:Layout,
	redirect:'/ebs/ebsList',
	children:[
		{
      		path:'ebsList',
      		name:'ebsList',
    		component:EbsList,
      	},
      	{
      		path:'ebsDetail/:serverId',
      		name:'ebsDetail',
    		component:EbsDetail,
    		meta:{
      			dependOn:'ebsSub'
      		}
      	},
      	{
      		path:'ebsCreate',
      		name:'ebsCreate',
    		component:EbsCreate,
    		meta:{
      			dependOn:'ebsSub',
      			aisdeHide:true
      		}
      	},
	]
}

export default router;



