<template>
	<div class="EipBind">
		<el-row class="action-tip" v-if='!fliterData.elb'>
			{{actionTip}}
		</el-row>
		<el-row v-if='!fliterData.elb && !fliterData.table'>
			<el-col>
				<console-table :tableData='ecmListData'
					:operationOrNot=false
					:tableOptions = 'fliterData.option'
					:noEffect=true
					:height='380'
					:key='fliterData.date'
					:eipRadioShow=true
					@radioChange = 'radioChange'
					:Pagination = 'Pagination' @pagingEvent = 'pagingChange'
					></console-table>
			</el-col>
		</el-row>
		<el-row v-if='fliterData.port'>
			<el-row>
				<el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm" size="small">
					<el-row>
						<el-form-item v-if='fliterData.table' label="云服务器：" prop="EcmName" required>
	    					<el-input style="width: 180px;" disabled='disabled' v-model="ruleForm.EcmName"></el-input>
						</el-form-item>
						<el-form-item label="公网端口" required class='port'>
					      	<el-col :span="4">
						      	<el-form-item prop="publicport">
						        	<el-input v-model.number="ruleForm.publicport" /></el-input>
						      	</el-form-item>
	    					</el-col>
					      	<el-col class="line" :span="2">-</el-col>
					      	<el-col :span="11">
						      	<el-form-item prop="publicendport">
						        	<el-input v-model.number="ruleForm.publicendport" /></el-input>
						      	</el-form-item>
	    					</el-col>
    					</el-form-item>
				   </el-row>
				   <el-row>
				   		<el-form-item label="私网端口" required class='port'>
					   		<el-col :span="4">
						      	<el-form-item prop="privateport">
						        	<el-input v-model.number="ruleForm.privateport" /></el-input>
						      	</el-form-item>
	    					</el-col>
					      	<el-col class="line" :span="2">-</el-col>
					      	<el-col :span="11">
						      	<el-form-item prop="privateendport">
						        	<el-input v-model.number="ruleForm.privateendport" /></el-input>
						      	</el-form-item>
	    					</el-col>
	    				</el-form-item>
				   </el-row>
			      	<el-form-item label="协议 :" prop="agreement">
			          	<el-select v-model='ruleForm.protocol'>
						<el-option v-for = 'item in agree'
							:label='item.num'
							:value='item.num' :key="item.num"></el-option>
						</el-select>
			      	</el-form-item>
			    </el-form>
			</el-row>
		</el-row>
		<el-row v-if='fliterData.elb'>
			<el-form :model="elbForm" :rules="elbRules" ref="elbForm" label-width="140px" class="elb-ruleForm" size="small">
				<el-form-item label="负载均衡名称：" prop="elbName">
    				<el-input v-model="elbForm.elbName" placeholder="请输入负载均衡名称"></el-input>
  				</el-form-item>
  				<el-form-item label="子网信息：" prop="subnet">
    				<el-input v-model="elbForm.subnet" placeholder="子网1/负载均衡子网"></el-input>
  				</el-form-item>
			</el-form>
		</el-row>
	</div>
</template>

<script>
	import ConsoleTable from '@/components/ConsoleTable'
	import i18n from '@/lang'
	import { EcmAction } from '@/api/ecm'
	import { getEcmList } from '@/api/vpc'
	export default{
		props:{
			actionTip:[String],
			form:[Object],
			tableData:[Object],
		},
		components:{
			ConsoleTable,
		},
		data(){
			var check = (rule, value, callback) => {
		        if (!Number.isInteger(value)) {
		            callback(new Error('端口值必须为数字值'));
		        } else {
		            if (value < 1) {
		              callback(new Error('端口范围在 1 到 65535 之间'));
		            } else if(value > 65535) {
		              callback(new Error('端口范围在 1 到 65535 之间'));
		            }else{
		            	callback();
		            }
		        }
		    };
		    var check2 = (rule, value, callback) => {
	          	if(this.ruleForm.publicport){
	          		if(value < this.ruleForm.publicport){
	          			callback(new Error('端口下限应小于等于端口上限'));
	          		}else{
	          			callback();
	          		}
	          	}
		    };
		    var check3 = (rule, value, callback) => {
		        if(this.ruleForm.privateport){
	          		if(value < this.ruleForm.privateport){
	          			callback(new Error('端口下限应小于等于端口上限'));
	          		}else{
	          			callback();
	          		}
		        }
		    };
			return{
				ruleForm:{
					EcmName:'',
					publicport:'',
					publicendport:'',
					privateport:'',
					privateendport:'',
					protocol:'TCP',
				},
				elbForm:{
					elbName:'',
					subnet:''
				},
				rules:{
					publicport:[
						{ required: true, message: '请输入端口范围', trigger: 'blur' },
						{ validator: check, trigger: 'blur' }
					],
					publicendport:[
						{ required: true, message: '请输入端口范围', trigger: 'blur' },
						{ validator: check, trigger: 'blur' },
						{ validator: check2, trigger: 'blur' }
					],
					privateport:[
						{ required: true, message: '请输入端口范围', trigger: 'blur' },
						{ validator: check, trigger: 'blur' }
					],
					privateendport:[
						{ required: true, message: '请输入端口范围', trigger: 'blur' },
						{ validator: check, trigger: 'blur' },
						{ validator: check3, trigger: 'blur' }
					],
				},
				elbRules:{
					elbName:[
						{ required: true, message: '请输入负载均衡名称', trigger: 'blur' },
						{ min: 1, max: 65, message: '长度范围在 1 到 65之间', trigger: 'blur' }
					],
					subnet:[
						{ required: true, message: '请输入子网信息', trigger: 'blur' },
						{ min: 1, max: 65, message: '端口范围在 1 到 65 之间', trigger: 'blur' }
					],
				},
				agree:[{
					num:'TCP'
				},{
					num:'UDP'
				}],
				Pagination:{
		        	'pagingData':[10,20,50],
		        	'total':0,
		        	'show':false
		        },
				filter:{
					satus:'',
					limit:10,
					page:1,
					name:'',
					id:''
				},
				ecmListData:[]
			}
		},
		methods:{
			radioChange(val){
				console.log(val);
				this.form.data = val;
			},
			getEcmList(){
	  			let params = this.filter;
	  			let vpcid = this.tableData.vpcid;
	  			getEcmList(params,vpcid).then((res)=>{
	  				let servers = Array.from(res.data.results);
	  				let totalCount = res.data.total_count;
	  				servers = this.filterServers(servers)
	  				this.ecmListData = servers;
	  				this.Pagination.total = totalCount;
      				if(totalCount > this.Pagination.pagingData[0]){
      					this.Pagination.show = true;
      				}
	  			}).catch((error)=>{
	  				console.log(error)
	  			})

	  		},
	  		filterServers(servers){
      			servers = servers.filter((item)=>{
      				if(item.state == 'Starting'){
      					return false
      				}else{
      					//item.privateIp = item.nic.map
	      				//获取私有IP
	      				let privateIp = item.nic ? item.nic.length && item.nic[0].ipaddress : item.addresses ? item.metadata && this._.find(item.addresses[item.metadata.vpcname],{'OS-EXT-IPS:type':'fixed'}).addr : '--'
	      				item.privateIp = privateIp || '--';
	      				return true;
      				}
      			})
      			return servers
      		},
	      	pagingChange(type,val){
      			if(type == 'num'){
      				this.filter.limit = val;
      			}else if(type == 'page'){
      				this.filter.page = val;
      			}
      			this.getEcmList();
      		},
	      	validateForm(){
	      		if(!this.tableData.vpcName){
	      			if(this.form.data){
		      			if(this.tableData.port){
			      			this.form.rule = this.ruleForm;
			      			return this.$refs['ruleForm'].validate();
			      		}else if(this.tableData.elb){
			      			this.form.data = this.ruleForm;
			      			return this.$refs['elbForm'].validate();
			      		}else{
			      			return new Promise((resolve,reject)=>{
			      				resolve();
			      			})
			      		}
		      		}else{
		      			this.$message({
				          	message: '请选择一台要绑定的云服务器',
				          	type: 'error'
				        });
				        return new Promise((resolve,reject)=>{
		      				reject();
		      			})
		      		}
	      		}else{
	      			this.form.rule = this.ruleForm;
			      	return this.$refs['ruleForm'].validate();
	      		}
	      		

	      	}
		},
		computed:{
			fliterData(){
				return this.tableData
			},
		},
		mounted(){
			if(!this.tableData.elb && !this.tableData.table){
				this.getEcmList(this.filter);
			}
			if(this.tableData.vpcName){
				this.ruleForm.EcmName = this.tableData.vpcName;
			}
		}
	}
</script>

<style lang="scss">
	.EipBind{
		min-width: 550px;
		.action-tip{
			color: #fad488;
			padding-bottom: 20px;
		}
		.el-form-item{
			.el-form-item__content{
				margin-left: 0!important;
			}
		}
		.elb-ruleForm{
			.el-form-item__label{
				text-align: left;
			}
			.el-form-item__content{
				.el-input{
					width: 200px;
					.el-input__inner{
						width: 200px;
						height: 30px;
					}
				}
			}
		}
		.demo-ruleForm{
			margin-top: 15px;
			.port{
				.el-form-item{
					.el-form-item__content{
						.el-input{
							.el-input__inner{
								width: 100px;
							}
						}
					}
				}
			}
		}
		.line{
			text-align: center;
		}
	}
</style>



