<template>
  <div class="main">
    <RegionList :name ='name' :regionData='regionData' :buttonText ='buttonText' @create='create' @changeRegion = 'changeRegion'></RegionList>
    <div class="content">
      <div class="content_top">
      	<div class="vpc_mark">您还可以创建{{vpcRemainCount}}个虚拟网络VPC</div>
        <ConsoleSearch
        	class="vpc_search"
          :options ='options'
          :optionsValue ='optionsValue'
          @searchEvent = 'search'
          @clear = 'clear'
          :btnClose = true
          @selectChange = "selectChange"
          :disableData='changeTableData'
          @refreshEvent = 'refresh'
        ></ConsoleSearch>
      </div>
      <div>
        <ConsoleTable class="vpclist"
          :tableData='vpclist'
          :tableOptions="tableOptions"
          :selectOrNot=true :dragable=true
          :loading = 'loading'
          :operationOrNot=true
          :OperationEvents='vpcOperation'
           @buttonClick = 'buttonClick'
           @changeTable="changeTable"
           :Pagination = 'Pagination'
           @pagingEvent = 'pagingChange'
        ></ConsoleTable>
      </div>
    </div>
  </div>
</template>

<script>
	import service from '@/utils/request'
  import RegionList from '@/components/RegionList'
  import ConsoleSearch from '@/components/ConsoleSearch'
  import ConsoleTable from '@/components/ConsoleTable'
  import VpcCreateChoose from '@/views/vpc/VpcCreateChoose'
  import vpcDilogView from './vpcDilogView'
  import { getRegionId, setRegionId } from '@/utils/auth'
  import i18n from '@/lang'
  import { statFormat } from '@/utils/statformat'
  import { getVpcList,deleteVpc ,editVpc,queryAsyncJob,GetVpcDetail,queryQuota} from '@/api/vpc'
  import { GetRegionNum } from '@/api/ecm'
  let timer;
  export default {
    props:{
      name:[String],
      buttonText:[String]
    },
    components:{
      RegionList,
      ConsoleSearch,
      ConsoleTable,
      VpcCreateChoose,
      vpcDilogView
    },
    data(){
      return {
        regionId:'',
        checkBox:true,
        statusOnOff:true,
        detailRefresh:false,
        loading:false,
        totalCount:0,
        vpcTotalCount:0,
        vpcRemainCounts:0,
        vpcLimitQuota:0,
        Pagination:{
          'pagingData':[10,20,50],
          'total':0,
          'show':false
        },
        vpclist:[],
        multipleSelection: [],
        changeTableData:[],
        regionData:[],
        filter:{
          status:'',
          limit:10,
          page:1,
          name:'',
          id:''
        },
        options: [{
          value: i18n.common.allStatus,
          label: i18n.common.allStatus
        }, {
          value: i18n.vpc.Enabled,
          label: i18n.vpc.Enabled
        }, {
          value: i18n.vpc.Disabled,
          label: i18n.vpc.Disabled
        }],
        optionsValue: i18n.common.allStatus,
        tableOptions:[{
          prop:'name',
          label:i18n.vpc.name,
          width:'172px',
          temp:true,
          detail:true,
          path:'/vpc/vpcDetail',
        },{
          prop:'displaytext',
          label:i18n.vpc.displaytext,
        },{
          prop:'stateTxt',
          label:i18n.vpc.state,
          temp:true,
        	status:true
        },{
        	prop:'vpc_type',
          label:i18n.vpc.vpc_type,
        },{
          prop:'cidr',
          label:i18n.vpc.cidr,
        },{
          prop:'net_num',
          label:i18n.vpc.net_num,
        }],
        vpcOperation:[{
          value:i18n.vpc.delete,
          fun:(data)=>{
            return this.delete(data);
          }
        },{
          value:i18n.vpc.edit,
          fun:(data)=>{
            return this.edit(data);
          }
        }]
      }
    },
    methods:{
      selectChange(value){
        this.filter.name=''
        this.filter.id=''
        this.filter.status =value==='启用'?'Enabled':'Disable';
        console.log(this.filter);
        this.getVpcList()
      },
      clear(){
				this.filter.keyword='';
				this.filter.name='';
				this.filter.id='';
			},
      search(filter){
        if(filter.name == 'id'){
      			this.filter.status = ''
						this.filter.name=''
						this.filter.id=filter.value;
	//				this.filter[filter.name] = filter.value;
						this.filter.keyword = '';
      	}else{
      			this.filter.status = ''
						this.filter.name=''
						this.filter.id=''
	//				this.filter[filter.name] = filter.value;
						this.filter.keyword = filter.value;
      	}
        this.getVpcList()
      },
      changeTable(val){
        this.changeTableData = val
      },
      handleSelectionChange(val) {
        this.multipleSelection = val;
      },
      delete(arg){
      	let alertMsg=this.$message;
      	if(arg.subnet_creating){
      		alertMsg({
				          	message: '该VPC下有正在创建的子网,不允许删除！请等待子网创建成功后再重试。',
				          	type: 'error'
				        });
      		return
      	}
        let view = vpcDilogView;
        let data = {}
        data.type='delete';
        data.title="删除虚拟私有云";
        let form ={
          name:arg.name
        }
        let option = {
          data,
          view,
          form
        }
        let vpcId=arg.id;
        let _this = this;
        this.$alertDialog(option).then((res)=>{
        		this.loading=true
            deleteVpc(vpcId).then((response)=>{
            	this.loading=false
              alertMsg('VPC刪除成功');
//            let count = 0;
//	            for(var i =0;i < _this.vpclist.length;i++){
//	            	if(vpcId == _this.vpclist[i].id){
//	            		count = i;
//	            		break;
//	            	}
//	            }
//	            _this.vpclist.splice(count,1);
                console.log(response);
                this.refresh();
            }).catch((error)=>{
            		this.loading=false
                console.log(error);
                this.refresh();
//            let result=error.response.data.msg
//            this.$message({
//              message: i18n.vpc[result],
//              type: 'error'
//            });
//              this.$message('删除VPC失败，此VPC下存在子网！');
            })
          });
        console.log('delete');
      },
      autoRefreshJob(jobIds){
  			if(jobIds){
  				if(typeof(jobIds)=='string'){
  					
  					this.getJobDetail(jobIds,data)
  					
      			}else if(Array.isArray(jobIds)){
      				jobIds.map(item =>{
      					console.log(item)
      					item.jobid && this.getJobDetail(item.jobid)
      				})
      			}else{
      				console.log(jobIds)
      			}
  			}
  		},
  		getJobDetail(id){
  			if(this.detailRefresh){
  				return
  			}
  			let timer;
  			let _this = this;
//    			timer && clearTimeout(timer)
  			queryAsyncJob(id).then(res=>{
				console.log(res)
				let result = res.data.results[0];
				if(result.jobstatus == 1){
					console.log(result.jobresult.vpc);
					this.getVpcDetail(result.jobresult.vpc.id)
				}else if(result.jobstatus == 2){
					clearTimeout(timer)
					this.refresh()
				}else{
					timer = setTimeout(()=>{
						_this.getJobDetail(result.jobid)
					},5000)
				}
				
			}).catch(error=>{
				this.detailRefresh = true;
				setTimeout(()=>{
					this.refresh()
				},3000)
				
			})
  	},
      edit(arg){
        let alertMsg=this.$message;
        let refresh=this.refresh;
        let view = vpcDilogView;
        let data = {}
        let _this = this;
        data.type='edit';
        data.title="修改虚拟私有云";
        let form ={
          name:arg.name,
          displaytext:arg.displaytext == '--' ? '' : arg.displaytext
        }
        let option = {
          data,
          view,
          form
        }
        let vpcId=arg.id;
        this.$alertDialog(option).then((res)=>{
        	console.log(res);
        	if(res.edited){
        		let data={
	            "name":res.name,
	            "desc":res.displaytext ||''
	          }
	          editVpc(vpcId,data).then((response)=>{
	            let jobid=response.data.results[0].jobid;
	            _this.detailRefresh = false;
	           	_this.autoRefreshJob(response.data.results);
	            console.log(response);
	          }).catch((error)=>{
	            console.log(error);
	//              this.$message('删除VPC失败，此VPC下存在子网！');
	          })
        	}else{
        		return
        	}
          
        });
      },
      buttonClick(funName,data){
        funName(data);
      },
      create(){
      	if(this.vpcRemainCount > 0){
//    		this.$router.push({path:'/vpc/creatVpc'})
					let tableData = [
      				{
      					svg:'privateVPC',
      					title:'创建虚拟网络VPC',
      					msg:''
      				},
      				{
      					svg:'physicalVPC',
      					title:'创建物理网络VPC',
      					msg:'在线创建物理VPC功能暂未开通，如需开通物理网络VPC，请联系您的客户经理。'
      				}
      			];
					let option = {
      				'data':{
      					'option':'',
      					'title':'创建虚拟私有云',
      					'actionTip':'',
      					'data':tableData
      				},
      				'view':VpcCreateChoose,
      				'form':{},
      				'ref':true
      			};
      			/*this.$dialog({
      				title:'创建虚拟私有云',
      				view:VpcCreateChoose,
      				width:"550px",
      				props:{
      					tableData:option.data,
      					form:{
      						data:""
      					}
      				},
      				win:'true'
      			},(win)=>{
      				win.location.href = '/#/vpc/creatVpc';
      			})
      			
      			
      			return*/
      			let _this = this;
      			this.$alertDialog(option).then((res)=>{
      				if(res.data && res.data.svg == 'privateVPC'){
//      				window.open('/#/vpc/creatVpc')
							let creatDom = document.getElementById("vpcBindChoose");
							setTimeout(function(){
								console.log(creatDom);
										creatDom.style.display = 'none';
							},1000)
								
								res.win.location = '/#/vpc/creatVpc';
      				}else{
      					res.win.close()
      				}
      			})
      	}else{
      		this.$message({
	          	message: '虚拟私有云配额不足!',
	          	type: 'warning'
	        });
      	}
        
      },
      changeRegion(uuid){
      	service.cancle('CancelToken');
        this.regionId = uuid;
//      this.GetRegionNums()
//      this.getVpcList(uuid)
				this.refresh();
      },
      getRegion(){
        let timer1;
        if(getRegionId()){
          clearTimeout(timer1)
          this.changeRegion();
        }else{
          let _this = this;
          timer1 = setTimeout(function(){
            _this.getRegion()
          },300)
        }
      },
      autoRefresh(jobIds){
				if(jobIds){
					jobIds.map(item =>{
						console.log(item)
						this.getVpcDetail(item)
					})
				}
			},
			getVpcDetail(id){
				let _this = this;
				if(this.detailRefresh) return
				timer && clearTimeout(timer);
  			GetVpcDetail(id).then(res=>{
					let result = res.data.results[0];
					console.log(result);
					if(!result.subnet_creating){
						result['net_num']=result.network.length;
          	result['status'] = result.state ? statFormat(result.state) : '--'
	          result['displaytext'] = result.displaytext ? result.displaytext : '--';
	          result['vpc_type'] = '虚拟网络VPC';
//        	result['state'] = result.state==='Enabled'?'启用':'停用';
          	result.fake_vpc && result.fake_vpc == 1 && _this.addOperation(result)
						_this.vpclist.map((item,index) =>{
							if(item.id == result.id){
								console.log(result);
								_this.$set(_this.vpclist,index,result)
								return true;
							}
						})
					}else{
						timer = setTimeout(()=>{
							_this.getVpcDetail(id)
						},5000)
					}
				
			}).catch(error=>{
				console.log(error)
				this.detailRefresh = true;
				timer = setTimeout(()=>{
						_this.refresh()
				},3000)
			})
			},
      getVpcList(uuid){
        let params =this.filter;
        if(this.regionType == 'os'){
        	params.detail = 1
        }
        this.loading=true
        getVpcList(params,uuid).then((res)=>{
//          console.log(res);
          let vpclist = res.data.results;
          this.filterServers(vpclist);
          this.vpclist=vpclist;
          let totalCount = res.data.total_count;
          this.totalCount = totalCount;
          this.Pagination.total = this.totalCount;
          this.vpcLimitQuota = res.data.real_vpc_count;
          if(totalCount>this.Pagination.pagingData[0]){
            this.Pagination.show = true
          }
          let jobIds = [];
  				vpclist.filter((item)=>{
  					if(item.subnet_creating){
  						jobIds.push(item.id);
  					}else {
  						return false
  					}
  				});
  				if(jobIds.length > 0){
  					this.detailRefresh = false;
  					this.autoRefresh(jobIds);
  				}
          this.loading=false
        }).catch((error)=>{
          if(error.message == 'CancelToken'){
      					
  				}else{
  					this.loading=false
  				}
          console.log(error)
        })

      },
      addOperation(vpc){
      	let operation = [{
          value:i18n.vpc.delete,
          disabled:true,
          fun:(data)=>{
            return this.delete(data);
          }
        },{
          value:i18n.vpc.edit,
          disabled:true,
          fun:(data)=>{
            return this.edit(data);
          }
        }];
//      this.$set(vpc,'name','默认网络')
				let name = vpc.name == 'fake_vpc' ? '默认网络' : vpc.name;
				this.$set(vpc,'name',name)
        this.$set(vpc,'cidr','--')
        this.$set(vpc,'vpc_type','物理网络VPC')
        this.$set(vpc,'operation',operation)
      },
      filterServers(list){
      	let _this = this;
        list.filter(function(vpc,index){
        	if(_this.regionType == 'os'){
        		let formateState = {
        			'ACTIVE':'Enabled'
        		}
        		vpc['net_num'] = vpc.subnet_list.length;
        		vpc['displaytext'] = vpc.description;
        		vpc.status =  statFormat(formateState[vpc.status]);
        	}
        	if(_this.regionType == 'cs'){
        		vpc['net_num']=vpc.network.length;
        		vpc['displaytext'] = vpc.displaytext ? vpc.displaytext : '--';
        		vpc['status'] = vpc.state ? statFormat(vpc.state) : '--';
        	}
          vpc['vpc_type'] = '虚拟网络VPC';
//        vpc['state'] = vpc.state==='Enabled'?'启用':'停用';
          //fake_vpc
          vpc.fake_vpc && vpc.fake_vpc == 1 && _this.addOperation(vpc)
          return true;
        })
      },
      refresh(){
        this.filter.status = ''
//      this.filter.name=''
        this.filter.id=''
//      this.filter.keyword=''
        this.GetRegionNums()
//      this.queryQuotas();
        this.getVpcList()
      },
      pagingChange(type,val){
        if(type == 'num'){
          this.filter.limit = val;
        }else if(type == 'page'){
          this.filter.page = val;
        }
        this.loading =true;
        this.getVpcList();
      },
      queryQuotas(){
      	queryQuota().then((res)=>{
          console.log(res.data.results);
          let Quota = res.data.returnObj;
//        this.vpcTotalCount = Quota.vpc_limit - 0;
					this.vpcTotalCount =Quota.vpc_limit ? Quota.vpc_limit - 0 :'';
        }).catch((error)=>{
          console.log(error)
        })
      },
      GetRegionNums(){
				let params ={
					resource:'vpc',
					regionid:getRegionId()
				}
				GetRegionNum(params).then( res => {
					let data = res.data.results[0].VPC;
					console.log(data)
					if(data && data.detail){
						this.regionData = data.detail;
//						this.vpcLimitQuota = data.total_count;
					}
					
				})
			},
    },
    computed:{
      changeRowSty(row,index){
        return{'background':'#f0f2f7'}
      },
      changethSty(row,index){
        return{'background':'#f0f2f7','color':'#333333','font-size':'12px','font-weight':'600',}
      },
      vpcRemainCount(){
      	this.vpcRemainCounts = this.vpcTotalCount - this.vpcLimitQuota;
      	if(this.vpcRemainCounts > 0){
      		return this.vpcRemainCounts
      	}else{
      		this.vpcRemainCounts = 0;
      		return this.vpcRemainCounts
      	}
      },
      regionType(){
      	return this.$store.getters.regionType;
      }
    },
    mounted(){
      this.getRegion();
//    this.queryQuotas();
			let quotas = this.$store.state.user.user.quotas;
			if(quotas.vpc_limit){
				this.vpcTotalCount = quotas.vpc_limit - 0;
			}else{
				this.queryQuotas();
			}
			
    },
    beforeDestroy(){
			this.detailRefresh = true;
		}
  }
</script>

<style lang="scss" scoped="scoped">
  .main{
    background: #f6f8fb;
  }
  .content{
    .content_top{
    	 display: flex;
      flex-direction: row;
      justify-content: space-between;
      background: #ffffff;
      padding-top: 30px;
      padding-left: 20px;
      padding-bottom: 20px;
      .vpc_mark{
      	/*display: inline-block;
        width: 40%;
        position: relative;
        top:15px;*/
       line-height: 35px;
        font-size: 12px;
      }
      .vpc_search{
        /*display: inline-block;*/
        width: 60%;
        text-align: right;
        .el-row{
          text-align: right;
          .el-col{
            float: right;
          }
        }
      }
    }
  }
</style>



