<template>
	<div class="logContent">
		<el-container>
			<el-header height="100px" class="content_bg">
				<div class="contentInfo">
					<div class="contentLeft">
						<h4 class="botton_15">所有日志</h4>
						<el-radio-group v-model="city" size="mini" fill="#f9cd76">
							<el-radio-button class="radio-group-btn" label="guangzhou">广州（3）</el-radio-button>
							<el-radio-button class="radio-group-btn" label="shanxi">陕西（0）</el-radio-button>
							<el-radio-button class="radio-group-btn" label="beijing">北京（5）</el-radio-button>
							<el-radio-button class="radio-group-btn" label="hunan">湖南（1）</el-radio-button>
							<el-radio-button class="radio-group-btn" label="chengdu">贵州（1）</el-radio-button>
							<el-radio-button class="radio-group-btn" label="shanghai">上海（2）</el-radio-button>
							<el-radio-button class="radio-group-btn" label="wulu">乌鲁木齐（2）</el-radio-button>
						</el-radio-group>
					</div>
					<div class="contentRight">
						<el-button type="primary"><router-link to="/logReport">日志报表</router-link></el-button>
					</div>
				</div>
			</el-header>
			<el-main class="content_bg top_10">
				<div class="logTableHeader">
					<el-select v-model="type" size="small" placeholder="类型">
					    <el-option
					      v-for="item in typeLst"
					      :key="item.value"
					      :label="item.label"
					      :value="item.value">
					    </el-option>
					 </el-select>
					 <el-date-picker
				      v-model="date"
				      type="date"
				      placeholder="选择日期"
				      default-value="2010-10-01" size="small">
				    </el-date-picker>
				    <el-button type="primary" size="small">搜索</el-button>
				</div>
				<el-table :data="tableData" headerCellClassName="tableHeaderbg">
				        <el-table-column prop="date" label="时间" width="140" align="center"></el-table-column>
				        <el-table-column prop="name" label="类型" width="120" align="center"></el-table-column>
				        <el-table-column prop="name" label="状态" width="120" align="center"></el-table-column>
				        <el-table-column prop="name" label="操作人" width="120" align="center"></el-table-column>
				        <el-table-column prop="address" label="描述" align="center"></el-table-column>
				</el-table>
				<el-pagination
				  background
				  layout="prev, pager, next"
				  :total="1000" class="paging">
				</el-pagination>
			</el-main>
		</el-container>
	</div>
</template>

<script>
	export default {
		data (){
			const item = {
		        date: '2016-05-02',
		        name: '王小虎',
		        address: '上海市普陀区金沙江路 1518 弄'
		     };
			return {
				city: 'guangzhou',
				tableData: Array(5).fill(item),
				type: '1',
				date:'',
				typeLst: [
					{
						value: '1',
						label: '类型1'
					},
					{
						value: '2',
						label: '类型2'
					},
					{
						value: '3',
						label: '类型3'
					},
				],
			}
		}
	}
</script>

//scoped
<style>
.logContent .paging {
	text-align: right;
    margin-top: 20px;
}
.logContent .tableHeaderbg {
	background-color: #f0f2f6 !important;
	color: #333;
}
.logContent .logTableHeader {
	text-align: right;
    padding-bottom: 20px;
    padding-top: 30px;
}
.logContent .content_bg {
	background-color: #fff;
    border-radius: 3px;
}
.logContent .contentInfo {
	height: 100%;
}
.logContent .contentLeft {
	display: inline-block;
    margin-top: 18px;
}
.logContent .top_10 {
	margin-top: 10px;
}
.logContent .botton_15 {
	margin-bottom: 15px;
}
.logContent .contentRight {
	display: inline-block;
    float: right;
    margin-top: 30px;
}	
.logContent .radio-group-btn {
	margin-right: 10px;
}
.logContent .el-radio-button__inner {
	border: 1px solid #dcdfe6;
    border-radius: 3px !important;
}
</style>


