<template>
	<div class="ip_renew">
		<el-container v-if="!isShowEcs">
			<el-header height="80px">
				<h4 class="bill_info_title">弹性IP续费</h4>
				<el-date-picker
					v-model="filter.expire_time"
					placeholder="选择日期" 
					value-format="yyyy-MM-dd" 
					size="small" style="width:200px">
				</el-date-picker>
				<div class="ip_search">
				  <el-input placeholder="请输入内容" v-model="inputType" class="input-with-select" size="small" style="width:350px">
				    <el-select v-model="selectVal" slot="prepend" placeholder="请选择">
				      <el-option label="公网IP" value="ip"></el-option>
				      <el-option label="ID" value="id"></el-option>
				    </el-select>
				    <el-button slot="append" icon="el-icon-search"></el-button>
				  </el-input>
				</div>
				<el-button type="primary" size="small" class="right">导出续费清单</el-button>
			</el-header>
			<el-main>
				<ConsoleTable :checkBox=true :operationOrNot =true :selectOrNot=true 
					:tableData = 'tableData' :tableOptions = 'tableOptions' 
					:OperationEvents='ebsOperation' :loading = 'loading' 
					:Pagination = 'Pagination' @pagingEvent = 'pagingChange' @buttonClick='options_callback' 
					@changeTable="changeTable"></ConsoleTable>
			</el-main>
		</el-container>
		<ecsRenew v-if="isShowEcs" :dataList="nodeItem"></ecsRenew>
	</div>
</template>

<script>
import ConsoleTable from '@/components/ConsoleTable'
import ecsRenew from './renewPay'
import {getIpServerList} from '@/api/cost'
	export default {
		components:{
			ConsoleTable:ConsoleTable,
			ecsRenew
		},
		data (){
			return {
				msg: '12345',
				inputType: '',
				selectVal: 'ip',
				isShowEcs: false,
				tableData: [],
				checkData: [],
				loading:false,
				Pagination:{
		        	'pagingData':[5,10,15],
		        	'total':0,
		        	'show':true
		        },
		        tableOptions: [
					{
						prop: "ipaddress",
						label: "公网IP",
					},
					{
						prop: "state",
						label: "状态"
					},
					{
						prop: "vpcname",
						label: "已绑定主机名称",
					},
					{
						prop: "networkrate",
						label: "宽带大小（Mbps）",
					},
					{
						prop: "expire_date",
						label: "到期时间"
					},
					{
						prop: "zonename",
						label: "倒计时",
					}
				],
				ebsOperation:[
					{
	        			value: '续费',
	        			fun:(data) => {
	        				return this.renew(data);
	        			}
	        		}
				],
				nodeItem: {},
      			filter:{
					limit:5,
					page:1,
					regionid: 'sjif259a8d3011e7aebb005056aaa8be',
					zoneid: '',
					list_all: 1,
					expire_time: '',
					id:'',
				},
			}
		},
		methods: {
			getIpServerData(){
				this.loading = true;
      			getIpServerList(this.filter).then(res => {
      				console.log(res,'sdafghjk');
      				if(res.status == 200){
						this.tableData = res.data.results;
						this.Pagination.total = res.data.total_count;
					}else{

					}
					this.loading = false;
      			},error => {
      				this.loading = false;
      				console.log("获取公网IP续费列表错误");
      			})
			},
			pagingChange(type,val){
      			if(type == 'num'){
      				this.filter.limit = val;
      			}else if(type == 'page'){
      				this.filter.page = val;
      			}
      			this.getIpServerData();
      		},
      		options_callback(fun,data){
      			fun(data);
      		},
      		renew(data){
      			console.log(data);
      			this.nodeItem = {
      				titleName: '弹性公网IP当前配置',
      				name: data.vpcname,
      				expire_date: '2018-06-10 12:40:32',
      				nodeList: [
      					{
      						title: '实例ID',
							value: 'wesdfgh234erty',
      					},
      					{
      						title: '线路类型',
							value: 'BGP（多线）',
      					},
      					{
      						title: '购买时常',
							value: '1个月',
      					},
      					{
      						title: '地域',
							value: '北京资源池',
      					},
      					{
      						title: '网络类型',
							value: '公网',
      					},
      					{
      						title: '操作系统',
							value: 'windows/64',
      					},
      				]
      			};
      			this.isShowEcs = true;
      		},
      		renewCallback() {
      			this.isShowEcs = false;
      			this.getIpServerData();
      		},
      		changeTable(data) {
      			this.checkData = data;
      		},
		},
		mounted: function(){
			this.getIpServerData();
		}
	}
</script>

<style lang="scss" scoped="scoped">
.ip_renew {
	background-color: #fff;
	height: 100%;
	.bill_info_title {
		margin: 15px 0;
	}
	.right {
		float: right;
	}
	.ip_search {
		display: inline-block;
	}
}
</style>
<style lang="scss">
.ip_renew .el-select .el-input {
    width: 90px;
  }
.ip_renew .input-with-select .el-input-group__prepend {
    background-color: #fff;
  }
</style>


