<template>
	<div class="ECSbatchRenew">
		<div class="stepBox">
			<div class="step_title">ECS批量续费</div>
			<el-row class="">
		  		<el-col :span="12">
		  			<div class="grid-content bg-purple steptitle_active">确认续费订单</div>
		  			<i class="step_line step_active"></i>
		  		</el-col>
		  		<el-col :span="12">
		  			<div class="grid-content bg-purple" v-bind:class="{steptitle_active : !submit}">提交</div>
		  			<i class="step_line" v-bind:class="{step_active : !submit}"></i>
		  		</el-col>
			</el-row>
		</div>
    	<div class="submit_content content" v-if="submit">
    		<div class="order_title">订单详情</div>
    		 <el-table
		      :data="tableData"
		      style="width: 100%; margin-top: 20px" size="mini"
		      :header-cell-style="objStyle">
		      <el-table-column label="实力名称&节点" width="180">
		        <template slot-scope="scope">
						<div>{{scope.row.name}}</div>
						<div>{{scope.row.dc_name}}</div>
				</template>
		      </el-table-column>
		      <el-table-column label="公网／内网IP">
		      	<template slot-scope="scope">
						<div>公网：{{scope.row.publicip}}</div>
						<div>内网：{{scope.row.ipaddress}}</div>
				</template>
		      </el-table-column>
		      <el-table-column label="配置" width="300">
		      	<template slot-scope="scope">
						<div>
							<label class="config_title">CPU：</label><span>{{scope.row.cup}}核</span>
							<label class="config_title">内存：</label><span>{{scope.row.mem}}GB</span>
							<label class="config_title">基础带宽：</label><span>{{scope.row.bandwidth}}Mbps</span>
						</div>
				</template>
		      </el-table-column>
		      <el-table-column
		        prop="renew_cycle"
		        label="续费周期">
		      </el-table-column>
		      <el-table-column label="详细时间">
		      	<template slot-scope="scope">
						<div>{{scope.row.expire_date}} 至</div>
						<div>{{scope.row.renew_expire_date}}</div>
				</template>
		      </el-table-column>
		      <el-table-column label="总价" width="100">
		        <template slot-scope="scope">
						<div class="yellow_color">{{scope.row.total_price}}</div>
				</template>
		      </el-table-column>
		      <el-table-column label="操作" align="center">
				   	<template slot-scope="scope">
						<el-button type="text" size="small" @click.stop="remove(scope.$index)">移除</el-button>
					</template>
				</el-table-column>
				</el-table-column>
		    </el-table>
		    <div class="renew_row">
				<label class="color_3 font_12">续费时长：</label>
				<el-radio-group v-model="renewTime" size="mini" fill="#f9cd76" @change="changeRenewTime">
					<el-radio-button class="radio-group-btn" :label="time.value" v-for="(time,index) in renewTimes" :key="index">{{time.name}}</el-radio-button>
				</el-radio-group>
			</div>
		    <div class="pay_row">
		    	<label class="YaHei_14_3">应付款：</label>
				<span class="pay_style">¥601.80</span>
			</div>
		    <div class="agreement_row">
				<el-checkbox v-model="checkedAgreement">我已经阅读并同意相关协议</el-checkbox>
				<a href="http://www.ctyun.cn/term/ecs" class="agreement" target="_blank">《弹性云主机服务协议》</a>
			</div>
		    <el-button type="primary" size="small" class="payment_btn" :disabled="!checkedAgreement" @click="submitPay">去支付</el-button>
    	</div>
    	<div class="success_content " v-if="!submit">
    		<div class="success_component">
			    <span class="success_icon">
			        <i class="el-icon-circle-check"></i>
			   </span>
			    <div>
			       <div class="create_state div_left">ECS批量续费成功</div>
			        <div class="div_left">
			          <span>订单编号：</span>
			          <span>{{so_number}}</span>
			        </div>
			        <div class="div_left" v-if="false">
			          <span>续费后日期：</span>
			          <span>2019-01-08</span>
			        </div>
			        <div class="reback_msg div_left">页面将于{{new_second_number}}秒内返回<span class="timer">{{new_second_number}}</span></div>
			        <div class="return_btn div_left">
			          <el-button type="primary" size="small" @click.stop="returnUrl">返回</el-button>
			        </div>
			    </div>
			  </div>
    	</div>
	</div>
</template>
<script>
import {renewPay} from '@/api/cost'
	export default {
		props:{
	        dataList: {
	            type: Array,
	            default: {}
	        }
	    },
	    data() {
	      	return {
	          	submit: true,
	          	checkedAgreement: false,
	          	objStyle: {
	          		color: '#333',
    				fontWeight: 'bold',
	          	},
	          	new_second_number: 5,
	          	renewTime: '1y',
	          	cycleType: 'Y',
				cycleCnt: 1,
				so_number: '-',
	          	tableData: [],
	          	nodeIdList: [],
		        renewTimes: [
					{
						name:'1月',
						value:'1m',
					},
					{
						name:'2月',
						value:'2m',
					},
					{
						name:'3月',
						value:'3m',
					},
					{
						name:'4月',
						value:'4m',
					},
					{
						name:'5月',
						value:'5m',
					},
					{
						name:'6月',
						value:'6m',
					},
					{
						name:'7月',
						value:'7m',
					},
					{
						name:'8月',
						value:'8m',
					},
					{
						name:'9月',
						value:'9m',
					},
					{
						name:'10月',
						value:'10m',
					},
					{
						name:'11月',
						value:'11m',
					},
					{
						name:'1年',
						value:'1y',
					},
					{
						name:'2年',
						value:'2y',
					},
					{
						name:'3年',
						value:'3y',
					},
				],
	      	}
	    },
	    methods: {
	    	submitPay(){
	    		const loading = this.$loading({
					          lock: true,
					          text: '拼命续费中',
					          spinner: 'el-icon-loading',
					          background: 'rgba(0, 0, 0, 0.7)'
					        });
	    		renewPay({
					id: this.nodeIdList,
					cycleType: this.cycleType,
					cycleCnt: this.cycleCnt,
				}).then((res) => {
					console.log(res);
					loading.close();
					if(res.status == 200){
						this.so_number = res.data.returnObj.so_number;
						this.submit = !this.submit;
        				this.timerOut();
					}else{
						this.$message({
				          showClose: true,
				          message: res.data.reason || res.data.msg || '批量续费失败，请稍后重试',
				          type: 'error'
				        });
					}
				},(error) => {
					loading.close();
					console.log(error);
					this.$message({
				          showClose: true,
				          message: '批量续费失败，请联系管理员',
				          type: 'error'
				        });
				});
	    	},
	    	returnUrl () {
          		this.$emit('callback');
      		},
      		changeRenewTime(val) {
      			var renew_date = parseInt(val),company = '';
				if(val.indexOf('y') !== -1){
					company = '年';
					this.cycleType = 'Y';
				}else{
					company = '月';
					this.cycleType = 'M';
				}
				this.cycleCnt = renew_date;
				this.tableData.map((item,index,array) => {
					var d = new Date(item.expire_date);
					if(company == '年'){
						d.setYear(d.getFullYear()+renew_date);
					}else{
						d.setMonth(d.getMonth()+renew_date);
					}
					item.renew_cycle = renew_date + company;
					item.renew_expire_date = d.Format("yyyy-MM-dd hh:mm");
				});
      		},
      		remove(index) {
      			this.tableData.splice(index,1);
      			this.nodeIdList.splice(index,1);
      			if(this.tableData.length == 0){
      				this.$emit('callback');
      			}
      		},
      		timerOut () {
		        this.timer = setTimeout(() => {
		                if(this.new_second_number === 1){
		                  this.returnUrl();
		                }else{
		                  this.new_second_number --;
		                  this.timerOut();
		                }
		            }, 1000);
		     }
	    },
      	mounted(){
      		this.dataList.map((item,index,array) => {
      			this.nodeIdList.push(item.id);
      			var d = new Date(item.expire_date);
      			d.setYear(d.getFullYear()+1);
      			this.tableData.push({
      				name: item.displayname,
      				dc_name: item.dc_name,
      				ipaddress: item.nic[0].ipaddress || '未绑定',
      				publicip: item.publicip || '未绑定',
      				cup: '1',
			        mem: '1',
			        bandwidth: 10,
			        renew_cycle: '1年',
			        expire_date: new Date(item.expire_date).Format("yyyy-MM-dd hh:mm"),
			        renew_expire_date: d.Format("yyyy-MM-dd hh:mm"),
			        total_price: '¥592.40',
      			});
      		});
      		if ( this.timer ) {  
	            clearTimeout(this.timer);  
	        }
      	},
      	computed: {

		}
	}
</script>

<style lang="scss" scoped="scoped">
.ECSbatchRenew {
	.stepBox {
		margin-bottom: 20px;
	}
	.step_title{
		margin-left: 20px;
		font-size: 14px;
		color: #333333;
		padding-top: 10px;
		padding-bottom: 35px;
	}
	.el-row {
		border-bottom: 20px solid #f6f8fb;
	}
	.renew_row {
		padding-top: 30px;
	}
	.pay_row {
		padding: 30px 0;
	}
	.agreement_row {
		padding-bottom: 20px; 
	}
	.content{
		padding-left: 20px;
    	padding-right: 20px;
    	padding-bottom: 20px;
	}
	.pay_style {
		font-size: 24px;
    	color: #f9a90c;
	}
	.YaHei_14_3 {
    	font-size: 14px;
    	color: #333;
	}
	.agreement {
		color: #3dc7fa;
		font-weight: bold;
		font-size: 12px;
	}
	.el-button--primary {
		background-color: #3dc7fa;
    	border-color: #3dc7fa;
	}
	.is-disabled {
		background-color: #75d3f3;
    	border-color: #75d3f3;
	}
	.el-table th, .el-table tr {
	    background-color: #f0f2f7;
	}
	.yellow_color {
		color: #f9cd76;
	}
	.config_title {
		font-size: 12px;
    	color: #b0b2b7;
	}
	.buttons{
	    text-align: center;
	  }
	  .success_icon{
	    .el-icon-circle-check{
	      color: #56d6c4;
	      font-size: 40px;
	      margin-right: 10px;
	    }
	  }
	  .return_btn {
	      padding: 20px 0 0 15px;
	      .el-button{
	      	background-color: #3ac0f1;
	      }
	  }
	  .create_state {
	      font-size: 20px;
	      padding-bottom: 5px;
	  }
	  .timer {
	      padding-left: 10px;
	      font-size: 15px;
	      color: #ecc79a;
	  }
    .success_component{
      display: flex;
      flex-direction: row;
      width: 30%;
      margin: 0 auto;
      text-align: center;
      margin-top: 5%;
      .reback_msg{
        color: #999999;
        margin-top: 20px;
        font-size: 12px;
      }
    }
    .div_left {
    	text-align: left;
    }
    .font_12 {
		font-size: 12px;
	}
	.color_3 {
		color: #333;
	}
}
</style>

<style lang="scss">
 .ECSbatchRenew{
  	background-color: #ffffff;
  	height: 100%;
		.steptitle_active{
			color: #3ac0f1;
		}
		.grid-content{
			text-align: center;
		}
		.step_line{
			margin-top: 20px;
			display: block;
		    height: 4px;
		    background: #f0f2f7;
		}
		.step_active{
			background: #3ac0f1;
		}
		.el-col{
			padding: 0;
		}
		.el-table__row {
			height: 70px;
		}
		.el-checkbox__label {
			font-size: 12px;
		}
		.el-checkbox__input.is-checked+.el-checkbox__label {
			color: #3dc7fa;
		}
		.el-checkbox__input.is-checked .el-checkbox__inner, .el-checkbox__input.is-indeterminate .el-checkbox__inner {
			background-color: #3dc7fa;
		    border-color: #3dc7fa;
		}
		.el-table th {
		    background-color: #f0f2f7;
		}

}
</style>



