<template>
	<div class="createEip">
		<ConsoleCreate ref="createProcess" :returnUrl='returnUrl' :system_type='name' :switchPage = 'nextPage' :checked='!checked' :submitForm="submitForm" @orderEvent = 'orderEvent'>
			<div slot='create_title' class="create_title" v-show="page==0">
				<EipCreateFirst ref="createOrder" @orderResult = 'orderResult'></EipCreateFirst>
			</div>
			<div slot="confirm_title" class="confirm_title" v-show="page==1">
				<div style="padding: 20px 0;font-size: 12px; background-color: #fff;">资源详情</div>
		        <ConsoleConfirm :tableData="tableData"></ConsoleConfirm>
		        <div class="Agreement_component">
				    <p>
				      	<el-checkbox class="checkedTip" v-model="checked">我已阅读并同意相关协议</el-checkbox>
				      	<span @click="goProtocol" class="agreement">《 虚拟私有云服务协议 》</span>
				    </p>
			  	</div>
			</div>
			<div slot="submit_title" class="submit_title"  v-if="page===2">
	        	<ConsoleSuccess :return_url='returnUrl' :creataWay=true :order_number='order_number' :system_type='name' :system_name="tableData.system_name" ></ConsoleSuccess>
	      	</div>
		</ConsoleCreate>
	</div>
	
</template>

<script>
	import ConsoleCreate from '@/components/ConsoleCreate'
	import ConsoleConfirm from '@/components/ConsoleConfirm'
  	import ConsoleSuccess from '@/components/ConsoleSuccess'
  	import EipCreateFirst from '@/views/vpc/EipCreateFirst'
	import i18n from '@/lang'
	import { CreateEip,listZone } from '@/api/vpc'
	import { getEcmPlatforms } from '@/api/ecm'
	import { getRegionId,getZoneId } from '@/utils/auth'
	export default {
		components:{
			ConsoleCreate:ConsoleCreate,
			ConsoleConfirm,
	     	ConsoleSuccess,
	      	EipCreateFirst,
		},
	    data() {
	      	return {
	      		page:0,
	      		checked:false,
	      		name:'弹性公网IP',
	      		returnUrl:'/vpc/eip/eipList',
	      		order_number:'',
	      		ebsID:'',
	      		Agreement:{
	          		url:'http://www.baidu.com',
	          		name:'<<服务协议>>'
	        	},
	      		tableData:{
		          	system_name:'',//资源名称
		          	tableOptions: [{
//		    			prop:'price',
//		    			label:i18n.vpc.price,
//		    		},{
		    			prop:'num',
		    			label:i18n.ebs.Number,
//		    		},{
//		    			prop:'total',
//		    			label:i18n.ebs.totalPrice,
		    		}],
		    		elseData : [{
		            		system_type:'VPC',//资源类型
//		      				price:'￥0.00/月',
		      				num:'1', 
//		      				total:'￥0.00',
		      				confirmData:[{
				      			label: i18n.vpc.specifications,
				      			value:'--'
				      		}],
		            	},{
		            		system_type:'弹性公网IP',//资源类型
//		      				price:'￥0.00/月',
		      				num:'1', 
//		      				total:'￥0.00',
		      				confirmData:[
		      				{
				      			label: i18n.vpc.time,
				      			value:'--'
				      		}
		      				],
		            	},
		            	{
		            		system_type:'带宽',//资源类型
//		      				price:'￥0.00/月',
		      				num:'',
//		      				total:'￥0.00',
		      				confirmData:[{
//				      			label: i18n.vpc.bandwidthName,
//				      			value:''
//				      		},{
//				      			label: i18n.vpc.sharedType,
//				      			value:''
//				      		},{
				      			label: i18n.vpc.bandwidthNum,
				      			value:''
				      		}],
		            	}],
		        },
		        ruleFormData:'',
	      	}
	    },
	
	    methods: {
	    	orderEvent(){
				this.$refs.createOrder.order();
	    	},
	    	goProtocol(){
	    		window.open('http://www.ctyun.cn/term/vpc');
	    	},
	    	orderResult(val,data){
	    		console.log(val);
				if(val){
					this.ruleFormData = data;
					console.log(this.ruleFormData);
					this.$refs.createProcess.next();
				}else{
					return
				}
	    	},
	      	nextPage(index){
	      		this.page=index;
	      		if(index == 1){
	      			let vpcOption = {
	            		system_type:'VPC',//资源类型
//	      				price:'￥0.00/月',
	      				num:'1', 
//	      				total:'￥0.00',
	      				confirmData:[{
			      			label: i18n.vpc.name,
			      			value:this.ruleFormData ? this.ruleFormData.vpcName : '--'
			      		}],
	            	};
	      			this.tableData.elseData = [{
	            		system_type:'弹性公网IP',//资源类型
//	      				price:'￥0.00/月',
	      				num:'1', 
//	      				total:'￥0.00',
	      				confirmData:[
	      				{
			      			label: i18n.vpc.time,
			      			value:this.ruleFormData.broadbandMsg
			      		}
	      				],
	            	},
	            	{
	            		system_type:'带宽',//资源类型
//	      				price:'￥0.00/月',
	      				num:this.ruleFormData ? this.ruleFormData.eipNumber : '',
//	      				total:'￥0.00',
	      				confirmData:[{
//			      			label: i18n.vpc.bandwidthName,
//			      			value:''
//			      		},{
//			      			label: i18n.vpc.sharedType,
//			      			value:'独享'
//			      		},{
			      			label: i18n.vpc.bandwidthNum,
			      			value:this.ruleFormData ? this.ruleFormData.bandwidth + 'Mbit/s' : ''
			      		}],
	            	}];
	            	this.regionType == 'cs' && this.tableData.elseData.unshift(vpcOption)
	      		}
	      	},
	      	submitForm(fn) {
	          	let params={
	      			regionid:getRegionId()
	      		};
	          	let data={
					'bandwidth':this.ruleFormData.bandwidth,
					'cycleType':this.ruleFormData.cycleType,
					'cycleCnt':this.ruleFormData.cycleCnt,
					'orderNum':this.ruleFormData.eipNumber,
					'regionid':this.ruleFormData.uuid,
					'networkId':this.ruleFormData.vpcid,
					"zoneId":getZoneId()
		          };
		        const loading = this.$loading({
		          	lock: true,
		          	text: '拼命创建中',
		          	spinner: 'el-icon-loading',
		          	background: 'rgba(0, 0, 0, 0.7)'
				});
		        CreateEip(data).then((res)=>{
      				console.log(res)
      				loading.close();
      				this.order_number = res.data.returnObj.so_number;
      				this.$message({
			          	message: '命令下发成功',
			          	type: 'success'
			        });
			        fn();
      			}).catch((error)=>{
      				loading.close();
      				console.log(error);
//    				this.$message({
//			          	message: '命令下发失败',
//			          	type: 'error'
//			        });
      			});
		    },
	    },
		computed:{
			regionType(){
				return this.$store.getters.regionType;
			}
		}
	}
</script>

<style lang="scss" scoped="scoped">
	.Agreement_component{
	    font-size: 12px;
	    margin-top: 20px;
      	.agreement {
			color: #3dc7fa;
			cursor: pointer;
			font-weight: 500;
		}
	}
</style>
<style lang="scss">
	.createEip{
		.checkedTip{
			.el-checkbox__label{
				font-size: 12px;
			}
		}
		.create_title{
			.el-form-item--small{
				label{
					text-align: left;
				}
			}
			.el-textarea{
				width: 180px;
				.el-textarea__inner{
					min-height: 80px!important;
				}
			}
		}
		.confirm_title{
			.mt5{
				display: block;
				margin-top: 5px;
			}
		}
	}
	
</style>



