<template>
	<div class="ecmCreate">
		<el-header height="40px" v-if="isShowOrderInfo" style="padding-left: 0;">
			<el-radio-group v-model="cycleTypeTemp" fill="#3dc7fa">
				<el-radio-button class="radio-group-btn" label="Y">包年包月</el-radio-button>
				<!--<el-radio-button class="radio-group-btn" label="cmdPay" disabled>按需付费</el-radio-button>
				<el-radio-button class="radio-group-btn" label="resource" disabled>资源包</el-radio-button>-->
			</el-radio-group>
		</el-header>
		<el-container v-if="isShowOrderInfo">
			  <el-main class="padding-left">
			  		<el-steps direction="vertical" :active="5" space='auto'>
						   <el-step title="基础配置" ref="basic" class="info-step">
							   	<div slot="description" class="stepContent">
							   		<div class=" stepIcon">
										<div class="stepIcon-title">1</div>
										<div class="stepIcon-rectangle">
											<div class="stepIcon-triangle"></div>
										</div>
									</div>
							   		<el-form ref="basicInfo" :model="basicInfo" :rules="rules" label-width="125px" label-position="left">
										<el-form-item label="地域：" required>
											<el-radio-group v-model="basicInfo.resources" size="mini" fill="#f9cd76" @change='changeRegion'>
											    <el-radio-button v-for="item in platformsList" class="radio-group-btn" :label="item.uuid" :key="item.uuid" >{{item.name}}</el-radio-button>
											</el-radio-group>
										</el-form-item>

										<el-form-item label="云服务器名称：" prop="vmName" :inline-message="inlineMessage" class="el-form-item-tip">
										    <el-input v-model.trim="basicInfo.vmName" placeholder="请输入云服务器名称" size="small"></el-input>
										    <el-tooltip content="只能由数字、字母、-组成,不能以数字和-开头、以-结尾,且长度为2-63字符" placement="right"><i class="el-icon-question"></i></el-tooltip>
										    <div class="infoTip infoTip_location">创建多台云服务器时，系统自动增加后缀，例如：我的云服务器-001</div>
										</el-form-item>
										<el-form-item label="可用分区：" required v-if="false">
										    <el-radio-group v-model="basicInfo.zoneId">
										      <el-radio v-for="(item,index) in zonesList" :label="item.id" :key="index">{{item.name}}</el-radio>
										    </el-radio-group>
										</el-form-item>
										<el-form-item label="云服务器类型：" required v-if="false">
										    <el-radio-group v-model="basicInfo.resourceType">
										      <el-radio label="通用型"></el-radio>
										      <el-radio label="内存优化型"></el-radio>
										    </el-radio-group>
										    <div class="infoTip">通用型云服务器提供均衡的计算，存储以及网络配置，适用于大多数场景，通用型云服务器可用于Web服务器、开发测试环境以及小型数据库应用等场景。</div>
										</el-form-item>
										<el-form-item label="vCPU：" required>
									   		<el-radio-group v-model="basicInfo.cpu" size="mini" fill="#f9cd76" @change="vCPUchange">
											    <el-radio-button v-for="vcpu in cpuList" class="radio-group-btn" :label=vcpu.vcpu :key="vcpu.vcpu">{{vcpu.vcpu}}核</el-radio-button>
											</el-radio-group>
										</el-form-item>
										<el-form-item label="内存：" class="el-form-item-tip" required>
									   		<el-radio-group v-model="basicInfo.mem" size="mini" fill="#f9cd76">
											    <el-radio-button v-for="mem in memList" class="radio-group-btn" :label="mem" :key="mem">{{mem}}GB</el-radio-button>
											</el-radio-group>
									   		<div class="infoTip infoTip_location">为了保证性能体验，Windows2008/2012系统建议选择2GB及以上内存。</div>
										</el-form-item>
										<el-form-item label="镜像类型：" required v-if="regionType == 'os'">
									   		<el-radio-group v-model="basicInfo.imageType" size="mini" fill="#f9cd76"  @change = 'changeImages' >
											    <el-radio-button class="radio-group-btn" v-for = "item in imageTypeList" :label="item.label" :key="item.value" v-if="!item.hide">{{ item.value }}</el-radio-button>
											    <!--<el-radio-button class="radio-group-btn" label="0">私有镜像</el-radio-button>
											    <el-radio-button class="radio-group-btn" label="4GB"  v-if="false">共享镜像</el-radio-button>-->
											</el-radio-group>
										</el-form-item>
										<el-form-item label="镜像：" required prop="os" class='form-item-os'>
										    <el-select :disabled = "seleceCheck" placeholder="请选择" v-model="basicInfo.osType" size="small" @change="changeImages" v-if="basicInfo.osTypeShow">
										      <el-option v-for="(item,index) in osTypeList" :value="item.value" :key="index">{{item.value}}</el-option>
										    </el-select>
										    <el-select :disabled = "seleceCheck" placeholder="请选择" v-model="basicInfo.os" size="small"  @change="changeOs">
										      <el-option v-for="item in osList" :label="item.name" :value="item.id" :key="item.id"></el-option>
										    </el-select>
										</el-form-item>
										<el-form-item label="磁盘：" class="form-item-bg" required >
											<span class="spanTip">系统盘</span>
											<el-select placeholder="普通IO" v-model="basicInfo.disk" size="small">
										      <el-option v-for="(key,value) in disksTypeObj" :label="key" :value="value" :key="value"></el-option>
										    </el-select>
										    <!--<el-input-number v-model="basicInfo.diskSize" :disabled="true" controls-position="right" :min="basicInfo.diskSizeMin" :max="basicInfo.diskSizeMax" @blur="diskBlur" @focus="diskFocus" size="small" @keypress.native="dian190($event)" ></el-input-number>-->
										    <el-input-number v-model="basicInfo.diskSize" :disabled="true" controls-position="right" size="small" @keypress.native="dian190($event)" ></el-input-number>
										    <span class="color_company">GB</span>
										    <el-button v-if="basicInfo.dataDisks.length == 0" class='addEbs' type="text" @click="addDataDisk" :disabled="isAddDataDisk">增加一块数据盘</el-button>
										</el-form-item>
										<el-form-item  label="" class="form-item-bg" v-for="(item,index) in basicInfo.dataDisks" :key="index" required>
											<span class="spanTip">数据盘</span>
											<el-select placeholder="普通IO" v-model="item.type" size="small">
										      <el-option v-for="(key,value) in disksTypeObj" :label="key" :value="value" :key="value"></el-option>
										    </el-select>
										    <el-input-number v-model="item.size" controls-position="right" :min="basicInfo._diskSizeMin" :max="basicInfo._diskSizeMax" size="small" :step="basicInfo._step" @keypress.native="dian190($event)" ></el-input-number>
										    <span class="color_company">GB</span>
										    <el-button v-if="basicInfo.dataDisks.length-1==index" type="text" @click="addDataDisk" :disabled="isAddDataDisk">增加一块数据盘</el-button>
										    <span v-if="basicInfo.dataDisks.length-1==index" class="addTip">你还可以增加{{basicInfo.maxDataDisk-basicInfo.dataDisks.length}}块磁盘（云硬盘）</span>
										    <el-button type="text" icon="el-icon-close" class="textClose" @click="delDataDisk(index)"></el-button>
										</el-form-item>
							   		</el-form>
							   	</div>
						   </el-step>
						   <el-step title="网络" ref="network" class="network-step">
						   		<div slot="description" class="stepContent">
						   			<div class=" stepIcon">
										<div class="stepIcon-title">2</div>
										<div class="stepIcon-rectangle">
											<div class="stepIcon-triangle"></div>
										</div>
									</div>
									
						   			<el-form ref="networkInfo" :model="networkInfo" :rules="rules" label-width="110px" label-position="left">
						   				<el-form-item label="VPC类型：" prop='vpcType' class="el-form-item-tip">
									   		<el-radio-group v-model="networkInfo.vpcType" size="mini" fill="#f9cd76" @change='vpcTypeChange'>
											    <el-radio-button class="radio-group-btn" label="privateVpc">虚拟网络VPC</el-radio-button>
											    <el-radio-button class="radio-group-btn" label="physicalVpc">物理网络VPC</el-radio-button>
											</el-radio-group>
										</el-form-item>
						   				<el-form-item label="虚拟私有云：" prop="vpc">
										    <el-select placeholder="请选择" v-model="networkInfo.vpc" size="small" @change="vpcsChange">
										      <el-option v-for="(item,index) in vpcTypeList" :label="item.name" :value="item.id" :key="index"></el-option>
										    </el-select>
										    <div class="creatVpc">
										    	<el-button icon="el-icon-refresh"  @click="refreshVpc" class="refreshBtn"></el-button>
										    	<span v-if='privateShow' class="creatVpc_title">如需创建新的VPC,您可<span class="router_link" @click="createVpc">前往控制台创建</span></span>
										    </div>
										    <p class="subCreateTip" v-if="subCreateTip">该虚拟私有云下子网正在创建，请耐心等待，或选择其他虚拟私有云进行创建</p>
										</el-form-item>
										<el-form-item label="安全组：" prop="securityGroup" v-if="false">
										    <el-select placeholder="请选择" v-model="networkInfo.securityGroup" size="small">
										      <el-option v-for="item in securityGroupsList" :label="item.name" :value="item.id" :key="item.id"></el-option>
										    </el-select>
										</el-form-item>
										<el-form-item label="网卡：" class="form-item-bg" prop="mainNetwork">
											<span class="spanTip">主网卡</span>
										    <el-select placeholder="请选择" v-model="networkInfo.mainNetwork" size="small"  @change="vpcNetWorkChange">
										      <el-option v-for="(item,index) in vpcsNetworkList" :label="item.networkCard" :value="item.id" :key="index"></el-option>
										    </el-select>
										    <el-input v-model="networkInfo.mainNetworkIP" placeholder="自动分配内网IP地址" size="small" clearable @blur="ipBlur"></el-input>
										    <div v-show="!isNetworkIP" style="display: inline-block;color: red;">请输入合法IP</div>
										    <el-button type="text" v-if="false">查看已使用的IP地址</el-button>
										</el-form-item>
										<el-form-item  v-if="false" class="form-item-bg" v-for="(extend,index) in networkInfo.extendNetworks" :key="index">
											<span class="spanTip">扩展网卡</span>
											<el-select placeholder="普通IO" v-model="extend.extendNetwork" size="small">
										      <el-option label="subnet-38192" value="shanghai"></el-option>
										      <el-option label="subnet-38aa123" value="beijing"></el-option>
										    </el-select>
										    <el-input v-model="extend.extendNetworkIP" placeholder="手动输入IP地址" size="small"></el-input>
										    <el-button v-if="networkInfo.extendNetworks.length-1==index" type="text" @click="addExtendNetwork" :disabled="isAddExtendNetwork">增加一块网卡</el-button>
										    <span class="addTip" v-if="networkInfo.extendNetworks.length-1==index">你还可以增加{{networkInfo.maxExtend-networkInfo.extendNetworks.length}}块网卡</span>
										    <el-button type="text" icon="el-icon-close" class="textClose" @click="delExtendNetwork"></el-button>
										</el-form-item>
										<el-form-item v-if='privateShow' label="弹性公网IP：" class="el-form-item-tip">
									   		<el-radio-group v-model="networkInfo.elasticIP" size="mini" fill="#f9cd76" @change = 'eipBuyTpe'>
											    <el-radio-button class="radio-group-btn" label="noUse">不使用</el-radio-button>
											    <el-radio-button v-if='eipShow' class="radio-group-btn" label="auto">自动分配</el-radio-button>
											    <el-radio-button v-if='eipShow' class="radio-group-btn" label="use">使用已有</el-radio-button>
											</el-radio-group>
											<div class="infoTip infoTip_location" v-if="isNoUse">不使用弹性公网IP的云服务器不能与互联网互通，仅可作为私有网络中部署业务或者集群所需云服务器进行使用。</div>
									   		<div class="infoTip infoTip_location" v-if="isAuto">自动为每台云服务器分配独享带宽的弹性公网IP，创建弹性云服务器过程中，请确保弹性公网IP配额充足。</div>
									   		<div class="infoTip infoTip_location" v-if="isUse">为云服务器分配已有弹性公网IP，不能批量创建云服务器。</div>
											<el-tooltip content="在备案功能上线前,Tcp协议暂不支持80、443、8080、8443端口 " placement="right"><i class="el-icon-question"></i></el-tooltip>
										</el-form-item>
										<el-form-item label="宽带：" required v-if="isAuto">
											<slideScale @ofterCallback="bbCallback" :isOften="isOften"></slideScale>
										</el-form-item>
										<el-form-item v-if="isUse" class="el-form-item-tip">
											 <el-select placeholder="请选择" v-model="networkInfo.existingIp" size="small" @change="eipSizeShow">
										      <el-option v-for="item in eipsdataList" :label="item.ipaddress" :value="item.id" :key="item.id"></el-option>
										    </el-select>
										    <div class="infoTip infoTip_location">当前弹性公网IP宽带：{{eipSize}}</div>
										</el-form-item>
						   			</el-form>
						   			
						   		</div>
						   </el-step>
						   <el-step title="安全设置" ref="login" class="login-step" v-if="false">
						   		<div slot="description" class="stepContent">
						   			<div class=" stepIcon">
										<div class="stepIcon-title">3</div>
										<div class="stepIcon-rectangle">
											<div class="stepIcon-triangle"></div>
										</div>
									</div>
						   			<el-form ref="loginInfo" :model="loginInfo" :rules="rules" label-width="110px" label-position="left">
						   				<el-form-item label="登录方式：" required>
										    <el-radio-group v-model="loginInfo.passwordType">
										      <el-radio label="rootPassword" >密码</el-radio>
										      <el-radio label="keyPairId" v-if="false">密钥对</el-radio>
										    </el-radio-group>
										    <div v-show="!isloginPass" class="infoTip">密钥对登陆方式创建的Linux云服务器，如需使用云服务器服务器页面的远程登陆功能，请先在云服务器创建完成后设置登录密码、<span class="huang">请妥善保管密钥对的私钥文件，登录、重装和切换云服务器操作系统时均使用该文件。</span></div>
										    <div v-show="isloginPass" class="infoTip">密码登陆方式创建的Linux云服务器，如需使用SSH密码方式登录，请先使用云服务器页面的远程登录功能登录云服务器，开启SSh密码登录方式，密码登录方式创建的Windows云服务器，请先使用云服务器页面的远程登录功能进行登录，再修改密码。</div>
										</el-form-item>
										<el-form-item v-show="!isloginPass" label="密钥对：" required>
										    <el-select placeholder="请选择" v-model="loginInfo.keyPair" size="small">
										      <el-option v-for="(item,index) in osKeypairsList" :label="item.name" :value="item.fingerprint" :key="index"></el-option>
										    </el-select>
										</el-form-item>
										<el-form-item v-show="isloginPass" label="用户名：" required>
										    <el-input type="text" v-model="userName" :disabled=true size="small"></el-input>
										</el-form-item>
										<el-form-item v-show="isloginPass" label="密码：" prop="pass">
										    <el-input type="password" v-model="loginInfo.pass" auto-complete="off"  size="small" @input="passChange" :minlength=6></el-input>
										    <div class="passGrade">
										    	<span class="infoTip">安全等级</span>
										    	<span class="gradeTabs">
										    		<span class="gradeTab" :class="{ passSimple: passGrade>0 }"></span>
										    		<span class="gradeTab" :class="{ passModerate: passGrade>1 }"></span>
										    		<span class="gradeTab" :class="{ passComplex: passGrade>2 }"></span>
										    	</span>
										    </div>
										</el-form-item>
										<el-form-item v-show="isloginPass" label="确认密码：" prop="checkPass">
										    <el-input type="password" v-model="loginInfo.checkPass" auto-complete="off"  size="small"></el-input>
										</el-form-item>
						   			</el-form>
						   		</div>
						   </el-step>
						   <el-step title="购买量" ref="createNumber" class="create-step">
						   		<div slot="description" class="stepContent">
						   			<div class=" stepIcon">
										<div class="stepIcon-title">3</div>
										<div class="stepIcon-rectangle">
											<div class="stepIcon-triangle"></div>
										</div>
									</div>
						   			<el-form ref="createNumberInfo" :model="createNumberInfo" label-width="110px" label-position="left">
						   				<el-form-item label="购买时长：" required>
											<slideScale :cycleCnt="createNumberInfo.cycleCnt" @ofterCallback="ofterCallback"></slideScale>
										</el-form-item>
										<el-form-item label="购买数量：" required>
											 <el-input-number v-model="createNumberInfo.orderNum" @keypress.native="dian190($event)" :min="createNumberInfo.min" :max="createNumberInfo.max" :label="createNumberInfo.label" size="medium" controls-position="right" :disabled="isBatch"></el-input-number><span class="spanTip">台</span><span class="infoTip">选择已有弹性公网IP或给网卡分配固定IP地址后，不能批量创建云服务器。</span>
										</el-form-item>
						   			</el-form>
						   		</div>
						   </el-step>
						   <el-step title="高级配置" ref="seniorConfig" class="config-step" v-if="false">
						   		<div slot="description" class="stepContent">
						   			<div class=" stepIcon">
										<div class="stepIcon-title">5</div>
										<div class="stepIcon-rectangle">
											<div class="stepIcon-triangle"></div>
										</div>
									</div>
						   			<el-form ref="seniorConfigInfo" :model="seniorConfigInfo" label-width="110px" label-position="left">
						   				<el-form-item label="文件注入：" required>
											<el-upload
											  class="upload-demo"
											  action="https://jsonplaceholder.typicode.com/posts/"
											  :on-preview="handlePreview"
											  :on-remove="handleRemove"
											  :before-remove="beforeRemove"
											  multiple
											  :limit="5"
											  :on-exceed="handleExceed"
											  :file-list="fileList">
											  <el-button size="small" type="primary">点击上传</el-button>
											  <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
											</el-upload>
										</el-form-item>
										<el-form-item label="自动恢复：" required>
											<el-radio-group v-model="seniorConfigInfo.resourceType">
										      <el-radio label="开启"></el-radio>
										      <el-radio label="关闭"></el-radio>
										    </el-radio-group>
										    <div class="infoTip">弹性云服务器所在的硬件出现故障时，系统自动将弹性云服务器迁移至正常的物理机，该过程会导致云服务重启。</div>
										</el-form-item>
										<el-form-item label="用户数据：" required>
											<el-radio-group v-model="seniorConfigInfo.resourceType">
										      <el-radio label="文本"></el-radio>
										      <el-radio label="文件"></el-radio>
										    </el-radio-group>
										</el-form-item>
										<el-form-item label="注入：">
											<el-input
											  type="textarea"
											  :rows="4"
											  placeholder="请输入内容"
											  v-model="seniorConfigInfo.textarea">
											</el-input>
										</el-form-item>
										<el-form-item label="云服务器组：" required>
											<el-select placeholder="请选择" v-model="seniorConfigInfo.mainNetwork" size="small">
										      <el-option label="云服务器1" value="shanghai"></el-option>
										      <el-option label="云服务器2" value="beijing"></el-option>
										    </el-select>
										    <el-button type="text">查看云服务器组</el-button>
										</el-form-item>
										<el-form-item label="标签：" required v-for="(tab,index) in seniorConfigInfo.tabs" :key="index">
											<span class="spanTip">Key</span><el-input type="text" v-model="tab.key" size="small"></el-input>
											<span class="spanTip">Value</span><el-input type="text" v-model="tab.value" size="small"></el-input>
											<el-button type="text" @click="addTab" :disabled="isTab" v-if="seniorConfigInfo.tabs.length-1==index">增加标签</el-button>
											<span class="infoTip" v-if="seniorConfigInfo.tabs.length-1==index">您还可以添加{{seniorConfigInfo.maxTabs-seniorConfigInfo.tabs.length}}个标签</span>
											<el-button v-if="seniorConfigInfo.tabs.length!==1" type="text" icon="el-icon-close" class="textClose" @click="delTab(index)"></el-button>
											<div class="huang" v-if="seniorConfigInfo.tabs.length-1==index">从以往最佳实践总结，我们建议您在TMS创建全局的预定义标签，在进行标签与资源的关联操作。</div>
										</el-form-item>
						   			</el-form>
						   		</div>
						   </el-step>
						</el-steps>
			  </el-main>
			  <el-aside width="200px">
			  	<div class="rightInfo">
			  		<div class="currentInfo">
						<h6>当前配置</h6>
						<ul>
							<li>
								<span class="infoTip">地域 :</span>{{resourcesName}}
							</li>
							<li>
								<span class="infoTip">规格 :</span>{{specifications}}
							</li>
							<li>
								<span class="infoTip">系统盘 :</span>{{systemData}}
							</li>
							<li v-if="false">
								<span class="infoTip">数据盘 :</span>{{datas}}
							</li>
							<li v-if='privateShow'>
								<span class="infoTip">弹性公网IP :</span>{{existingIpValue}}
							</li>
							<li>
								<span class="infoTip">购买时长 :</span>{{cycleCntValue}}
							</li>
							<li>
								<span class="infoTip">购买数量 :</span>{{orderNumValue}}
							</li>
						</ul>
						<el-button size="mini" type="warning" icon="el-icon-check">预付费</el-button>
						<div class="infoBtn">
							<!--<el-button type="primary" @click.stop="order('immediately')">立即购买</el-button>-->
							<el-button type="primary" @click.stop="order('indirect')">订购</el-button>
						</div>
					</div>
					<div class="operationBtns" :style="{ top: scrollY + 'px' }">
						<div class="btn"><el-button size="117" :type="basicInfo.btnType" :icon="basicInfo.btnIcon" @click.stop="setScroll(0,basicInfo)">基础配置</el-button></div>
						<div class="btn"><el-button :type="networkInfo.btnType" :icon="networkInfo.btnIcon" size="117" @click.stop.prevent="setScroll(1,networkInfo)">网络</el-button></div>
						<div class="btn"><el-button :type="loginInfo.btnType" :icon="loginInfo.btnIcon" size="117" @click.stop="setScroll(2,loginInfo)" v-if="false">安全设置</el-button></div>
						<div class="btn"><el-button :type="createNumberInfo.btnType" :icon="createNumberInfo.btnIcon" size="117" @click.stop="setScroll(3,createNumberInfo)">购买量</el-button></div>
						<div class="btn"><el-button :type="seniorConfigInfo.btnType" :icon="seniorConfigInfo.btnIcon" size="117" @click.stop="setScroll(4,seniorConfigInfo)" v-if="false">高级配置</el-button></div>
					</div>
				</div>
			  </el-aside>
		</el-container>
		<el-footer height="40px" v-if="isShowOrderInfo">
			<el-button type="info" @click.stop="cancel">取消</el-button>
			<el-button type="primary" @click.stop="order('indirect')">订购</el-button>
		</el-footer>
		<ecm-create-after @backBuyEcm='backup' v-if="!isShowOrderInfo" :orderInfo="orderInfo" :orderData="orderData" :isShowCreateInfoP="isShowCreateInfoP" :orderNumberP="orderNumberP"></ecm-create-after>
	</div>
</template>

<script>
import service from '@/utils/request'
import { getEcmImages,getEcmSecurityGroups,getEcmPlatforms,getEcmZones,getEcmOsKeypairs,getEcmVpcs,getEcmBuyserver,getEcmEipsdata,getServerextenddata,requestsAll,getEcmList } from '@/api/ecm/';
import { analyzePasswordSecurityLevel } from '@/utils/validator'
import { getRegionId, setRegionId, SetZoneId, SetVnc,getZoneId, SetRegionType , getRegionType } from '@/utils/auth'
import EcmCreateAfter from './EcmCreateAfter'
import { GetVpcDetail,queryQuota} from '@/api/vpc'
import { getName } from '@/utils/shareFunction'
import slideScale from '@/components/SlideScale'
let timer;
export default {
	components:{
		EcmCreateAfter,
		slideScale
	},
	data () {
		var validatePass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请输入密码'));
        } else {
          if (this.loginInfo.checkPass !== '') {
            this.$refs.loginInfo.validateField('checkPass');
          }
          callback();
        }
      };
      var validatePass2 = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请再次输入密码'));
        } else if (value !== this.loginInfo.pass) {
          callback(new Error('两次输入密码不一致!'));
        } else {
          callback();
        }
      };
      var nameCheck = (rule, value, callback) => {
      	let nameRule = /^[a-zA-Z][a-zA-Z0-9\-]*[a-zA-Z0-9]$/;
      	if(value.length > 63 || value.length < 2){
      		callback(new Error('长度为2-63字符'));
		}else if(!nameRule.test(value)){
			callback(new Error('只能由数字、字母、-组成,不能以数字和-开头、以-结尾'));
      	}else{
      		let params = {
				status:'',
				page:1,
				name:value,
				attached:'',
			}
	        getEcmList(params).then((res)=>{
				let servers = res.data.results;
				if(servers.length > 0){
					callback(new Error('云服务器名称重复，请重新输入！'));
				}else{
					callback();
				}
			}).catch((error)=>{
				console.log(error);
			})
      	}
	    };
		return {
			isOften: false,
			isNetworkIP: true,
			isShowOrderInfo: true,
			isShowCreateInfoP: true,
			inlineMessage: true,
			privateShow: true,
			subCreateTip: false,
			seleceCheck: true,
			orderNumberP: '--',
			returnUrl: '/ecm/ecmList',
			orderInfo: {},
			orderData: {},
			temp: 14,
			eipSize:'--',
			//scaleWidth:60,
			temp2:30,
			temp21:4,
			broadbandStep:10,
			cycleTypeTemp: 'Y',
			cycleType: 'M',
			fileList: [],
			scrollY: 500,
			passGrade: 0,
			imageTypeList:[
				{
					label:'1',
					value:'公共镜像'
				},
				{
					label:'0',
					value:'私有镜像'
				},
				{
					label:'share',
					value:'共享镜像',
					hide:true
				},
			],
			osTypeList:[
				{
					value: "Linux"
				},
				{
					value: "Windows"
				},
				{
					value: "Redhat"
				}
			],
			disksTypeObj:{
				SATA:'普通IO',SAS:'高IO',SSD:'超高IO'
			},
			osList:[],
			securityGroupsList:[],
			platformsList:[],
			zonesList:[],
			osKeypairsList:[],
			vpcsList:[],
			vpcTypeList:[],
			vpcsNetworkList:[],
			eipsdataList:[],
			cpuList:[],
			memList:[],
			basicInfo: {
				btnType: 'success',
				btnIcon: 'el-icon-edit',
				resources:'',
				vmName: '',
				zoneId: '',
				resourceType: '',
				cpu: 1,
				mem: 1,
				imageType:'1',
				osTypeShow:true,
				osType: 'Linux',
				os: '',
				osName: '',
				disk: '',
				maxDataDisk: 5,
				diskSize : 15,
				diskSizeMin: 15,
				diskSizeMax: 200,
				_diskSizeMin: 5,
				_diskSizeMax: 2048,
				_step: 10,
				dataDisks: [
					{
						type: '',
						size:30
					}
				],
			},
			networkInfo: {
				btnType: 'primary',
				btnIcon: '',
				vpcType:'privateVpc',
				vpc: '',
				vpcName: '',
				securityGroup: '',
				mainNetwork: '',
				mainNetName: '',
				mainNetworkIP: '',
				maxExtend: 5,
				extendNetworks:[
					{
						extendNetwork: '',
						extendNetworkIP: '',
					}
				],
				elasticIP: 'noUse',
				bandwidth: 100,
				broadbandMax: 300,
				showInput: true,
				existingIp:''
			},
			loginInfo: {
				btnType: 'primary',
				btnIcon: '',
				passwordType: 'rootPassword',
				keyPair: '',
				userName: '',
				pass: '',
				checkPass: '',
			},
			createNumberInfo: {
				btnType: 'primary',
				btnIcon: '',
				cycleCnt: 6,
				broadbandMin: 1,
				broadbandMax: 14,
				showInput: true,
				min: 1,
				max: 50,
				orderNum: 1 ,
				label: '台',
			},
			seniorConfigInfo: {
				btnType: 'primary',
				btnIcon: '',
				textarea: '',
				maxTabs: 10,
				tabs: [
					{
						key: '键',
						value: '值',
					}
				]

			},
			rules: {
				vmName: [
		            { required: true, message: '请输入云服务器名称', trigger: 'blur' },
		            { min: 1, max: 63, message: '长度在 1 到 63个字符', trigger: 'blur' },
		            { validator: nameCheck, trigger: 'blur' }
		        ],
		        os: [
	            	{ required: true, message: '请选择镜像', trigger: 'change' }
	          	],
	          	vpcType: [
	            	{ required: true, message: '请选择Vpc类型', trigger: 'blur,change' }
	          	],
	          	vpc: [
	            	{ required: true, message: '请选择虚拟私有云', trigger: 'blur,change' }
	          	],
	          	securityGroup: [
	            	{ required: true, message: '请选择安全组', trigger: 'change' }
	          	],
	          	mainNetwork: [
	            	{ required: true, message: '请选择主网卡', trigger: 'change' }
	          	],
	          	userName: [
	            	{ required: true, message: '请输入用户名', trigger: 'blur' }
	          	],
	          	pass: [
	            	{ required: true, validator: validatePass, trigger: 'blur' },
	            	{ min: 6,  message: '密码长度输入6个字符以上', trigger: 'blur' }
	          	],
	          	checkPass: [
	            	{ required: true, validator: validatePass2, trigger: 'blur' }
	          	],
			},
			
		}
	},
	methods: {
		refreshVpc(){
			let data = {
				regionid: getRegionId()
			};
			this.getVpcs(data);
		},
		createVpc(){
			window.open('#/vpc/creatVpc');
		},
	  handleScroll () {
	  	if(!this.isShowOrderInfo) return;
	  	var scrollY = window.scrollY;
	    if(scrollY < 350){
	    	this.scrollY = 500-scrollY;
	    }else{
	    	this.scrollY = 200;
	    }
	    this.basicInfo.btnType = 'primary';
	  	this.basicInfo.btnIcon = '';
	  	this.networkInfo.btnType = 'primary';
	  	this.networkInfo.btnIcon = '';
	  	this.loginInfo.btnType = 'primary';
	  	this.loginInfo.btnIcon = '';
	  	this.createNumberInfo.btnType = 'primary';
	  	this.createNumberInfo.btnIcon = '';
	  	this.seniorConfigInfo.btnType = 'primary';
	  	this.seniorConfigInfo.btnIcon = '';

	  	var basicScrollHeight = this.$refs.basic.$el.scrollHeight,
	  		networkScrollHeight = basicScrollHeight + this.$refs.network.$el.scrollHeight,
	  		//loginScrollHeight = networkScrollHeight + this.$refs.login.$el.scrollHeight,
	  		createNumberScrollHeight = networkScrollHeight + this.$refs.createNumber.$el.scrollHeight;
	  		//seniorConfigScrollHeight = createNumberScrollHeight + this.$refs.seniorConfig.$el.scrollHeight;
	    if(scrollY < basicScrollHeight){
	    	this.basicInfo.btnType = 'success';
	  		this.basicInfo.btnIcon = 'el-icon-edit';
	    }else if(scrollY < networkScrollHeight){
	    	this.networkInfo.btnType = 'success';
	  		this.networkInfo.btnIcon = 'el-icon-edit';
	    }else if(false){//&&scrollY < loginScrollHeight
	    	this.loginInfo.btnType = 'success';
	  		this.loginInfo.btnIcon = 'el-icon-edit';
	    }else if(scrollY < createNumberScrollHeight){
	    	this.createNumberInfo.btnType = 'success';
	  		this.createNumberInfo.btnIcon = 'el-icon-edit';
	    }else{
	    	this.seniorConfigInfo.btnType = 'success';
	  		this.seniorConfigInfo.btnIcon = 'el-icon-edit';
	    }
	  },
	  setScroll (value,item) {
	  	this.basicInfo.btnType = 'primary';
	  	this.basicInfo.btnIcon = '';
	  	this.networkInfo.btnType = 'primary';
	  	this.networkInfo.btnIcon = '';
	  	this.loginInfo.btnType = 'primary';
	  	this.loginInfo.btnIcon = '';
	  	this.createNumberInfo.btnType = 'primary';
	  	this.createNumberInfo.btnIcon = '';
	  	this.seniorConfigInfo.btnType = 'primary';
	  	this.seniorConfigInfo.btnIcon = '';
	  	item.btnType = 'success';
	  	item.btnIcon = 'el-icon-edit';
	  	var basicScrollHeight = this.$refs.basic.$el.scrollHeight,
	  		networkScrollHeight = basicScrollHeight + this.$refs.network.$el.scrollHeight;
	  		//loginScrollHeight = networkScrollHeight + this.$refs.login.$el.scrollHeight,
	  		//createNumberScrollHeight = networkScrollHeight + this.$refs.createNumber.$el.scrollHeight;
	    if(value == 0){
	    }else if(value == 1){
	    	value = basicScrollHeight;
	    }else if(value == 2){
	    	value = networkScrollHeight;
	    }else if(value == 3){
	    	value = networkScrollHeight;//loginScrollHeight;
	    }else{
	    	value = createNumberScrollHeight;
	    }
	  	window.scrollTo(0,value);
	  	console.log(value,item);
	  },
      dian190: function (event) {
        let code = window.event ? event.keyCode : event.which;
        if(code == 46){
        	event.preventDefault();return;
        }
      },
      handleRemove(file, fileList) {
        console.log(file, fileList);
      },
      handlePreview(file) {
        console.log(file);
      },
      handleExceed(files, fileList) {
        this.$message.warning(`当前限制选择 5 个文件，本次选择了 ${files.length} 个文件，共选择了 ${files.length + fileList.length} 个文件`);
      },
      beforeRemove(file, fileList) {
        return this.$confirm(`确定移除 ${ file.name }？`);
      },
      addDataDisk () {
      		this.basicInfo.dataDisks.push({type: 'SATA',size: 30});
      },
      delDataDisk (index) {
      		this.basicInfo.dataDisks.splice(index,1);
      },
      eipSizeShow(val){
      	console.log(val);
      	var eipData = this._.find(this.eipsdataList, { 'id': val});
      	this.eipSize = eipData.networkrate;
      },
      addExtendNetwork () {
      		this.networkInfo.extendNetworks.push(
      			{
					extendNetwork: 'shanghai',
					extendNetworkIP: '',
				}
      		);
      },
      delExtendNetwork (index) {
      		this.networkInfo.extendNetworks.splice(index,1);
      },
      addTab () {
      		this.seniorConfigInfo.tabs.push({
      			key: '键',
				value: '值',
      		});
      },
      delTab (index) {
      		this.seniorConfigInfo.tabs.splice(index,1);
      },
      vpcTypeChange(val){
      	if(val == 'privateVpc'){
      		this.privateShow = true;
      	}else{
      		this.privateShow = false;
      	}
      	let _this = this;
      	this.vpcTypeList = [];
      	let jobIds = [];
		this.vpcTypeList = this.vpcsList.filter(function(item){
			if(item.subnet_creating){
				jobIds.push(item.id);
			}
			if(_this.privateShow){
      			if(item.fake_vpc && item.fake_vpc == 1 ){
//							_this.$set(item,'name','默认网络')
					return false
				}else{
					return true;
				}
      		}else{
      			if(item.fake_vpc && item.fake_vpc == 1 ){
      				let fakeVpc = item.fake_vpc && (item.fake_vpc == 1 );
      				item.name = fakeVpc ? (item.name == 'fake_vpc' ? '默认网络' : item.name) : item.name;
//							_this.$set(item,'name','默认网络')
					return true
				}else{
					return false;
				}
      		}
		})
		if(jobIds.length > 0){
			this.autoRefresh(jobIds);
		}
      	if(this.vpcTypeList.length == 0 && this.privateShow){
			this.vpcTypeList = [{name:'默认私有云',id:'default'}];
			this.networkInfo.vpc = 'default';
			this.networkInfo.vpcName = '默认私有云';
		}else if(this.vpcTypeList.length == 0 && !this.privateShow){
			this.networkInfo.vpc = '';
			this.networkInfo.vpcName = '';
		}else{
			this.networkInfo.vpc = this.vpcTypeList[0].id;
			this.networkInfo.vpcName = this.vpcTypeList[0].name;
		}
		this.vpcsChange(this.networkInfo.vpc);
      	console.log(this.vpcTypeList);
      },
      eipBuyTpe(val){
      	console.log(val);
      	console.log(this.networkInfo.elasticIP);
      },
      	autoRefresh(jobIds){
			if(jobIds){
				jobIds.map(item =>{
					console.log(item)
					this.getJobDetail(item)
				})
			}
		},
		getJobDetail(id){
			let _this = this;
			if(this.detailRefresh) return
			timer && clearTimeout(timer);
  			GetVpcDetail(id).then(res=>{
				let result = res.data.results[0];
				console.log(result);
				if(!result.subnet_creating){
					_this.vpcsList.map((item,index) =>{
						if(item.id == result.id){
							console.log(result);
							_this.$set(_this.vpcsList,index,result)
							return true;
						}
					});
					_this.vpcTypeList.map((item,index) =>{
						if(item.id == result.id){
							console.log(result);
							_this.$set(_this.vpcTypeList,index,result)
							return true;
						}
					})
					if(_this.networkInfo.vpc){
						_this.vpcsChange(_this.networkInfo.vpc);
					}
				}else{
					timer = setTimeout(()=>{
						_this.getJobDetail(id)
					},5000)
				}
				
			}).catch(error=>{
				console.log(error)
				timer = setTimeout(()=>{
					_this.refresh()
				},5000)
			})
		},
      vpcsChange (vpcsId){
      	if(vpcsId){
      		this.networkInfo.mainNetwork = '';
			this.networkInfo.existingIp = '';
			this.eipsdataList = [];
			if(vpcsId == 'default'){
				this.networkInfo.mainNetwork = 'default';
				this.vpcsNetworkList = [{name:'默认子网',id:'default'}];
				this.networkInfo.mainNetName = this.vpcsNetworkList.length>0 ? this.vpcsNetworkList[0].name : '';;
				return;
			}
      		var vpcData = this._.find(this.vpcTypeList, { 'id': vpcsId});
      		this.networkInfo.vpcName = vpcData.name;
      		
			/*os 判断子网详情*/
      		this.vpcsNetworkList = vpcData.network ? vpcData.network : vpcData.subnet_list ? vpcData.subnet_list : [];


      		if(this.vpcsNetworkList.length>0){
      			this.subCreateTip = false;
      			this.vpcsNetworkList.filter(function(item){
      				return item.networkCard = item.name + '(' + item.cidr + ')'
      			});
      			this.networkInfo.mainNetwork = this.vpcsNetworkList[0].id;
      			this.networkInfo.mainNetName = this.vpcsNetworkList[0].networkCard;
      		}else{
      			this.networkInfo.mainNetwork = '';
      			this.networkInfo.mainNetName = '';
      			this.subCreateTip = false;
      			if(vpcData.subnet_creating){
      				this.subCreateTip = true;
      			}
      		}
      		if(this.privateShow){
      			this.getEipsdata({vpcid : vpcsId,attached : 0});
      		}else{
      			
      		}
      	}else{
      		this.vpcsNetworkList = [];
      		this.networkInfo.mainNetwork = '';
			this.networkInfo.existingIp = '';
			this.eipsdataList = [];
			this.networkInfo.mainNetwork = '';
      		this.networkInfo.mainNetName = '';
      	}
			
      		//this.networkInfo.mainNetwork && this.changeNetwork(this.networkInfo.mainNetwork)
      },
      vpcNetWorkChange (value) {
      	this.networkInfo.mainNetName = this._.find(this.vpcsNetworkList, { 'id': value}).networkCard;
      },
      changeImages (value) {
      		this.basicInfo.os = '';
      		let regionid = this.basicInfo.resources;
      		let imageParams = {
      			regionid
      		};
      		let osType;
      		if(value == 'Linux'){
      			osType = 1;
      		}else if(value == 'Windows'){
      			osType = 2;
      		}else if(value == 'Redhat'){
      			osType = 3;
      		}
      		this.basicInfo.osTypeShow = this.basicInfo.imageType == 1 ? true : false;
      		if('os' == this.regionType){
      			this.basicInfo.osTypeShow && (imageParams.osType = osType)
      			imageParams.visibility = this.basicInfo.imageType;
      		}
      		if('cs' == this.regionType){
      			imageParams.osType = osType
      		}
      		console.log(this.$refs['basicInfo']);
      		this.$refs['basicInfo'].fields[1].resetField();

      		this.getImages(imageParams);

      },
      changeOs (value) {
      	console.log(value);
      	let OsData = this._.find(this.osList, { 'id': value});
      	this.basicInfo.osName = OsData.name;
      	console.log(OsData);
      	if(OsData.size){
      		this.basicInfo.diskSize = parseInt(OsData.size/1024/1024/1024);
      	}else{
      		if(this.basicInfo.osType == 'Windows'){
      			this.basicInfo.diskSize = '40'
      		}else{
      			this.basicInfo.diskSize = '15'
      		}
      	}
      },
      changeNetwork (NetworkId) {
			this.networkInfo.existingIp = '';
      		var networkData = this._.find(this.vpcsNetworkList, { 'id': NetworkId});
      		this.getEipsdata({subnet_uuid:networkData.physicalnetworkid});
      },
      vCPUchange (val) {
      	var vcpuData = this._.find(this.cpuList, { 'vcpu': val});
      	this.memList = this._.sortBy(vcpuData.ram,function(item){
					return item;
				});
      	//this.memList = vcpuData.ram;
		this.basicInfo.mem = this.memList[0];
      },
      ofterCallback (val) {
      	this.createNumberInfo.cycleCnt = val;
      },
      bbCallback (val) {
      	this.networkInfo.bandwidth = val;
      },
      diskBlur (event) {
      	//console.log(event);
//    	if(this.basicInfo.diskSize < 15){
//    		this.basicInfo.diskSize = 15;
//    	}
//    	this.basicInfo.diskSizeMin = 15;
      },
      diskFocus (event) {
      	//console.log(event);
      	this.basicInfo.diskSizeMin = 1; 
      },
      _diskBlur (event) {
      	console.log(event);

      	this.basicInfo.diskSizeMin = 15;
      },
      _diskFocus (event) {
      	console.log(event);
      	this.basicInfo.diskSizeMin = 1; 
      },
      getImages (params) {
      		this.seleceCheck = true;
      		getEcmImages(params).then(res => {
      			console.log(res);
      			if(res.status == 200){
      				this.seleceCheck = false;
      				this.osList = res.data.results;
      				if(this.osList.length>0){
