    			return this.basicInfo.cpu+'核 | '+this.basicInfo.mem+'GB';
    		}else{
    			return '--';
    		}
    	},
    	systemData: function () {
    		if(this.basicInfo.disk !== '' && this.basicInfo.diskSize !== ''){
    			return this.disksTypeObj[this.basicInfo.disk]+'   '+this.basicInfo.diskSize+'GB';
    		}else{
    			return '--';
    		}
    	},
    	datas: function () {
