    	eipShow(){
    		let that = this;
    		let fakeVpcs = this.vpcsList.filter(item=>{
    			return item.fake_vpc && (item.fake_vpc == 1)
    		});
    		let hasFakeVpc = fakeVpcs.find(item=>{
    			return item.id == that.networkInfo.vpc
    		});
    		return !hasFakeVpc
    	}
    },
    mounted: function () {
    	window.addEventListener('scroll', this.handleScroll);
    	this.platformsList = this.$store.state.user.regionList;
//    	this.basicInfo.resources = this.platformsList[0].uuid;
      	this.basicInfo.resources = getRegionId();
      	this.changeRegion();
      	console.log(this.platformsList[0].details.storage_type);
      	this.disksTypeObj = {};
      	this.platformsList[0].details.storage_type.map((storage,index,arr) => {
      		if(index == 0){
      			this.basicInfo.disk = this.basicInfo.dataDisks[0].type = storage.type;
      		}
			this.disksTypeObj[storage.type] = storage.name;
		});
    	//this.scaleWidth = this.$refs.scaleInfo.clientWidth / (this.temp-1);
    	
    	//this.getPlatforms({
    		//regionid:'all'
    	//});
    	this.basicInfo.vmName = 'ecm-' + getName();
    	let quotas = this.$store.state.user.user.quotas;
    	if(quotas.vm_limit_each_time){
    		this.createNumberInfo.max = quotas.vm_limit_each_time - 0;
    	}else{
			this.queryQuotas();
		}
    },
    beforeDestroy: function () {
    	window.removeEventListener('scroll', this.handleScroll);
    }
}
</script>

