<template>
	<div class="create_title">
		<el-container>
		  	<el-main class="padding-left">
		  		<el-form :model="ruleForm" size='small' :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm" label-position="left">
					<el-form-item label="地域：" prop="resourceType" required>
  						<el-radio-group v-model="ruleForm.resourceType" size="medium" fill='#f9cc76'>
					      	<el-radio-button class="radio-group-btn" :label="item.uuid" v-for='(item,index) in regionList' :key='index' @change.native='regionChange(item)'>{{item.name}}</el-radio-button>
    					</el-radio-group>
  					</el-form-item>
					<el-form-item label="磁盘名称：" prop="name">
    					<el-input style="width: 180px;" v-model.trim="ruleForm.name"></el-input>
    					<el-tooltip content="不能使用中文,且长度为2-63字符" placement="right"><i class="el-icon-question"></i></el-tooltip>
  					</el-form-item>
  					<!--<el-form-item label="可用分区：" prop="resource">
				    	<el-radio-group v-model="ruleForm.resource">
				      		<el-radio label="可用区1"></el-radio>
				      		<el-radio label="可用区2"></el-radio>
				    	</el-radio-group>
				  	</el-form-item>-->
				  	<!--<el-form-item label="从备份创建：" prop="bksCreate">
					    <el-checkbox-group v-model="ruleForm.bksCreate" @change='selectBks'>
					      	<el-checkbox label="从备份创建" name="type"></el-checkbox>
					    </el-checkbox-group>
				  	</el-form-item>
				  	<el-form-item label="备份：" prop="bksName" v-if='ruleForm.bksCreate'>
    					<el-input style="width: 180px;" disabled='disabled' v-model="ruleForm.bksName"></el-input>
  						<el-button type="warning" @click='chooseBks'>选择备份源</el-button>
					</el-form-item>-->
				  	<el-form-item label="容量(GB)：" prop="size">
    					<el-input-number v-model="ruleForm.size" controls-position="right" @change="sizeChange" @keypress.native="dian190($event)" :step='10' :min="5" :max="2048"></el-input-number>
  					</el-form-item>
  					<el-form-item label="磁盘类型：" prop="storageType">
  						<el-radio-group v-model="ruleForm.storageType" @change="storageChange" size="medium" fill='#f9cc76'>
					      	<el-radio-button class="radio-group-btn" v-for='(storageType,index) in storageTypeList' :key='index' :label="storageType.name" ></el-radio-button>
					      	<!--<el-radio-button class="radio-group-btn" label="高IO"></el-radio-button>
					      	<el-radio-button class="radio-group-btn" label="超高IO"></el-radio-button>-->
    					</el-radio-group>
  					</el-form-item>
  					<el-form-item label="付费方式：" prop="chargeType">
  						<el-radio-group v-model="ruleForm.chargeType" @change="chargeChange" size="medium" fill='#f9cc76'>
					      	<el-radio-button class="radio-group-btn" label="包年/包月" ></el-radio-button>
					      	<!--<el-radio-button class="radio-group-btn" label="按需"></el-radio-button>-->
    					</el-radio-group>
  					</el-form-item>
  					<el-form-item label="数量：" prop="ebsNumber">
    					<el-input-number v-model="ruleForm.ebsNumber" controls-position="right" @change="numChange" @keypress.native="dian190($event)" :min="1" :max="maxCreate"></el-input-number>
  					</el-form-item>
  					<el-form-item label="创建时长：" required>
						<!--<el-slider v-model="ruleForm.broadband" :min="createNumberInfo.broadbandMin" :max="createNumberInfo.broadbandMax" :show-input="createNumberInfo.showInput" @change="timeChange" :format-tooltip="sliderFormatTooltip"></el-slider>
						<div class="slider-mark">
							<div class="scale">
								<span v-for="number in temp" :style="'width:' + 100/(temp-1) + '%'">|</span>
							</div>
							<div class="scaleInfo" ref="scaleInfo">
								<span  v-for="number in temp" :style="{width: 100/(temp-1) + '%',marginLeft:  (number == 1 ?(-100/(temp-1)/2):'') + '%'}" :key="number"><span v-if="number == 12">1年</span>
								<span v-else-if="number == 13">2年</span>
								<span v-else-if="number == 14">3年</span>
								<span v-else>{{number}}个月</span>
								</span>
							</div>
						</div>-->
						<slideScale :cycleCnt="ruleForm.broadband" @ofterCallback="ofterCallback"></slideScale>
					</el-form-item>
				</el-form>
		  	</el-main>
		  	<el-aside width="200px">
		  		<div class="currentInfo">
		  			<h4>当前配置</h4>
			  		<div class="message">
			  			<dl>
				  			<dt>地域：</dt>
				  			<dd>{{ruleForm.regionName ? ruleForm.regionName : '--'}}</dd>
				  		</dl>
			  			<dl>
				  			<dt>磁盘名称：</dt>
				  			<dd>{{ruleForm.name ? ruleForm.name : '--'}}</dd>
				  		</dl>
			  			<dl v-if='ruleForm.bksCreate'>
	
				  			<dt>备份源：</dt>
	
				  			<dd>{{ruleForm.bksName ? ruleForm.bksName : '--'}}</dd>
				  		</dl>
				  		<!--<dl>
				  			<dt>可用分区：</dt>
				  			<dd>{{ruleForm.resource}}</dd>
				  		</dl>	-->
				  		<dl>
				  			<dt>容量：</dt>
				  			<dd>{{ruleForm.size}}</dd>
				  		</dl>
				  		<dl>
				  			<dt>磁盘类型：</dt>
				  			<dd>{{ruleForm.storageType}}</dd>
				  		</dl>	
				  		<dl>
				  			<dt>付费方式：</dt>
				  			<dd>{{ruleForm.chargeType}}</dd>
				  		</dl>
				  		<dl>
				  			<dt>数量：</dt>
				  			<dd>{{ruleForm.ebsNumber}}</dd>
				  		</dl>
				  		<dl>
				  			<dt>创建时长：</dt>
				  			<dd>{{ruleForm.broadbandMsg}}</dd>
				  		</dl>
			  		</div>
			  		<el-button class='prepaid' type="warning" icon='el-icon-check'>预付费</el-button>
			  		<!--<div class="show_money">
			  			<span class=" money_icon">￥</span><span class="money_icon price">0.00</span>元
			  		</div>-->
			  		<!--<p class="link" @click="openHelp">了解计费详情</p>-->
		  		</div>
		  	</el-aside>
		</el-container>
	</div>
</template>

<script>
	import service from '@/utils/request'
	import i18n from '@/lang'
	import BksAttachAction from '@/views/ebs/bksAttachAction'
	import { getName } from '@/utils/shareFunction'
	import { getEbsList } from '@/api/ebs'
	import { getRegionId, setRegionId, SetZoneId, SetVnc } from '@/utils/auth'
	import {getRegionList} from '@/api/login'
	import { queryQuota } from '@/api/vpc'
	import slideScale from '@/components/SlideScale'
	import { nameRule } from '@/utils/validator';
	export default{
		components:{
			BksAttachAction,
			slideScale
		},
		data(){
			var nameCheck = (rule, value, callback) => {
//				let nameRule = /[\u4E00-\u9FA5]+/;
		      	if(value.length > 63 || value.length < 2){
		      		callback(new Error('长度为2-63字符'));
      			}else if(nameRule(value)){
      				callback(new Error('不能使用中文'));
		      	}else{
					let params = {
						status:'',
						page:1,
						name:value,
						attached:'',
					}
			        getEbsList(params).then((res)=>{
	      				let servers = res.data.results;
	      				if(servers.length > 0){
	      					callback(new Error('云硬盘名称重复，请重新输入！'));
	      				}else{
	      					callback();
	      				}
	      			}).catch((error)=>{
	      				console.log(error);
	      			})
	      		}
		    };
			return {
				temp: 14,
				scaleWidth:60,
				maxCreate:0,
				volumeSize_min:0,
				volumeSize_max:0,
				regionList:[],
				storageTypeList:[],
				ruleForm:{
		      		name: '',
		      		bksName:'',
		      		bksId:'',
		      		resourceType:'',
		      		regionName:'',
		      		uuid:'',
//		      		resource:'可用区1',
		      		bksCreate: false,
		      		size:30,
		      		storageType:'普通IO',
		      		dataType:'',
		      		chargeType:'包年/包月',
		      		ebsNumber:1,
		      		broadband:6,
		      		broadbandMsg:'6个月',
		      		price:'0.00',
		      		cycleType:'Y',
		      		cycleCnt:1,
		      	},
		      	createNumberInfo: {
					btnType: 'primary',
					btnIcon: '',
					broadbandMin: 1,
					broadbandMax: 14,
					showInput: true,
					num: 2 ,
				},
				rules:{
		      		name: [
	            		{ required: true, message: '请输入磁盘名称', trigger: 'blur' },
	            		{ min: 1, max: 63, message: '长度在 1 到 63 个字符', trigger: 'blur' },
	            		{ validator: nameCheck, trigger: 'blur' }
	            		
	          		],
	          		resourceType: [
	            		{ required: true, message: '请选择地域', trigger: 'change' }
	          		],
	          		type: [
	            		{ type: 'Boolean', required: false, message: '请至少选择一个活动性质', trigger: 'change' }
	          		],
	          		bksName:[
	          			{ required: true, message: '请输入备份源名称', trigger: 'blur' },
	            		{ min: 3, max: 255, message: '长度在 3 到 255 个字符', trigger: 'blur' }
	          		],
	          		size: [
	            		{ required: true, message: '请选择可用区', trigger: 'change' }
	          		],
	          		storageType: [
	            		{ required: true, message: '请选择可用区', trigger: 'change' }
	          		],
	          		chargeType: [
	            		{ required: true, message: '请选择可用区', trigger: 'change' }
	          		],
	          		ebsNumber: [
	            		{ required: true, message: '请选择可用区', trigger: 'change' }
	          		],
				},
			}
		},
		methods:{
			regionChange(val){
				service.cancle('CancelToken');
				console.log(val);
				this.storageTypeList = [];
				this.ruleForm.regionName = val.name;
				this.ruleForm.uuid = val.uuid;
				this.storageTypeList = val.details.storage_type;
				this.ruleForm.storageType=val.details.storage_type[0].name;
				this.ruleForm.dataType=val.details.storage_type[0].type;
				setRegionId(val.uuid)
				SetZoneId(val.details.zoneId)
				SetVnc(val.details.hasVnc)
			},
			dian190(ev) {
		        let code = window.event ? event.keyCode : event.which;
		        if(code == 46){
		        	event.preventDefault();return;
		        }
		   	},
			getRegionList(){
				let _this = this;
				this.regionList = this.$store.state.user.regionList;
				console.log(this.regionList);
				this.ruleForm.uuid = getRegionId();
				this.ruleForm.resourceType = getRegionId();
				this.regionList.forEach(function(item){
					if(item.uuid == _this.ruleForm.uuid){
						_this.ruleForm.regionName = item.name;
						_this.storageTypeList = item.details.storage_type;
						_this.ruleForm.storageType=item.details.storage_type[0].name;
						_this.ruleForm.dataType=item.details.storage_type[0].type;
					}
				});
			},
			ofterCallback (val) {
	      		this.ruleForm.broadband = val;
	      		if(val == 12){
	      			this.ruleForm.broadbandMsg = '1年';
	      			this.ruleForm.cycleCnt = 1;
	      			this.ruleForm.cycleType = 'Y';
	      		}else if(val == 13){
	      			this.ruleForm.cycleCnt = 2;
	      			this.ruleForm.broadbandMsg = '2年';
	      			this.ruleForm.cycleType = 'Y';
	      		}else if(val == 14){
	      			this.ruleForm.broadbandMsg = '3年';
	      			this.ruleForm.cycleCnt = 3;
	      			this.ruleForm.cycleType = 'Y';
	      		}else{
	      			this.ruleForm.cycleType = 'M';
	      			this.ruleForm.cycleCnt = this.ruleForm.broadband;
	      			this.ruleForm.broadbandMsg = this.ruleForm.broadband + '个月';
	      		}
	      	},
			storageChange(val){
				let chooseType = this.storageTypeList.filter(function(item){
					if(item.name == val){
						return true;
					}
				});
				console.log(chooseType);
				this.ruleForm.storageType=val;
				this.ruleForm.dataType=chooseType[0].type;
			},
			chargeChange(val){
				this.ruleForm.chargeType = val;
			},
			numChange(val){
				this.ruleForm.ebsNumber=val;
			},
//			sliderFormatTooltip (value) {
//	      		if(value == 12){
//	      			this.cycleType = 'Y';
//	      			return '1年';
//	      		}else if(value == 13){
//	      			this.cycleType = 'Y';
//	      			return '2年';
//	      		}else if(value == 14){
//	      			this.cycleType = 'Y';
//	      			return '3年';
//	      		}else{
//	      			this.cycleType = 'M';
//	      			return value+'月';
//	      		}
//	      	},
//			timeChange(val){
//				if(val == 12){
//	      			this.ruleForm.broadbandMsg = '1年';
//	      			this.ruleForm.cycleCnt = 1;
//	      		}else if(val == 13){
//	      			this.ruleForm.cycleCnt = 2;
//	      			this.ruleForm.broadbandMsg = '2年';
//	      		}else if(val == 14){
//	      			this.ruleForm.broadbandMsg = '3年';
//	      			this.ruleForm.cycleCnt = 3;
//	      		}else{
//	      			this.ruleForm.cycleType = 'M';
//	      			this.ruleForm.cycleCnt = this.ruleForm.broadband;
//	      			this.ruleForm.broadbandMsg = this.ruleForm.broadband + '个月';
//	      		}
//			},
			sizeChange(val){
	      		this.ruleForm.size=val;
	      	},
	      	order(){
	      		var basicInfo_valid = false;
	      		this.$refs['ruleForm'].validate((valid) => {
		          if (valid) {
		          	basicInfo_valid = true;
		          	this.$emit('orderResult',true,this.ruleForm);
		          }else{
		          	this.$emit('orderResult',false,'');
		          }
		        });
	      	},
	      	selectBks(val){
	      		if(val && this.$route.query.id){
	      			this.ruleForm.bksName = this.$route.query.name + '(' + this.$route.query.id +')';
					this.ruleForm.bksId = this.$route.query.id;
	      		}
	      	},
	      	queryQuotas(){
		      	queryQuota().then((res)=>{
		          console.log(res.data.results);
		          let Quota = res.data.returnObj;
//		          this.snapshotLimitQuota = Quota.snapshot_limit - 0;
					this.maxCreate = Quota.volume_limit_each_time ? Quota.volume_limit_each_time - 0　:　'';
					this.volumeSize_min =Quota.volume_size_lower_limit　? Quota.volume_size_lower_limit - 0　:　'';
					this.volumeSize_max =　Quota.volume_size_limit　? Quota.volume_size_limit - 0　:　'';
		        }).catch((error)=>{
		          console.log(error)
		        })
		    },
	      	chooseBks(){
	      		let DataOption = [{
	          		prop:'name',
        			label:i18n.common.nameOrID,
        			temp:true,
        			detail:true,
	          	},{
	          		prop:'status',
        			label:i18n.common.status,
        			temp:true,
        			status:true
	          	},{
	          		prop:'size',
        			label:i18n.ebs.capacityGB,
	          	},{
	          		prop:'created',
        			label:i18n.common.createAt,
	          	}];
	          	let SelectOptions =[{
		          	value: '',
		          	label: i18n.common.allStatus
		        }, {
		          	value: 'allocated',
		          	label: i18n.ebs.allocated
		        }, {
		          	value: 'ready',
		          	label: i18n.ebs.ready
		        }];
				let option = {
      				'data':{
      					'option':DataOption,
      					'title':'选择备份',
      					'actionTip':'',
      					'data':'',
      					'selectOptions':SelectOptions,
      					'statusOnOff':true,
      					'optionsValue':'',
      				},
      				'view':BksAttachAction,
      				'form':{}
      			};
      			let _this = this;
      			this.$alertDialog(option).then((res)=>{
  					console.log(res);
  					_this.ruleForm.bksName = res.data.name + '(' + res.data.id +')';
					_this.ruleForm.bksId = res.data.id;
      			})
	      	},
	      	openHelp(){
	      		open("http://www.ctyun.cn/help");
	      	}
		},
		mounted(){
			this.getRegionList();
			if(this.$route.query.id){
				this.ruleForm.bksCreate = true;
				this.ruleForm.bksName = this.$route.query.name + '(' + this.$route.query.id +')';
				this.ruleForm.bksId = this.$route.query.id;
			}
			this.ruleForm.name = 'ebs-' + getName();
			let quotas = this.$store.state.user.user.quotas;
			console.log(quotas);
			if(quotas.volume_limit_each_time && quotas.volume_size_lower_limit && quotas.volume_size_limit){
				this.maxCreate = quotas.volume_limit_each_time - 0;
				this.volumeSize_min = quotas.volume_size_lower_limit - 0;
				this.volumeSize_max = quotas.volume_size_limit - 0;
			}else{
				this.queryQuotas();
			}
		}
	}
</script>

<style lang="scss" scoped="scoped">
	.create_title{
		padding: 0 0px 20px 0px;
		min-height: 260px;
		.padding-left{
			padding-left: 0px;
		}
		.prepaid{
			background-color: #f9cc76;
			border-color: #f9cc76;
		}
		.el-main{
			padding-left: 20px;
			background-color: #fff;
			padding-right: 0px;
			margin-right: 20px;
		}
		.el-aside{
			background-color: #fff;
		}
		.currentInfo{
			background-color: #fff;
		    height: 360px;
		    padding: 10px;
		    padding-top: 18px;
		    border-radius: 3px;
		    .el-button{
		    	padding: 7px 15px;
		    }
		}
		.el-radio-group{
			.el-radio-button{
				margin-right: 10px;
				
			}
		}
		/*width: 300px;*/
		.message{
			margin-top: 20px;
			dl{
				font-size: 12px;
				color: #333333;
				padding-bottom: 15px; 
				&:after{
					display: table;
					content: '';
				}
				dt{
					float: left;
					font-size: 12px;
					color: #333333;
				}
				dd{
					white-space: nowrap;
					overflow: hidden;
					text-overflow: ellipsis;
					font-size: 13px;
				}
			}
		}	
		.show_money{
			margin-top: 30px;
			.money_icon{
				color: #f9cd76;
				font-size: 17px;
				font-weight: 600;
			}
		}
		.link{
			color: #3dc7fa;
			cursor: pointer;
			font-size: 12px;
			margin-top: 15px;
		}
	}
</style>
<style lang="scss">
	.create_title{
		/*.el-input--small .el-input__inner{
			height: 30px;
		}
		.el-form-item--small .el-form-item__content, 
		.el-form-item--small .el-form-item__label{
			height: 30px;
		}*/
		.el-form-item--small{
			label{
				text-align: left;
			}
		}
		.radio-group-btn > span {
		    border: 1px solid #dcdfe6;
		    border-radius: 4px!important;
    		background: #ebedf2;
		}
		.el-form-item__content{
			.el-radio-group{
				.el-radio-button__inner{
					padding: 7px 15px;
				}
			}
		}
		/*.slider-mark {
			margin-right: 160px;
		    margin-top: -15px;
		    color: #afbfda;
		}
		.slider-mark > .scale {
			height: 20px;
		}
		.slider-mark > .scale > span {
			display: inline-block;
			height:100%;
		}
		.slider-mark > .scale > span:last-child {
			width: auto !important;
			display: inline;
		    float: right;
		    margin-top: -32px;
		}
		.slider-mark > .scaleInfo {
			font-size: 12px;
		}
		.slider-mark > .scaleInfo > span{
			display: inline-block;
			text-align: center;
		}
		.slider-mark > .scaleInfo > span:last-child {
		    float: right;
		    margin-top: 0px;
		    margin-right: -25px;
		}*/
	}
</style>


