<template>
	<div class='monitor_dashboard'>
		<div class="header">
			<h4 class="font_14_3">{{title}}</h4>
			<div class="operation">
				<label class="font_12_6">监控面板：</label>
				<el-select v-model="selectBoard_id" placeholder="请选择" size="small" @change="boardChange">
				    <el-option v-for="item in boardList" :key="item.id" :label="item.name" :value="item.id">
				    </el-option>
				</el-select>
				 <el-button type="success" size="small" @click.stop="updateBoard">重命名</el-button>
				 <el-button size="small" @click.stop="delBoard">删除</el-button>
				 <el-button icon="el-icon-refresh" size="small" @click.stop="_getBoard"></el-button>
				 <el-button type="primary" size="small" class="f_right" @click.stop="createBoard">创建监控面板</el-button>
			</div>
			<p class="font_12_6">单击“+”配置监控项可以添加监控视图至选择的监控面板，您还可以添加{{30-collectList.length}}个监控视图</p>
		</div>
		<div class="main m_t_20">
			<el-row :gutter="20">
			  <el-col :span="8" v-for="(collect,index) in collectList" :key="index">
			  	<div class="grid-content bg-line">
			  		<echarts :id="collect.id+''" width='370px' height='280px' :monitorData="collect" :isCollect=true @maximization="openMax" @delItem="delMonitorConfig(collect)" @updateItem="updateMonitorConfig(collect)"></echarts>
			  	</div>
			  </el-col>
			  <el-col :span="8">
			  	<div class="grid-content bg-line add_config">
			  		<i class="el-icon-plus" style="cursor: pointer;" @click.stop="addMonitorConfig"></i>
			  	</div>
			  </el-col>
			</el-row>
		</div>
		<div v-if="dialogMaxEchartVisible">
			<el-dialog :visible.sync="dialogMaxEchartVisible" :showClose=false class="maxEchartContent">
			  	<echarts :id="maxEchart.id+'__'" width='700px' height='400px' :monitorData="maxEchart" @minimization="closeMin" @getMonitor="getMonitor" :isMax=true></echarts>
			</el-dialog>
		</div>
	</div>
</template>

<script>
import echarts from '@/components/Echarts'
import editBoard from './dialogViews/editBoard'
import del from './dialogViews/del'
import monitorConfig from './dialogViews/monitorConfig'
import { metric } from './config/monitorConfig'
import { getBoard,addBoard,updateBoard,delBoard,getCollect,addCollect,updateCollect,delCollect,getMonitorData } from '@/api/monitor'
	export default {
		components:{
			echarts,
		},
		data (){
			return {
				title :'监控面板',
				selectBoard: '',
				selectBoard_id: '',
				boardList: [],
				collectList: [],
				monitorData: {
					metric_name: '网络连接数',
					data: {
						xAxis_data: ['周一','周二','周三','周四','周五','周六','周日'],
						chartData: [220, 332, 901, 534, 1290, 130, 1320]
					},
					id: new Date().getTime()+'',
				},
				dialogMaxEchartVisible: false,
				maxEchart: {},
			}
		},
		created(){
			this._getBoard();
		},
		mounted(){
			
		},
		methods:{
			machiningCollectData(){
				this.collectList.map((collect,index,array) => {
					this.$set(collect,'series',[]);
					this.$set(collect,'xAxis_data',[]);
					let content = JSON.parse(collect.content);
					this.$set(collect,'metric_name',collect.title);
					this.$set(collect,'region_name',this._.find(this.$store.state.user.regionList,{uuid:content[0].regionid}).name);
					for(let i=0; i<content.length; i++){
						getMonitorData({
							uuid: content[i].uuid,
          					metric_name: content[i].item,
					        period: 300,
					        filter: 'avg',
					        time_range: '3h',
					        regionid: content[i].regionid,
						}).then(res => {
							if(res.data.status == 0){
								let tempData = this.machiningMonitorData(res.data.data,'3h');
								collect.series.push({
									name: content[i].ser+'-'+content[i].uuidName+'-'+metric.hash[content[i].item]+'('+metric.hashUnit[content[i].item]+')',
					                type:'line',
					                smooth: true,
					                connectNulls: true,
					                data: tempData.chartData,
								});
								collect.xAxis_data = tempData.xAxis_data;
							}else{
								collect.series.push({
									name: content[i].ser+'-'+content[i].uuidName+'-'+metric.hash[content[i].item],
					                type:'line',
					                smooth: true,
					                connectNulls: true,
					                data: []
								});
								this.$notify.info({
						          title: '消息',
						          message: collect.title+'-'+content[i].ser+'-'+content[i].uuidName+'-'+metric.hash[content[i].item]+':'+res.data.msg
						        });
							}
						},error => {

						});
					}
				});
			},
			getMonitor(params,callback){
				let content = JSON.parse(this.maxEchart.content);
				this.maxEchart.series = [];
				this.maxEchart.xAxis_data = [];
				for(let i=0; i<content.length; i++){
						getMonitorData({
							uuid: content[i].uuid,
          					metric_name: content[i].item,
					        period: params.period,
					        filter: params.filter,
					        time_range: params.time_range,
					        time_from: params.time_from,
          					time_till: params.time_till,
					        regionid: content[i].regionid,
						}).then(res => {
							if(res.data.status == 0){
								let tempData = this.machiningMonitorData(res.data.data,params.time_range||params.time_from);
								this.maxEchart.series.push({
									name: content[i].ser+'-'+content[i].uuidName+'-'+metric.hash[content[i].item]+'('+metric.hashUnit[content[i].item]+')',
					                type:'line',
					                smooth: true,
					                connectNulls: true,
					                data: tempData.chartData,
								});
								this.maxEchart.xAxis_data = tempData.xAxis_data;
								callback&&callback();
							}else{
								this.maxEchart.series.push({
									name: content[i].ser+'-'+content[i].uuidName+'-'+metric.hash[content[i].item],
					                type:'line',
					                smooth: true,
					                connectNulls: true,
					                data: []
								});
								//callback&&callback();
								this.$notify.info({
						          title: '消息',
						          message: this.maxEchart.title+'-'+content[i].ser+'-'+content[i].uuidName+'-'+metric.hash[content[i].item]+':'+res.data.msg
						        });
							}
						},error => {

						});
				}
			},
			machiningMonitorData(data,time_range){
				var xAxis_data = [],
		            chartData = [],
		            len = data.v_list.length;
		        for (let i = 0; i < len; i++) {
		            var t = new Date(data.v_list[i].timestamp);
		            if(!time_range || time_range == '1h' || time_range == '3h' || time_range == '12h' || time_range == '1d'){
		            	xAxis_data.push(t.Format("hh:mm"));
		            }else{
		            	xAxis_data.push(t.Format("yyyy-MM-dd hh:mm"));
		            }
		            if(typeof data.v_list[i].value == 'string'){
		                chartData.push('无');
		            }else{
		                chartData.push(data.v_list[i].value);
		            }
		        }
		       	return {xAxis_data,chartData};
			},
			_getBoard(){
				/*let loading = this.$loading({
				    lock: true,
				    text: '获取面板中...',
				    spinner: 'el-icon-loading',
				    background: 'rgba(0, 0, 0, 0.7)'
				});*/
				getBoard({regionid:'all'}).then(res => {
				    //loading.close();
					//loading = null;
					if(res.data.status == 0){
						this.boardList = res.data.data;
						if(this.boardList.length > 0){
							if(this.selectBoard_id == ''){
								this.selectBoard_id = this.boardList[0].id;
							}
							this.getMonitorConfig();
						}else{
							this.selectBoard_id = '';
							this.collectList = [];
						}
					}else{
						this.$message({
							message: res.msg || res.data.msg || '获取监控面板失败',
							type:'error'
						});
					}
				},error => {
					//loading.close();
					//loading = null;
				});
			},
			createBoard(){
				var data = {
					data: [],
					title: '创建监控面板',
				};
				var obj = {
					data,
					view: editBoard,
					form: {boardName:''}
				};
				this.$alertDialog(obj).then((formData) => {
					let loading = this.$loading({
			          lock: true,
			          text: '拼命创建中...',
			          spinner: 'el-icon-loading',
			          background: 'rgba(0, 0, 0, 0.7)'
			        });
					addBoard({name:formData.boardName,regionid:'all'}).then(res => {
						loading.close();
						if(res.data.status == 0){
							this.boardList.push({
								id: res.data.data.id,
								name: formData.boardName,
								os_id: 'all'
							});
							this.$message({
								message: '创建监控面板成功',
								type:'success'
							});
						}else{
							this.$message({
								message: res.msg || res.data.msg || '创建监控面板失败',
								type:'error'
							});
						}
						loading = null;
					},error => {
						loading.close();
						loading = null;
					});
				},error => {
					loading.close();
					loading = null;
				});
			},
			updateBoard(){
				if(this.selectBoard_id === ''){
					this.$message({
						message: '请在下拉框中选择您要修改的监控面板',
						type:'warning'
					});
				}
				var selectItem = this._.find(this.boardList,{id:this.selectBoard_id});
				var data = {
					data: this.$store.state.user.regionList,
					title: '修改面板名称',
					isUpdate: true,
				};
				var obj = {
					data,
					view: editBoard,
					form: {boardName:selectItem.name}
				};
				this.$alertDialog(obj).then(formData => {
					let loading = this.$loading({
			          lock: true,
			          text: '拼命修改中...',
			          spinner: 'el-icon-loading',
			          background: 'rgba(0, 0, 0, 0.7)'
			        });
					updateBoard({id:selectItem.id,name:formData.boardName,regionid:'all'}).then(res => {
						loading.close();
						if(res.data.status == 0){
							selectItem.name = formData.boardName;
							this.$message({
								message: '重命名监控面板成功',
								type:'success'
							});
						}else{
							this.$message({
								message: res.msg || res.data.msg || '重命名监控面板失败',
								type:'error'
							});
						}
						loading = null;
					},error => {
						loading.close();
						loading = null;
					});
				},error => {
					loading.close();
					loading = null;
				});
			},
			delBoard(){
				if(this.selectBoard_id === ''){
					this.$message({
						message: '请在下拉框中选择您要删除的监控面板',
						type:'warning'
					});
				}
				var selectItem = this._.find(this.boardList,{id:this.selectBoard_id});
				var data = {
					title: '删除监控面板',
					actionTip: '删除监控面板将会删除该面板上所有监控项，确定要删除吗？',
					delName: selectItem.name,
				};
				var obj = {
					data,
					view: del,
				};
				this.$alertDialog(obj).then(formData => {
					let loading = this.$loading({
			          lock: true,
			          text: '拼命删除中...',
			          spinner: 'el-icon-loading',
			          background: 'rgba(0, 0, 0, 0.7)'
			        });
					delBoard({id:selectItem.id,regionid:'all'}).then(res => {
						loading.close();
						if(res.data.status == 0){
							this.selectBoard_id = '';
							this._getBoard();
							this.$message({
								message: '删除监控面板成功',
								type:'success'
							});
						}else{
							this.$message({
								message: res.msg || res.data.msg || '删除监控面板失败',
								type:'error'
							});
						}
						loading = null;
					},error => {
						loading.close();
						loading = null;
					});
				},error => {
					loading.close();
					loading = null;
				});
			},
			getMonitorConfig(){
				let loading = this.$loading({
			        lock: true,
			        text: '获取配置项中...',
			        spinner: 'el-icon-loading',
			        background: 'rgba(0, 0, 0, 0.7)'
			    });
				getCollect({cb_id: this.selectBoard_id, regionid: 'all'}).then(res => {
					loading.close();
					loading = null;
					if(res.data.status == 0){
						this.collectList = res.data.data;
						this.machiningCollectData();
					}else{
						this.$message({
							message: res.msg || res.data.msg || '获取收藏列表失败',
							type:'error'
						});
					}
				},error => {
					loading.close();
					loading = null;
				});
			},
			updateMonitorConfig(item){
				var data = {
					data: this.$store.state.user.regionList,
					isUpdate: true,
					title: '配置监控项',
					num_index: 5,
				};
				var obj = {
					data,
					view: monitorConfig,
					form: {cid: item.id,regionid: JSON.parse(item.content)[0].regionid,title:item.title,content:item.content}
				};
				this.$alertDialog(obj).then(formData => {
					let loading = this.$loading({
			          lock: true,
			          text: '拼命修改中...',
			          spinner: 'el-icon-loading',
			          background: 'rgba(0, 0, 0, 0.7)'
			        });
					updateCollect(formData).then(res => {
						loading.close();
						loading = null;
						if(res.data.status == 0){
							this.getMonitorConfig();
							this.$message({
								message: '修改监控项成功',
								type:'success'
							});
						}else{
							this.$message({
								message: res.msg || res.data.msg || '修改监控项失败',
								type:'error'
							});
						}
					},error => {
						loading.close();
						loading = null;
					});
				},error => {
					loading.close();
					loading = null;
				});
			},
			delMonitorConfig(item){
				var data = {
					title: '删除监控项',
					actionTip: '确定要删除以下监控项吗？',
					delName: item.title,
				};
				var obj = {
					data,
					view: del,
				};
				this.$alertDialog(obj).then(formData => {
					let loading = this.$loading({
			          lock: true,
			          text: '拼命修改中...',
			          spinner: 'el-icon-loading',
			          background: 'rgba(0, 0, 0, 0.7)'
			        });
					delCollect({cid:item.id,regionid:'all'}).then(res => {
						loading.close();
						loading = null;
						if(res.data.status == 0){
							this.getMonitorConfig();
							this.$message({
								message: '删除监控项成功',
								type:'success'
							});
						}else{
							this.$message({
								message: res.msg || res.data.msg || '删除监控项失败',
								type:'error'
							});
						}
					},error => {
						loading.close();
						loading = null;
						this.$message({
							message: error.msg || error.data.msg || '删除监控项失败',
							type:'error'
						});
					});
				},error => {
					loading.close();
					loading = null;
				});
			},
			boardChange(){
				this.getMonitorConfig();
			},
			addMonitorConfig(){
				if(this.selectBoard_id == ''){
					this.$message('请选择下拉框监控面板或者添加监控面板后添加配置项');
					return;
				}else if(this.collectList.length >= 30){
					this.$message('每个监控面板最多添加30个监控视图');
					return;
				}
				var data = {
					data: this.$store.state.user.regionList,
					//isUpdate: true,
					title: '配置监控项',
					num_index: 5,
				};
				var obj = {
					data,
					view: monitorConfig,
					form: {cb_id: this.selectBoard_id,regionid: '',title:'',content:''}
				};
				this.$alertDialog(obj).then(formData => {
					let loading = this.$loading({
			          lock: true,
			          text: '拼命添加中...',
			          spinner: 'el-icon-loading',
			          background: 'rgba(0, 0, 0, 0.7)'
			        });
					addCollect(formData).then(res => {
						loading.close();
						loading = null;
						if(res.data.status == 0){
							this.getMonitorConfig();
							this.$message({
								message: '添加监控项成功',
								type:'success'
							});
						}else{
							this.$message({
								message: res.msg || res.data.msg || '添加监控项失败',
								type:'error'
							});
						}
					},error => {
						loading.close();
						loading = null;
					});
				},error => {
					loading.close();
					loading = null;
				});
			},
			openMax(monitorData){
				this.maxEchart = JSON.parse(JSON.stringify(monitorData));
				this.dialogMaxEchartVisible = true;
			},
			closeMin(){
				this.dialogMaxEchartVisible = false;
			},
		},
		computed: {
			
		}
	}
</script>

<style scoped lang="scss">
.monitor_dashboard{
    height: 100%;
	.header {
		background: #fff;
		height: 100px;
		padding: 20px;
	}
	.m_t_20 {
		margin-top: 20px;
	}
	.font_14_3 {
		font-size: 14px;
    	color: #333;
	}
	.font_12_6 {
		font-size: 12px;
    	color: #666;
	}
	.operation {
		padding: 20px 0;
	}
	.f_right {
		float: right;
	}
	.main {

	}
	.bg-line {
		width: 100%;
		height: 280px;
		background: #fff;
		margin-bottom: 20px;
	}
	.add_config {
		text-align: center;
	    line-height: 280px;
	    font-size: 50px;
	    color: #f0f2f7;
	    background-color: #fbfcfd;
	}
}
</style>
<style lang="scss">
.monitor_dashboard {
  .el-button--primary {
      background-color: #3ac0f1;
      border-color: #3ac0f1;
  }
  .maxEchartContent {
	.el-dialog__header{
		padding: 0;
	}
	.el-dialog__body{
		padding: 0 10px;
		height: 450px;
	}
 }
 .el-button--success{
 	background-color: #56d6c4;
    border-color: #56d6c4;
 }
}
</style>


